import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';
import { Topaz } from "../../../../../../../database/characters";

const TopazDebuffs = () => {

    const {eidolon, skillLevel, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    const topaz = new Topaz();

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    const [prevSkillLevel, setPrevSkillLevel] = useState(skillLevel);

    useEffect(() => {

        if(eidolon < 6 && characterBuffStates[3]) {
            updateStatesFromJson(E6Buff, true);
            characterBuffStates[3] = false;
        }

    }, [eidolon]);

    useEffect(() => {

        if(characterBuffStates[2]) {
            const oldSkillBuff = {
                effect: {
                    followUpVulnerability: topaz.skillAttack(prevSkillLevel).vulnerability,
                }
            }

            updateStatesFromJson(oldSkillBuff, true);
            
            const newSkillBuff = {
                effect: {
                    followUpVulnerability: topaz.skillAttack(skillLevel).vulnerability,
                }
            }

            updateStatesFromJson(newSkillBuff, false);
        }

        setPrevSkillLevel(skillLevel);

    }, [skillLevel])

    const skillBuff = {
        effect: {
            followUpVulnerability: topaz.skillAttack(skillLevel).vulnerability,
        }
    }

    const E6Buff = {
        effect: {
            firePen: 0.10,
        }
    }

    return (
        <>

            <FormControlLabel
                className='header'
                control={<Checkbox checked={characterBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, skillBuff)} />}
                label={topaz.skillDescription().name}
            />
            <div>{topaz.skillSimple(skillLevel).description}</div>

            {eidolon >= 6 &&
                <>
                    <FormControlLabel
                        className='header'
                        control={<Checkbox checked={characterBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, E6Buff)} />}
                        label={"(E6) " + topaz.E6().name}
                    />
                    <div>{topaz.E6().description}</div>
                </>
            }

        </>
    )
}

export const TopazGlobalDebuffs = (props) => {

    const topazBuffStates = props.buffStates;
    const setTopazBuffStates = props.setBuffStates;
    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    const [skillLevel, setSkillLevel] = useState(0);

    useEffect(() => {

        if(topazBuffStates[0]) {
            updateStatesFromJson(skillBuff, true);
            topazBuffStates[0] = false;
        }

        console.log(skillBuff);

        setDone(!done);
    }, [characterSwitched]);

    const { updateStatesFromJson } = useContext(globalData);
    const topaz = new Topaz();

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...topazBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setTopazBuffStates(updatedStates);
    }

    const [prevSkillLevel, setPrevSkillLevel] = useState(skillLevel);

    useEffect(() => {

        if(topazBuffStates[0]) {
            const oldSkillBuff = {
                effect: {
                    followUpVulnerability: topaz.skillAttack(prevSkillLevel).vulnerability,
                }
            }

            updateStatesFromJson(oldSkillBuff, true);
            
            const newSkillBuff = {
                effect: {
                    followUpVulnerability: topaz.skillAttack(skillLevel).vulnerability,
                }
            }

            updateStatesFromJson(newSkillBuff, false);
        }

        setPrevSkillLevel(skillLevel);

    }, [skillLevel])

    const skillBuff = {
        effect: {
            followUpVulnerability: topaz.skillAttack(skillLevel).vulnerability,
        }
    }

    const updateSkillLevel = (event) => {
        setSkillLevel(event.target.value);
    }

    return (
        <>

            <FormControlLabel
                className='header'
                control={<Checkbox checked={topazBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, skillBuff)} />}
                label={topaz.skillDescription().name}
            />
            <div>{topaz.skillSimple(skillLevel).description}</div>
            <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                <div style={{display: "flex"}}>
                    <div>Skill Level</div>
                    <select className="levels buff-input" style={{width: "45%"}} value={skillLevel} onChange={updateSkillLevel}>
                        <option value={0}>1</option>
                        <option value={1}>2</option>
                        <option value={2}>3</option>
                        <option value={3}>4</option>
                        <option value={4}>5</option>
                        <option value={5}>6</option>
                        <option value={6}>7</option>
                        <option value={7}>8</option>
                        <option value={8}>9</option>
                        <option value={9}>10</option>
                        <option value={10}>11</option>
                        <option value={11}>12</option>
                    </select>
                </div>
            </div>

        </>
    )
}

export default TopazDebuffs;