export class Trend {
    constructor() {
        this.stats = [
            [1, 16.8, 21, 48],
            [20, 64.68, 80.85, 185],
            [20, 84.84, 106.05, 242],
            [40, 162.12, 202.65, 463],
            [40, 189, 236.25,  540],
            [50, 214.2, 267.75,  612],
            [50, 241.08, 301.35, 689],
            [60, 266.28, 332.85, 761],
            [60, 293.16, 366.45, 838],
            [70, 318.36, 397.95, 910],
            [70, 345.24, 431.55, 986],
            [80, 370.44, 463.05, 1058],
        ];
        
        this.name = "Trend of the Universal Market";
        this.id = "trend";
        this.path = "Preservation";
        this.rarity = "fourStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text"> DEF </div>
                <div> by </div>
                <div className="buff-text">{((0.16 + imposition * 0.02) * 100).toFixed(1)}% </div>
                <div> When the wearer is attacked, there is a </div>
                <div className="buff-text">{((1 + imposition * 0.05) * 100).toFixed(1)}% </div>
                <div> base chance to Burn the enemy. For each turn, the wearer deals DoT that is equal to </div>
                <div className="buff-text">{((0.4 + imposition * 0.1) * 100).toFixed(1)}% </div>
                <div> of the wearer's DEF for 2 turn(s). </div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{
                defPercent: 0.16 + imposition * 0.02,
            },
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                // id: "adve1",
                // name: this.name,
                // description: (
                //     <div className="buff-description">
                //         <div> If the wearer's current HP is </div>
                //         <div className="buff-text"> lower than 50% </div>
                //         <div> increases their </div>
                //         <div className="buff-text"> DEF </div>
                //         <div> by a further </div>
                //         <div className="buff-text">{((0.16 + imposition * 0.04) * 100).toFixed(1)}% </div>
                //     </div>
                // ),
                // effect: {},
                // type: "buff",
                // hasStack: false,
                // hasInput: false,
            },
        }
    }
}

export default Trend;