export class SecretVow {
    constructor() {
        this.stats = [
            [1,  21.6,   12,     48],
            [20, 83.16,  46.2,  185],
            [20, 109.08,  60.6,  242],
            [40, 208.44, 115.8, 463],
            [40, 243,    135, 540],
            [50, 275.4,  153, 612],
            [50, 309.96, 172.2, 689],
            [60, 342.36, 190.2, 761],
            [60, 376.92, 209.4, 838],
            [70, 409.32, 227.4, 910],
            [70, 443.88, 246.6, 986],
            [80, 476.28, 264.6, 1058],
        ];
        
        this.name = "A Secret Vow";
        this.path = "Destruction";
        this.id = "secretvow";
        this.rarity = "fourStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases DMG dealt by the wearer by </div>
                <div className="buff-text"> {((0.2 + imposition * 0.05) * 100).toFixed(1)}%.</div> 
                <div> The wearer also deals an extra </div> 
                <div className="buff-text"> {((0.2 + imposition * 0.05) * 100).toFixed(1)}%.</div> 
                <div> of DMG to enemies whose current HP percentage is equal to or higher than the wearer's current HP percentage. </div> 
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "silenceb",
            name: this.name,
            effect:{
                iceDmg: (0.2 + imposition * 0.05),
                quantumDmg: (0.2 + imposition * 0.05),
                fireDmg: (0.2 + imposition * 0.05),
                lightningDmg: (0.2 + imposition * 0.05),
                windDmg: (0.2 + imposition * 0.05),
                physicalDmg: (0.2 + imposition * 0.05) ,
                imaginaryDmg: (0.2 + imposition * 0.05),
            },
            type: "buff",
            hasStack: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "silence0",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div>Increases DMG dealt by the wearer by </div>
                        <div className="buff-text"> {((0.2 + imposition * 0.05) * 100).toFixed(1)}%.</div> 
                        <div> The wearer also deals an extra </div> 
                        <div className="buff-text"> {((0.2 + imposition * 0.05) * 100).toFixed(1)}%.</div> 
                        <div> of DMG to enemies whose current HP percentage is equal to or higher than the wearer's current HP percentage. </div>  
                    </div>
                ),
                effect: {
                    iceDmg: (0.2 + imposition * 0.05),
                    quantumDmg: (0.2 + imposition * 0.05),
                    fireDmg: (0.2 + imposition * 0.05),
                    lightningDmg: (0.2 + imposition * 0.05),
                    windDmg: (0.2 + imposition * 0.05),
                    physicalDmg: (0.2 + imposition * 0.05) ,
                    imaginaryDmg: (0.2 + imposition * 0.05),
                },
                type: "buff",
                hasStack: false,

                hasInput: false,
            },
        }
    }
}

export default SecretVow;