                // description: (
                //     <div className="buff-description">
                //         <div> When the wearer's HP is reduced, all allies' DMG dealt increases by </div>
                //         <div className="buff-text">{((0.9 + imposition * 0.015) * 100).toFixed(1)}% </div>
                //     </div>
                // ),
                // effect: {
                //     iceDmg: 0.9 + imposition * 0.015,
                //     quantumDmg: 0.9 + imposition * 0.015,
                //     fireDmg: 0.9 + imposition * 0.015,
                //     lightningDmg: 0.9 + imposition * 0.015,
                //     windDmg: 0.9 + imposition * 0.015,
                //     physicalDmg: 0.9 + imposition * 0.015,
                //     imaginaryDmg: 0.9 + imposition * 0.015,
