import { Pan_Galagtic } from '../../../../database/planars';
import { globalData } from '../../../../globalData';
import React, { useContext, useState, useEffect } from 'react';
import { Checkbox,  FormControlLabel } from '@mui/material';

function PanGalacticBuffs() {

    const {
        effectHitRate,
        updateStatesFromJson,
        planarBuffStates,
        setPlanarBuffStates,
        ornamentPiece,
    } = useContext(globalData);

    const ApplyPlanarBuffFromCheckBox = (index, buffValue) => {
        const updatedStates = [...planarBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setPlanarBuffStates(updatedStates);
    }

    useEffect(() => {

        if(planarBuffStates[1]) {
            updateStatesFromJson(ornamentPiece.planerEffect()[1], true);
            
        }
        ornamentPiece.setEHR(effectHitRate);

        if(planarBuffStates[1]) {
            updateStatesFromJson(ornamentPiece.planerEffect()[1], false);
        }

    }, [effectHitRate])

    return (
        <>
            <FormControlLabel
                className='header'
                control={<Checkbox checked={planarBuffStates[1] || false} onChange={() => ApplyPlanarBuffFromCheckBox(1, ornamentPiece.planerEffect()[1])} />}
                label={ornamentPiece.planerEffect()[1].description}
            />
        </>
    )
}

export default PanGalacticBuffs;