import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';

import { Jingliu } from "../../../../../../../database/characters";

const JingliuBuffs = () => {

    const {level, character, eidolon, characterBuffStates, atk, talentLevel, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    
    const jingliu = new Jingliu();
    const [hpLost, setHpLost] = useState(0.0);
    const [prevLevel, setPrevLevel] = useState(level);
    const [prevAtk, setPrevAtk] = useState(0);
    const [prevTalentLevel, setPrevTalentLevel] = useState(talentLevel);
    const [prevEidolon, setPrevEidolon] = useState(eidolon);
    const [E2ON, setE2ON] = useState(false);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];

        if(index === 0 && updatedStates[index] && level >= 3) {
            updateStatesFromJson(A2Buff, false);
        }else if(index === 0 && !updatedStates[index] && level >= 3){
            updateStatesFromJson(A2Buff, true);
        }

        if(index === 0 && updatedStates[index] && level >= 10) {
            updateStatesFromJson(A6Buff, false);
        }else if(index === 0 && !updatedStates[index] && level >= 10){
            updateStatesFromJson(A6Buff, true);
        }

        if(index === 0 && updatedStates[index] && eidolon >= 6) {
            updateStatesFromJson(E6Buff, false);
        }else if(index === 0 && !updatedStates[index] && eidolon >= 6){
            updateStatesFromJson(E6Buff, true);
        }

        setCharacterBuffStates(updatedStates);
    }

    useEffect(() => {

        if(eidolon < 1 && characterBuffStates[1]) {
            updateStatesFromJson(E1Buff, true);
            characterBuffStates[1] = false;
        }



        if(eidolon < 4 && characterBuffStates[0] && prevEidolon >= 4) {
            let oldTalentBuff = {
                effect: {
                    atkFlat: 6.3 * hpLost * 0.04,
                    critRate: jingliu.talent(talentLevel).critRate,
                }
            }

            if(oldTalentBuff.effect.atkFlat > (jingliu.talent(talentLevel).cap + 0.3) * prevAtk) {
                oldTalentBuff.effect.atkFlat = (jingliu.talent(talentLevel).cap + 0.3) * prevAtk;
            }

            updateStatesFromJson(oldTalentBuff, true);

            let tempTalentBuff = {
                effect: {
                    atkFlat: 5.4 * hpLost * 0.04,
                    critRate: jingliu.talent(talentLevel).critRate,
                },
            }

            if(tempTalentBuff.effect.atkFlat > jingliu.talent(talentLevel).cap * atk) {
                tempTalentBuff.effect.atkFlat = jingliu.talent(talentLevel).cap * atk;
            }
            
            updateStatesFromJson(tempTalentBuff, false);
        }

        if(eidolon >= 4 && characterBuffStates[0] && prevEidolon < 4) {
            let oldTalentBuff = {
                effect: {
                    atkFlat: 5.4 * hpLost * 0.04,
                    critRate: jingliu.talent(talentLevel).critRate,
                }
            }

            if(oldTalentBuff.effect.atkFlat > jingliu.talent(talentLevel).cap * prevAtk) {
                oldTalentBuff.effect.atkFlat = jingliu.talent(talentLevel).cap * prevAtk;
            }

            updateStatesFromJson(oldTalentBuff, true);

            let tempTalentBuff = {
                effect: {
                    atkFlat: 6.3 * hpLost * 0.04,
                    critRate: jingliu.talent(talentLevel).critRate,
                },
            }

            if(tempTalentBuff.effect.atkFlat > (jingliu.talent(talentLevel).cap + 0.3) * atk) {
                tempTalentBuff.effect.atkFlat = (jingliu.talent(talentLevel).cap + 0.3) * atk;
            }

            updateStatesFromJson(tempTalentBuff, false);
        }

        if(eidolon >= 6 && characterBuffStates[0] && prevEidolon < 6) {
            updateStatesFromJson(E6Buff, false);
        }

        if(eidolon < 6 && characterBuffStates[0] && prevEidolon >= 6) {
            updateStatesFromJson(E6Buff, true);
        }

        setPrevEidolon(eidolon);

    }, [eidolon]);

    useEffect(() => {

        if(characterBuffStates[0]) {
            let oldTalentBuff = {
                effect: {
                    atkFlat: 5.4 * hpLost * 0.04,
                    critRate: jingliu.talent(prevTalentLevel).critRate,
                }
            }

            if(oldTalentBuff.effect.atkFlat > jingliu.talent(prevTalentLevel).cap * atk) {
                oldTalentBuff.effect.atkFlat = jingliu.talent(prevTalentLevel).cap * atk;
            }

            if(eidolon >= 4) {
                oldTalentBuff = {
                    effect: {
                        atkFlat: 6.3 * hpLost * 0.04,
                        critRate: jingliu.talent(prevTalentLevel).critRate,
                    }
                }
        
                if(oldTalentBuff.effect.atkFlat > (jingliu.talent(prevTalentLevel).cap + 0.3) * atk) {
                    oldTalentBuff.effect.atkFlat = (jingliu.talent(prevTalentLevel).cap + 0.3) * atk;
                }
                
            }

            updateStatesFromJson(oldTalentBuff, true);

            let newTalentBuff = {
                effect: {
                    atkFlat: 5.4 * hpLost * 0.04,
                    critRate: jingliu.talent(talentLevel).critRate,
                }
            }

            if(newTalentBuff.effect.atkFlat > jingliu.talent(talentLevel).cap * atk) {
                newTalentBuff.effect.atkFlat = jingliu.talent(talentLevel).cap * atk;
            }

            if(eidolon >= 4) {
                newTalentBuff = {
                    effect: {
                        atkFlat: 6.3 * hpLost * 0.04,
                        critRate: jingliu.talent(talentLevel).critRate,
                    }
                }
        
                if(newTalentBuff.effect.atkFlat > (jingliu.talent(talentLevel).cap + 0.3) * atk) {
                    newTalentBuff.effect.atkFlat = (jingliu.talent(talentLevel).cap + 0.3) * atk;
                }
                
            }

            updateStatesFromJson(newTalentBuff, false);
        }

        setPrevTalentLevel(talentLevel);
    }, [talentLevel]);

    useEffect(() => {
        if(level < 3 && characterBuffStates[0] && prevLevel >= 3) {
            updateStatesFromJson(A2Buff, true);
        }


        if(level < 10 && characterBuffStates[0] && prevLevel >= 10) {
            updateStatesFromJson(A6Buff, true);
        }

        if(level >= 3 && characterBuffStates[0] && prevLevel < 3) {
            updateStatesFromJson(A2Buff, false);
        }

        if(level >= 10 && characterBuffStates[0] && prevLevel < 10) {
            updateStatesFromJson(A6Buff, false);
        }

        setPrevLevel(level);
    }, [level]);

    useEffect(() => {
        
        if(characterBuffStates[0]) {
            let oldTalentBuff = {
                effect: {
                    atkFlat: 5.4 * hpLost * 0.04,
                    critRate: jingliu.talent(talentLevel).critRate,
                }
            }

            if(oldTalentBuff.effect.atkFlat > jingliu.talent(talentLevel).cap * prevAtk) {
                oldTalentBuff.effect.atkFlat = jingliu.talent(talentLevel).cap * prevAtk;
            }

            if(eidolon >= 4) {
                oldTalentBuff = {
                    effect: {
                        atkFlat: 6.3 * hpLost * 0.04,
                        critRate: jingliu.talent(talentLevel).critRate,
                    }
                }
        
                if(oldTalentBuff.effect.atkFlat > (jingliu.talent(talentLevel).cap + 0.3) * atk) {
                    oldTalentBuff.effect.atkFlat = (jingliu.talent(talentLevel).cap + 0.3) * atk;
                }
                
            }

            updateStatesFromJson(oldTalentBuff, true);

            let newTalentBuff = {
                effect: {
                    atkFlat: 5.4 * hpLost * 0.04,
                    critRate: jingliu.talent(talentLevel).critRate,
                }
            }

            if(newTalentBuff.effect.atkFlat > jingliu.talent(talentLevel).cap * atk) {
                newTalentBuff.effect.atkFlat = jingliu.talent(talentLevel).cap * atk;
            }

            if(eidolon >= 4) {
                newTalentBuff = {
                    effect: {
                        atkFlat: 6.3 * hpLost * 0.04,
                        critRate: jingliu.talent(talentLevel).critRate,
                    }
                }
        
                if(newTalentBuff.effect.atkFlat > (jingliu.talent(talentLevel).cap + 0.3) * atk) {
                    newTalentBuff.effect.atkFlat = (jingliu.talent(talentLevel).cap + 0.3) * atk;
                }
                
            }

            updateStatesFromJson(newTalentBuff, false);
        }

        setPrevAtk(atk);


    }, [atk]);

    let talentBuff = {
        effect: {
            atkFlat: 5.4 * hpLost * 0.04,
            critRate: jingliu.talent(talentLevel).critRate,
        }
    }
    
    if(talentBuff.effect.atkFlat > jingliu.talent(talentLevel).cap * atk) {
        talentBuff.effect.atkFlat = jingliu.talent(talentLevel).cap * atk;
    }

    if(eidolon >= 4) {
        talentBuff = {
            effect: {
                atkFlat: 6.3 * hpLost * 0.04,
                critRate: jingliu.talent(talentLevel).critRate,
            }
        }

        if(talentBuff.effect.atkFlat > (jingliu.talent(talentLevel).cap + 0.3) * atk) {
            talentBuff.effect.atkFlat = (jingliu.talent(talentLevel).cap + 0.3) * atk;
        }

    }

    const A2Buff = {
        effect: {
            effectRes: 0.35,
        }
    }

    const A6Buff = {
        effect: {
            ultimateBoost: 0.2,
        }
    }

    const E1Buff = {
        effect: {
            critDmg: 0.24,
        }
    }

    const E6Buff = {
        effect: {
            critDmg: 0.5,
        }
    }


    let talentDescription = (
        <div className="buff-description">
            <div> ATK increases based on 540% of the total HP consumed from allies capped at </div>
            <div className="buff-text"> {(jingliu.talent(talentLevel).cap * 100).toFixed(1)}% </div>
            <div> of her </div>
            <div className="buff-text"> Base ATK. </div>
        </div>
    )

    if(level >= 3) {
        talentDescription = (
            <div className="buff-description">
                <div> ATK increases based on 540% of the total HP consumed from allies capped at </div>
                <div className="buff-text"> {(jingliu.talent(talentLevel).cap * 100).toFixed(1)}% </div>
                <div> of her </div>
                <div className="buff-text"> Base ATK, </div>
                <br/>
                <div>Additionally increase </div>
                <div className="buff-text"> Effect Res </div>
                <div> by </div>
                <div className="buff-text"> 35%. </div>
            </div>
        )
    }

    if(level >= 10) {
        talentDescription = (
            <div className="buff-description">
                <div> ATK increases based on 540% of the total HP consumed from allies capped at </div>
                <div className="buff-text"> {(jingliu.talent(talentLevel).cap * 100).toFixed(1)}% </div>
                <div> of her </div>
                <div className="buff-text"> Base ATK, </div>
                <br/>
                <div>Additionally increase </div>
                <div className="buff-text"> Effect Res </div>
                <div> by </div>
                <div className="buff-text"> 35% </div>
                <div> and </div>
                <div className="buff-text"> Ultimate DMG </div>
                <div> by </div>
                <div className="buff-text"> 20%. </div>
            </div>
        )
    }

    const handleLossChange = (event) => {
        const re = /^[0-9]*\.?[0-9]+$/;
        if (event.target.value === '' || !re.test(event.target.value)) {
            setHpLost(0);
            return;
        }

        if(characterBuffStates[0]) {
            updateStatesFromJson(talentBuff, true);

            let tempTalentBuff = {
                effect: {
                    atkFlat: 5.4 * event.target.value * 0.04,
                    critRate: jingliu.talent(talentLevel).critRate,
                },
            }

            if(tempTalentBuff.effect.atkFlat > jingliu.talent(talentLevel).cap * atk) {
                tempTalentBuff.effect.atkFlat = jingliu.talent(talentLevel).cap * atk;
            }

            if(eidolon >= 4) {
                tempTalentBuff = {
                    effect: {
                        atkFlat: 6.3 * event.target.value * 0.04,
                        critRate: jingliu.talent(talentLevel).critRate,
                    }
                }
        
                if(tempTalentBuff.effect.atkFlat > (jingliu.talent(talentLevel).cap + 0.3) * atk) {
                    tempTalentBuff.effect.atkFlat = (jingliu.talent(talentLevel).cap + 0.3) * atk;
                }
                
            }

            updateStatesFromJson(tempTalentBuff, false);
        }

        setHpLost((event.target.value));
    }

    let eidolonContent = "";
    if(eidolon >= 6) {
        eidolonContent = (
            <div className="buff-description">
                <div> Additionally increase </div>
                <div className="buff-text"> Crit DMG </div>
                <div> by </div>
                <div className="buff-text"> 50%. </div>
            </div>
        )
    }

    const handleE2Change = (e) => {
        setE2ON(e.target.checked);
        character.setE2On(e.target.checked);
        const updatedStates = [...characterBuffStates];
        setCharacterBuffStates(updatedStates);
    }

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, talentBuff)} />}
            label="Spectral Transmigration state"
            />
            <div>{talentDescription}</div>
            <div className="mb-2">{eidolonContent}</div>
            <div style={{display: "flex", width: "70%"}}>
                <div className="centered">Allies Total Max HP</div>
                <input type="text" className="critDmg buff-input" style={{width: "65px"}} maxLength={5} value={parseInt(hpLost)} onChange={handleLossChange}/>
            </div>
            {eidolon >= 4 ? 
                <div className="muted-text mb-3">Current Cap: {(atk * (jingliu.talent(talentLevel).cap + 0.3)/(0.04 * 6.3)).toFixed(0)} HP</div>
                :
                <div className="muted-text mb-3">Current Cap: {(atk * jingliu.talent(talentLevel).cap/(0.04 * 5.4)).toFixed(0)} HP</div>
            }

            {eidolon >= 1 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, E1Buff)} />}
                    label={"(E1) " + jingliu.E1().name}
                    />
                    <div className="buff-description">
                        <div> When using  Ultimate or Enhanced Skill increase </div>
                        <div className="buff-text"> CRIT DMG </div>
                        <div> by </div>
                        <div className="buff-text"> 24%. </div>
                    </div>
                </>
            }

            {eidolon >= 2 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={E2ON || false} onChange={handleE2Change} />}
                    label={"(E2) " + jingliu.E2().name}
                    />
                    <div>{jingliu.E2().description}</div>
                </>
            }
        </>
    )
}

export const JingliuGlobalBuffs = (props) => {

    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {
        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <div>No Buffs</div>
        </>
    )
}

export default JingliuBuffs;