class PhysicalTrailBlazer {
    constructor() {
        this.stats = [
            {level: 1,  atk: 84.48,  def: 62.7,   hp: 164, spd: 100, taunt: 125, energy: 120},
            {level: 20, atk: 164.74, def: 122.26, hp: 319, spd: 100, taunt: 125, energy: 120, atkPercent: 0.04},
            {level: 20, atk: 198.53, def: 147.34, hp: 385, spd: 100, taunt: 125, energy: 120, atkPercent: 0.04},
            {level: 40, atk: 316.8,  def: 235.12, hp: 614, spd: 100, hpPercent: 0.04, taunt: 125, energy: 120, atkPercent: 0.04},
            {level: 40, atk: 350.59, def: 260.2,  hp: 679, spd: 100, hpPercent: 0.04, taunt: 125, energy: 120, atkPercent: 0.08, defPercent: 0.05},
            {level: 50, atk: 392.83, def: 291.55, hp: 761, spd: 100, hpPercent: 0.04, taunt: 125, energy: 120, atkPercent: 0.08, defPercent: 0.05},
            {level: 50, atk: 426.62, def: 316.63, hp: 827, spd: 100, hpPercent: 0.04, taunt: 125, energy: 120, atkPercent: 0.14, defPercent: 0.05},
            {level: 60, atk: 468.86, def: 347.98, hp: 908, spd: 100, hpPercent: 0.04, taunt: 125, energy: 120, atkPercent: 0.14, defPercent: 0.05},
            {level: 60, atk: 502.66, def: 373.06, hp: 974, spd: 100, hpPercent: 0.1,  taunt: 125, energy: 120, atkPercent: 0.2,  defPercent: 0.5},
            {level: 70, atk: 544.9,  def: 404.41, hp: 1056,spd: 100, hpPercent: 0.1,  taunt: 125, energy: 120, atkPercent: 0.2,  defPercent: 0.5},
            {level: 70, atk: 578.69, def: 429.49, hp: 1121,spd: 100, hpPercent: 0.1,  taunt: 125, energy: 120, atkPercent: 0.2,  defPercent: 0.125},
            {level: 80, atk: 620.93, def: 460.84, hp: 1203,spd: 100, hpPercent: 0.18,  taunt: 125, energy: 120, atkPercent: 0.28, defPercent: 0.125},
        ];

        this.name = "Trailblazer";
        this.path = "Destruction";
        this.rarity = 'fiveStar';
        this.element = "Physical";
        this.id = "trailblazerdestruction";
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Farewell Hit",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of the {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return 0.625 + (level * 0.0625);
        }else if(level < 10){
            return  0.9375 + ((level - 5) * 0.0781);
        }else {
            return 1.25 + ((level - 9) * 0.0625);
        }
    }

    skillDescription(level) {
        return {
            name: "RIP Home Run",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                    <div> of the {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy and enemies adjacent to it. </div>
                </div>
            ),
        }
    }
        

    ultimateAttack1(level) {
        if(level < 6) {
            return 3 + (level * 0.15);
        }else if(level < 10){
            return 3.75 + ((level - 5) * 0.1875);
        }else {
            return 4.5 + ((level - 9) * 0.15);
        }
    }

    ultimateAttack2(level) {
        if(level < 6) {
            return {
                single: 1.8 + (level * 0.09),
                spread: 1.08 + (level * 0.054),
            }
        }else if(level < 10){
            return {
                single: 2.25 + ((level - 5) * 0.1125),
                spread: 1.35 + ((level - 5) * 0.0675),
            }
        }else {
            return {
                single: 2.7 + ((level - 9) * 0.09),
                spread: 1.62 + ((level - 9) * 0.054),
            }
        }
    }

    ultimateDescription(level) {
        return {
            name: "Stardust Ace",
            description: (
                <div className="buff-description">
                    <div>Choose between two attack modes to deliver a full strike. </div>
                    <br/>
                    <div>Blowout: Farewell Hit deals </div>
                    <div className={this.element}>{this.element} DMG</div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.ultimateAttack1(level) * 100).toFixed(1)}% </div>
                    <div> of the {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to a single enemy. </div>
                    <br/>
                    <div>Blowout: RIP Home Run deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.ultimateAttack2(level).single * 100).toFixed(1)}% </div>
                    <div> of the {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to a single enemy , and </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.ultimateAttack2(level).spread * 100).toFixed(1)}% </div>
                    <div> of the {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to enemies adjacent to it. </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 0.1 + (level * 0.01);
        }else if(level < 10){
            return 0.15 + ((level - 5) * 0.0125);
        }else {
            return 0.2 + ((level - 9) * 0.01);
        }
    }

    talentDescription(level) { 
        return {
            name: "Perfect Pickoff",
            description: (
                <div className="buff-description">
                    <div> Each time after this character inflicts Weakness Break on an enemy </div>
                    <div className="buff-text"> ATK </div>
                    <div>  increases by </div>
                    <div className="buff-text"> {(this.talent(level) * 100).toFixed(1)}% </div>
                    <div> This effect stacks up to 2 time(s). </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Immortal Third Strike",
            description: (
                <div className="buff-description">
                    <div> Immediately heals all allies for 15% of their respective Max HP after using this Technique. </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Ready for Battle",
            description: (
                <div className="buff-description">
                    <div> At the start of the battle, immediately regenerates 15 Energy. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Perseverance",
            description: (
                <div className="buff-description">
                    <div> Each Talent stack increases the Trailblazer's DEF by 10%. </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Fighting Will",
            description: (
                <div className="buff-description">
                    <div> When using Skill or Ultimate "Blowout: RIP Home Run", DMG dealt to the target enemy is increased by 25%. </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "A Falling Star",
            description: (
                <div className="buff-description">
                    <div> When enemies are defeated due to the Trailblazer's Ultimate, the Trailblazer regenerates 10 extra Energy. This effect can only be triggered once per attack. </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "An Unwilling Host",
            description: (
                <div className="buff-description">
                    <div> Attacking enemies with Physical Weakness restores the Trailblazer's HP equal to 5% of the Trailblazer's ATK. </div>
                </div>
            )
        }
    }

    E3() {
        return {
            name: "A Leading Whisper",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }

    E4() {
        return {
            name: "A Destructing Glance",
            description:(
                <div className="buff-description">
                    <div>When attacking an enemy with Weakness Break, CRIT Rate is increased by 25%. </div>
                </div>
            )
        }
    }

    E5() {
        return {
            name: "A Surviving Hope",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: "A Trailblazing Will",
            description: (
                <div className="buff-description">
                    <div>The Trailblazer's Talent is also triggered when they defeat an enemy.</div>
                </div>
            ),
        }
    }
}

export default PhysicalTrailBlazer;