import React, { useContext } from 'react';
import { globalData } from '../../../globalData';

function TrailBlazerFireTable() {

    const levelMult = {
        1: 54.0000,
        20: 139.7703,
        40: 363.6658,
        50: 774.9041,
        60: 1640.3068,
        70: 2659.6406,
        80: 3767.5533,
    }

    const {
        character,
        fireDmg,
        atk,
        atkPercent,
        atkFlat,
        def,
        defPercent,
        defFlat,
        critRate,
        critDmg,
        energyRegen,
        level,
        enemyLevel,
        firePen,
        breakMultiplier,
        defReduction,
        enemyRes,
        basicAttackLevel,
        skillLevel,
        breakEffect,
        enemyToughness,
        ultimateLevel,
        basicBoost,
        eidolon,
        dotBoost,
        dotVulnerability,
        ultimateBoost,
        shieldStrength,
        fireVulnerability,
        talentLevel,
    } = useContext(globalData);

    let tbAtk = (atk * atkPercent) + atkFlat;
    let tbDef = (def * defPercent) + defFlat;

    let damageBoost = (1 + fireDmg);
    let critDmgBoost = (1 + critDmg);

    let basicDmgBoost = (damageBoost + basicBoost);
    let basicBaseDmg = tbAtk * character.basicAttack(basicAttackLevel);

    if(eidolon >= 1) {
        basicBaseDmg += tbDef * 0.25;
    }

    let enhancedBasicBaseDmgSingle = tbAtk * character.basicAttack1(basicAttackLevel).single;
    let enhancedBasicBaseDmgSread = tbAtk * character.basicAttack1(basicAttackLevel).spread;

    if(eidolon >= 1) {
        enhancedBasicBaseDmgSingle += tbDef * 0.5;
        enhancedBasicBaseDmgSread += tbDef * 0.5;
    }

    let ultimateDmgBoost = (damageBoost + ultimateBoost);
    let ultimateBaseDmg = tbAtk * character.ultimateAttack(ultimateLevel).atk + tbDef * character.ultimateAttack(ultimateLevel).def;

    let talentShield = tbDef * character.talent(talentLevel).multiplier + character.talent(talentLevel).addition;

    if(eidolon >= 2) {
        talentShield = tbDef * (character.talent(talentLevel).multiplier + 0.02) + (character.talent(talentLevel).addition + 27);
    }

    let techShield = tbDef * 0.3 + 384;

    let shieldBoost = (1 + shieldStrength);

    let resMult = (1 - (enemyRes - firePen));
    let vulnerabilityMultiplier = (1 + fireVulnerability);
    let defReductionStat = (1 - defReduction);

    if(defReductionStat < 0) {
        defReductionStat = 0;
    }
    
    let defMult = (character.stats[level]["level"] + 20)/((enemyLevel + 20) * defReductionStat + character.stats[level]["level"] + 20);
    let breakMult = breakMultiplier;

    let critRateStat = critRate;

    if(critRateStat > 1) {
        critRateStat = 1;
    }

    let maxToughnessMult = 0.5 + (enemyToughness/120);
    let breakBaseDmg = 2 * levelMult[character.stats[level]["level"]] * maxToughnessMult;
    let breakDmg = breakBaseDmg * (1 + breakEffect) * defMult * resMult * vulnerabilityMultiplier * breakMult; 
    let burnBaseDmg = 1 * levelMult[character.stats[level]["level"]];
    let burn = burnBaseDmg * (1 + breakEffect) * resMult * defMult * (vulnerabilityMultiplier + dotVulnerability) * breakMult * (damageBoost + dotBoost);

    return (
        <div className="damage-details">
            <div className='header'>Damage Details</div>
            <div className="divider"></div>
            <div className="damage-title header">Basic Attack ({basicAttackLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Basic</td>
                        <td className='tb-text'>{((basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{((basicBaseDmg * basicDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Enhanced Basic Attack ({basicAttackLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>DMG</td>
                        <td className='tb-text'>{((enhancedBasicBaseDmgSingle * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{((enhancedBasicBaseDmgSingle * basicDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((enhancedBasicBaseDmgSingle * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>DMG Blast</td>
                        <td className='tb-text'>{((enhancedBasicBaseDmgSread * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{((enhancedBasicBaseDmgSread * basicDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((enhancedBasicBaseDmgSread * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{60}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{60}</td>
                    </tr>
                    <tr>
                        <td>Break Blast</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Skill Attack ({skillLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Source</th>
                        <th scope="col">Amount</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                    </tr>
                </tbody>
            </table>

        <div className="damage-title header">Ultimate Attack ({ultimateLevel - 1 + 2})</div>
        <table className="table table-striped attack-table">
            <thead>
                <tr>
                    <th scope="col"></th>
                    <th scope="col">Non-CRIT</th>
                    <th scope="col">CRIT</th>
                    <th scope="col">Average</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>DMG</td>
                    <td className='tb-text'>{((ultimateBaseDmg * ultimateDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                    <td className='tb-text'>{((ultimateBaseDmg * ultimateDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                    <td className='tb-text'>{(((ultimateBaseDmg * ultimateDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                </tr>
                <tr>
                    <td>Energy</td>
                    <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                    <td className='tb-text'>-</td>
                    <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                </tr>
                <tr>
                    <td>Break</td>
                    <td className='tb-text'>{60}</td>
                    <td className='tb-text'>-</td>
                    <td className='tb-text'>{60}</td>
                </tr>
            </tbody>
        </table>

        <div className="damage-title header">Talent Shield ({talentLevel - 1 + 2})</div>
        <table className="table table-striped attack-table">
            <thead>
                <tr>
                    <th scope="col">Source</th>
                    <th scope="col">Amount</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>Shield</td>
                    <td className='tb-text'>{parseInt(talentShield * shieldBoost).toFixed(0)}</td>
                </tr>
            </tbody>
        </table>

        <div className="damage-title header">Technique</div>
        <table className="table table-striped attack-table">
            <thead>
                <tr>
                    <th scope="col">Source</th>
                    <th scope="col">Amount</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>Shield</td>
                    <td className='tb-text'>{parseInt(techShield * shieldBoost).toFixed(0)}</td>
                </tr>
            </tbody>
        </table>

        <div className="damage-title header">Weakness Break</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Type</th>
                        <th scope="col">Value</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Break Damage</td>
                        <td className='tb-text'>{(breakDmg).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Burn DMG</td>
                        <td className='tb-text'>{(burn).toFixed(0)}</td>
                    </tr>
                </tbody>
            </table>
        </div>
    )
}


export default TrailBlazerFireTable;