class Arlan {
    constructor() {
        this.stats = [
            {level: 1,  atk: 81.6,   def: 45,     hp: 163, spd: 102, taunt: 125, energy: 110},
            {level: 20, atk: 159.12, def: 87.75,  hp: 318, spd: 102, taunt: 125, energy: 110, atkPercent: 0.04},
            {level: 20, atk: 191.76, def: 105.75, hp: 384, spd: 102, taunt: 125, energy: 110, atkPercent: 0.04},
            {level: 40, atk: 306,    def: 168.75, hp: 612, spd: 102, effectRes: 0.04, taunt: 125, energy: 110, atkPercent: 0.04},
            {level: 40, atk: 338.64, def: 186.75, hp: 677, spd: 102, effectRes: 0.04, taunt: 125, energy: 110, atkPercent: 0.08, hpPercent: 0.04},
            {level: 50, atk: 379.44, def: 209.25, hp: 759, spd: 102, effectRes: 0.04, taunt: 125, energy: 110, atkPercent: 0.08, hpPercent: 0.04},
            {level: 50, atk: 412.08, def: 227.25, hp: 824, spd: 102, effectRes: 0.04,  taunt: 125, energy: 110, atkPercent: 0.14, hpPercent: 0.04},
            {level: 60, atk: 452.88, def: 249.75, hp: 906, spd: 102, effectRes: 0.04,  taunt: 125, energy: 110, atkPercent: 0.14, hpPercent: 0.04},
            {level: 60, atk: 485.52, def: 267.75, hp: 971, spd: 102, effectRes: 0.1,  taunt: 125, energy: 110, atkPercent: 0.2,  hpPercent: 0.04},
            {level: 70, atk: 526.32, def: 290.25, hp: 1053,spd: 102, effectRes: 0.1,  taunt: 125, energy: 110, atkPercent: 0.2, hpPercent: 0.04},
            {level: 70, atk: 558.96, def: 308.25, hp: 1118,spd: 102, effectRes: 0.1,  taunt: 125, energy: 110, atkPercent: 0.2,  hpPercent: 0.1},
            {level: 80, atk: 599.76, def: 330.75, hp: 1200,spd: 102, effectRes: 0.18, taunt: 125, energy: 110, atkPercent: 0.28, hpPercent: 0.1},
        ];

        this.name = "Arlan";
        this.path = "Destruction";
        this.rarity = 'fourStar';
        this.element = "Lightning";
        this.id = "arlan";
        this.E6ON = false;
    }

    setE6ON = (bool) => {
        this.E6ON = bool;
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Lightning Rush",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return 1.2 + (level * 0.12);
        }else if(level < 10){
            return 1.8 + ((level - 5) * 0.15);
        }else {
            return 2.4 + ((level - 9) * 0.12);
        }
    }

    skillDescription(level) {
        return {
            name: "Shackle Breaker",
            description: (
                <div className="buff-description">
                    <div>Consumes {this.name}'s </div>
                    <div className="buff-text"> HP </div>
                    <div> equal to </div>
                    <div className="buff-text"> 15% </div>
                    <div> of his </div>
                    <div className="buff-text"> Max HP </div>
                    <div>to deal </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name} </div>
                    <div className="buff-text"> ATK </div>
                    <div> to a single enemy. </div>
                    <div> If Arlan does not have sufficient HP, his HP will be reduced to 1 after using his Skill. </div>
                </div>
            ),
        }
    }
        

    ultimateAttack(level) {
        if(level < 6) {
            return {
                single: 1.92 + (level * 0.128),
                splash: 0.96 + (level * 0.064),
            }
        }else if(level < 10){
            return {
                single: 2.56 + ((level - 5) * 0.16),
                splash: 1.28 + ((level - 5) * 0.08),
            }
        }else {
            return {
                single: 3.2 + ((level - 9) * 0.128),
                splash: 1.6 + ((level - 9) * 0.064),
            }
        }
    }

    ultimateDescription(level) {
        return {
            name: "Frenzied Punishment",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}>{this.element} DMG</div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).single * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to a single enemy. </div>
                    <div> and </div>
                    <div className={this.element}>{this.element} DMG</div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).splash * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to enemies adjacent to it. </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 0.36 + (level * 0.036);
        }else if(level < 10){
            return 0.54 + ((level - 5) * 0.045);
        }else {
            return 0.72 + ((level - 9) * 0.036);
        }
    }

    talentDescription(level) { 
        return {
            name: "Pain and Anger",
            description: (
                <div className="buff-description">
                    <div> Increases {this.name}'s </div>
                    <div className="buff-text"> DMG </div>
                    <div> for every percent of HP below his Max HP up to a max of </div>
                    <div className="buff-text"> {(this.talent(level) * 100).toFixed(1)}% </div>
                    <div> more DMG.</div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Swift Harvest",
            description: (
                <div className="buff-description">
                    <div> Immediately attacks the enemy. After entering battle, deals Lightning DMG equal to 80% of Arlan's ATK to all enemies. </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Revival",
            description: (
                <div className="buff-description">
                    <div> If the current HP percentage is 30% or lower when defeating an enemy, immediately restores HP equal to 20% of Max HP. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Endurance",
            description: (
                <div className="buff-description">
                    <div> The chance to resist DoT Debuffs increases by 50%. </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Repel",
            description: (
                <div className="buff-description">
                    <div> Upon entering battle, if Arlan's HP is less than or equal to 50%, he can nullify all DMG received except for DoTs until he is attacked. </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "To the Bitter End",
            description: (
                <div className="buff-description">
                    <div> When HP is lower than or equal to 50% of Max HP, increases Skill's DMG by 10%. </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Breaking Free",
            description: (
                <div className="buff-description">
                    <div> Using Skill or Ultimate removes 1 debuff from oneself. </div>
                </div>
            )
        }
    }

    E3() {
        return {
            name: "Power Through",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }

    E4() {
        return {
            name: "Turn the Tables",
            description:(
                <div className="buff-description">
                    <div>When struck by a killing blow after entering battle, instead of becoming knocked down, Arlan immediately restores his HP to 25% of his Max HP. This effect is automatically removed after it is triggered once or after 2 turn(s) have elapsed. </div>
                </div>
            )
        }
    }

    E5() {
        return {
            name: "Hammer and Tongs",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: "Self-Sacrifice",
            description: (
                <div className="buff-description">
                    <div>When HP drops to 50% or below, Ultimate deals 20% more DMG. The DMG multiplier of DMG taken by the target enemy now applies to adjacent enemies as well. </div>
                </div>
            ),
        }
    }
}

export default Arlan;