class TrailblazerFire {
    constructor() {
        this.stats = [
            {level: 1,  atk: 81.84,  def: 82.5,   hp: 169, spd: 95, taunt: 150, energy: 120},
            {level: 20, atk: 159.59, def: 160.87, hp: 329, spd: 95, taunt: 150, energy: 120, defPercent: 0.05},
            {level: 20, atk: 192.32, def: 193.87, hp: 397, spd: 95, taunt: 150, energy: 120, defPercent: 0.05},
            {level: 40, atk: 306.9,  def: 309.37, hp: 634, spd: 95, taunt: 150, energy: 120, defPercent: 0.1, atkPercent: 0.04},
            {level: 40, atk: 339.64, def: 342.37, hp: 701, spd: 95, taunt: 150, energy: 120, defPercent: 0.1, atkPercent: 0.04,  hpPercent: 0.04},
            {level: 50, atk: 380.56, def: 383.62, hp: 786, spd: 95, taunt: 150, energy: 120, defPercent: 0.1, atkPercent: 0.04,  hpPercent: 0.04},
            {level: 50, atk: 413.29, def: 416.62, hp: 853, spd: 95, taunt: 150, energy: 120, defPercent: 0.175,atkPercent: 0.04, hpPercent: 0.04},
            {level: 60, atk: 454.21, def: 457.87, hp: 938, spd: 95, taunt: 150, energy: 120, defPercent: 0.175,atkPercent: 0.04, hpPercent: 0.04},
            {level: 60, atk: 486.95, def: 490.87, hp: 1005,spd: 95, taunt: 150, energy: 120, defPercent: 0.25, atkPercent: 0.1,  hpPercent: 0.04},
            {level: 70, atk: 527.87, def: 532.12, hp: 1090,spd: 95, taunt: 150, energy: 120, defPercent: 0.25, atkPercent: 0.1,  hpPercent: 0.04},
            {level: 70, atk: 560.6,  def: 565.12, hp: 1157,spd: 95, taunt: 150, energy: 120, defPercent: 0.25, atkPercent: 0.1,  hpPercent: 0.1},
            {level: 80, atk: 601.52, def: 606.37, hp: 1242,spd: 95, taunt: 150, energy: 120, defPercent: 0.35, atkPercent: 0.18, hpPercent: 0.1},
        ];

        this.name = "Trailblazer";
        this.path = "Preservation";
        this.rarity = 'fiveStar';
        this.element = "Fire";
        this.id = "trailblazertank";
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Ice-Breaking Light",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of the {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy and gains 1 stack of Magma Will. </div>
                </div>
            ),
        }
    }

    basicAttack1(level) {
        return {
            single: 0.9 + (level * 0.09),
            spread: 0.36 + (level * 0.036),
        };   
    }

    basic1Description(level) {
        return {
            name: "Ice-Breaking Light",
            description: (
                <div className="buff-description">
                    <div>Consumes 4 stacks of Magma Will to enhance Basic ATK, dealing </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack1(level).single * 100).toFixed(1)}%</div>
                    <div> of the {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy and </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack1(level).spread * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to enemies adjacent to it. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return 0.4 + (level * 0.01);
        }else if(level < 10) {
            return 0.45 + ((level - 5) * 0.0125);
        }else {
            return 0.5 + ((level - 9) * 0.01);
        }
    }

    skillDescription(level) {
        return {
            name: "Ever-Burning Amber",
            description: (
                <div className="buff-description">
                    <div> Increases the Trailblazer's DMG Reduction by </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                    <div> and gains 1 stack of Magma Will, with a 100% base chance to Taunt all enemies for 1 turn(s). </div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level < 6) {
            return {
                atk: 0.5 + (level * 0.05),
                def: 0.75 + (level * 0.075),
            }
        }else if(level < 10){
            return {
                atk: 0.75 + ((level - 5) * 0.0625),
                def: 1.12 + ((level - 5) * 0.0938),
            }
        }else {
            return {
                atk: 1 + ((level - 9) * 0.05),
                def: 1.5 + ((level - 5) * 0.075),
            }
        }
    }

    ultimateDescription(level) {
        return {
            name: "War-Flaming Lance",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.ultimateAttack(level).atk * 100).toFixed(1)}%</div>
                    <div>  of the {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> plus </div>
                    <div className="buff-text">{(this.ultimateAttack(level).def * 100).toFixed(1)}%</div>
                    <div>  of the {this.name}'s </div>
                    <div className="buff-text"> DEF </div>
                    <div> to all enemies. </div>
                    <br/>
                    <div> The next Basic ATK will be automatically enhanced and does not cost Magma Will. </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level == 0) {
            return {
                multiplier: 0.04,
                addition: 20,
            }
        }else if(level == 1) {
            return {
                multiplier: 0.0425,
                addition: 32,
            }
        }
        else if(level == 2) {
            return {
                multiplier: 0.045,
                addition: 41,
            }
        }
        else if(level == 3) {
            return {
                multiplier: 0.0475,
                addition: 50,
            }
        }
        else if(level == 4) {
            return {
                multiplier: 0.05,
                addition: 56,
            }
        }
        else if(level == 5) {
            return {
                multiplier: 0.052,
                addition: 62,
            }
        }
        else if(level == 6) {
            return {
                multiplier: 0.054,
                addition: 67,
            }
        }
        else if(level == 7) {
            return {
                multiplier: 0.056,
                addition: 71,
            }
        }
        else if(level == 8) {
            return {
                multiplier: 0.058,
                addition: 76,
            }
        }
        else if(level == 9) {
            return {
                multiplier: 0.06,
                addition: 80,
            }
        }
        else if(level == 10) {
            return {
                multiplier: 0.062,
                addition: 85,
            }
        }
        else if(level == 11) {
            return {
                multiplier: 0.064,
                addition: 89,
            }
        }
    }

    talentDescription(level) { 
        return {
            name: "Treasure of the Architects",
            description: (
                <div className="buff-description">
                    <div>Each time the Trailblazer is hit, they gain 1 stack of Magma Will for a max of 8 stack(s).</div>
                    <br/>
                    <div>When Magma Will has no fewer than 4 stacks, the Trailblazer's Basic ATK becomes enhanced, dealing DMG to a single enemy and enemies adjacent to it. </div>
                    <br/>
                    <div>When the Trailblazer uses Basic ATK, Skill, or Ultimate, apply a Shield to all allies that absorbs DMG equal to </div>
                    <div className="buff-text">{(this.talent(level).multiplier * 100).toFixed(1)}%</div>
                    <div>  of {this.name}'s </div>
                    <div className="buff-text"> DEF </div>
                    <div> plus </div>
                    <div className="buff-text">{this.talent(level).addition}</div>
                    <br/>
                    <div> he Shield lasts for 2 turn(s). </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Call of the Guardian",
            description: (
                <div className="buff-description">
                    <div> After using Technique, at the start of the next battle, gains a Shield that absorbs DMG equal to 30% of the Trailblazer's DEF plus 384 for 1 turn(s). </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "The Strong Defend the Weak",
            description: (
                <div className="buff-description">
                    <div> After using the Skill, the DMG taken by all allies reduces by 15% for 1 turn(s). </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Unwavering Gallantry",
            description: (
                <div className="buff-description">
                    <div> Using Enhanced Basic ATK restores the Trailblazer's HP by 5% of their Max HP. </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Action Beats Overthinking",
            description: (
                <div className="buff-description">
                    <div> When the Trailblazer is protected by a Shield at the beginning of the turn, increases their ATK by 15% and regenerates 5 Energy until the action is over. </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Earth-Shaking Resonance",
            description: (
                <div className="buff-description">
                    <div>When the Trailblazer uses their Basic ATK, additionally </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to 25% of the Trailbazer's </div>
                    <div className="buff-text"> DEF </div>
                    <br/>
                    <div>When the Trailblazer uses their enhanced Basic ATK, additionally deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to 50% of the Trailbazer's </div>
                    <div className="buff-text"> DEF </div>
                    <div>.</div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Time-Defying Tenacity",
            description: (
                <div className="buff-description">
                    <div> The Shield applied to all allies from the Trailblazer's Talent will block extra DMG equal to 2% of the Trailblazer's DEF plus 27. </div>
                </div>
            )
        }
    }
    
    E3() {
        return {
            name: "Trail-Blazing Blueprint",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }
    
    E4() {
        return {
            name: "Nation-Building Oath",
            description:(
                <div className="buff-description">
                    <div>At the start of the battle, immediately gains 4 stack(s) of Magma Will.</div>
                </div>
            )
        }
    }
    
    
    E5() {
        return {
            name: "Spirit-Warming Flame",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }
    
    E6() {
        return {
            name: "City-Forging Bulwarks",
            description: (
                <div className="buff-description">
                    <div> After the Trailblazer uses enhanced Basic ATK or Ultimate, their DEF increases by 10%. Stacks up to 3 time(s). </div>
                </div>
            ),
        }
    }
}

export default TrailblazerFire;