import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';

import { PhysicalTrailBlazer } from "../../../../../../../database/characters";

const PhysicalTBBuffs = () => {

    const { character, talentLevel, eidolon, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    
    const tb = new PhysicalTrailBlazer();
    const [stacks, setStacks] = useState(1);
    const [prevTalentLevel, setPrevTalentLevel] = useState(1);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    useEffect(() => {

        if(eidolon < 4 && characterBuffStates[1]) {
            updateStatesFromJson(E4Buff, true);
            characterBuffStates[1] = false;
        }

    }, [eidolon]);

    useEffect(() => {
        if(characterBuffStates[0]) {
            const tempUltBuff = {
                effect: {
                    atkPercent: character.talent(prevTalentLevel) * stacks,
                }
            }
            updateStatesFromJson(tempUltBuff, true);

            const newTempUltBuff = {
                effect: {
                    atkPercent: character.talent(talentLevel) * stacks,
                }
            }
            updateStatesFromJson(newTempUltBuff, false);
        }
        
        setPrevTalentLevel(talentLevel);
    }, [talentLevel]);

    const talentBuff = {
        effect: {
            atkPercent: tb.talent(talentLevel) * stacks,
        },
    }

    const E4Buff = {
        effect: {
            critRate: 0.25,
        }
    }

    const updateStacks = (event) => {

        if(characterBuffStates[0]) {
            const tempUltBuff = {
                effect: {
                    atkPercent: character.talent(talentLevel) * stacks,
                }
            }
            updateStatesFromJson(tempUltBuff, true);
            
            const newTempUltBuff = {
                effect: {
                    atkPercent: character.talent(talentLevel) * event.target.value,
                }
            }
            updateStatesFromJson(newTempUltBuff, false);
        }

        setStacks(event.target.value);
    }

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, talentBuff)} />}
            label={tb.talentDescription(talentLevel).name}
            />
            <div className="mb-3">{tb.talentDescription(talentLevel).description}</div>
            <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                <div style={{display: "flex"}}>
                    <div>Stacks</div>
                    <select className="levels buff-input" style={{width: "45%"}} value={stacks} onChange={updateStacks}>
                        <option value={1}>1</option>
                        <option value={2}>2</option>
                    </select>
                </div>
            </div>

            {eidolon >= 4 &&
            <>
                <FormControlLabel
                className='header'
                control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, E4Buff)} />}
                label={"(E4) " + tb.E4().name}
                />
                <div>{tb.E4().description}</div>
            </>
            }

        </>
    )
}

export const PhysicalTBGlobalBuffs = (props) => {

    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {
        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <div>No Buffs</div>
        </>
    )
}

export default PhysicalTBBuffs;