class Natasha {
    constructor() {
        this.stats = [
            {level: 1,  atk: 64.8,   def: 69,     hp: 158, spd: 98, taunt: 100, energy: 90},
            {level: 20, atk: 126.36, def: 134.55, hp: 309, spd: 98, taunt: 100, energy: 90, hpPercent: 0.04},
            {level: 20, atk: 152.28, def: 162.15, hp: 372, spd: 98, taunt: 100, energy: 90, hpPercent: 0.04},
            {level: 40, atk: 243,    def: 258.75, hp: 594, spd: 98, taunt: 100, energy: 90, hpPercent: 0.04, defPercent: 0.05},
            {level: 40, atk: 268.92, def: 286.35, hp: 657, spd: 98, taunt: 100, energy: 90, hpPercent: 0.08, effectRes: 0.04, defPercent: 0.05},
            {level: 50, atk: 301.32, def: 320.85, hp: 737, spd: 98, taunt: 100, energy: 90, hpPercent: 0.08, effectRes: 0.04, defPercent: 0.05},
            {level: 50, atk: 327.24, def: 348.45, hp: 800, spd: 98, taunt: 100, energy: 90, hpPercent: 0.14, effectRes: 0.04, defPercent: 0.05},
            {level: 60, atk: 359.64, def: 382.95, hp: 879, spd: 98, taunt: 100, energy: 90, hpPercent: 0.14, effectRes: 0.04, defPercent: 0.05},
            {level: 60, atk: 385.56, def: 410.55, hp: 942, spd: 98, taunt: 100, energy: 90, hpPercent: 0.20, effectRes: 0.1, defPercent: 0.05},
            {level: 70, atk: 417.96, def: 445.05, hp: 1022,spd: 98, taunt: 100, energy: 90, hpPercent: 0.20, effectRes: 0.1, defPercent: 0.05},
            {level: 70, atk: 443.88, def: 472.65, hp: 1085,spd: 98, taunt: 100, energy: 90, hpPercent: 0.20, effectRes: 0.1, defPercent: 0.05},
            {level: 80, atk: 476.28, def: 507.15, hp: 1164,spd: 98, taunt: 100, energy: 90, hpPercent: 0.28, effectRes: 0.18, defPercent: 0.125},
        ];

        this.name = "Natasha";
        this.path = "Abundance";
        this.rarity = 'fourStar';
        this.element = "Physical";
        this.id = "natasha";
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Behind the Kindness",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return {
                multiplier: 0.07 + (level * 0.0044),
                addition: 70 + (level * 28),
                continousMult: 0.048 + (level * 0.003),
                continousAddition: 48 + (level * 17),
            }
        }else if(level < 10){
            return {
                multiplier: 0.091 + ((level - 5) * 0.0035),
                addition: 217 + ((level - 5) * 16),
                continousMult: 0.0624 + ((level - 5) * 0.0024),
                continousAddition: 142 + ((level - 5) * 13),
            }
        }else {
            return {
                multiplier: 0.105 + ((level - 9) * 0.0035),
                addition: 280 + ((level - 9) * 16),
                continousMult: 0.072 + ((level - 9) * 0.0024),
                continousAddition: 182 + ((level - 9) * 11),
            }
        }
    }

    skillDescription(level) {
        return {
            name: "Love, Heal, and Choose",
            description: (
                <div className="buff-description">
                    <div> Restores a single ally for </div>
                    <div className="buff-text">{(this.skillAttack(level).multiplier * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">Max HP</div>
                    <div> plus </div>
                    <div className="buff-text">{this.skillAttack(level).addition}.</div>
                    <div> Restores the ally for another </div>
                    <div className="buff-text">{(this.skillAttack(level).continousMult * 100).toFixed(1)}%</div>
                    <div> plus </div>
                    <div className="buff-text">{this.skillAttack(level).continousAddition}.</div>
                    <div> at the beginning of each turn for 2 turn(s). </div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level < 6) {
            return {
                multiplier: 0.092 + (level * 0.0058),
                addition: 92 + (level * 32),
            }
        }else if(level < 10){
            return {
                multiplier: 0.1196 + ((level - 5) * 0.0046),
                addition: 285 + ((level - 5) * 21),
            }
        }else {
            return {
                multiplier: 0.138 + ((level - 9) * 0.0046),
                addition: 348 + ((level - 9) * 21),
            }
        }
    }

    ultimateDescription(level) {
        return {
            name: "Gift of Rebirth",
            description: (
                <div className="buff-description">
                    <div>Heals all allies for </div>
                    <div className="buff-text">{(this.ultimateAttack(level).multiplier * 100).toFixed(1)}%</div>
                    <div>  of Natasha's </div>
                    <div className="buff-text"> Max HP </div>
                    <div> plus </div>
                    <div className="buff-text"> {this.ultimateAttack(level).addition} </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 0.25 + (level * 0.025);
        }else if(level < 10){
            return 0.375 + ((level - 5) * 0.0313);
        }else {
            return 0.5 + ((level - 9) * 0.025);
        }
    }

    talentDescription(level) { 
        return {
            name: "Innervation",
            description: (
                <div className="buff-description">
                    <div>When healing allies with HP percentage at </div>
                    <div className="buff-text"> 30% or lower </div>
                    <div>, increases {this.name}'s outgoing Healing by </div>
                    <div className="buff-text"> {(this.talent(level) * 100).toFixed(1)}% </div>
                    <div> This effect also works on continuous healing. </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Hypnosis Research",
            description: (
                <div className="buff-description">
                    <div> Immediately attacks the enemy. After entering battle, deals Physical DMG equal to 80% of Natasha's ATK to a random enemy, with a 100% base chance to Weaken all enemies. While Weakened, enemies deal 30% less DMG to allies for 1 turn(s). </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Soothe",
            description: (
                <div className="buff-description">
                    <div> The Skill removes 1 debuff(s) from a target ally. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Healer",
            description: (
                <div className="buff-description">
                    <div> Natasha's </div>
                    <div className="buff-text"> Outgoing Healing </div>
                    <div> increases by </div>
                    <div className="buff-text"> 10% </div>
                    <div>.</div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Recuperation",
            description: (
                <div className="buff-description">
                    <div> increases the duration of Skill's continuous healing effect for 1 turn(s). </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Pharmacology Expertise",
            description: (
                <div className="buff-description">
                    <div> After being attacked, if the current HP percentage is 30% or lower, heals self for 1 time to restore HP by an amount equal to 15% of Max HP plus 400. This effect can only be triggered 1 time per battle. </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Clinical Research",
            description: (
                <div className="buff-description">
                    <div> When Natasha uses her Ultimate, grant continuous healing for 1 turn(s) to allies whose HP is at 30% or lower. And at the beginning of their turn, their HP is restored by an amount equal to 6% of Natasha's Max HP plus 160. </div>
                </div>
            )
        }
    }
    
    E3() {
        return {
            name: "The Right Cure",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }
    
    E4() {
        return {
            name: "Miracle Cure",
            description:(
                <div className="buff-description">
                    <div>After being attacked, regenerates 5 extra Energy.</div>
                </div>
            )
        }
    }
    
    E5() {
        return {
            name: "Preventive Treatment",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: "Doctor's Grace",
            description: (
                <div className="buff-description">
                    <div> Natasha's Basic ATK additionally deals Physical DMG equal to 40% of her Max HP. </div>
                </div>
            ),
        }
    }
}

export default Natasha;