import { globalData } from '../../../../../globalData';
import React, { useContext, useState, useEffect } from 'react';
import { Carve } from '../../../../../database/lightCones';
import { Checkbox,  FormControlLabel } from '@mui/material';


function CarveBuffs() {

    let carve = new Carve();

    const {
        updateStatesFromJson,
        lcBuffStates: buffStates,
        setLcBuffStates: setBuffStates,
        imposition,
        lightCone,
    } = useContext(globalData);

    const buffs = {
        buff1: {
            effect: {
                atkPercent: 0.1 + imposition * 0.02,
            }
        },
        buff2: {
            effect: {
                critDmg: 0.12 + imposition * 0.03,
            }
        },
        buff3: {
            effect: {
                ERR: 0.06 + imposition * 0.01,
            }
        }
    }

    const [currentBuff, setCurrentBuff] = useState("buff1");
    const [activeBuff, setActiveBuff] = useState(buffs[currentBuff]);
    

    const ApplyLCBuffFromCheckBox = (index, buffValue) => {
        const updatedStates = [...buffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setBuffStates(updatedStates);
    }

    useEffect(() => {

        if(buffStates[0]) {
            updateStatesFromJson(activeBuff, true);
            updateStatesFromJson(buffs[currentBuff], false);
        }

        setActiveBuff(buffs[currentBuff]);
    }, [imposition])

    const handleBuffChange = (event) => {
        if(buffStates[0]) {
            updateStatesFromJson(activeBuff, true);
            updateStatesFromJson(buffs[event.target.value], false);
        }
        setCurrentBuff(event.target.value);
        lightCone.setActiveBuff(event.target.value);
        setActiveBuff(buffs[event.target.value]);
    }

    return (
        <div>
            <div className='centered header' style={{textAlign: "center"}}>{carve.name}</div>
            <FormControlLabel
                className='buff'
                control={<Checkbox checked={buffStates[0] || false} onChange={() => ApplyLCBuffFromCheckBox(0, activeBuff)} />}
                label={carve.weaponDescription(imposition)}
            />
            <div style={{display: "flex", width: "100%", justifyContent: "space-between"}}>
                <div  style={{display: "flex"}}>
                    <div>Buff:</div>
                    <select name="imp" id="imp" className='levels buff-input' style={{width: "100%"}} value={currentBuff} onChange={(e) => handleBuffChange(e)}>
                        <option value="buff1">ATK</option>
                        <option value="buff2">CRIT DMG</option>
                        <option value="buff3">Energy</option>
                    </select>
                </div>
            </div>
            
        </div>
    )
}

export function GlobalCarveBuffs(props) {

    let carve = new Carve();
    const [imposition, setImposition] = useState(0);
    const buffStates = props.buffStates;
    const setBuffStates = props.setBuffStates;

    const {
        updateStatesFromJson,
        isChecked,
        lightCone,
    } = useContext(globalData);

    useEffect(() => {
        if(lightCone) {
            if(lightCone.id === carve.id) {
                for(let i = 0; i < buffStates.length; i++) {
                    if(buffStates[i]) {
                        updateStatesFromJson(activeBuff, true);
                    }
                }

                setImposition(0);
                // fill buff states with false
                const updatedStates = [...buffStates];
                for(let i = 0; i < updatedStates.length; i++) {
                    updatedStates[i] = false;
                }
                setBuffStates(updatedStates);
            }
        }else {
            setImposition(0);
            // fill buff states with false
            const updatedStates = [...buffStates];
            for(let i = 0; i < updatedStates.length; i++) {
                updatedStates[i] = false;
            }
            setBuffStates(updatedStates);
        }
    }, [isChecked])

    const buffs = {
        buff1: {
            effect: {
                atkPercent: 0.1 + imposition * 0.02,
            }
        },
        buff2: {
            effect: {
                critDmg: 0.12 + imposition * 0.03,
            }
        },
        buff3: {
            effect: {
                ERR: 0.06 + imposition * 0.01,
            }
        }
    }

    const [currentBuff, setCurrentBuff] = useState("buff1");
    const [activeBuff, setActiveBuff] = useState(buffs[currentBuff]);
    
    const ApplyLCBuffFromCheckBox = (index, buffValue) => {
        const updatedStates = [...buffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setBuffStates(updatedStates);
    }

    useEffect(() => {

        if(buffStates[0]) {
            updateStatesFromJson(activeBuff, true);
            updateStatesFromJson(buffs[currentBuff], false);
        }

        setActiveBuff(buffs[currentBuff]);
    }, [imposition])

    const handleSelectImposition = (event) => {
        setImposition(event.target.selectedIndex);
    }

    const handleBuffChange = (event) => {
        if(buffStates[0]) {
            updateStatesFromJson(activeBuff, true);
            updateStatesFromJson(buffs[event.target.value], false);
        }
        setCurrentBuff(event.target.value);
        setActiveBuff(buffs[event.target.value]);
    }

    if(lightCone && carve.id === lightCone.id) {
        return null;
    }

    return (
        <div>
            <div className='centered header' style={{textAlign: "center"}}>{carve.name}</div>
            <FormControlLabel
                className='buff'
                control={<Checkbox checked={buffStates[0] || false} onChange={() => ApplyLCBuffFromCheckBox(0, activeBuff)} />}
                label={carve.weaponDescription(imposition)}
            />
            <div style={{display: "flex", width: "100%", justifyContent: "space-between"}}>
                <div  style={{display: "flex"}}>
                    <div>Imposition:</div>
                    <select name="imp" id="imp" className='levels buff-input' style={{width: "100%"}} value={imposition + 1} onChange={(e) => handleSelectImposition(e)}>
                        <option value="1">1</option>
                        <option value="2">2</option>
                        <option value="3">3</option>
                        <option value="4">4</option>
                        <option value="5">5</option>
                    </select>
                </div>

                <div  style={{display: "flex"}}>
                    <div>Buff:</div>
                    <select name="imp" id="imp" className='levels buff-input' style={{width: "100%"}} value={currentBuff} onChange={(e) => handleBuffChange(e)}>
                        <option value="buff1">ATK</option>
                        <option value="buff2">CRIT DMG</option>
                        <option value="buff3">Energy</option>
                    </select>
                </div>
            </div>
            
        </div>
    )
}

export default CarveBuffs;