import { globalData } from '../../../globalData';
import React, { useContext, useState, useEffect } from 'react';
import { Incessant, Resolution } from '../../../database/lightCones';
import { Checkbox,  FormControlLabel } from '@mui/material';
import * as SpecialCones from './specialCones';

function GlobalLCDebuffs() {

    const cones = [
        new Incessant(),
        new Resolution(),
    ]

    const initialState = Array(10).fill("1");
    const initialState1 = Array(10).fill(0);
    const [globalLCImpositionStates, setGlobalLCImpositionStates] = useState(initialState1);
    const [stackStates, setStackStates] = useState(initialState);
    
    const [incessantStates, setIncessantStates] = useState(initialState1);

    const specialCones = {
        incessant: (buffStates, setBuffStates) => <SpecialCones.GlobalInessantBuffs buffStates={buffStates} setBuffStates={setBuffStates}></SpecialCones.GlobalInessantBuffs>
    }

    const {
        globalLCDebuffStates,
        setGlobalLCDebuffStates,
        updateStatesFromJson,
        lightCone,
        isChecked,
    } = useContext(globalData);

    useEffect(() => {
        if(lightCone) {
            for(let i = 0; i < cones.length; i++) {
                if(lightCone.id === cones[i].id && globalLCDebuffStates[i]) {
                    let x = cones[i].weaponEffect(globalLCImpositionStates[i], stackStates[i]);
                    updateStatesFromJson(x[0], true);
                    const updatedStates = [...globalLCDebuffStates];
                    updatedStates[i] = false;
                    setGlobalLCDebuffStates(updatedStates);
                }
            }
        }
    }, [isChecked]);


    const ApplyLCBuffFromCheckBox = (index, buffValue) => {
        const updatedStates = [...globalLCDebuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setGlobalLCDebuffStates(updatedStates);
    }

    const handleSelectImposition = (event, idx, index) => {
        const updatedStates = [...globalLCImpositionStates];
        updatedStates[index] = event.target.selectedIndex;

        let x = cones[index].weaponEffect(globalLCImpositionStates[index], stackStates[index]);
        let y = cones[index].weaponEffect(updatedStates[index], stackStates[index]);

        if(globalLCDebuffStates[index]) {
            updateStatesFromJson(x[idx], true);
            updateStatesFromJson(y[idx], false);
        }
        setGlobalLCImpositionStates(updatedStates);
    }

    const updateSelectedStates = (e, index, lc, buffID) => {
        const updatedStates = [...stackStates];
        updatedStates[index] = e.target.value;
        
        if(globalLCDebuffStates[index] === true) {
            const json = lc.weaponEffect(globalLCImpositionStates[index], stackStates[index]);
            let foundObject = null;
            for (const key in json) {
                if (json.hasOwnProperty(key) && json[key].id === buffID) {
                    foundObject = json[key];
                    break;
                }
            }
            updateStatesFromJson(foundObject, true);

            const json1 = lc.weaponEffect(globalLCImpositionStates[index], updatedStates[index]);
            foundObject = null;
            for (const key in json1) {
                if (json1.hasOwnProperty(key) && json1[key].id === buffID) {
                    foundObject = json1[key];
                    break;
                }
            }
            updateStatesFromJson(foundObject, false);
        }

        setStackStates(updatedStates);
    }

    return (
        <div>
            {cones.map((cone, idx) => {

                if(specialCones[cone.id]) {
                    return (<div key={idx}>{specialCones[cone.id](incessantStates, setIncessantStates)}</div>);
                }

                if(lightCone && cone.id === lightCone.id) {
                    return null;
                }
                
                return (
                    <div key={idx}>
                        <div className='header centered'>{cone.name}</div>
                        {
                        Object.entries(cone.weaponEffect(globalLCImpositionStates[idx])).map(([buffName, buffValue], index) => {
                            let optionsContent = "";
                            if (buffValue.type === 'debuff') {
                                if(buffValue.hasStack) {
    
                                    const length = buffValue.stacks;
                                    const options = Array.from({ length }, (_, index) => index + 1);
                                    optionsContent = (
                                        <div style={{display: "flex", alignItems: "flex-end", justifyContent: "flex-end"}}>
                                            <div>Stacks:</div>
                                            <select className='levels buff-input' style={{width: "100%"}} name={buffValue.id} id={buffValue.id} key={buffValue.id} value={stackStates[index + idx]} onChange={(e) => updateSelectedStates(e, index + idx, cone, buffValue.id)}>
                                                {options.map((item) => {
                                                    return <option key={item} value={item}>{item}</option>
                                                })}
                                            </select>
                                        </div>
                                    );

                                    const json = cone.weaponEffect(globalLCImpositionStates[idx], stackStates[index + idx]);
                                    // find object with ID
                                    let foundObject = null;
                                    for (const key in json) {
                                        if (json.hasOwnProperty(key) && json[key].id === buffValue.id) {
                                            foundObject = json[key];
                                            break;
                                        }
                                    }
    
                                    buffValue = foundObject;
                                }
    
                                return (
                                    <div key={buffName}>
                                        <FormControlLabel
                                            className='buff'
                                            key={buffName}
                                            control={<Checkbox checked={globalLCDebuffStates[index + idx] || false} onChange={() => ApplyLCBuffFromCheckBox(index + idx, buffValue)} />}
                                            label={buffValue.description}
                                        />
                                        <div style={{display: "flex", width: "100%", justifyContent: "space-between"}}>
                                            <div  style={{display: "flex"}}>
                                                <div>Imposition:</div>
                                                <select name="imp" id="imp" className='levels buff-input' value={globalLCImpositionStates[idx] + 1} onChange={(e) => handleSelectImposition(e, index, idx)} style={{width: "100%"}}>
                                                    <option value="1">1</option>
                                                    <option value="2">2</option>
                                                    <option value="3">3</option>
                                                    <option value="4">4</option>
                                                    <option value="5">5</option>
                                                </select>
                                            </div>
                                            {optionsContent}
                                        </div>
                                    </div>
                                );
                            }
                        })}
                    </div>
                )
            })}
        </div>

    )
}

export default GlobalLCDebuffs;