import React, { useContext } from 'react';
import { globalData } from '../../../globalData';

function TopazTable() {

    const levelMult = {
        1: 54.0000,
        20: 139.7703,
        40: 363.6658,
        50: 774.9041,
        60: 1640.3068,
        70: 2659.6406,
        80: 3767.5533,
    }

    const {
        character,
        fireDmg,
        atk,
        atkPercent,
        atkFlat,
        eidolon,
        critRate,
        critDmg,
        energyRegen,
        level,
        enemyLevel,
        firePen,
        breakEffect,
        defReduction,
        enemyRes,
        basicAttackLevel,
        skillLevel,
        ultimateLevel,
        breakMultiplier,
        fireVulnerability,
        enemyToughness,
        dotVulnerability,
        dotBoost,
        followUpCritDmg,
        followUpBoost,
        talentLevel,
        followUpVulnerability,
    } = useContext(globalData);

    let topazAtk = (atk * atkPercent) + atkFlat;
    let damageBoost = (1 + fireDmg);
    let critDmgBoost = (1 + critDmg);
    
    let basicDmgBoost = damageBoost + followUpBoost;
    let basicBaseDmg = topazAtk * character.basicAttack(basicAttackLevel);
    
    let skillDmgBoost = damageBoost + followUpBoost;
    let skillBaseDmg = topazAtk * character.skillAttack(skillLevel).multiplier;

    let talentDmgBoost = damageBoost + followUpBoost;
    let talentBaseDmg = topazAtk * character.talent(talentLevel);
    
    let resMult = (1 - (enemyRes - firePen));
    let defReductionStat = (1 - defReduction);
    let vulnerabilityMultiplier = (1 + fireVulnerability);

    if(defReductionStat < 0) {
        defReductionStat = 0;
    }

    let defMult = (character.stats[level]["level"] + 20)/((enemyLevel + 20) * defReductionStat + character.stats[level]["level"] + 20);
    let breakMult = breakMultiplier;
    
    let critRateStat = critRate;
    
    if(critRateStat > 1) {
        critRateStat = 1;
    }
    
    let maxToughnessMult = 0.5 + (enemyToughness/120);
    let breakBaseDmg = 2 * levelMult[character.stats[level]["level"]] * maxToughnessMult;
    let breakDmg = breakBaseDmg * (1 + breakEffect) * defMult * resMult * vulnerabilityMultiplier * breakMult; 
    let burnBaseDmg = 1 * levelMult[character.stats[level]["level"]];
    let burn = burnBaseDmg * (1 + breakEffect) * resMult * defMult * (vulnerabilityMultiplier + dotVulnerability) * breakMult * (damageBoost + dotBoost);

    return (
        <div className="damage-details">
            <div className='header'>Damage Details</div>
            <div className="divider"></div>
            <div className="damage-title header">Basic Attack ({basicAttackLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Source</th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Basic</td>
                        <td className='tb-text'>{(basicBaseDmg * basicDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult).toFixed(0)}</td>
                        <td className='tb-text'>{(basicBaseDmg * basicDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult * (critDmgBoost + followUpCritDmg)).toFixed(0)}</td>
                        <td className='tb-text'>{((basicBaseDmg * basicDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult) * (1 + (critDmg + followUpCritDmg) * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
            </table>
            <div className="damage-title header">Skill Attack ({skillLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Source</th>
                        <th scope="col" className='tb-text-header'>Non-CRIT</th>
                        <th scope="col" className='tb-text-header'>CRIT</th>
                        <th scope="col" className='tb-text-header'>Average</th>

                    </tr>
                </thead>
                <tbody>
                    {character.ultimateOn ? 
                        <tr>
                            <td>Skill</td>
                            <td className='tb-text'>{((skillBaseDmg + character.ultimateAttack(ultimateLevel).multiplier * topazAtk) * skillDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult).toFixed(0)}</td>
                            <td className='tb-text'>{((skillBaseDmg + character.ultimateAttack(ultimateLevel).multiplier * topazAtk) * skillDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult * (critDmgBoost + character.ultimateAttack(ultimateLevel).critDmg + followUpCritDmg)).toFixed(0)}</td>
                            <td className='tb-text'>{(((skillBaseDmg + character.ultimateAttack(ultimateLevel).multiplier * topazAtk) * skillDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult) * (1 + (critDmg + character.ultimateAttack(ultimateLevel).critDmg + followUpCritDmg) * critRateStat)).toFixed(0)}</td>
                        </tr>
                    :
                        <tr>
                            <td>Skill</td>
                            <td className='tb-text'>{(skillBaseDmg * skillDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult).toFixed(0)}</td>
                            <td className='tb-text'>{(skillBaseDmg * skillDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult * (critDmgBoost + followUpCritDmg)).toFixed(0)}</td>
                            <td className='tb-text'>{((skillBaseDmg * skillDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult) * (1 + (critDmg + followUpCritDmg) * critRateStat)).toFixed(0)}</td>
                        </tr>
                    }
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{60}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{60}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Talent Attack ({talentLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Source</th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    {character.ultimateOn ?
                        <tr>
                            <td>Follow Up</td>
                            <td className='tb-text'>{((talentBaseDmg + character.ultimateAttack(ultimateLevel).multiplier * topazAtk) * talentDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult).toFixed(0)}</td>
                            <td className='tb-text'>{((talentBaseDmg + character.ultimateAttack(ultimateLevel).multiplier * topazAtk) * talentDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult * (critDmgBoost + character.ultimateAttack(ultimateLevel).critDmg + followUpCritDmg)).toFixed(0)}</td>
                            <td className='tb-text'>{(((talentBaseDmg + character.ultimateAttack(ultimateLevel).multiplier * topazAtk) * talentDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult) * (1 + (critDmg + character.ultimateAttack(ultimateLevel).critDmg + followUpCritDmg) * critRateStat)).toFixed(0)}</td>
                        </tr>
                    :
                        <tr>
                            <td>Follow Up</td>
                            <td className='tb-text'>{(talentBaseDmg * talentDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult).toFixed(0)}</td>
                            <td className='tb-text'>{(talentBaseDmg * talentDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult * (critDmgBoost + followUpCritDmg)).toFixed(0)}</td>
                            <td className='tb-text'>{((talentBaseDmg * talentDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult) * (1 + (critDmg + followUpCritDmg) * critRateStat)).toFixed(0)}</td>
                        </tr>
                    }
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{60}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{60}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Weakness Break</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Type</th>
                        <th scope="col">Value</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Break Damage</td>
                        <td className='tb-text'>{(breakDmg).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Burn DMG</td>
                        <td className='tb-text'>{(burn).toFixed(0)}</td>
                    </tr>
                </tbody>
            </table>
        </div>
    )
}


export default TopazTable;