import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';

import { Pela } from "../../../../../../../database/characters";

const PelaBuffs = () => {

    const {level, eidolon, character, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    
    const pela = new Pela();

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);

    }
    
    const A2Buff = {
        effect: {
            iceDmg: 0.2,
            quantumDmg: 0.2,
            fireDmg: 0.2,
            lightningDmg: 0.2,
            windDmg: 0.2,
            physicalDmg: 0.2,
            imaginaryDmg: 0.2,
        }
    }

    const A4Buff = {
        effect: {
            effectHitRate: 0.1,
        },
    }

    const A6Buff = {
        effect: {
            iceDmg: 0.2,
            quantumDmg: 0.2,
            fireDmg: 0.2,
            lightningDmg: 0.2,
            windDmg: 0.2,
            physicalDmg: 0.2,
            imaginaryDmg: 0.2,
        }
    }


    const E2Buff = {
        effect: {
            spdPercent: 0.1,
        },
    }

    useEffect(() => {
        if(eidolon < 2 && characterBuffStates[3]) {
            updateStatesFromJson(E2Buff, true);
            characterBuffStates[3] = false;
        }

        if(eidolon < 6 && characterBuffStates[4]) {
            character.setE6(false);
            characterBuffStates[4] = false;
            const updatedStates = [...characterBuffStates];
            setCharacterBuffStates(updatedStates);
        }
    }, [eidolon]);

    useEffect(() => {
        
        if(level < 11 && characterBuffStates[2]) {
            updateStatesFromJson(A6Buff, true);
            characterBuffStates[2] = false;
        }
        
        if(level < 6 && characterBuffStates[1]) {
            updateStatesFromJson(A4Buff, true);
            characterBuffStates[1] = false;
        }

        if(level < 2 && characterBuffStates[0]) {
            updateStatesFromJson(A2Buff, true);
            characterBuffStates[0] = false;
        }

    }, [level]);

    const turnE6On = (index) => {
        const updatedStates = [...characterBuffStates];
        updatedStates[index] = !updatedStates[index];
        character.setE6(updatedStates[index]);
        setCharacterBuffStates(updatedStates);
    }

    return (
        <>

            {level >= 2 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, A2Buff)} />}
                    label={"(A2) " + pela.A2().name}
                    />
                    <div>{pela.A2().description}</div>
                </>
            }

            {level >= 6 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, A4Buff)} />}
                    label={"(A4) " + pela.A4().name}
                    />
                    <div>{pela.A4().description}</div>
                </>
            }

            {level >= 11 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, A6Buff)} />}
                    label={"(A6) " + pela.A6().name}
                    />
                <div>{pela.A6().description}</div>
            </>
            }

            {eidolon >= 2 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, E2Buff)} />}
                    label={"(E2) " + pela.E2().name}
                    />
                    <div>{pela.E2().description}</div>
                </>
            }

            {eidolon >= 6 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[4] || false} onChange={() => turnE6On(4)} />}
                    label={"(E6) " + pela.E6().name}
                    />
                    <div>{pela.E6().description}</div>
                </>
            }
        </>
    )
}

export const PelaDebuffs = () => {

    const { ultimateLevel, eidolon, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    
    const pela = new Pela();

    const [prevUltimateLevel, setPrevUltimateLevel] = useState(ultimateLevel);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);

    }

    useEffect(() => {
        if(characterBuffStates[4]) {
            const tempUltBuff = {
                effect: {
                    defIgnore: pela.ultimateAttack(prevUltimateLevel).defIgnore,
                },
            }
            updateStatesFromJson(tempUltBuff, true);
            
            const newTempUltBuff = {
                effect: {
                    defIgnore: pela.ultimateAttack(ultimateLevel).defIgnore,
                },
            }
            updateStatesFromJson(newTempUltBuff, false);
        }
        
        setPrevUltimateLevel(ultimateLevel);
    }, [ultimateLevel]);

    const techBuff = {
        effect: {
            defIgnore: 0.2,
        },
    }

    const ultBuff = {
        effect: {
            defIgnore: pela.ultimateAttack(ultimateLevel).defIgnore,
        },
    }

    const E4Buff = {
        effect: {
            icePen: 0.12,
        },
    }

    useEffect(() => {
        if(eidolon < 4 && characterBuffStates[6]) {
            updateStatesFromJson(E4Buff, true);
            characterBuffStates[6] = false;
        }
    }, [eidolon]);

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[4] || false} onChange={() => applyCharacterBuffs(4, ultBuff)} />}
            label={pela.ultimateDescription(ultimateLevel).name}
            />
            <div>{pela.ultimateDescription(ultimateLevel).description}</div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[5] || false} onChange={() => applyCharacterBuffs(5, techBuff)} />}
            label={pela.techniqueDescription().name}
            />
            <div>{pela.techniqueDescription().description}</div>

            {eidolon >= 4 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[6] || false} onChange={() => applyCharacterBuffs(6, E4Buff)} />}
                    label={"(E4) " + pela.E4().name}
                    />
                    <div>{pela.E4().description}</div>
                </>
            }
        </>
    )
}


export const PelaGlobalBuffs = (props) => {

        
    const pela = new Pela();

    const pelaBuffStates = props.buffStates;
    const setPelaBuffStates = props.setBuffStates;
    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {
        if(pelaBuffStates[0]) {
            updateStatesFromJson(A4Buff, true);
            pelaBuffStates[0] = false;
        }
        setDone(!done);
    }, [characterSwitched]);

    const { updateStatesFromJson } = useContext(globalData);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...pelaBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setPelaBuffStates(updatedStates);
    }

    const A4Buff = {
        effect: {
            effectHitRate: 0.1,
        },
    }

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={pelaBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, A4Buff)} />}
            label={"(A4) " + pela.A4().name}
            />
            <div>{pela.A4().description}</div>
        </>
    )
}   

export const PelaGlobalDebuffs = (props) => {

    const pelaBuffStates = props.buffStates;
    const setPelaBuffStates = props.setBuffStates;
    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    const { updateStatesFromJson } = useContext(globalData);
    const [ultimateLevel, setUltimateLevel] = useState(0);
    const [prevUltimateLevel, setPrevUltimateLevel] = useState(ultimateLevel);


    useEffect(() => {
        if(pelaBuffStates[1]) {
            updateStatesFromJson(ultBuff, true);
            pelaBuffStates[1] = false;
        }
        if(pelaBuffStates[2]) {
            updateStatesFromJson(techBuff, true);
            pelaBuffStates[2] = false;
        }
        if(pelaBuffStates[3]) {
            updateStatesFromJson(E4Buff, true);
            pelaBuffStates[3] = false;
        }

        setDone(!done);
    }, [characterSwitched]);

    const pela = new Pela();

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...pelaBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setPelaBuffStates(updatedStates);
    }

    const updateUltLevel = (event) => {
        setUltimateLevel(event.target.value);
    }

    const techBuff = {
        effect: {
            defIgnore: 0.2,
        },
    }

    const ultBuff = {
        effect: {
            defIgnore: pela.ultimateAttack(ultimateLevel).defIgnore,
        },
    }


    const E4Buff = {
        effect: {
            icePen: 0.12,
        },
    }

    useEffect(() => {
        if(pelaBuffStates[1]) {
            const tempUltBuff = {
                effect: {
                    defIgnore: pela.ultimateAttack(prevUltimateLevel).defIgnore,
                },
            }
            updateStatesFromJson(tempUltBuff, true);
            
            const newTempUltBuff = {
                effect: {
                    defIgnore: pela.ultimateAttack(ultimateLevel).defIgnore,
                },
            }
            updateStatesFromJson(newTempUltBuff, false);
        }
        
        setPrevUltimateLevel(ultimateLevel);
    }, [ultimateLevel]);

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={pelaBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, ultBuff)} />}
            label={pela.ultimateDescription(ultimateLevel).name}
            />
            <div  className="mb-3">{pela.simpleUlt(ultimateLevel).description}</div>
            <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                <div style={{display: "flex"}}>
                    <div>Ult Level</div>
                    <select className="levels buff-input" style={{width: "45%"}} value={ultimateLevel} onChange={updateUltLevel}>
                        <option value={0}>1</option>
                        <option value={1}>2</option>
                        <option value={2}>3</option>
                        <option value={3}>4</option>
                        <option value={4}>5</option>
                        <option value={5}>6</option>
                        <option value={6}>7</option>
                        <option value={7}>8</option>
                        <option value={8}>9</option>
                        <option value={9}>10</option>
                        <option value={10}>11</option>
                        <option value={11}>12</option>
                    </select>
                </div>
            </div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={pelaBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, techBuff)} />}
            label={pela.techniqueDescription().name}
            />
            <div>{pela.simpleTech().description}</div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={pelaBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, E4Buff)} />}
            label={"(E4) " + pela.E4().name}
            />
            <div>{pela.E4().description}</div>
        </>
    )
}  

export default PelaBuffs;