import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';

import { Kafka } from "../../../../../../../database/characters";

const KafkaBuffs = () => {

    const {eidolon, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    
    const kafka = new Kafka();

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);

    }

    const E2Buff = {
        effect: {
            dotBoost: 0.25,
        },
    }

    useEffect(() => {
        if(eidolon < 2 && characterBuffStates[3]) {
            updateStatesFromJson(E2Buff, true);
            characterBuffStates[3] = false;
        }
    }, [eidolon]);


    return (
        <>
            {eidolon >= 2 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, E2Buff)} />}
                    label={"(E2) " + kafka.E2().name}
                    />
                    <div>{kafka.E2().description}</div>
                </>
            }
        </>
    )
}

export const KafkaGlobalBuffs = (props) => {

    const kafkaBuffStates = props.buffStates;
    const setKafkaBuffStates = props.setBuffStates;
    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    const { updateStatesFromJson } = useContext(globalData);

    const E2Buff = {
        effect: {
            dotBoost: 0.25,
        },
    }

    const kafka = new Kafka();

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...kafkaBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setKafkaBuffStates(updatedStates);

    }

    useEffect(() => {

        if(kafkaBuffStates[0]) {
            updateStatesFromJson(E2Buff, true);
            kafkaBuffStates[0] = false;
        }

        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={kafkaBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, E2Buff)} />}
            label={"(E2) " + kafka.E2().name}
            />
            <div>{kafka.E2().description}</div>
        </>
    )
}

export default KafkaBuffs;