export class Loop {
    constructor() {
        this.stats = [
            [1, 14.4, 12, 38],
            [20, 55.44, 46.2, 148],
            [20, 72.72, 60.6, 194],
            [40, 138.96 ,115.8,371],
            [40, 162 ,236.135,432],
            [50, 183.6, 267.153, 490],
            [50, 206.64,172.2, 551],
            [60, 228.24,190.2, 609],
            [60, 251.28,209.4, 670],
            [70, 272.88,227.4, 728],
            [70, 295.92,246.6, 789],
            [80, 317.52,264.6, 847],
        ];
        
        this.name = "Loop";
        this.path = "Nihility";
        this.id = "loop";
        this.rarity = "threeStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases DMG dealt from its wearer to Slowed enemies by  </div>
                <div className="buff-text">{((0.24 + imposition * 0.06) * 100).toFixed(1)}%.</div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{},
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "adve1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div>Increases DMG dealt from its wearer to Slowed enemies by  </div>
                        <div className="buff-text">{((0.24 + imposition * 0.06) * 100).toFixed(1)}%.</div>
                    </div>
                ),
                effect: {
                    iceDmg: 0.24 + imposition * 0.06,
                    quantumDmg: 0.24 + imposition * 0.06,
                    fireDmg: 0.24 + imposition * 0.06,
                    lightningDmg: 0.24 + imposition * 0.06,
                    windDmg: 0.24 + imposition * 0.06,
                    physicalDmg: 0.24 + imposition * 0.06,
                    imaginaryDmg: 0.24 + imposition * 0.06,
                },
                type: "buff",
                hasStack: false,
                hasInput: false,
            },
        }
    }
}

export default Loop;