export class Goodnight {
    constructor() {
        this.stats = [
            [1, 21.6, 15, 43],
            [20, 83.16, 57.75, 166],
            [20, 109.08, 75.75, 218],
            [40, 208.44, 144.75,417],
            [40, 243, 168.75,  486],
            [50, 275.4, 191.25,551],
            [50, 309.96, 215.25,620],
            [60, 342.36, 237.75,685],
            [60, 376.92, 261.75,754],
            [70, 409.32, 284.25,819],
            [70, 443.88, 308.25,888],
            [80, 476.28, 330.75,953],
        ];
        
        this.name = "Good Night and Sleep Well";
        this.path = "Nihility";
        this.id = "goodnight";
        this.rarity = "fourStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>For every debuff the target enemy has, the </div>
                <div className="buff-text"> DMG dealt </div>
                <div> by the wearer increases by </div>
                <div className="buff-text">{((0.12 + imposition * 0.03) * 100).toFixed(1)}%.</div>
                <div> stacking up to 3 time(s). This effect also applies to DoT. </div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{},
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition, stack) {
        return {
            0: {
                id: "adve1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div>For every debuff the target enemy has, the </div>
                        <div className="buff-text"> DMG dealt </div>
                        <div> by the wearer increases by </div>
                        <div className="buff-text">{((0.12 + imposition * 0.03) * 100).toFixed(1)}%.</div>
                        <div> stacking up to 3 time(s). This effect also applies to DoT. </div>
                    </div>
                ),
                effect: {
                    iceDmg: (0.12 + imposition * 0.03) * stack,
                    quantumDmg: (0.12 + imposition * 0.03) * stack,
                    fireDmg: (0.12 + imposition * 0.03) * stack,
                    lightningDmg: (0.12 + imposition * 0.03) * stack,
                    windDmg: (0.12 + imposition * 0.03) * stack,
                    physicalDmg: (0.12 + imposition * 0.03) * stack,
                    imaginaryDmg: (0.12 + imposition * 0.03) * stack,
                },
                type: "buff",
                hasStack: true,
                stacks: 3,
                hasInput: false,
            },
        }
    }
}

export default Goodnight;