import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';

import { SilverWolf } from "../../../../../../../database/characters";

const SilverWolfBuffs = () => {

    const {eidolon, character, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    
    const silverwolf = new SilverWolf();

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    useEffect(() => {

        if(eidolon < 4 && characterBuffStates[0]) {
            character.setE4(false);
            character.setE4Stacks(0);
            const updatedStates = [...characterBuffStates];
            updatedStates[0] = false;
            setE4Stacks(1);
            setCharacterBuffStates(updatedStates);
        }
        if(eidolon < 6 && characterBuffStates[1]) {
            updateStatesFromJson(E6Buff, true);
            setE6Stacks(1);
            characterBuffStates[1] = false;
        }
        

    }, [eidolon]);

    const [E4Stacks, setE4Stacks] = useState(1);
    const [E6Stacks, setE6Stacks] = useState(1);
    
    const E6Buff = {
        effect: {
            quantumDmg: 0.2 * E6Stacks,
            iceDmg: 0.2 * E6Stacks,
            fireDmg: 0.2 * E6Stacks,
            lightningDmg: 0.2 * E6Stacks,
            windDmg: 0.2 * E6Stacks,
            imaginaryDmg: 0.2 * E6Stacks,
            physicalDmg: 0.2 * E6Stacks,
        }
    }

    const turnE4On = (index) => {
        const updatedStates = [...characterBuffStates];
        updatedStates[index] = !updatedStates[index];

        if(updatedStates[index]) {
            character.setE4Stacks(E4Stacks);
        } else {
            character.setE4Stacks(0);
        }
        character.setE4(updatedStates[index]);
        setCharacterBuffStates(updatedStates);
    }

    const updateE6Stacks = (e) => {

        if(characterBuffStates[1]) {
            updateStatesFromJson(E6Buff, true);
        }

        if(characterBuffStates[1]) {
            const tempE6Buff = {
                effect: {
                    quantumDmg: 0.2 * e.target.value,
                    iceDmg: 0.2 * e.target.value,
                    fireDmg: 0.2 * e.target.value,
                    lightningDmg: 0.2 * e.target.value,
                    windDmg: 0.2 * e.target.value,
                    imaginaryDmg: 0.2 * e.target.value,
                    physicalDmg: 0.2 * e.target.value,
                },
            }
            updateStatesFromJson(tempE6Buff, false);
        }
        setE6Stacks(e.target.value);
    }

    const updateE4Stacks = (e) => {
        setE4Stacks(parseInt(e.target.value)); 
        character.setE4Stacks(parseInt(e.target.value));
        const updatedStates = [...characterBuffStates];
        setCharacterBuffStates(updatedStates);
    }

    return (
        <>

            {eidolon >= 4 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => turnE4On(0)} />}
                    label={"(E4) " + silverwolf.E4().name}
                    />
                    <div>{silverwolf.E4().description}</div>
                    <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                        <div style={{display: "flex"}}>
                            <div>Debuffs </div>
                            <select className="levels buff-input" style={{width: "45%"}} value={E4Stacks} onChange={updateE4Stacks}>
                                <option value={1}>1</option>
                                <option value={2}>2</option>
                                <option value={3}>3</option>
                                <option value={4}>4</option>
                                <option value={5}>5</option>
                            </select>
                        </div>
                    </div>
                </>
            }

            {eidolon >= 6 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, E6Buff)} />}
                    label={"(E6) " + silverwolf.E6().name}
                    />
                    <div>{silverwolf.E6().description}</div>
                    <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                        <div style={{display: "flex"}}>
                            <div>Debuffs </div>
                            <select className="levels buff-input" style={{width: "45%"}} value={E6Stacks} onChange={updateE6Stacks}>
                                <option value={1}>1</option>
                                <option value={2}>2</option>
                                <option value={3}>3</option>
                                <option value={4}>4</option>
                                <option value={5}>5</option>
                            </select>
                        </div>
                    </div>
                </>
            }
        </>
    )
}

export const SilverWolfGlobalBuffs = (props) => {
    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {
        setDone(!done);
    }, [characterSwitched]);


    return (
        <>
            <div>No Buffs</div>
        </>
    )
}   


export default SilverWolfBuffs;