import React from 'react';
import {Dialog, DialogTitle, DialogContent} from '@mui/material';
import baseAtk from '../../assets/baseAtkExplain.webp';
import baseAtkInGame from '../../assets/baseAtkExplainInGame.webp';
import dmgBonusCalc from '../../assets/DmgBoostExplain.webp';
import dmgBoostInGame from '../../assets/DmgBoostExplainInGame.webp';
import DmgTestCalc from '../../assets/qqDmgTestInCalc.webp';
import DmgTestInGame from '../../assets/qqDmgTest.webp';
import enemyInGame from '../../assets/enemyStats.webp';
import enemyInCalc from '../../assets/enemyStatsInGame.webp';


function Intro(props) {

    const open = props.open;
    const setOpen = props.setOpen;

    return (
        <div className="dialog">
            <Dialog aria-label='dialog-title' open={open} onClose={() => setOpen(false)} maxWidth="md">
                <DialogTitle className='dialog-container' id='dialog-title'> 
                <div style={{display: "flex", justifyContent: "space-between", alignItems: "center"}}>
                    <div>Introduction</div>
                    <button onClick={() => setOpen(false)} className='calc-btn' style={{width: "37px", display: "flex", backgroundColor: "red", textAlign:"center", alignItems: "center", justifyContent: "center", margin: "0"}}>&#10005;</button>
                </div>
                </DialogTitle>
                <DialogContent className='dialog-content'>
                    <div className='date'>About me</div>
                    <div className='changes'>- Hi, I am Zeeka, the creator of this calculator, I made this app in an attempt to learn the React Framework better and to test different builds in star rail without needing to log into the game each time to try something new.</div>
                    <div className='changes'>- If you encounter any bugs please let me know and ill fix them right away, you can reach me through my <a href='https://www.reddit.com/user/Zeeka-' target='_blank'>reddit</a> or on discord, my username is zeeka32</div>
                    <div className='date'>About This Calculator</div>
                    <div className='changes'>- This application doesn't work very well on mobile phones with screen sizes less than 400px in width</div>
                    <div className='changes'>- Due to rounding errors and such the results aren't 100% accurate, but on average the margin of error was less than 0.03%, so its still very accurate</div>
                    <div className='changes'>- Finally this calculator is not official and is not affiliated with Hoyoverse</div>
                    <div className='date'>FAQ</div>
                    <div className='changes'>- Where are the traces ?</div>
                    <div className='changes'>traces are included with the levels, which means at level 60 for example, A2, A4 and the minor traces unlocked at that level will be included automatically.</div>
                    <div className='changes'>- I changed my charcter's level/eidolon but the buffs didn't appear.</div>
                    <div className='changes'>If a buff doesn't have a condition to activate it will generally be added automatically when you reach that eidolon/trace.</div>
                    <div className='changes'>- I have equipped and added all relics like in game, but the stats don't match .. why is that ? </div>
                    <div className='changes'>This is probably because of the way traces are baked into the levels, if you set your character to level 80 but haven't unlocked the ATK +8% trace for example, the attack in the calculator will be more than in game. </div>
                    <br/>
                    <div className='changes'>Generally, you just want to make sure the base ATK/HP/DEF and ATK/HP/DEF bonus matches what is in game like in this image.</div>
                    <div style={{display: "flex", alignItems: "center", justifyContent: "space-around"}}>
                        <img src={baseAtk} loading='lazy' alt='baseAtkExample' style={{width: "25%"}}/>
                        <img src={baseAtkInGame} loading='lazy' alt='baseAtkExampleInGame' style={{width: "45%"}}/>
                    </div>
                    <br/>
                    <div className='changes'>For Qinque, she only scales with ATK so we only need to check that stat and make sure it matches.</div>
                    <div className='changes'>Next is your CRIT DMG, that should also match your CRIT DMG in game, and lastly, your elemental DMG Bonus should also match, this is a Qingque with 3 stacks of her skill at level 80, skill lv 10, so thats +114% DMG boost from her skill</div>
                    <div style={{display: "flex", alignItems: "center", justifyContent: "space-around"}}>
                        <img src={dmgBonusCalc} loading='lazy' alt='baseAtkExample' style={{width: "25%"}}/>
                        <img src={dmgBoostInGame} loading='lazy' alt='baseAtkExampleInGame' style={{width: "45%"}}/>
                    </div>
                    <br/>
                    <div className='changes'>And for the final metric, which is the enemy, we are testing her against Cocolia, who is at lvl 66 and is weak to quantum so her quantum Resistance is at 0%, you can check all of this data on the star rail fandom</div>
                    <div style={{display: "flex", alignItems: "center", justifyContent: "space-around"}}>
                        <img src={enemyInCalc} loading='lazy' alt='baseAtkExample' style={{width: "25%"}}/>
                        <img src={enemyInGame} loading='lazy' alt='baseAtkExampleInGame' style={{width: "45%"}}/>
                    </div>
                    <br/>
                    <div className='changes'>Nice, all of our stats match what is in game, so based on the calculator the expected DMG from her enhanced basic ATK should be 25139 </div>
                    <div style={{display: "flex", alignItems: "center", justifyContent: "space-around"}}>
                        <img src={DmgTestCalc} loading='lazy' alt='baseAtkExample' style={{width: "40%"}}/>
                        <img src={DmgTestInGame} loading='lazy' alt='baseAtkExampleInGame' style={{width: "45%"}}/>
                    </div>
                    <br/>
                    <div className='changes'>Why isn't it an exact match ? well, thats because of rounding errors, the result is still 99.9% from the original DMG, and thats how you get the most accurate results.</div>
                </DialogContent>
            </Dialog>
        </div>
    )
}

export default Intro;