export class Carve {
    constructor() {
        this.stats = [
            [1,  21.6,   15,     43],
            [20, 83.16,  57.75,  166],
            [20, 109.08,  75.75,  218],
            [40, 208.44, 144.75, 417],
            [40, 243,    168.75, 486],
            [50, 275.4,  191.25, 551],
            [50, 309.96, 215.25, 620],
            [60, 342.36, 237.75, 685],
            [60, 376.92, 261.75, 754],
            [70, 409.32, 284.25, 819],
            [70, 443.88, 308.25, 888],
            [80, 476.28, 330.75, 953],
        ];
        
        this.name = "Carve the Moon, Weave the Clouds";
        this.path = "Harmony";
        this.id = "carve";
        this.rarity = "fourStar";
        this.activeBuff = "buff1";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>At the start of the battle and whenever the wearer's turn begins, one of the following effects is applied randomly: All allies' </div>
                <div className="buff-text"> ATK </div> 
                <div> increases by </div> 
                <div className="buff-text">{((0.1 + imposition * 0.02) * 100).toFixed(1)}%,</div>
                <div> all allies' </div> 
                <div className="buff-text"> CRIT DMG </div> 
                <div> increases by </div> 
                <div className="buff-text">{((0.12 + imposition * 0.03) * 100).toFixed(1)}%,</div>
                <div> or all allies' </div> 
                <div className="buff-text"> Energy Regeneration Rate </div> 
                <div> increases by </div> 
                <div className="buff-text">{((0.06 + imposition * 0.01) * 100).toFixed(1)}%.</div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{},
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {

        if(this.activeBuff === "buff1") {
            return {
                0: {
                    id: "adve1",
                    name: this.name,
                    description: (
                        <div className="buff-description">
                            <div> All allies' </div> 
                            <div className="buff-text"> ATK </div> 
                            <div> increases by </div> 
                            <div className="buff-text">{((0.1 + imposition * 0.02) * 100).toFixed(1)}%,</div>
                            <div> all allies' </div> 
                            <div className="buff-text"> CRIT DMG </div> 
                            <div> increases by </div> 
                            <div className="buff-text">{((0.12 + imposition * 0.03) * 100).toFixed(1)}%,</div>
                            <div> or all allies' </div> 
                            <div className="buff-text"> Energy Regeneration Rate </div> 
                            <div> increases by </div> 
                            <div className="buff-text">{((0.06 + imposition * 0.01) * 100).toFixed(1)}%.</div> 
                        </div>
                    ),
                    effect: {
                        atkPercent: 0.1 + imposition * 0.02,
                    },
                    type: "buff",
                    hasStack: false,
                    hasInput: false,
                },
            }
        }else if (this.activeBuff === "buff2") {
            return {
                0: {
                    id: "adve1",
                    name: this.name,
                    description: (
                        <div className="buff-description">
                            <div> All allies' </div> 
                            <div className="buff-text"> ATK </div> 
                            <div> increases by </div> 
                            <div className="buff-text">{((0.1 + imposition * 0.02) * 100).toFixed(1)}%,</div>
                            <div> all allies' </div> 
                            <div className="buff-text"> CRIT DMG </div> 
                            <div> increases by </div> 
                            <div className="buff-text">{((0.12 + imposition * 0.03) * 100).toFixed(1)}%,</div>
                            <div> or all allies' </div> 
                            <div className="buff-text"> Energy Regeneration Rate </div> 
                            <div> increases by </div> 
                            <div className="buff-text">{((0.06 + imposition * 0.01) * 100).toFixed(1)}%.</div> 
                        </div>
                    ),
                    effect: {
                        critDmg: 0.12 + imposition * 0.03,
                    },
                    type: "buff",
                    hasStack: false,
                    hasInput: false,
                },
            }
        }else if(this.activeBuff === "buff3") {
            return {
                0: {
                    id: "adve1",
                    name: this.name,
                    description: (
                        <div className="buff-description">
                            <div> All allies' </div> 
                            <div className="buff-text"> ATK </div> 
                            <div> increases by </div> 
                            <div className="buff-text">{((0.1 + imposition * 0.02) * 100).toFixed(1)}%,</div>
                            <div> all allies' </div> 
                            <div className="buff-text"> CRIT DMG </div> 
                            <div> increases by </div> 
                            <div className="buff-text">{((0.12 + imposition * 0.03) * 100).toFixed(1)}%,</div>
                            <div> or all allies' </div> 
                            <div className="buff-text"> Energy Regeneration Rate </div> 
                            <div> increases by </div> 
                            <div className="buff-text">{((0.06 + imposition * 0.01) * 100).toFixed(1)}%.</div> 
                        </div>
                    ),
                    effect: {
                        ERR: 0.06 + imposition * 0.01,
                    },
                    type: "buff",
                    hasStack: false,
                    hasInput: false,
                },
            }
        }

    }

    // add needed variables to class themselves, then modify them before applying base effect, or modify them mid usage and just remove when needed

    setActiveBuff(buff) {
        this.activeBuff = buff;
    }
}

export default Carve;