import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';

import { TrailblazerFire } from "../../../../../../../database/characters";

const FireTBBuffs = () => {

    const {level, eidolon, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    
    const tb = new TrailblazerFire();

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    const [stacks, setStacks] = useState(1);

    const A6Buff = {
        effect: {
            atkPercent: 0.15,
        }
    }


    const E6Buff = {
        effect: {
            defPercent: 0.1 * stacks,
        },
    }

    useEffect(() => {
        if(eidolon < 6 && characterBuffStates[4]) {
            updateStatesFromJson(E6Buff, true);
            characterBuffStates[4] = false;
        }
    }, [eidolon]);

    useEffect(() => {
        
        if(level < 11 && characterBuffStates[2]) {
            updateStatesFromJson(A6Buff, true);
            characterBuffStates[2] = false;
        }

    }, [level]);


    const updateStacks = (event) => {

        if(characterBuffStates[4]) {
            const tempUltBuff = {
                effect: {
                    defPercent: 0.15 * stacks,
                }
            }
            updateStatesFromJson(tempUltBuff, true);
            
            const newTempUltBuff = {
                effect: {
                    defPercent: 0.15 * event.target.value,
                }
            }
            updateStatesFromJson(newTempUltBuff, false);
        }

        setStacks(event.target.value);
    }

    return (
        <>


            {
            level >= 10 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, A6Buff)} />}
                    label={"(A6) " + tb.A6().name}
                    />
                    <div>{tb.A6().description}</div>
                </>
            }

            {eidolon >= 6 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[4] || false} onChange={() => applyCharacterBuffs(4, E6Buff)} />}
                    label={"(E6) " + tb.E6().name}
                    />
                    <div>{tb.E6().description}</div>
                    <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                    <div style={{display: "flex"}}>
                        <div>Stacks</div>
                        <select className="levels buff-input" style={{width: "45%"}} value={stacks} onChange={updateStacks}>
                            <option value={1}>1</option>
                            <option value={2}>2</option>
                            <option value={3}>3</option>
                        </select>
                    </div>
                </div>
                </>
            }
        </>
    )
}

export const FireTBGlobalBuffs = (props) => {

    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {
        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <div>No Buffs</div>
        </>
    )
}


export default FireTBBuffs;