export class Peaceful {
    constructor() {
        this.stats = [
            [1, 24,    15,     38 ],
            [20, 92.4,  57.75,  148],
            [20, 121.2, 75.75,  194],
            [40, 231.6, 144.75, 371],
            [40, 270,    168.75, 432],
            [50, 306,  191.25, 490],
            [50, 344.4, 215.25, 551],
            [60, 380.4, 237.75, 609],
            [60, 418.8, 261.75, 670],
            [70, 454.8, 284.25, 728],
            [70, 493.2, 308.25, 789],
            [80, 529.2, 330.75, 847],
        ];
        
        this.name = "Today Is Another Peaceful Day";
        this.path = "Erudition";
        this.id = "peaceful";
        this.rarity = "fourStar";
        this.maxEnergy = 120;
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>After entering battle, increases the wearer's </div>
                <div className="buff-text"> DMG </div> 
                <div> based on their </div> 
                <div className="buff-text"> Max Energy </div>
                <div> DMG increases by </div>
                <div className="buff-text"> {((0.002 + imposition * 0.0005) * 100).toFixed(1)}%.</div> 
                <div> per point of Energy, up to 160 Energy. </div> 
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "silenceb",
            name: this.name,
            effect:{
                iceDmg: (0.002 + imposition * 0.0005) * this.maxEnergy,
                quantumDmg: (0.002 + imposition * 0.0005) * this.maxEnergy,
                fireDmg: (0.002 + imposition * 0.0005) * this.maxEnergy,
                lightningDmg: (0.002 + imposition * 0.0005) * this.maxEnergy,
                windDmg: (0.002 + imposition * 0.0005) * this.maxEnergy,
                physicalDmg: (0.002 + imposition * 0.0005)* this.maxEnergy,
                imaginaryDmg: (0.002 + imposition * 0.0005) * this.maxEnergy,
            },
            type: "buff",
            hasStack: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                // id: "silence0",
                // name: this.name,
                // description: (
                //     <div className="buff-description">
                //         <div>After entering battle, increases the wearer's </div>
                //         <div className="buff-text"> DMG </div> 
                //         <div> based on their </div> 
                //         <div className="buff-text"> Max Energy </div>
                //         <div> DMG increases by </div>
                //         <div className="buff-text"> {((0.002 + imposition * 0.0005) * 100).toFixed(1)}%.</div> 
                //         <div> per point of Energy, up to 160 Energy. </div> 
                //     </div>
                // ),
                // effect: {

                // },
                // type: "buff",
                // hasStack: false,
                // hasInput: false,
            },
        }
    }

    setMaxEnergy(energy) {
        this.maxEnergy = energy;
    }
}

export default Peaceful;