import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';

import { Himeko } from "../../../../../../../database/characters";

const HimekoBuffs = () => {

    const { eidolon, level, characterBuffStates, talentLevel, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    
    const himeko = new Himeko();


    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    useEffect(() => {

        if(eidolon < 1 && characterBuffStates[3]) {
            updateStatesFromJson(E1Buff, true);
            characterBuffStates[3] = false;
        }

        if(eidolon < 2 && characterBuffStates[2]) {
            updateStatesFromJson(E2Buff, true);
            characterBuffStates[2] = false;
        }

    }, [eidolon]);

    useEffect(() => {

        if(level < 6 && characterBuffStates[0]) {
            updateStatesFromJson(A4Buff, true);
            characterBuffStates[0] = false;
        }

        if(level < 10 && characterBuffStates[1]) {
            updateStatesFromJson(A6Buff, true);
            characterBuffStates[1] = false;
        }

    }, [level]);

    const A4Buff = {
        effect: {
            skillBoost: 0.2,
        }
    }

    const A6Buff = {
        effect: {
            critRate: 0.15,
        }
    }

    const E1Buff = {
        effect: {
            spdPercent: 0.2,
        }
    }

    const E2Buff = {
        effect: {
            quantumDmg: 0.15,
            fireDmg: 0.15,
            lightningDmg: 0.15,
            windDmg: 0.15,
            physicalDmg: 0.15,
            imaginaryDmg: 0.15,
            iceDmg: 0.15,
        },
    }

    return (
        <>  

            {level >= 6 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, A4Buff)} />}
                    label={"(A4) " + himeko.A4().name}
                    />
                    <div>{himeko.A4().description}</div>
                </>
            }

            {level >= 10 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, A6Buff)} />}
                    label={"(A6) " + himeko.A6().name}
                    />
                    <div>{himeko.A6().description}</div>
                </>
            }

            {eidolon >= 1 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, E1Buff)} />}
                    label={"(E1) " + himeko.E1().name}
                    />
                    <div>{himeko.E1().description}</div>
                </>
            }

            {eidolon >= 2 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, E2Buff)} />}
                    label={"(E2) " + himeko.E2().name}
                    />
                    <div>{himeko.E2().description}</div>
                </>
            }

        </>
    )
}

export const HimekoGlobalBuffs = (props) => {

    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {
        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <div>No Buffs</div>
        </>
    )
}

export default HimekoBuffs;