import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';

import { Arlan } from "../../../../../../../database/characters";

const ArlanBuffs = () => {

    const { character, talentLevel, eidolon, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    
    const arlan = new Arlan();

    const [hpLost, setHpLost] = useState(1);
    const [prevHpLost, setPrevHpLost] = useState(1);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    useEffect(() => {
        if(eidolon < 1 && characterBuffStates[1]) {
            updateStatesFromJson(E1Buff, true);
            characterBuffStates[1] = false;
        }

        if(eidolon < 6 && characterBuffStates[2]) {
            character.setE6ON(false);
            const updatedStates = [...characterBuffStates];
            updatedStates[2] = false;
            setCharacterBuffStates(updatedStates);
        }

    }, [eidolon]);

    useEffect(() => {

        if(characterBuffStates[0]) {
            
            const tempTalentBuff = {
                effect: {
                    quantumDmg: 0.01 * prevHpLost,
                    fireDmg: 0.01 * prevHpLost,
                    lightningDmg: 0.01 * prevHpLost,
                    windDmg: 0.01 * prevHpLost,
                    physicalDmg: 0.01 * prevHpLost,
                    imaginaryDmg: 0.01 * prevHpLost,
                    iceDmg: 0.01 * prevHpLost,
                },
            }

            updateStatesFromJson(tempTalentBuff, true);
            updateStatesFromJson(talentBuff, false);
        }



        const updatedStates = [...characterBuffStates];
        setCharacterBuffStates(updatedStates);
        
        setPrevHpLost(hpLost);
    }, [hpLost]);

    const talentBuff = {
        effect: {
            quantumDmg: 0.01 * hpLost,
            fireDmg: 0.01 * hpLost,
            lightningDmg: 0.01 * hpLost,
            windDmg: 0.01 * hpLost,
            physicalDmg: 0.01 * hpLost,
            imaginaryDmg: 0.01 * hpLost,
            iceDmg: 0.01 * hpLost,
        },
    }

    const E1Buff = {
        effect: {
            skillBoost: 0.1,
        }
    }

    const handleLossChange = (event) => {
        const re = /^[0-9]*\.?[0-9]+$/;
        if (event.target.value === '' || !re.test(event.target.value)) {
            return;
        }

        if(event.target.value > (arlan.talent(talentLevel) * 100)) {
            setHpLost((arlan.talent(talentLevel) * 100));
        } else {
            setHpLost((event.target.value));
        }

    }

    const turnE6ON = (index) => {
        const updatedStates = [...characterBuffStates];
        updatedStates[index] = !updatedStates[index];
        character.setE6ON(updatedStates[index]);
        setCharacterBuffStates(updatedStates);
    }

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, talentBuff)} />}
            label={arlan.talentDescription(talentLevel).name}
            />
            <div>{arlan.talentDescription(talentLevel).description}</div>
            <div style={{display: "flex", width: "60%"}}>
                <div className="centered">HP Lost</div>
                <input type="text" className="critDmg buff-input" style={{width: "40px"}} maxLength={3} value={parseInt(hpLost)} onChange={handleLossChange}/>
                <div>%</div>
            </div>

            {eidolon >= 1 &&
            <>
                <FormControlLabel
                className='header'
                control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, E1Buff)} />}
                label={"(E1) " + arlan.E1().name}
                />
                <div>{arlan.E1().description}</div>
            </>
            }

            {eidolon >= 6 &&
                <>
                <FormControlLabel
                className='header'
                control={<Checkbox checked={characterBuffStates[2] || false} onChange={() => turnE6ON(2)} />}
                label={"(E6) " + arlan.E6().name}
                />
                <div>{arlan.E6().description}</div>
            </>
            }

        </>
    )
}

export const ArlanGlobalBuffs = (props) => {

    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {
        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <div>No Buffs</div>
        </>
    )
}

export default ArlanBuffs;