import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';
import { Asta } from "../../../../../../../database/characters";

const AstaBuffs = () => {

    const {
        level, character, eidolon, skillLevel, ultimateLevel, talentLevel, characterBuffStates, setCharacterBuffStates, updateStatesFromJson 
    } = useContext(globalData);

    const asta = new Asta();

    const [prevTalentLevel, setPrevTalentLevel] = useState(talentLevel);
    const [prevUltimateLevel, setPrevUltimateLevel] = useState(ultimateLevel);
    const [stacks, setStacks] = useState(1);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];

        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    useEffect(() => {
        if(characterBuffStates[0]) {
            const tempUltBuff = {
                effect: {
                    spdFlat: character.ultimateAttack(prevUltimateLevel),
                },
            }
            updateStatesFromJson(tempUltBuff, true);

            const newTempUltBuff = {
                effect: {
                    spdFlat: character.ultimateAttack(ultimateLevel),
                },
            }
            updateStatesFromJson(newTempUltBuff, false);
        }
        
        setPrevUltimateLevel(ultimateLevel);
    }, [ultimateLevel]);

    useEffect(() => {
        if(characterBuffStates[1]) {
            const tempUltBuff = {
                effect: {
                    atkPercent: character.talent(prevTalentLevel) * stacks,
                }
            }
            updateStatesFromJson(tempUltBuff, true);

            const newTempUltBuff = {
                effect: {
                    atkPercent: character.talent(talentLevel) * stacks,
                }
            }
            updateStatesFromJson(newTempUltBuff, false);
        }
        
        setPrevTalentLevel(talentLevel);
    }, [talentLevel]);

    useEffect(() => {

        if(eidolon < 4 && characterBuffStates[4]) {
            updateStatesFromJson(E4Buff, true);
            characterBuffStates[4] = false;
        }
    }, [eidolon]);

    useEffect(() => {
        
        if(level < 6 && characterBuffStates[3]) {
            updateStatesFromJson(A4Buff, true);
            characterBuffStates[3] = false;
        }

    }, [level]);

    const ultBuff = {
        effect: {
            spdFlat: character.ultimateAttack(ultimateLevel),
        },
    }

    const talentBuff = {
        effect: {
            atkPercent: character.talent(talentLevel) * stacks,
        }
    }

    const A4Buff = {
        effect: {
            fireDmg: 0.18,
        },
    }

    const E4Buff = {
        effect: {
            ERR: 0.15,
        }
    }

    const updateStacks = (event) => {

        if(characterBuffStates[1]) {
            const tempUltBuff = {
                effect: {
                    atkPercent: character.talent(talentLevel) * stacks,
                }
            }
            updateStatesFromJson(tempUltBuff, true);
            
            const newTempUltBuff = {
                effect: {
                    atkPercent: character.talent(talentLevel) * event.target.value,
                }
            }
            updateStatesFromJson(newTempUltBuff, false);
        }

        setStacks(event.target.value);
    }

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, ultBuff)} />}
            label={asta.ultimateDescription(ultimateLevel).name}
            />
            <div>{asta.ultimateDescription(ultimateLevel).description}</div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, talentBuff)} />}
            label={asta.talentDescription(talentLevel).name}
            />
            <div>{asta.talentSimple(talentLevel).description}</div>
            <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                <div style={{display: "flex"}}>
                    <div>Stacks</div>
                    <select className="levels buff-input" style={{width: "45%"}} value={stacks} onChange={updateStacks}>
                        <option value={1}>1</option>
                        <option value={2}>2</option>
                        <option value={3}>3</option>
                        <option value={4}>4</option>
                        <option value={5}>5</option>
                    </select>
                </div>
            </div>

            {level >= 6 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, A4Buff)} />}
                    label={"(A4) " + asta.A4().name}
                    />
                    <div>{asta.A4().description}</div>
                </>
            }

            {eidolon >= 4 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[4] || false} onChange={() => applyCharacterBuffs(4, E4Buff)} />}
                    label={"(E4) " + asta.E4().name}
                    />
                    <div>{asta.E4().description}</div>
                </>
            }
        </>
    )
}

export const AstaGlobalBuffs = (props) => {
    
    const { updateStatesFromJson } = useContext(globalData);
    const asta = new Asta();

    const [talentLevel, setTalentLevel] = useState(0);
    const [ultimateLevel, setUltimateLevel] = useState(0);
    
    const astaBuffStates = props.buffStates;
    const setAstaBuffStates = props.setBuffStates;
    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;
    
    const [prevTalentLevel, setPrevTalentLevel] = useState(talentLevel);
    const [prevUltimateLevel, setPrevUltimateLevel] = useState(ultimateLevel);
    const [stacks, setStacks] = useState(1);

    const updateUltLevel = (event) => {
        setUltimateLevel(event.target.value);
    }

    const updateTalentLevel = (event) => {
        setTalentLevel(event.target.value);
    }

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...astaBuffStates];

        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setAstaBuffStates(updatedStates);
    }
    
    useEffect(() => {
        if(astaBuffStates[0]) {
            const tempUltBuff = {
                effect: {
                    spdFlat: asta.ultimateAttack(prevUltimateLevel),
                },
            }
            updateStatesFromJson(tempUltBuff, true);

            const newTempUltBuff = {
                effect: {
                    spdFlat: asta.ultimateAttack(ultimateLevel),
                },
            }
            updateStatesFromJson(newTempUltBuff, false);
        }
        
        setPrevUltimateLevel(ultimateLevel);
    }, [ultimateLevel]);

    useEffect(() => {
        if(astaBuffStates[1]) {
            const tempUltBuff = {
                effect: {
                    atkPercent: asta.talent(prevTalentLevel) * stacks,
                }
            }
            updateStatesFromJson(tempUltBuff, true);

            const newTempUltBuff = {
                effect: {
                    atkPercent: asta.talent(talentLevel) * stacks,
                }
            }
            updateStatesFromJson(newTempUltBuff, false);
        }
        
        setPrevTalentLevel(talentLevel);
    }, [talentLevel]);

    const ultBuff = {
        effect: {
            spdFlat: asta.ultimateAttack(ultimateLevel),
        },
    }

    const talentBuff = {
        effect: {
            atkPercent: asta.talent(talentLevel) * stacks,
        }
    }

    const A4Buff = {
        effect: {
            fireDmg: 0.18,
        },
    }

    const updateStacks = (event) => {

        if(astaBuffStates[1]) {
            const tempUltBuff = {
                effect: {
                    atkPercent: asta.talent(talentLevel) * stacks,
                }
            }
            updateStatesFromJson(tempUltBuff, true);
            
            const newTempUltBuff = {
                effect: {
                    atkPercent: asta.talent(talentLevel) * event.target.value,
                }
            }
            updateStatesFromJson(newTempUltBuff, false);
        }

        setStacks(event.target.value);
    }

    useEffect(() => {
        if(astaBuffStates[0]) {
            updateStatesFromJson(ultBuff, true);
            astaBuffStates[0] = false;
        }

        if(astaBuffStates[1]) {
            updateStatesFromJson(talentBuff, true);
            astaBuffStates[1] = false;
        }

        if(astaBuffStates[3]) {
            updateStatesFromJson(A4Buff, true);
            astaBuffStates[3] = false;
        }

        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={astaBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, ultBuff)} />}
            label={asta.ultimateDescription(ultimateLevel).name}
            />
            <div className="mb-3">{asta.ultimateDescription(ultimateLevel).description}</div>
            <div style={{display: "flex"}}>
                <div>Ult Level</div>
                <select className="levels buff-input" style={{width: "15%"}} value={ultimateLevel} onChange={updateUltLevel}>
                    <option value={0}>1</option>
                    <option value={1}>2</option>
                    <option value={2}>3</option>
                    <option value={3}>4</option>
                    <option value={4}>5</option>
                    <option value={5}>6</option>
                    <option value={6}>7</option>
                    <option value={7}>8</option>
                    <option value={8}>9</option>
                    <option value={9}>10</option>
                    <option value={10}>11</option>
                    <option value={11}>12</option>
                </select>
            </div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={astaBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, talentBuff)} />}
            label={asta.talentDescription(talentLevel).name}
            />
            <div>{asta.talentSimple(talentLevel).description}</div>
            <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                <div style={{display: "flex"}}>
                    <div>Talent Lv</div>
                    <select className="levels buff-input" style={{width: "45%"}} value={talentLevel} onChange={updateTalentLevel}>
                        <option value={0}>1</option>
                        <option value={1}>2</option>
                        <option value={2}>3</option>
                        <option value={3}>4</option>
                        <option value={4}>5</option>
                        <option value={5}>6</option>
                        <option value={6}>7</option>
                        <option value={7}>8</option>
                        <option value={8}>9</option>
                        <option value={9}>10</option>
                        <option value={10}>11</option>
                        <option value={11}>12</option>
                    </select>
                </div>
                <div style={{display: "flex"}}>
                    <div>Stacks</div>
                    <select className="levels buff-input" style={{width: "45%"}} value={stacks} onChange={updateStacks}>
                        <option value={1}>1</option>
                        <option value={2}>2</option>
                        <option value={3}>3</option>
                        <option value={4}>4</option>
                        <option value={5}>5</option>
                    </select>
                </div>
            </div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={astaBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, A4Buff)} />}
            label={"(A4) " + asta.A4().name}
            />
            <div>{asta.A4().description}</div>

        </>
    )
}   

export default AstaBuffs;