import React, { useContext, useState, useEffect } from 'react';
import { globalData } from '../globalData';
import SelectionMenu from './setUpParts/optionsSelect';
import starImage from '../../assets/star.webp';
import Skills from './characterDetailsParts/skills';

function CharacterDetails() {
  const {
    character,
    quantumDmg,
    imaginaryDmg,
    atk,
    atkPercent,
    atkFlat,
    def,
    defPercent,
    defFlat,
    hpPercent,
    hp,
    hpFlat,
    spd,
    spdPercent,
    spdFlat,
    chars,
    critRate,
    critDmg,
    taunt,
    energy,
    breakEffect,
    outgoingHealing,
    energyRegen,
    effectHitRate,
    effectRes,
    physicalDmg,
    fireDmg,
    iceDmg,
    lightningDmg,
    windDmg,
    setCharacter,
    setCharRank,
    level,
    basicBoost,
    skillBoost,
    dotBoost,
    ultimateBoost,
    followUpBoost,
    showBeta,
  } = useContext(globalData);

    
  const handleSelection = (event) => {
    setCharacter(event.target.dataset.id);
  }
    
  const [characterImage, setCharacterImage] = useState("");
  const [open, setOpen] = useState(false);
  const [skillOpen, setSkillOpen] = useState(false);
  const [images, setImages] = useState("");
  const [isHPHovered, setIsHPHovered] = useState(false);
  const [isDefHovered, setIsDefHovered] = useState(false);
  const [isAtkHovered, setIsAtkHovered] = useState(false);
  const [isSpdHovered, setIsSpdHovered] = useState(false);
  const [filteredChars, setFilteredChars] = useState([]);

  const handleSelectChange = (event) => {
    const selectedIndex = event.target.selectedIndex;
    setCharRank(selectedIndex);
  };

  function loadImg (imageName) {
    import(`../../assets/characterImages/${imageName}.webp`).then((image) => setCharacterImage(image.default));
  }

  function loadAllImg(imageName) {
    return import(`../../assets/characterImages/${imageName}.webp`).then((image) => {
      return image.default;
      });
  }

  const handleHPMouseEnter = () => {
    setIsHPHovered(true);
  };

  const handleHPMouseLeave = () => {
    setIsHPHovered(false);
  };

  const handleAtkMouseEnter = () => {
    setIsAtkHovered(true);
  };

  const handleAtkMouseLeave = () => {
    setIsAtkHovered(false);
  };

  const handleDefMouseEnter = () => {
    setIsDefHovered(true);
  };

  const handleDefMouseLeave = () => {
    setIsDefHovered(false);
  };

  const handleSpdMouseEnter = () => {
    setIsSpdHovered(true);
  };

  const handleSpdMouseLeave = () => {
    setIsSpdHovered(false);
  };

  useEffect(() => {
    const newChars = chars.filter((char) => {
        if(showBeta) {
            return true;
        }else {
            return !char.isBeta;
        }
    });
    setFilteredChars(newChars);
}, [character, chars]);

useEffect(() => {
    const loadAllImages = async () => {
      const initial = Array(filteredChars.length).fill("");
      setImages(initial);

      const promises = filteredChars.map((char, index) => {
        return loadAllImg(char.id, index);
      });

      const loadedImages = await Promise.all(promises);
      setImages(loadedImages);
      
    };

    loadAllImages();
}, [filteredChars]);

  useEffect(() => {
    loadImg(character.id);
  }, [character]);


  let finalAtk = parseFloat((atk * atkPercent) + atkFlat).toFixed(1);
  let finalHp = parseFloat((hp * hpPercent) + hpFlat).toFixed(1);
  let finalDef = parseFloat((def * defPercent) + defFlat).toFixed(1);
  let finalSpd = parseFloat((spd * spdPercent) + spdFlat).toFixed(1);

  let rarityContent = "";
  let background = "";
  if(character.rarity === "fiveStar") {
    rarityContent = (
      <>
        <img src={starImage} style={{marginRight: "1px", marginLeft: "1px"}} width={25} alt=""></img>
        <img src={starImage} style={{marginRight: "1px", marginLeft: "1px"}} width={25} alt=""></img>
        <img src={starImage} style={{marginRight: "1px", marginLeft: "1px"}} width={25} alt=""></img>
        <img src={starImage} style={{marginRight: "1px", marginLeft: "1px"}} width={25} alt=""></img>
        <img src={starImage} style={{marginRight: "1px", marginLeft: "1px"}} width={25} alt=""></img>
      </>
    );
    background = (
      <img src={characterImage} width={95} height={95} alt="" onClick={() => setOpen(true)} style={{borderRadius: "200px"}} className='fiveStar'></img>
    );
  }

  if(character.rarity === "fourStar") {
    rarityContent = (
      <>
        <img src={starImage} style={{marginRight: "1px", marginLeft: "1px"}} width={25} alt=""></img>
        <img src={starImage} style={{marginRight: "1px", marginLeft: "1px"}} width={25} alt=""></img>
        <img src={starImage} style={{marginRight: "1px", marginLeft: "1px"}} width={25} alt=""></img>
        <img src={starImage} style={{marginRight: "1px", marginLeft: "1px"}} width={25} alt=""></img>
      </>
    );
    background = (
      <img src={characterImage} width={95}  height={95} alt="" onClick={() => setOpen(true)} style={{borderRadius: "200px"}} className='fourStar'></img>
    );
  }

  return (
    <div className="calc-item" style={{overflow: "hidden"}}>
      <div className='character-crumbs'>
        <div className="character-selector">
            <SelectionMenu images={images} text={"Select the character"} characterList={filteredChars} handleSelection={handleSelection} open={open} setOpen={setOpen}></SelectionMenu>
            {background}
        </div>
        <div className='character-stuff'>
          <div className={`name ${character.element}`}>{character.name}</div>
          <div style={{display: "flex", alignItems: "center", justifyContent: "center"}}>{rarityContent}</div>
          <div className='rank'>
            <div>Level</div>
            <select name="level" id="level" className='levels' onChange={handleSelectChange} value={level}>
              <option value="0">1</option>
              <option value="1">20</option>
              <option value="2">20+</option>
              <option value="3">40</option>
              <option value="4">40+</option>
              <option value="5">50</option>
              <option value="6">50+</option>
              <option value="7">60</option>
              <option value="8">60+</option>
              <option value="9">70</option>
              <option value="10">70+</option>
              <option value="11">80</option>
            </select>
          </div>
          <div className='centered'>
            <button className='calc-btn' onClick={() => setSkillOpen(true)}>Skills</button>
            <Skills open={skillOpen} setOpen={setSkillOpen}></Skills>
          </div>
        </div>
      </div>
      <div className="character-details">
        <div className="centered">Attributes</div>
        <div className="attributes">
          <div className="attribute-item" 
          onMouseEnter={handleHPMouseEnter}
          onMouseLeave={handleHPMouseLeave} >
            <div className='attribute'>HP</div>
            <div className="value">
              {isHPHovered ? (<> {Math.abs(parseFloat(hp).toFixed(1))}+<div style={{color: "#00e7ff"}}>{Math.abs(((hpPercent * hp) + hpFlat - hp).toFixed(1))}</div></>): finalHp}</div>
          </div>
          <div className="attribute-item"
          onMouseEnter={handleAtkMouseEnter}
          onMouseLeave={handleAtkMouseLeave}
          >
            <div className='attribute'>ATK</div>
            <div className="value">
              {isAtkHovered ? (<> {Math.abs(parseFloat(atk).toFixed(1))}+<div style={{color: "#00e7ff"}}>{Math.abs(((atkPercent * atk) + atkFlat - atk).toFixed(1))}</div></>): finalAtk}
            </div>
          </div>
          <div className="attribute-item"
          onMouseEnter={handleDefMouseEnter}
          onMouseLeave={handleDefMouseLeave}
          >
            <div className='attribute'>DEF</div>
            <div className="value">
              {isDefHovered ? (<> {Math.abs(parseFloat(def).toFixed(1))}+<div style={{color: "#00e7ff"}}>{Math.abs(((defPercent * def) + defFlat - def).toFixed(1))}</div></>): finalDef}
            </div>
          </div>
          <div className="attribute-item"
          onMouseEnter={handleSpdMouseEnter}
          onMouseLeave={handleSpdMouseLeave}
          >
            <div className='attribute'>SPD</div>
            <div className="value">
              {isSpdHovered ? (<> {Math.abs(parseFloat(spd).toFixed(1))}+<div style={{color: "#00e7ff"}}>{Math.abs(((spdPercent * spd) + spdFlat - spd).toFixed(1))}</div></>): finalSpd}
            </div>
          </div>
          <div className="attribute-item">
            <div className='attribute'>CRIT Rate</div>
            <div className="value">{(critRate * 100).toFixed(1)}%</div>
          </div>
          <div className="attribute-item">
            <div className='attribute'>CRIT Damage</div>
            <div className="value">{(critDmg * 100).toFixed(1)}%</div>
          </div>
          <div className="attribute-item">
            <div className='attribute'>Break Effect</div>
            <div className="value">{(breakEffect * 100).toFixed(1)}%</div>
          </div>
          <div className="attribute-item">
            <div className='attribute'>Outgoing Healing</div>
            <div className="value">{(outgoingHealing * 100).toFixed(1)}%</div>
          </div>
          <div className="attribute-item">
            <div className='attribute'>Max Energy</div>
            <div className="value">{energy}</div>
          </div>
          <div className="attribute-item">
            <div className='attribute'>Energy Regeneration Rate</div>
            <div className="value">{(energyRegen * 100).toFixed(1)}%</div>
          </div>
          <div className="attribute-item">
            <div className='attribute'>Effect Hit Rate</div>
            <div className="value">{(effectHitRate * 100).toFixed(1)}%</div>
          </div>
          <div className="attribute-item">
            <div className='attribute'>Effect Res</div>
            <div className="value">{(effectRes * 100).toFixed(1)}%</div>
          </div>
          <div className="attribute-item">
            <div className='attribute'>Taunt</div>
            <div className="value">{taunt}</div>
          </div>
          <div className="attribute-item">
            <div className='attribute'>Physical DMG Boost</div>
            <div className="value">{Math.abs((physicalDmg * 100).toFixed(1))}%</div>
          </div>
          <div className="attribute-item">
            <div className='attribute'>Fire DMG Boost</div>
            <div className="value">{Math.abs((fireDmg * 100).toFixed(1))}%</div>
          </div>
          <div className="attribute-item">
            <div className='attribute'>Ice DMG Boost</div>
            <div className="value">{Math.abs((iceDmg * 100).toFixed(1))}%</div>
          </div>
          <div className="attribute-item">
            <div className='attribute'>Lighting DMG Boost</div>
            <div className="value">{Math.abs((lightningDmg * 100).toFixed(1))}%</div>
          </div>
          <div className="attribute-item">
            <div className='attribute'>Wind DMG Boost</div>
            <div className="value">{Math.abs((windDmg * 100).toFixed(1))}%</div>
          </div>
          <div className="attribute-item">
            <div className='attribute'>Quantum DMG Boost</div>
            <div className="value">{Math.abs((quantumDmg * 100).toFixed(1))}%</div>
          </div>
          <div className="attribute-item">
            <div className='attribute'>Imaginary DMG Boost</div>
            <div className="value">{Math.abs((imaginaryDmg * 100).toFixed(1))}%</div>
          </div>
          <div className="attribute-item">
            <div className='attribute'>Basic Attack DMG Boost</div>
            <div className="value">{Math.abs((basicBoost * 100).toFixed(1))}%</div>
          </div>
          <div className="attribute-item">
            <div className='attribute'>Skill DMG Boost</div>
            <div className="value">{Math.abs((skillBoost * 100).toFixed(1))}%</div>
          </div>
          <div className="attribute-item">
            <div className='attribute'>Ultimate DMG Boost</div>
            <div className="value">{Math.abs((ultimateBoost * 100).toFixed(1))}%</div>
          </div>
          <div className="attribute-item">
            <div className='attribute'>Follow Up DMG Boost</div>
            <div className="value">{Math.abs((followUpBoost * 100).toFixed(1))}%</div>
          </div>
          <div className="attribute-item">
            <div className='attribute'>DoT Boost</div>
            <div className="value">{Math.abs((dotBoost * 100).toFixed(1))}%</div>
          </div>
        </div>
      </div>
    </div>
)}

export default CharacterDetails;