import React, { useContext } from 'react';
import { globalData } from '../../../globalData';

function PhysicalTBTable() {

    const levelMult = {
        1: 54.0000,
        20: 139.7703,
        40: 363.6658,
        50: 774.9041,
        60: 1640.3068,
        70: 2659.6406,
        80: 3767.5533,
    }

    const {
        character,
        physicalDmg,
        atk,
        atkPercent,
        atkFlat,
        critRate,
        critDmg,
        energyRegen,
        level,
        enemyLevel,
        physicalPen,
        breakEffect,
        defReduction,
        enemyRes,
        basicAttackLevel,
        skillLevel,
        ultimateLevel,
        skillBoost,
        breakMultiplier,
        basicBoost,
        physicalVulnerability,
        enemyToughness,
        enemyMaxHP,
        isBoss,
        dotVulnerability,
        dotBoost,
        ultimateBoost,
    } = useContext(globalData);

    let tbAtk = (atk * atkPercent) + atkFlat;
    let damageBoost = (1 + physicalDmg);
    let critDmgBoost = (1 + critDmg);

    let basicDmgBoost = damageBoost + basicBoost;
    let basicBaseDmg = tbAtk * character.basicAttack(basicAttackLevel);

    let skillDmgBoost = damageBoost + skillBoost;
    let skillBaseDmg = tbAtk * character.skillAttack(skillLevel);

    if(level >= 10) {
        skillDmgBoost += 0.25;
    }

    let ultimateDmgBoost = damageBoost + ultimateBoost;
    let ultimate1BaseDmg = tbAtk * character.ultimateAttack1(ultimateLevel);

    let ultimate2DmgBoost = damageBoost + ultimateBoost;
    let ultimate2BaseDmgSingle = tbAtk * character.ultimateAttack2(ultimateLevel).single;
    let ulitmate2BaseDmgSpread = tbAtk * character.ultimateAttack2(ultimateLevel).spread;

    if(level >= 10) {
        ultimate2DmgBoost += 0.25;
    }

    let resMult = (1 - (enemyRes - physicalPen));
    let defReductionStat = (1 - defReduction);
    let vulnerabilityMultiplier = (1 + physicalVulnerability);

    if(defReductionStat < 0) {
        defReductionStat = 0;
    }
    
    let defMult = (character.stats[level]["level"] + 20)/((enemyLevel + 20) * defReductionStat + character.stats[level]["level"] + 20);
    let breakMult = breakMultiplier;
    
    let critRateStat = critRate;
    
    if(critRateStat > 1) {
        critRateStat = 1;
    }
    
    let maxToughnessMult = 0.5 + (enemyToughness/120);
    let breakBaseDmg = 2 * levelMult[character.stats[level]["level"]] * maxToughnessMult;
    let breakDmg = breakBaseDmg * (1 + breakEffect) * defMult * resMult * vulnerabilityMultiplier * breakMult;


    let bleedBaseDmg = 0;
    if(isBoss) {
        bleedBaseDmg = 0.07 * enemyMaxHP;
    }else {
        bleedBaseDmg = 0.16 * enemyMaxHP;
    }

    if(2 * levelMult[character.stats[level]["level"]] * maxToughnessMult < bleedBaseDmg) {
        bleedBaseDmg = 2 * levelMult[character.stats[level]["level"]] * maxToughnessMult;
    }

    let bleed = bleedBaseDmg * (1 + breakEffect) * resMult * defMult * (vulnerabilityMultiplier + dotVulnerability) * breakMult * (damageBoost + dotBoost);

    return (
        <div className="damage-details">
            <div className='header'>Damage Details</div>
            <div className="divider"></div>
            <div className="damage-title header">Basic Attack ({basicAttackLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Basic</td>
                        <td className='tb-text'>{(basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult).toFixed(0)}</td>
                        <td className='tb-text'>{(basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult * critDmgBoost).toFixed(0)}</td>
                        <td className='tb-text'>{((basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
            </table>
            <div className="damage-title header">Skill Attack ({skillLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col" className='tb-text-header'>Non-CRIT</th>
                        <th scope="col" className='tb-text-header'>CRIT</th>
                        <th scope="col" className='tb-text-header'>Average</th>

                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Skill</td>
                        <td className='tb-text'>{(skillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult).toFixed(0)}</td>
                        <td className='tb-text'>{(skillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult * critDmgBoost).toFixed(0)}</td>
                        <td className='tb-text'>{((skillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{60}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{60}</td>
                    </tr>
                    <tr>
                        <td>Break Spread</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
            </table>
            <div className="damage-title header">Ultimate (Farewell Hit) ({ultimateLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Source</th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>DMG</td>
                        <td className='tb-text'>{((ultimate1BaseDmg * ultimateDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{((ultimate1BaseDmg * ultimateDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((ultimate1BaseDmg * ultimateDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{90}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{90}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Ultimate (RIP Home Run) ({ultimateLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Source</th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>DMG</td>
                        <td className='tb-text'>{((ultimate2BaseDmgSingle * ultimate2DmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{((ultimate2BaseDmgSingle * ultimate2DmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((ultimate2BaseDmgSingle * ultimate2DmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Blast</td>
                        <td className='tb-text'>{((ulitmate2BaseDmgSpread * ultimate2DmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{((ulitmate2BaseDmgSpread * ultimate2DmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((ulitmate2BaseDmgSpread * ultimate2DmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{60}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{60}</td>
                    </tr>
                    <tr>
                        <td>Break Blast</td>
                        <td className='tb-text'>{60}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{60}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Weakness Break</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Type</th>
                        <th scope="col">Value</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Break Damage</td>
                        <td className='tb-text'>{(breakDmg).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Bleed Damage</td>
                        <td className='tb-text'>{(bleed).toFixed(0)}</td>
                    </tr>
                </tbody>
            </table>
    </div>
    )
}


export default PhysicalTBTable;