export class River {
    constructor() {
        this.stats = [
            [1, 21.6,    18,     38],
            [20, 83.16,  69.3,  148],
            [20, 109.08, 90.9,  194],
            [40, 208.44, 173.7, 371],
            [40, 243,    202.5, 432],
            [50, 275.4,  229.5, 490],
            [50, 309.96, 258.3, 551],
            [60, 342.36, 285.3, 609],
            [60, 376.92, 314.1, 670],
            [70, 409.32, 341.1, 728],
            [70, 443.88, 369.9, 789],
            [80, 476.28, 396.9, 847],
        ];
        
        this.name = "River Flows in Spring";
        this.path = "Hunt";
        this.id = "river";
        this.rarity = "fourStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>After entering battle, increases the wearer's </div>
                <div className="buff-text">SPD </div> 
                <div> by </div> 
                <div className="buff-text">{((0.08 + imposition * 0.01) * 100).toFixed(1)}%</div>
                <div> and DMG by </div>
                <div className="buff-text">{((0.12 + imposition * 0.03) * 100).toFixed(1)}%</div> 
                <div> When the wearer takes DMG, this effect will disappear. This effect will resume after the end of the wearer's next turn.</div> 
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "silenceb",
            name: this.name,
            effect:{
                spdPercent: 0.08 + imposition * 0.01,
                skillBoost: 0.12 + imposition * 0.03,
            },
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                // id: "silence0",
                // name: this.name,
                // description: (
                //     <div className="buff-description">
                //         <div>Increases   </div>
                //         <div className="buff-text"> Basic Attack and skill DMG </div>
                //         <div> by an extra </div>
                //         <div className="buff-text">{((0.24 + imposition * 0.06) * 100).toFixed(1)}%</div> 
                //         <div> when the wearer's current Energy reaches its max level.</div>
                //     </div>
                // ),
                // effect: {},
                // type: "buff",
                // hasStacks: false,
                // hasInput: false,
            },
        }
    }
}

export default River;