import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';

import { Qingque } from "../../../../../../../database/characters";

const QingqueBuffs = () => {

    const { character, talentLevel, level, skillLevel, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    
    const qingque = new Qingque();
    const [stacks, setStacks] = useState(1);
    const [prevTalentLevel, setPrevTalentLevel] = useState(0);
    const [prevSkillLevel, setPrevSkillLevel] = useState(0);
    const [prevLevel, setPrevLevel] = useState(level);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    useEffect(() => {

        if(level < 6 && prevLevel >= 6 && characterBuffStates[0]) {

            const newTempSkillBuff = {
                effect: {
                    quantumDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                    fireDmg:( character.skillAttack(skillLevel) + 0.1) * stacks,
                    lightningDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                    windDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                    physicalDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                    imaginaryDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                    iceDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                },
            }
            updateStatesFromJson(newTempSkillBuff, true);

            const tempSkillBuff = {
                effect: {
                    quantumDmg: character.skillAttack(skillLevel) * stacks,
                    fireDmg: character.skillAttack(skillLevel) * stacks,
                    lightningDmg: character.skillAttack(skillLevel) * stacks,
                    windDmg: character.skillAttack(skillLevel) * stacks,
                    physicalDmg: character.skillAttack(skillLevel) * stacks,
                    imaginaryDmg: character.skillAttack(skillLevel) * stacks,
                    iceDmg: character.skillAttack(skillLevel) * stacks,
                }
            }
            updateStatesFromJson(tempSkillBuff, false);

        }

        if(level >= 6 && prevLevel < 6 && characterBuffStates[0]) {
            const tempSkillBuff = {
                effect: {
                    quantumDmg: character.skillAttack(skillLevel) * stacks,
                    fireDmg: character.skillAttack(skillLevel) * stacks,
                    lightningDmg: character.skillAttack(skillLevel) * stacks,
                    windDmg: character.skillAttack(skillLevel) * stacks,
                    physicalDmg: character.skillAttack(skillLevel) * stacks,
                    imaginaryDmg: character.skillAttack(skillLevel) * stacks,
                    iceDmg: character.skillAttack(skillLevel) * stacks,
                }
            }
            updateStatesFromJson(tempSkillBuff, true);
            
            const newTempSkillBuff = {
                effect: {
                    quantumDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                    fireDmg:( character.skillAttack(skillLevel) + 0.1) * stacks,
                    lightningDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                    windDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                    physicalDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                    imaginaryDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                    iceDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                },
            }
            updateStatesFromJson(newTempSkillBuff, false);
        }

        if(level < 10 && characterBuffStates[1]) {
            updateStatesFromJson(A6Buff, true);
            characterBuffStates[1] = false;
        }

        setPrevLevel(level);

    }, [level]);

    useEffect(() => {
        if(characterBuffStates[2]) {
            const tempTalentBuff = {
                effect: {
                    atkPercent: qingque.talent(prevTalentLevel),
                },
            }
            updateStatesFromJson(tempTalentBuff, true);

            const newTempTalentBuff = {
                effect: {
                    atkPercent: qingque.talent(talentLevel),
                },
            }
            updateStatesFromJson(newTempTalentBuff, false);
        }
        
        setPrevTalentLevel(talentLevel);
    }, [talentLevel]);

    useEffect(() => {
        if(characterBuffStates[0]) {

            if(level >= 6) {
                const tempSkillBuff = {
                    effect: {
                        quantumDmg: (character.skillAttack(prevSkillLevel) + 0.1) * stacks,
                        fireDmg:( character.skillAttack(prevSkillLevel) + 0.1) * stacks,
                        lightningDmg: (character.skillAttack(prevSkillLevel) + 0.1) * stacks,
                        windDmg: (character.skillAttack(prevSkillLevel) + 0.1) * stacks,
                        physicalDmg: (character.skillAttack(prevSkillLevel) + 0.1) * stacks,
                        imaginaryDmg: (character.skillAttack(prevSkillLevel) + 0.1) * stacks,
                        iceDmg: (character.skillAttack(prevSkillLevel) + 0.1) * stacks,
                    },
                }
                updateStatesFromJson(tempSkillBuff, true);

                const newTempSkillBuff = {
                    effect: {
                        quantumDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                        fireDmg:( character.skillAttack(skillLevel) + 0.1) * stacks,
                        lightningDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                        windDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                        physicalDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                        imaginaryDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                        iceDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                    },
                }
                updateStatesFromJson(newTempSkillBuff, false);
            } else {
                const tempSkillBuff = {
                    effect: {
                        quantumDmg: character.skillAttack(prevSkillLevel) * stacks,
                        fireDmg: character.skillAttack(prevSkillLevel) * stacks,
                        lightningDmg: character.skillAttack(prevSkillLevel) * stacks,
                        windDmg: character.skillAttack(prevSkillLevel) * stacks,
                        physicalDmg: character.skillAttack(prevSkillLevel) * stacks,
                        imaginaryDmg: character.skillAttack(prevSkillLevel) * stacks,
                        iceDmg: character.skillAttack(prevSkillLevel) * stacks,
                    }
                }
                updateStatesFromJson(tempSkillBuff, true);

                const newTempSkillBuff = {
                    effect: {
                        quantumDmg: character.skillAttack(skillLevel) * stacks,
                        fireDmg: character.skillAttack(skillLevel) * stacks,
                        lightningDmg: character.skillAttack(skillLevel) * stacks,
                        windDmg: character.skillAttack(skillLevel) * stacks,
                        physicalDmg: character.skillAttack(skillLevel) * stacks,
                        imaginaryDmg: character.skillAttack(skillLevel) * stacks,
                        iceDmg: character.skillAttack(skillLevel) * stacks,
                    },
                }
                updateStatesFromJson(newTempSkillBuff, false);
            }
        }
        
        setPrevSkillLevel(skillLevel);
    }, [skillLevel]);

    let skillBuff = {
        effect: {
            quantumDmg: character.skillAttack(skillLevel) * stacks,
            fireDmg: character.skillAttack(skillLevel) * stacks,
            lightningDmg: character.skillAttack(skillLevel) * stacks,
            windDmg: character.skillAttack(skillLevel) * stacks,
            physicalDmg: character.skillAttack(skillLevel) * stacks,
            imaginaryDmg: character.skillAttack(skillLevel) * stacks,
            iceDmg: character.skillAttack(skillLevel) * stacks,
        }
    }

    if(level >= 6) {
        skillBuff = {
            effect: {
                quantumDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                fireDmg:( character.skillAttack(skillLevel) + 0.1) * stacks,
                lightningDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                windDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                physicalDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                imaginaryDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
                iceDmg: (character.skillAttack(skillLevel) + 0.1) * stacks,
            }
        }
    }

    const talentBuff = {
        effect: {
            atkPercent: character.talent(talentLevel),
        }
    }

    const A6Buff = {
        effect: {
            spdPercent: 0.1,
        },
    }

    const updateStacks = (e) => {
        if(characterBuffStates[0]) {
            updateStatesFromJson(skillBuff, true);

            if(level >= 6) {
                const tempSkillBuff = {
                    effect: {
                        quantumDmg: (character.skillAttack(skillLevel) + 0.1) * e.target.value,
                        fireDmg:( character.skillAttack(skillLevel) + 0.1) * e.target.value,
                        lightningDmg: (character.skillAttack(skillLevel) + 0.1) * e.target.value,
                        windDmg: (character.skillAttack(skillLevel) + 0.1) * e.target.value,
                        physicalDmg: (character.skillAttack(skillLevel) + 0.1) * e.target.value,
                        imaginaryDmg: (character.skillAttack(skillLevel) + 0.1) * e.target.value,
                        iceDmg: (character.skillAttack(skillLevel) + 0.1) * e.target.value,
                    }
                }
                updateStatesFromJson(tempSkillBuff, false);
            } else {
                
                const tempE6Buff = {
                    effect: {
                        quantumDmg: character.skillAttack(skillLevel) * e.target.value,
                        fireDmg: character.skillAttack(skillLevel) * e.target.value,
                        lightningDmg: character.skillAttack(skillLevel) * e.target.value,
                        windDmg: character.skillAttack(skillLevel) * e.target.value,
                        physicalDmg: character.skillAttack(skillLevel) * e.target.value,
                        imaginaryDmg: character.skillAttack(skillLevel) * e.target.value,
                        iceDmg: character.skillAttack(skillLevel) * e.target.value,
                    },
                }
                updateStatesFromJson(tempE6Buff, false);
            }

        }

        setStacks(e.target.value);
    }

    return (
        <>  

            <FormControlLabel
                className='header'
                control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, skillBuff)} />}
                label={qingque.skillDescription(skillLevel).name}
            />
            <div>{qingque.skillDescription(skillLevel).description}</div>
            <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}} className="mt-2">
            <div style={{display: "flex"}}>
                <div>Stacks </div>
                    <select className="levels buff-input" style={{width: "45%"}} value={stacks} onChange={updateStacks}>
                        <option value={1}>1</option>
                        <option value={2}>2</option>
                        <option value={3}>3</option>
                        <option value={4}>4</option>
                    </select>
                </div>
            </div>

            <FormControlLabel
                className='header'
                control={<Checkbox checked={characterBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, talentBuff)} />}
                label={qingque.talentDescription(talentLevel).name}
            />
            <div>{qingque.talentSimple(talentLevel).description}</div>

            {level >= 10 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, A6Buff)} />}
                    label={"(A6) " + qingque.A6().name}
                    />
                    <div>{qingque.A6().description}</div>
                </>
            }

        </>
    )
}

export const QingqueGlobalBuffs = (props) => {

    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {
        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <div>No Buffs</div>
        </>
    )
}

export default QingqueBuffs;