export class Bronya {
    constructor() {
        this.stats = [
            {level: 1,  atk: 79.2,   def: 72.6,   hp: 169,  spd: 99, taunt: 100, energy: 120},
            {level: 20, atk: 154.44, def: 141.57, hp: 329,  spd: 99, taunt: 100, energy: 120, windDmg: 0.032},
            {level: 20, atk: 186.12, def: 170.61, hp: 397,  spd: 99, taunt: 100, energy: 120, windDmg: 0.032},
            {level: 40, atk: 297,    def: 272.25, hp: 634,  spd: 99, taunt: 100, energy: 120, windDmg: 0.064, critDmg: 0.053, effectRes: 0.04},
            {level: 40, atk: 328.68, def: 301.29, hp: 701,  spd: 99, taunt: 100, energy: 120, windDmg: 0.064, critDmg: 0.053, effectRes: 0.04},
            {level: 50, atk: 368.28, def: 337.59, hp: 786,  spd: 99, taunt: 100, energy: 120, windDmg: 0.112, critDmg: 0.053, effectRes: 0.04},
            {level: 50, atk: 399.96, def: 366.63, hp: 853,  spd: 99, taunt: 100, energy: 120, windDmg: 0.112, critDmg: 0.053, effectRes: 0.04},
            {level: 60, atk: 439.56, def: 402.93, hp: 938,  spd: 99, taunt: 100, energy: 120, windDmg: 0.112, critDmg: 0.053, effectRes: 0.04},
            {level: 60, atk: 471.24, def: 431.97, hp: 1005, spd: 99, taunt: 100, energy: 120, windDmg: 0.16,  critDmg: 0.133, effectRes: 0.04},
            {level: 70, atk: 510.84, def: 468.27, hp: 1090, spd: 99, taunt: 100, energy: 120, windDmg: 0.16,  critDmg: 0.133, effectRes: 0.04},
            {level: 70, atk: 542.52, def: 497.31, hp: 1157, spd: 99, taunt: 100, energy: 120, windDmg: 0.16,  critDmg: 0.133, effectRes: 0.1},
            {level: 80, atk: 582.12, def: 533.61, hp: 1242, spd: 99, taunt: 100, energy: 120, windDmg: 0.224, critDmg: 0.24,  effectRes: 0.1},
        ];

        this.name = "Bronya";
        this.path = "Harmony";
        this.rarity = 'fiveStar';
        this.element = "Wind";
        this.id = "bronya";
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Windrider Bullet",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className="Wind"> Wind DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of Bronya's </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return 0.33 + (level * 0.033);
        }else if(level < 10){
            return 0.495 + ((level - 5) * 0.0413);
        }else {
            return 0.66 + ((level - 9) * 0.033);
        }
    }

    skillDescription(level) {
        return {
            name: "Combat Redeployment",
            description: (
                <div className="buff-description">
                    <div>Dispels a debuff from a single ally, allows them to immediately take action, and increases their </div>
                    <div className="buff-text">DMG</div>
                    <div> by </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                    <div> for </div>
                    <div className="buff-text"> 1 turn(s). </div>
                    <div> When this Skill is used on Bronya herself, she cannot immediately take action again </div>
                </div>
            ),
        }
    }

    skillSimple(level) {
        return {
            name: "Combat Redeployment",
            description: (
                <div className="buff-description">
                    <div>Increases </div>
                    <div className="buff-text">DMG</div>
                    <div> by </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                    <div> for </div>
                    <div className="buff-text"> 1 turn(s). </div>
                </div>
            ),
        }
    }

    ultimateBuff(level) {
        if(level < 6) {
            return {
                atk: 0.33 + (level * 0.022),
                critDmg: 0.12 + (level * 0.004),
                bonusCritDmg: 0.12 + (level * 0.008),
            };
        }else if(level < 10){
            return {
                atk: 0.44 + ((level - 5) * 0.0275),
                critDmg: 0.14 + ((level - 5) * 0.005),
                bonusCritDmg: 0.16 + ((level - 5) * 0.01),
            };
        }else {
            return {
                atk: 0.55 + ((level - 9) * 0.022),
                critDmg: 0.16 + ((level - 9) * 0.004),
                bonusCritDmg: 0.20 + ((level - 9) * 0.008),
            };
        }
    }

    ultimateDescription(level) {
        return {
            name: "The Belobog March",
            description: (
                <div className="buff-description">
                    <div>Increases the ATK of all allies by </div>
                    <div className="buff-text">{(this.ultimateBuff(level).atk * 100).toFixed(1)}%</div>
                    <div> and increase their </div>
                    <div className="buff-text"> CRIT DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.ultimateBuff(level).critDmg * 100).toFixed(1)}%</div>
                    <div> of Bronya's </div>
                    <div className="buff-text"> CRIT DMG </div>
                    <div> plus </div>
                    <div className="buff-text"> {(this.ultimateBuff(level).bonusCritDmg * 100).toFixed(1)}% </div>
                    <div> for </div>
                    <div className="buff-text"> 2 turn(s). </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 0.15 + (level * 0.015);
        }else if(level < 10){
            return 0.225 + ((level - 5) * 0.0188);
        }else {
            return 0.3 + ((level - 9) * 0.015);
        }
    }

    talentDescription(level) { 
        return {
            name: "Leading the Way",
            description: (
                <div className="buff-description">
                    <div>After using her </div>
                    <div className="buff-text"> Basic ATK </div>
                    <div> Bronya's next action will be </div>
                    <div className="buff-text"> Advanced Forward </div>
                    <div> by </div>
                    <div className="buff-text"> {(this.talent(level) * 100).toFixed(1)}%.</div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Banner of Command",
            description: (
                <div className="buff-description">
                    <div> After using Bronya's Technique, in the next battle, all allies' </div>
                    <div className="buff-text"> ATK </div>
                    <div>increases by </div>
                    <div className="buff-text"> 15% </div>
                    <div> for </div>
                    <div className="buff-text"> 2 turn(s).</div>
                </div>
            ),
        }
    }


    A2() {
        return {
            name: "Command",
            description: (
                <div className="buff-description">
                    <div>The </div>
                    <div className="buff-text"> CRIT Rate </div>
                    <div> for Basic ATK increases to </div>
                    <div className="buff-text"> 100% </div>
                </div>
            ),
        };
    }

    A4() {
        return {
            name: "Battlefield",
            description: (
                <div className="buff-description">
                    <div>At the start of the battle, all allies' DEF increases by </div>
                    <div className="buff-text"> 20% </div>
                    <div> for </div>
                    <div className="buff-text"> 2 turn(s).</div>
                </div>
            ),
        };
    }

    A6() {
        return {
            name: "Military Might",
            description: (
                <div className="buff-description">
                    <div>When Bronya is on the field, all allies deal  </div>
                    <div className="buff-text"> 10% </div>
                    <div> more </div>
                    <div className="buff-text"> DMG. </div>
                </div>
            ),
        };
    }

    E1() {
        return {
            name: "Hone Your Strength",
            description: (
                <div className="buff-description">
                    <div>Using Bronya's Skill has a </div>
                    <div className="buff-text"> 50% </div>
                    <div> fixed chance of recovering </div>
                    <div className="buff-text"> 1 Skill Point </div>
                    <div> This effect has a </div>
                    <div className="buff-text"> 1 turn </div>
                    <div> cooldown.</div>
                </div>
            ),
        }
    }

    E2() {
        return {
            name: "Quick March",
            description: (
                <div className="buff-description">
                    <div>When using Skill, the target ally's SPD increases by </div>
                    <div className="buff-text"> 30% </div>
                    <div> after taking action, lasting for </div>
                    <div className="buff-text"> 1 turn. </div>
                </div>
            ),
        }
    }

    E3() {
        return {
            name: "Bombardment",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }

    E4() {
        return {
            name: "Take by Surprise",
            description: (
                <div className="buff-description">
                    <div>After an ally uses Basic ATK on an enemy with </div>
                    <div className="Wind"> Wind Weakness </div>
                    <div>Bronya immediately launches a follow-up attack on the target, dealing </div>
                    <div className="buff-text"> Wind DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> 80% </div>
                    <div> of Bronya's </div>
                    <div className="buff-text"> Basic ATK DMG. </div>
                    <div>This effect can only be triggered 1 time per turn </div>
                </div>
            ),
        }
    }

    E5() {
        return {
            name: "Unstoppable",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: "Piercing Rainbow",
            description: (
                <div className="buff-description">
                    <div>The duration of the DMG Boost effect placed by the Skill on the target ally increases by </div>
                    <div className="buff-text"> 1 turn(s). </div>
                </div>
            ),
        }
    }

}

export default Bronya;