class FuXuan {
    constructor() {
        this.stats = [
            {level: 1,  atk: 63.36,  def: 82.5,   hp: 201,  spd: 100, taunt: 150, energy: 135},
            {level: 20, atk: 123.55, def: 160.87, hp: 391,  spd: 100, taunt: 150, energy: 135, critRate: 0.027},
            {level: 20, atk: 148.9,  def: 193.87, hp: 472,  spd: 100, taunt: 150, energy: 135, critRate: 0.027},
            {level: 40, atk: 237.6,  def: 309.37, hp: 752,  spd: 100, taunt: 150, energy: 135, critRate: 0.027, hpPercent: 0.04},
            {level: 40, atk: 262.94, def: 342.37, hp: 833,  spd: 100, taunt: 150, energy: 135, critRate: 0.054, hpPercent: 0.04, effectRes: 0.04},
            {level: 50, atk: 294.62, def: 383.62, hp: 933,  spd: 100, taunt: 150, energy: 135, critRate: 0.054, hpPercent: 0.04, effectRes: 0.04},
            {level: 50, atk: 319.97, def: 416.62, hp: 1013, spd: 100, taunt: 150, energy: 135, critRate: 0.094, hpPercent: 0.04, effectRes: 0.04},
            {level: 60, atk: 351.65, def: 457.87, hp: 1114, spd: 100, taunt: 150, energy: 135, critRate: 0.094, hpPercent: 0.04, effectRes: 0.04},
            {level: 60, atk: 376.99, def: 490.87, hp: 1194, spd: 100, taunt: 150, energy: 135, critRate: 0.134, hpPercent: 0.1,  effectRes: 0.04},
            {level: 70, atk: 408.67, def: 532.12, hp: 1294, spd: 100, taunt: 150, energy: 135, critRate: 0.134, hpPercent: 0.1,  effectRes: 0.04},
            {level: 70, atk: 434.02, def: 565.12, hp: 1374, spd: 100, taunt: 150, energy: 135, critRate: 0.134, hpPercent: 0.1,  effectRes: 0.04},
            {level: 80, atk: 465.7,  def: 606.37, hp: 1475, spd: 100, taunt: 150, energy: 135, critRate: 0.187, hpPercent: 0.18, effectRes: 0.1},
        ];

        this.name = "Fu Xuan";
        this.path = "Preservation";
        this.rarity = 'fiveStar';
        this.element = "Quantum";
        this.id = "fuxuan";
        this.hpLost = 0.0;
    }

    setHpLost(hpLost) {
        this.hpLost = hpLost;
    }

    basicAttack(level) {
        return 0.25 + (level * 0.05);
    }

    basicDescription(level) {
        return {
            name: "Fist of Conviction",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">Max HP</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return {
                critRate: 0.06 + (level * 0.006),
                hpPercent: 0.03 + (level * 0.003),
            }
        }else if(level < 10){
            return {
                critRate: 0.09 + ((level - 5) * 0.0075),
                hpPercent: 0.045 + ((level - 5) * 0.0038),
            }
        }else {
            return {
                critRate: 0.12 + ((level - 9) * 0.006),
                hpPercent: 0.06 + ((level - 5) * 0.003),
            }
        }
    }

    skillDescription(level) {
        return {
            name: "Known by Stars, Shown by Hearts",
            description: (
                <div className="buff-description">
                    <div> Activates Matrix of Prescience. Other team members will Apportion 65% of the DMG they receive (before this DMG is mitigated by any Shields) to Fu Xuan for 3 turn(s). All team members are affected by Matrix of Prescience and gain the Knowledge effect, which increases their respective   </div>
                    <div className="buff-text">Max HP</div>
                    <div> by </div>
                    <div className="buff-text">{(this.skillAttack(level).hpPercent * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">Max HP</div>
                    <div> and increases </div>
                    <div className="buff-text">CRIT Rate</div>
                    <div> by </div>
                    <div className="buff-text">{(this.skillAttack(level).critRate * 100).toFixed(1)}%</div>
                    <div> When Fu Xuan is knocked down, the Matrix of Prescience will be dispelled. </div>
                </div>
            ),
        }
    }

    skillSimple(level) {
        return {
            name: "Known by Stars, Shown by Hearts",
            description: (
                <div className="buff-description">
                    <div> increases </div>
                    <div className="buff-text">Max HP</div>
                    <div> by </div>
                    <div className="buff-text">{(this.skillAttack(level).hpPercent * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">Max HP</div>
                    <div> and increases </div>
                    <div className="buff-text">CRIT Rate</div>
                    <div> by </div>
                    <div className="buff-text">{(this.skillAttack(level).critRate * 100).toFixed(1)}%</div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level < 6) {
            return 0.6 + (level * 0.04);
        }else if(level < 10){
            return 0.8 + ((level - 5) * 0.05);
        }else {
            return 1 + ((level - 9) * 0.04);
        }
    }

    ultimateDescription(level) {
        return {
            name: "Woes of Many Morphed to One",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.ultimateAttack(level) * 100).toFixed(1)}%</div>
                    <div>  of {this.name}'s </div>
                    <div className="buff-text"> Max HP </div>
                    <div> to all enemies and obtains 1 trigger count for the HP Restore from Fu Xuan's Talent. </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return {
                dmgReduction: 0.1 + (level * 0.008),
                hpRestore: 0.8 + (level * 0.01),
            }
        }else if(level < 10) {
            return {
                dmgReduction: 0.14 + ((level - 5) * 0.01),
                hpRestore: 0.8 + ((level - 5) * 0.0125),
            }
        }else {
            return {
                dmgReduction: 0.18 + ((level - 9) * 0.008),
                hpRestore: 0.9 + ((level - 9) * 0.01),
            }
        }
    }

    talentDescription(level) { 
        return {
            name: "Bleak Breeds Bliss",
            description: (
                <div className="buff-description">
                    <div>While Fu Xuan has not been knocked down, Misfortune Avoidance is applied to the entire team. While affected by Misfortune Avoidance, allies take </div>
                    <div className="buff-text">{(this.talent(level).dmgReduction * 100).toFixed(1)}%</div>
                    <div> less DMG. </div>
                    <div>  HP Restore will be triggered for Fu Xuan when her HP falls to 50% or less of her Max HP. This effect will restore her HP by </div>
                    <div className="buff-text">{(this.talent(level).hpRestore * 100).toFixed(1)}%</div>
                    <div> of the amount of HP she is currently missing. This effect cannot be triggered if she receives a killing blow. This effect possesses 1 triggered count by default, and can possess up to 2 trigger counts maximum. </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Of Fortune Comes Fate",
            description: (
                <div className="buff-description">
                    <div> After the Technique is used, all team members receive a Barrier, lasting for 20 seconds. This Barrier can block all enemy attacks, and the team will not enter battle when attacked. Entering battle while the Barrier is active will have Fu Xuan automatically activate Matrix of Prescience at the start of the battle, lasting for 2 turn(s). </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Taiyi, the Macroscopic",
            description: (
                <div className="buff-description">
                    <div> When Matrix of Prescience is active, Fu Xuan will regenerate 20 extra Energy when she uses her Skill. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Dunjia, the Metamystic",
            description: (
                <div className="buff-description">
                    <div> When Fu Xuan's Ultimate is used, heals all other allies by 5% of Fu Xuan's Max HP + 133. </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Liuren, the Sexagenary",
            description: (
                <div className="buff-description">
                    <div> If enemies attempt to apply Crowd Control debuffs to allies while the Matrix of Prescience is active, all allies will resist all Crowd Control debuffs applied by the enemy target during the current action. This effect can only be triggered once. Upon reactivating Matrix of Prescience, the number of times this effect can be triggered will be refreshed. </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Dominus Mortis",
            description: (
                <div className="buff-description">
                    <div> All units with Knowledge increase their </div>
                    <div className="buff-text">CRIT DMG</div>
                    <div> by </div>
                    <div className="buff-text">30%.</div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Optimus Felix",
            description: (
                <div className="buff-description">
                    <div> When Matrix of Prescience is active while any team member is struck by a killing blow, all allies who were struck by a killing blow during this action will not be knocked down and 70% of their Max HP is immediately restored. This effect can trigger 1 time per battle. </div>
                </div>
            )
        }
    }
    
    E3() {
        return {
            name: "Apex Nexus",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }


    E4() {
        return {
            name: "Propitious Star",
            description:(
                <div className="buff-description">
                    <div>When other allies under Matrix of Prescience are attacked, Fu Xuan regenerates 5 Energy.</div>
                </div>
            )
        }
    }
    
    E5() {
        return {
            name: "Arbiter Primus",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }
    
    E6() {
        return {
            name: "Omnia Vita",
            description: (
                <div className="buff-description">
                    <div> Once Matrix of Prescience is activated, it will accumulate the total HP lost by all team members in the current battle. Fu Xuan's Ultimate DMG will increase by 200% of the total HP lost by all team members in the current battle. The total HP lost by all team members in the current battle is capped at 120% of Fu Xuan's Max HP. This value will be reset and re-accumulated after her Ultimate has been used. </div>
                </div>
            ),
        }
    }
}

export default FuXuan;