import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';

import { Luka } from "../../../../../../../database/characters";

const LukaBuffs = () => {

    const {eidolon, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    
    const luka = new Luka();

    const [stack, setStack] = useState(1);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    const E1Buff = {
        effect: {
            physicalDmg: 0.15,
            iceDmg: 0.15,
            fireDmg: 0.15,
            lightningDmg: 0.15,
            quantumDmg: 0.15,
            windDmg: 0.15,
            imaginaryDmg: 0.15,
        },
    }

    const E4Buff = {
        effect: {
            atkPercent: 0.05 * stack,
        }
    }

    useEffect(() => {
        if(eidolon < 1 && characterBuffStates[3]) {
            updateStatesFromJson(E1Buff, true);
            characterBuffStates[3] = false;
        }

        if(eidolon < 4 && characterBuffStates[0]) {
            updateStatesFromJson(E4Buff, true);
            characterBuffStates[0] = false;
        }

    }, [eidolon]);


    const updateStacks = (e) => {

        if(characterBuffStates[0]) {
            updateStatesFromJson(E4Buff, true);

            const newE4Buff = {
                effect: {
                    atkPercent: 0.05 * e.target.value,
                }
            }

            updateStatesFromJson(newE4Buff, false);
        }

        setStack(e.target.value);
    }

    return (
        <>
            {eidolon >= 1 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, E1Buff)} />}
                    label={"(E1) " + luka.E1().name}
                    />
                    <div>{luka.E1().description}</div>
                </>
            }

            {eidolon >= 4 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, E4Buff)} />}
                    label={"(E4) " + luka.E4().name}
                    />
                    <div>{luka.E4().description}</div>
                    <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                        <div style={{display: "flex"}}>
                            <div>Stacks </div>
                            <select className="levels buff-input" style={{width: "45%"}} value={stack} onChange={updateStacks}>
                                <option value={1}>1</option>
                                <option value={2}>2</option>
                                <option value={3}>3</option>
                                <option value={4}>4</option>
                                <option value={5}>5</option>
                            </select>
                        </div>
                    </div>
                </>
            }
        </>
    )
}

export const LukaGlobalBuffs = (props) => {

    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {
        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <div>No Buffs</div>
        </>
    )
}

export default LukaBuffs;