export class PerfectTiming {
    constructor() {
        this.stats = [
            [1,  19.2,   18,     43],
            [20, 73.92,  69.3,  166],
            [20, 96.96,  90.9,  218],
            [40, 185.28, 173.7, 417],
            [40, 216,    202.5, 486],
            [50, 244.8,  229.5, 551],
            [50, 275.52, 258.3, 620],
            [60, 304.32, 285.3, 685],
            [60, 335.04, 314.1, 754],
            [70, 363.84, 341.1, 819],
            [70, 394.56, 369.9, 888],
            [80, 423.36, 396.9, 953],
        ];
        
        this.name = "Perfect Timing";
        this.path = "Abundance";
        this.id = "perfecttiming";
        this.rarity = "fourStar";
        this.ER = 0.16;
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text"> Effect RES </div> 
                <div> by </div> 
                <div className="buff-text">{((0.16 + imposition * 0.04) * 100).toFixed(1)}%.</div>
                <div> and increases Outgoing Healing by an amount that is equal to  </div> 
                <div className="buff-text"> {((0.33 + imposition * 0.03) * 100).toFixed(1)}%. </div>
                <div> of Effect RES. Outgoing Healing can be increased this way by up to </div> 
                <div className="buff-text"> {((0.15 + imposition * 0.03) * 100).toFixed(1)}%. </div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        if((0.33 + imposition * 0.03) * this.ER >= (0.15 + imposition * 0.03)) {
            return {
                id: "advsb",
                name: this.name,
                effect:{
                    effectRes: 0.16 + imposition * 0.04,
                    OGH: 0.15 + imposition * 0.03,
                },
                type: "buff",
                hasStacks: false,
                hasInput: false,
            }
        }
        else {
                return {
                id: "advsb",
                name: this.name,
                effect:{
                    effectRes: 0.16 + imposition * 0.04,
                    OGH: (0.33 + imposition * 0.03) * this.ER,
                },
                type: "buff",
                hasStacks: false,
                hasInput: false,
            }
        }
    }

    setER(ER) {
        this.ER = ER;
    }

    getER(imposition) {
        return 0.16 + imposition * 0.04;
    }

    weaponEffect(imposition) {
        return {
            0: {
            },
        }
    }
}

export default PerfectTiming;