import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';
import { Yanqing } from "../../../../../../../database/characters";

const YanqingBuffs = () => {

    const {level, eidolon, character, ultimateLevel, talentLevel, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    const yanqing = new Yanqing();

    const [prevTalentLevel, setPrevTalentLevel] = useState(talentLevel);
    const [prevUltimateLevel, setPrevUltimateLevel] = useState(ultimateLevel);
    const [E1ON, setE1ON] = useState(false);
    const [A2ON, setA2ON] = useState(false);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];

        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    useEffect(() => {
        if(characterBuffStates[1]) {
            const tempUltBuff = {
                effect: {
                    critDmg: yanqing.ultimateAttack(prevUltimateLevel).critDmg,
                },
            }
            updateStatesFromJson(tempUltBuff, true);
            
            const newTempUltBuff = {
                effect: {
                    critDmg: yanqing.ultimateAttack(ultimateLevel).critDmg,
                },
            }
            updateStatesFromJson(newTempUltBuff, false);
        }
        
        setPrevUltimateLevel(ultimateLevel);
    }, [ultimateLevel]);

    useEffect(() => {
        if(characterBuffStates[2]) {
            const tempUltBuff = {
                effect: {
                    critRate: yanqing.talent(prevTalentLevel).critRate,
                    critDmg: yanqing.talent(prevTalentLevel).critDmg,
                },
            }
            updateStatesFromJson(tempUltBuff, true);
            
            const newTempUltBuff = {
                effect: {
                    critRate: yanqing.talent(talentLevel).critRate,
                    critDmg: yanqing.talent(talentLevel).critDmg,
                },
            }
            
            updateStatesFromJson(newTempUltBuff, false);
        }
        
        setPrevTalentLevel(talentLevel);
    }, [talentLevel]);

    useEffect(() => {

        if(level < 3 && A2ON) {
            character.setA2ON(false);
            const updatedStates = [...characterBuffStates];
            setCharacterBuffStates(updatedStates);
            setA2ON(false);
        }
        
        if(level < 6 && characterBuffStates[4]) {
            updateStatesFromJson(A4Buff, true);
            characterBuffStates[4] = false;
        }

        if(level < 11 && characterBuffStates[5]) {
            updateStatesFromJson(A6Buff, true);
            characterBuffStates[5] = false;
        }

    }, [level]);

    useEffect(() => {

        if(eidolon < 1 && E1ON) {
            character.setE1ON(false);
            const updatedStates = [...characterBuffStates];
            setCharacterBuffStates(updatedStates);
            setE1ON(false);
        }

        if(eidolon < 2 && characterBuffStates[7]) {
            updateStatesFromJson(E2Buff, true);
            characterBuffStates[7] = false;
        }

        if(eidolon < 4 && characterBuffStates[8]) {
            updateStatesFromJson(E4Buff, true);
            characterBuffStates[8] = false;
        }
        
    }, [eidolon]);

    const ultBuff = {
        effect: {
            critRate: 0.6,
        },
    }

    const ultBuff2 = {
        effect: {
            critDmg: yanqing.ultimateAttack(ultimateLevel).critDmg,
        },
    }

    const talentBuff = {
        effect: {
            critRate: yanqing.talent(talentLevel).critRate,
            critDmg: yanqing.talent(talentLevel).critDmg,
        },
    }

    const techBuff = {
        effect: {
            quantumDmg: 0.3,
            fireDmg: 0.3,
            iceDmg: 0.3,
            lightningDmg: 0.3,
            physicalDmg: 0.3,
            imaginaryDmg: 0.3,
            windDmg: 0.3,
        },
    }

    const A4Buff = {
        effect: {
            effectRes: 0.2,
        },
    }

    const A6Buff = {
        effect: {
            spdPercent: 0.1,
        },
    }

    const E2Buff = {
        effect: {
            ERR: 0.10,
        },
    }

    const E4Buff = {
        effect: {
            icePen: 0.12,
        },
    }

    const turnA2On = () => {
        setA2ON(!A2ON);
        character.setA2ON(!A2ON);
        const updatedStates = [...characterBuffStates];
        setCharacterBuffStates(updatedStates);
    }

    const turnE1On = () => {
        setE1ON(!E1ON);
        character.setE1ON(!E1ON);
        const updatedStates = [...characterBuffStates];
        setCharacterBuffStates(updatedStates);
    }

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, ultBuff)} />}
            label={yanqing.ultimate1(ultimateLevel).name}
            />
            <div>{yanqing.ultimate1(ultimateLevel).description}</div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, ultBuff2)} />}
            label={yanqing.ultimate2(ultimateLevel).name}
            />
            <div>{yanqing.ultimate2(ultimateLevel).description}</div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, talentBuff)} />}
            label={yanqing.talentSimple(talentLevel).name}
            />
            <div>{yanqing.talentSimple(talentLevel).description}</div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, techBuff)} />}
            label={yanqing.techniqueDescription().name}
            />
            <div>{yanqing.techniqueDescription().description}</div>

            {level >= 3 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={A2ON || false} onChange={turnA2On} />}
                    label={"(A2) " + yanqing.A2().name}
                    />
                    <div>{yanqing.A2().description}</div>
                </>
            }

            {level >= 6 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[4] || false} onChange={() => applyCharacterBuffs(4, A4Buff)} />}
                    label={"(A4) " + yanqing.A4().name}
                    />
                    <div>{yanqing.A4().description}</div>
                </>
            }

            {level >= 11 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[5] || false} onChange={() => applyCharacterBuffs(5, A6Buff)} />}
                    label={"(A6) " + yanqing.A6().name}
                    />
                    <div>{yanqing.A6().description}</div>
                </>
            }

            {eidolon >= 1 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={E1ON || false} onChange={turnE1On} />}
                    label={"(E1) " + yanqing.E1().name}
                    />
                    <div>{yanqing.E1().description}</div>
                </>
            }

            {eidolon >= 2 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[7] || false} onChange={() => applyCharacterBuffs(7, E2Buff)} />}
                    label={"(E2) " + yanqing.E2().name}
                    />
                    <div>{yanqing.E2().description}</div>
                </>
            }

            {eidolon >= 4 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[8] || false} onChange={() => applyCharacterBuffs(8, E4Buff)} />}
                    label={"(E4) " + yanqing.E4().name}
                    />
                    <div>{yanqing.E4().description}</div>
                </>
            }
        </>
    )
}

export const YanqingGlobalBuffs = (props) => {

    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {
        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <div>No Buffs</div>
        </>
    )
}

export default YanqingBuffs;