import React, { useContext } from 'react';
import { globalData } from '../../../globalData';

function LukaTable() {

    const levelMult = {
        1: 54.0000,
        20: 139.7703,
        40: 363.6658,
        50: 774.9041,
        60: 1640.3068,
        70: 2659.6406,
        80: 3767.5533,
    }

    const {
        character,
        physicalDmg,
        atk,
        atkPercent,
        atkFlat,
        critRate,
        critDmg,
        energyRegen,
        level,
        enemyLevel,
        physicalPen,
        breakEffect,
        defReduction,
        effectHitRate,
        enemyRes,
        basicAttackLevel,
        skillLevel,
        ultimateLevel,
        ultimateBoost,
        skillBoost,
        breakMultiplier,
        basicBoost,
        ultimateCritDmg,
        physicalVulnerability,
        enemyToughness,
        enemyMaxHP,
        dotVulnerability,
        isBoss,
        talentLevel,
        dotBoost,
    } = useContext(globalData);

    let lukaAtk = (atk * atkPercent) + atkFlat;
    let damageBoost = (1 + physicalDmg);
    let critDmgBoost = (1 + critDmg);

    let basicBaseDmg = lukaAtk * character.basicAttack(basicAttackLevel);
    let basicDmgBoost = damageBoost + basicBoost;
    let enhancedBasicBaseDmg = lukaAtk * character.basicAttack1(basicAttackLevel).hits;
    let enhancedBasicBaseDmgFinalHit = lukaAtk * character.basicAttack1(basicAttackLevel).finalHit;

    let skillDmgBoost = damageBoost + skillBoost;
    let skillBaseDmg = lukaAtk * character.skillAttack(skillLevel).single;

    let skillBleed = 0.24 * enemyMaxHP;

    if(skillBleed > character.skillAttack(skillLevel).dotCap * lukaAtk) {
        skillBleed = character.skillAttack(skillLevel).dotCap * lukaAtk;
    }

    let ultDmgBoost = damageBoost + ultimateBoost;
    let ultimateBaseDmg = lukaAtk * character.ultimateAttack(ultimateLevel).multiplier;

    let resMult = (1 - (enemyRes - physicalPen));
    let defReductionStat = (1 - defReduction);
    let vulnerabilityMultiplier = (1 + physicalVulnerability);

    let techDmg = lukaAtk * 0.5;

    if(defReductionStat < 0) {
        defReductionStat = 0;
    }

    let defMult = (character.stats[level]["level"] + 20)/((enemyLevel + 20) * defReductionStat + character.stats[level]["level"] + 20);
    let breakMult = breakMultiplier;
    
    let critRateStat = critRate;
    
    if(critRateStat > 1) {
        critRateStat = 1;
    }
    
    let maxToughnessMult = 0.5 + (enemyToughness/120);
    let breakBaseDmg = 2 * levelMult[character.stats[level]["level"]] * maxToughnessMult;
    let breakDmg = breakBaseDmg * (1 + breakEffect) * defMult * resMult * vulnerabilityMultiplier * breakMult; 

    let bleedBaseDmg = 0;
    if(isBoss) {
        bleedBaseDmg = 0.07 * enemyMaxHP;
    }else {
        bleedBaseDmg = 0.16 * enemyMaxHP;
    }

    if(2 * levelMult[character.stats[level]["level"]] * maxToughnessMult < bleedBaseDmg) {
        bleedBaseDmg = 2 * levelMult[character.stats[level]["level"]] * maxToughnessMult;
    }

    let bleed = bleedBaseDmg * (1 + breakEffect) * resMult * defMult * (vulnerabilityMultiplier + dotVulnerability) * breakMult * (damageBoost + dotBoost);

    return (
        <div className="damage-details">
            <div className='header'>Damage Details</div>
            <div className="divider"></div>
            <div className='muted-text' style={{textAlign: "center"}}>DoT Triggers assume Skill DoT only</div>
            <div className="damage-title header">Basic Attack ({basicAttackLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Basic</td>
                        <td className='tb-text'>{(basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult).toFixed(0)}</td>
                        <td className='tb-text'>{(basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult * critDmgBoost).toFixed(0)}</td>
                        <td className='tb-text'>{((basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Enhanced Basic Attack ({basicAttackLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Hits DMG</td>
                        <td className='tb-text'>3x{(enhancedBasicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult).toFixed(0)}</td>
                        <td className='tb-text'>3x{(enhancedBasicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult * critDmgBoost).toFixed(0)}</td>
                        <td className='tb-text'>3x{((enhancedBasicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Final Punch</td>
                        <td className='tb-text'>{(enhancedBasicBaseDmgFinalHit * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult).toFixed(0)}</td>
                        <td className='tb-text'>{(enhancedBasicBaseDmgFinalHit * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult * critDmgBoost).toFixed(0)}</td>
                        <td className='tb-text'>{((enhancedBasicBaseDmgFinalHit * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{60}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{60}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Skill Attack ({skillLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col" className='tb-text-header'>Non-CRIT</th>
                        <th scope="col" className='tb-text-header'>CRIT</th>
                        <th scope="col" className='tb-text-header'>Average</th>

                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Skill</td>
                        <td className='tb-text'>{(skillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult).toFixed(0)}</td>
                        <td className='tb-text'>{(skillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult * critDmgBoost).toFixed(0)}</td>
                        <td className='tb-text'>{((skillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{60}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{60}</td>
                    </tr>
                    <tr>
                        <td>Bleed Chance</td>
                        <td className='tb-text'>{(1 * (1 + effectHitRate) * (1 - 0.4) * 100).toFixed(1)}%</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(1 * (1 + effectHitRate) * (1 - 0.4) * 100).toFixed(1)}%</td>
                    </tr>
                    <tr>
                        <td>Bleed DMG</td>
                        <td className='tb-text'>{(skillBleed * (damageBoost + dotBoost) * defMult * (vulnerabilityMultiplier + dotVulnerability) * resMult * breakMult).toFixed(0)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{((skillBleed * (damageBoost + dotBoost) * defMult * (vulnerabilityMultiplier + dotVulnerability) * resMult * breakMult)).toFixed(0)}</td>
                    </tr>
                </tbody>
            </table>
            <div className="damage-title header">Ultimate Attack ({ultimateLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Ultimate</td>
                        <td className='tb-text'>{(ultimateBaseDmg * ultDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult).toFixed(0)}</td>
                        <td className='tb-text'>{(ultimateBaseDmg * ultDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult * (critDmgBoost + ultimateCritDmg)).toFixed(0)}</td>
                        <td className='tb-text'>{((ultimateBaseDmg * ultDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult) * (1 + (critDmg + ultimateCritDmg) * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{90}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{90}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Talent DoT ({talentLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Source</th>
                        <th scope="col">DMG</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>DoT Trigger</td>
                        <td className='tb-text'>{(character.talent(talentLevel) * (skillBleed * (damageBoost + dotBoost) * defMult * (vulnerabilityMultiplier + dotVulnerability) * resMult * breakMult)).toFixed(0)}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Technique</div>
                <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>DMG</td>
                        <td className='tb-text'>{((techDmg * damageBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{((techDmg * damageBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((techDmg * damageBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{60}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{60}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Weakness Break</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Type</th>
                        <th scope="col">Value</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Break Damage</td>
                        <td className='tb-text'>{(breakDmg).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Bleed Damage</td>
                        <td className='tb-text'>{(bleed).toFixed(0)}</td>
                    </tr>
                </tbody>
            </table>
    </div>
    )
}


export default LukaTable;