export class CruisingintheStellarSea {
    constructor() {
        this.stats = [
            [1, 24, 21, 43],
            [20, 92.4, 80.85, 166],
            [20, 121.2, 106.05, 218],
            [40,231.6,202.65,417],
            [40,270,236.25,486],
            [50,306,267.75,551],
            [50,344.4,301.35,620],
            [60,380.4,332.85,685],
            [60,418.8,366.45,754],
            [70,454.8,397.95,819],
            [70,493.2,431.55,888],
            [80,529.2,463.05,953],
        ];
        
        this.name = "Cruising in the Stellar Sea";
        this.path = "Hunt";
        this.id = "stellarsea";
        this.rarity = "fiveStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text">CRIT Rate</div> 
                <div> by </div> 
                <div className="buff-text">{((0.08 + imposition * 0.02) * 100).toFixed(1)}%</div>
                <div>, and increases their </div>
                <div className="buff-text">CRIT Rate</div> 
                <div> against enemies with HP less than or equal to </div> 
                <div className="buff-text">50%</div> 
                <div> by an extra </div>
                <div className="buff-text">{((0.08 + imposition * 0.02) * 100).toFixed(1)}%.</div>
                <div> When the wearer defeats an enemy, their </div>
                <div className="buff-text">ATK</div> 
                <div> is increased by </div> 
                <div className="buff-text">{((0.2 + imposition * 0.05) * 100).toFixed(1)}%</div>
                <div> for </div>
                <div className="buff-text">2</div> 
                <div> turn(s).</div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "stellarseab",
            name: this.name,
            effect:{
                critRate: 0.08 + imposition * 0.02,
            },
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "stellarseae1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div>when the wearer defeats an enemy, their</div>
                        <div className="buff-text"> ATK </div>
                        <div> is increased by </div>
                        <div className="buff-text">{((0.2 + imposition * 0.05) * 100).toFixed(1)}%</div> 
                        <div> for </div>
                        <div className="buff-text">2</div>
                        <div> turn(s).</div>
                    </div>
                ),
                effect: {
                    atkPercent: 0.2 + imposition * 0.05,
                },
                type: "buff",
                hasStacks: false,
                hasInput: false,
            },
            1: {
                id: "stellarseae2",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div>Increases their </div> 
                        <div className="buff-text"> CRIT Rate </div> 
                        <div> against enemies with HP less than or equal to </div>
                        <div className="buff-text"> 50% </div> 
                        <div> by an extra </div> 
                        <div className="buff-text">{((0.08 + imposition * 0.02) * 100).toFixed(1)}%.</div>
                    </div>),
                effect:{
                    critRate: 0.08 + imposition * 0.02,
                },
                type: "buff",
                hasStacks: false,
                hasInput: false,
            }
        }
    }
}