export class Fermata {
    constructor() {
        this.stats = [
            [1, 21.6, 15, 43],
            [20, 83.16, 57.75, 166],
            [20, 109.08, 75.75, 218],
            [40, 208.44, 144.75,417],
            [40, 243, 168.75,  486],
            [50, 275.4, 191.25,551],
            [50, 309.96, 215.25,620],
            [60, 342.36, 237.75,685],
            [60, 376.92, 261.75,754],
            [70, 409.32, 284.25,819],
            [70, 443.88, 308.25,888],
            [80, 476.28, 330.75,953],
        ];
        
        this.name = "Fermata";
        this.path = "Nihility";
        this.id = "fermata";
        this.rarity = "fourStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the  </div>
                <div className="buff-text">Break Effect</div>
                <div> dealt by the wearer by </div>
                <div className="buff-text">{((0.16 + imposition * 0.04) * 100).toFixed(1)}% </div>
                <div> and increases their DMG to enemies afflicted with Shock or Wind Shear by </div>
                <div className="buff-text">{((0.16 + imposition * 0.04) * 100).toFixed(1)}%. </div>
                <div> This also applies to DoT. </div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{
                BE: (0.16 + imposition * 0.04),
            },
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "adve1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> DMG to enemies afflicted with Shock or Wind Shear by </div>
                        <div className="buff-text">{((0.16 + imposition * 0.04) * 100).toFixed(1)}%. </div>
                        <div> This also applies to DoT. </div>
                    </div>
                ),
                effect: {
                    iceDmg: 0.16 + imposition * 0.04,
                    quantumDmg: 0.16 + imposition * 0.04,
                    fireDmg: 0.16 + imposition * 0.04,
                    lightningDmg: 0.16 + imposition * 0.04,
                    windDmg: 0.16 + imposition * 0.04,
                    physicalDmg: 0.16 + imposition * 0.04,
                    imaginaryDmg: 0.16 + imposition * 0.04,
                },
                type: "buff",
                hasStack: false,
                hasInput: false,
            },
        }
    }
}

export default Fermata;