export class Silence {
    constructor() {
        this.stats = [
            [1, 21.6, 15, 43],
            [20, 83.16, 57.75, 166],
            [20, 109.08, 75.75, 218],
            [40, 208.44, 144.75,417],
            [40, 243, 168.75,  486],
            [50, 275.4, 191.25,551],
            [50, 309.96, 215.25,620],
            [60, 342.36, 237.75,685],
            [60, 376.92, 261.75,754],
            [70, 409.32, 284.25,819],
            [70, 443.88, 308.25,888],
            [80, 476.28, 330.75,953],
        ];
        
        this.name = "Only Silence Remains";
        this.path = "Hunt";
        this.id = "silence";
        this.rarity = "fourStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text">ATK</div> 
                <div> by </div> 
                <div className="buff-text">{((0.16 + imposition * 0.04) * 100).toFixed(1)}%</div>
                <div>, If there are 2 or fewer enemies on the field, increases wearer's </div>
                <div className="buff-text">CRIT Rate</div> 
                <div> by </div> 
                <div className="buff-text">{((0.12 + imposition * 0.03) * 100).toFixed(1)}%</div> 
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "silenceb",
            name: this.name,
            effect:{
                atkPercent: 0.16 + imposition * 0.04,
            },
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "silence0",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div>If there are 2 or fewer enemies on the field, increases wearer's</div>
                        <div className="buff-text"> CRIT Rate </div>
                        <div> by </div>
                        <div className="buff-text">{((0.12 + imposition * 0.03) * 100).toFixed(1)}%</div> 
                    </div>
                ),
                effect: {
                    critRate: 0.12 + imposition * 0.03,
                },
                type: "buff",
                hasStacks: false,
                hasInput: false,
            },
        }
    }
}

export default Silence;