class Luocha {
    constructor() {
        this.stats = [
            {level: 1,  atk: 102.96,  def: 49.5,   hp: 174, spd: 101, taunt: 100, energy: 100},
            {level: 20, atk: 200.77, def: 96.53,  hp: 340, spd: 101, taunt: 100, energy: 100, atkPercent: 0.04},
            {level: 20, atk: 241.96, def: 116.33, hp: 409, spd: 101, taunt: 100, energy: 100, atkPercent: 0.04},
            {level: 40, atk: 386.1,  def: 185.63, hp: 653, spd: 101, taunt: 100, energy: 100, atkPercent: 0.04, defPercent: 0.04},
            {level: 40, atk: 427.28, def: 205.43, hp: 723, spd: 101, taunt: 100, energy: 100, atkPercent: 0.08, hpPercent: 0.04,  defPercent: 0.05},
            {level: 50, atk: 478.76, def: 230.18, hp: 810, spd: 101, taunt: 100, energy: 100, atkPercent: 0.08, hpPercent: 0.04,  defPercent: 0.05},
            {level: 50, atk: 519.95, def: 249.98, hp: 880, spd: 101, taunt: 100, energy: 100, atkPercent: 0.14, hpPercent: 0.04,  defPercent: 0.05},
            {level: 60, atk: 571.43, def: 274.73, hp: 967, spd: 101, taunt: 100, energy: 100, atkPercent: 0.14, hpPercent: 0.04,  defPercent: 0.05},
            {level: 60, atk: 612.61, def: 294.53, hp: 1037,spd: 101, taunt: 100, energy: 100, atkPercent: 0.20, hpPercent: 0.1, defPercent: 0.05},
            {level: 70, atk: 664.09, def: 319.28, hp: 1124,spd: 101, taunt: 100, energy: 100, atkPercent: 0.20, hpPercent: 0.1, defPercent: 0.05},
            {level: 70, atk: 705.28, def: 339.08, hp: 1194,spd: 101, taunt: 100, energy: 100, atkPercent: 0.20, hpPercent: 0.1, defPercent: 0.05},
            {level: 80, atk: 756.76, def: 363.83, hp: 1281,spd: 101, taunt: 100, energy: 100, atkPercent: 0.28, hpPercent: 0.18, defPercent: 0.125},
        ];

        this.name = "Luocha";
        this.path = "Abundance";
        this.rarity = 'fiveStar';
        this.element = "Imaginary";
        this.id = "luocha";
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Thorns of the Abyss",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level == 0) {
            return {
                multiplier: 0.4,
                addition: 200,
            }
        }else if(level == 1) {
            return {
                multiplier: 0.425,
                addition: 420,
            }
        }
        else if(level == 2) {
            return {
                multiplier: 0.45,
                addition: 410,
            }
        }
        else if(level == 3) {
            return {
                multiplier: 0.475,
                addition: 500,
            }
        }
        else if(level == 4) {
            return {
                multiplier: 0.5,
                addition: 560,
            }
        }
        else if(level == 5) {
            return {
                multiplier: 0.52,
                addition: 620,
            }
        }
        else if(level == 6) {
            return {
                multiplier: 0.54,
                addition: 665,
            }
        }
        else if(level == 7) {
            return {
                multiplier: 0.56,
                addition: 710,
            }
        }
        else if(level == 8) {
            return {
                multiplier: 0.58,
                addition: 755,
            }
        }
        else if(level == 9) {
            return {
                multiplier: 0.6,
                addition: 800,
            }
        }
        else if(level == 10) {
            return {
                multiplier: 0.62,
                addition: 845,
            }
        }
        else if(level == 11) {
            return {
                multiplier: 0.64,
                addition: 890,
            }
        }
    }

    skillDescription(level) {
        return {
            name: "Prayer of Abyss Flower",
            description: (
                <div className="buff-description">
                    <div> After using his Skill, Luocha immediately restores the target ally's HP equal to </div>
                    <div className="buff-text">{(this.skillAttack(level).multiplier * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> plus </div>
                    <div className="buff-text">{this.skillAttack(level).addition}.</div>
                    <div> Meanwhile, Luocha gains 1 stack of Abyss Flower. When any ally's HP percentage drops to 50% or lower, an effect equivalent to Luocha's Skill will immediately be triggered and applied to this ally for one time (without consuming Skill Points). This effect can be triggered again after 2 turn(s). </div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level < 6) {
            return 1.2 + (level * 0.08);
        }else if(level < 10){
            return 1.6 + ((level - 5) * 0.1);
        }else {
            return 2 + ((level - 9) * 0.08);
        }
    }

    ultimateDescription(level) {
        return {
            name: "Death Wish",
            description: (
                <div className="buff-description">
                    <div>Removes 1 buff(s) from all enemies and deals all enemies  </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.ultimateAttack(level) * 100).toFixed(1)}%</div>
                    <div>  of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> At the same time, Luocha gains 1 stack of Abyss Flower. </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level == 0) {
            return {
                multiplier: 0.12,
                addition: 60,
            }
        }else if(level == 1) {
            return {
                multiplier: 0.1275,
                addition: 96,
            }
        }
        else if(level == 2) {
            return {
                multiplier: 0.135,
                addition: 123,
            }
        }
        else if(level == 3) {
            return {
                multiplier: 0.1425,
                addition: 150,
            }
        }
        else if(level == 4) {
            return {
                multiplier: 0.15,
                addition: 168,
            }
        }
        else if(level == 5) {
            return {
                multiplier: 0.156,
                addition: 186,
            }
        }
        else if(level == 6) {
            return {
                multiplier: 0.162,
                addition: 200,
            }
        }
        else if(level == 7) {
            return {
                multiplier: 0.168,
                addition: 213,
            }
        }
        else if(level == 8) {
            return {
                multiplier: 0.174,
                addition: 227,
            }
        }
        else if(level == 9) {
            return {
                multiplier: 0.18,
                addition: 240,
            }
        }
        else if(level == 10) {
            return {
                multiplier: 0.186,
                addition: 254,
            }
        }
        else if(level == 11) {
            return {
                multiplier: 0.192,
                addition: 267,
            }
        }
    }

    talentDescription(level) { 
        return {
            name: "Cycle of Life",
            description: (
                <div className="buff-description">
                    <div>When Abyss Flower reaches 2 stacks, Luocha consumes all stacks of Abyss Flower to deploy a Field against the enemy. When any enemy in the Field is attacked by an ally, the attacking ally's HP is immediately restored by an amount equal to </div>
                    <div className="buff-text">{(this.talent(level).multiplier * 100).toFixed(1)}%</div>
                    <div>  of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> plus </div>
                    <div className="buff-text"> {this.talent(level).addition} </div>
                    <div> The Field's effect lasts for 2 turns. When Luocha is knocked down, the Field will be dispelled. </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Mercy of a Fool",
            description: (
                <div className="buff-description">
                    <div> After the Technique is used, the Talent will be immediately triggered at the start of the next battle. </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Cleansing Revival",
            description: (
                <div className="buff-description">
                    <div> When the Skill's effect is triggered, removes 1 debuff(s) from a target ally. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Sanctified",
            description: (
                <div className="buff-description">
                    <div> When any enemy in the Field is attacked by an ally, all allies (except the attacker) restore HP equal to 7.0% of Luocha's ATK plus 93. </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Through the Valley",
            description: (
                <div className="buff-description">
                    <div> The chance to resist Crowd Control debuffs increases by 70%. </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Ablution of the Quick",
            description: (
                <div className="buff-description">
                    <div> While the Field is active, ATK of all allies increases by 20%. </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Bestowal From the Pure",
            description: (
                <div className="buff-description">
                    <div> When his Skill is triggered, if the target ally's HP is lower than 50%, Luocha's Outgoing Healing increases by 30%. If the target ally's HP is at 50% or higher, the ally receives a Shield that can absorb DMG equal to 18% of Luocha's ATK plus 240, lasting for 2 turns. </div>
                </div>
            )
        }
    }
    
    E3() {
        return {
            name: "Surveyal by the Fool",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }
    
    E4() {
        return {
            name: "Heavy Lies the Crown",
            description:(
                <div className="buff-description">
                    <div>When Luocha's Field is active, enemies become Weakened and deal 12% less DMG.</div>
                </div>
            )
        }
    }
    
    E5() {
        return {
            name: " Cicatrix 'Neath the Pain",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: "Reunion With the Dust",
            description: (
                <div className="buff-description">
                    <div> When Ultimate is used, there is a 100% fixed chance to reduce all enemies' All-Type RES by 20% for 2 turn(s). </div>
                </div>
            ),
        }
    }
}

export default Luocha;