export class BeforeDawn {
    constructor() {
        this.stats = [
            [1,  26.4,    21,     48],
            [20, 101.64,  80.85,  185],
            [20, 133.32, 106.05,  242],
            [40, 254.76, 202.65, 463],
            [40, 297,   236.25, 540],
            [50, 336.6,   267.75, 612],
            [50, 378.84, 301.35, 689],
            [60, 418.44, 332.85, 761],
            [60, 460.68, 366.45, 838],
            [70, 500.28, 397.95, 910],
            [70, 542.52, 431.55, 986],
            [80, 582.12, 463.05, 1058],
        ];
        
        this.name = "Before Dawn";
        this.path = "Erudition";
        this.id = "beforedawn";
        this.rarity = "fiveStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text"> CRIT DMG </div> 
                <div> by </div> 
                <div className="buff-text">{((0.36 + imposition * 0.06) * 100).toFixed(1)}%.</div>
                <div> Increases the wearer's </div>
                <div className="buff-text"> Skill and Ultimate DMG</div> 
                <div> by </div> 
                <div className="buff-text"> {((0.18 + imposition * 0.03) * 100).toFixed(1)}%.</div> 
                <div> After the wearer uses their </div>
                <div className="buff-text">Skill or Ultimate</div>
                <div> they gain Somnus Corpus. Upon triggering a </div>
                <div className="buff-text"> Follow-up attack</div> 
                <div> Somnus Corpus will be consumed and the follow-up attack DMG increases by </div>
                <div className="buff-text"> {((0.48 + imposition * 0.08) * 100).toFixed(1)}%. </div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "silenceb",
            name: this.name,
            effect:{
                critDmg: 0.36 + imposition * 0.06,
                skillBoost: 0.18 + imposition * 0.03,
                ultimateBoost: 0.18 + imposition * 0.03,
            },
            type: "buff",
            hasStack: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "silence0",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> After the wearer uses their </div>
                        <div className="buff-text">Skill or Ultimate</div>
                        <div> they gain Somnus Corpus. Upon triggering a </div>
                        <div className="buff-text"> Follow-up attack</div> 
                        <div> Somnus Corpus will be consumed and the follow-up attack DMG increases by </div>
                        <div className="buff-text"> {((0.48 + imposition * 0.08) * 100).toFixed(1)}%. </div>
                    </div>
                ),
                effect: {
                    followUpBoost: 0.48 + imposition * 0.08,
                },
                type: "buff",
                hasStack: false,
                hasInput: false,
            },
        }
    }
}

export default BeforeDawn;