export class Salsotto {
    constructor(rarity, mainStat, type) {
        this.id = "salsotto";
        this.name = "Inert Salsotto";
        this.rarity = rarity;
        this.mainStat = mainStat;
        this.type = type;
        this.mainStatValue = 0;
        this.subStats = {
            0: {
                "subType": "SPD",
                "value": 0,
            },
            1: {
                "subType": "HP",
                "value": 0,
            },
            2: {
                "subType": "HP%",
                "value": 0,
            },
            3: {
                "subType": "ATK",
                "value": 0,
            },
        };
    }

    planerEffect() {
        return {
            0: {
                id: this.id + 0,
                description: (
                    <div className="buff-description">
                        <div>Increases the wearer's </div>
                        <div className="buff-text">CRIT Rate</div>
                        <div> by </div>
                        <div className="buff-text">8%</div>
                    </div>
                ),
                effect: {
                    critRate: 0.08,
                },
                type: "buff",
            },
            1: {
                id: this.id + 1,
                description: (
                    <div className="buff-description">
                        <div>When the wearer's current </div>
                        <div className="buff-text"> CRIT Rate </div>
                        <div> reaches </div>
                        <div className="buff-text"> 50% or higher </div>
                        <div> , the wearer's </div>
                        <div className="buff-text">Ultimate</div>
                        <div> and </div>  
                        <div className="buff-text">follow-up attack DMG</div>  
                        <div> increases by </div>
                        <div className="buff-text">15%.</div> 
                    </div>
                ),
                effect: {
                    ultimateBoost: 0.15,
                    followUpBoost: 0.15,
                },
                type: "buff",
            },
        }
    }

    // function to change mainStat
    changeMainStat(mainStat) {
        this.mainStat = mainStat;
    }

    setMainStatValue(value) {
        this.mainStatValue = value;
    }

}

