import React, { useContext } from 'react';
import { globalData } from '../../../globalData';

function BailuTable() {

    const levelMult = {
        1: 54.0000,
        20: 139.7703,
        40: 363.6658,
        50: 774.9041,
        60: 1640.3068,
        70: 2659.6406,
        80: 3767.5533,
    }

    const {
        character,
        lightningDmg,
        atk,
        atkPercent,
        atkFlat,
        hp,
        eidolon,
        hpPercent,
        hpFlat,
        talentLevel,
        critRate,
        critDmg,
        energyRegen,
        level,
        enemyLevel,
        lightningPen,
        breakEffect,
        defReduction,
        enemyRes,
        basicAttackLevel,
        skillLevel,
        ultimateLevel,
        breakMultiplier,
        basicBoost,
        outgoingHealing,
        lightningVulnerability,
        enemyToughness,
        dotBoost,
        dotVulnerability,
    } = useContext(globalData);

    let bailuAtk = (atk * atkPercent) + atkFlat;
    let bailuHp = (hp * hpPercent) + hpFlat;

    let damageBoost = (1 + lightningDmg);
    let critDmgBoost = (1 + critDmg);
    let basicDmgBoost = damageBoost + basicBoost;
    let basicBaseDmg = bailuAtk * character.basicAttack(basicAttackLevel);

    let skillHealing = (((bailuHp * character.skillAttack(skillLevel).multiplier) + character.skillAttack(skillLevel).addition) * (1 + outgoingHealing));

    let ultimateHealing = (((bailuHp * character.ultimateAttack(ultimateLevel).multiplier) + character.ultimateAttack(ultimateLevel).addition) * (1 + outgoingHealing));

    let talentHealing = (((bailuHp * character.talent(level).multiplier) + character.talent(level).addition) * (1 + outgoingHealing));

    let talentReviveHeal = (((bailuHp * character.talent(level).reviveMult) + character.talent(level).reviveAdd) * (1 + outgoingHealing));

    let resMult = (1 - (enemyRes - lightningPen));
    let defReductionStat = (1 - defReduction);
    let vulnerabilityMultiplier = (1 + lightningVulnerability);

    if(defReductionStat < 0) {
        defReductionStat = 0;
    }

    let defMult = (character.stats[level]["level"] + 20)/((enemyLevel + 20) * defReductionStat + character.stats[level]["level"] + 20);
    let breakMult = breakMultiplier;
    
    let critRateStat = critRate;
    
    if(critRateStat > 1) {
        critRateStat = 1;
    }
    
    let maxToughnessMult = 0.5 + (enemyToughness/120);
    let breakBaseDmg = 1 * levelMult[character.stats[level]["level"]] * maxToughnessMult;
    let breakDmg = breakBaseDmg * (1 + breakEffect) * defMult * resMult * vulnerabilityMultiplier * breakMult; 
    let shockBaseDmg = 2 * levelMult[character.stats[level]["level"]];
    let shock = shockBaseDmg * (1 + breakEffect) * resMult * defMult * (vulnerabilityMultiplier + dotVulnerability) * breakMult * (damageBoost + dotBoost);

    return (
        <div className="damage-details">
            <div className='header'>Damage Details</div>
            <div className="divider"></div>
            <div className="damage-title header">Basic Attack ({basicAttackLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Basic</td>
                        <td className='tb-text'>{(basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult).toFixed(0)}</td>
                        <td className='tb-text'>{(basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult * critDmgBoost).toFixed(0)}</td>
                        <td className='tb-text'>{((basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
                
            </table>

            <div className="damage-title header">Skill Healing ({skillLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Source</th>
                        <th scope="col" className='tb-text-header'>Amount</th>

                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Heal</td>
                        <td className='tb-text'>{(skillHealing).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Bounce 1</td>
                        <td className='tb-text'>{(skillHealing * 0.85).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Bounce 2</td>
                        <td className='tb-text'>{(skillHealing * 0.85 * 0.85).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Ultimate Healing ({ultimateLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Source</th>
                        <th scope="col">Amount</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Heal</td>
                        <td className='tb-text'>{(ultimateHealing).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Talent Healing ({talentLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Source</th>
                        <th scope="col">Amount</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Invigoration</td>
                        <td className='tb-text'>{(talentHealing).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Revive Heal</td>
                        <td className='tb-text'>{(talentReviveHeal).toFixed(0)}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Weakness Break</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Type</th>
                        <th scope="col">Value</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Break Damage</td>
                        <td className='tb-text'>{(breakDmg).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Shock Damage</td>
                        <td className='tb-text'>{(shock).toFixed(0)}</td>
                    </tr>
                </tbody>
            </table>
    </div>
    )
}


export default BailuTable;