export class Breakfast {
    constructor() {
        this.stats = [
            [1, 21.6,    18,     38],
            [20, 83.16,  69.3,  148],
            [20, 109.08, 90.9,  194],
            [40, 208.44, 173.7, 371],
            [40, 243,    202.5, 432],
            [50, 275.4,  229.5, 490],
            [50, 309.96, 258.3, 551],
            [60, 342.36, 285.3, 609],
            [60, 376.92, 314.1, 670],
            [70, 409.32, 341.1, 728],
            [70, 443.88, 369.9, 789],
            [80, 476.28, 396.9, 847],
        ];
        
        this.name = "The Seriousness of Breakfast";
        this.path = "Erudition";
        this.id = "breakfast";
        this.rarity = "fourStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text"> DMG </div> 
                <div> by </div> 
                <div className="buff-text"> {((0.12 + imposition * 0.03) * 100).toFixed(1)}% </div> 
                <div> For every defeated enemy, the wearer's  </div> 
                <div className="buff-text">ATK</div>
                <div>  increases by </div> 
                <div className="buff-text"> {((0.04 + imposition * 0.01) * 100).toFixed(1)}% </div> 
                <div> stacking up to 3 time(s). </div> 
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{
                iceDmg: 0.12 + imposition * 0.03,
                quantumDmg: 0.12 + imposition * 0.03,
                fireDmg: 0.12 + imposition * 0.03,
                lightningDmg: 0.12 + imposition * 0.03,
                windDmg: 0.12 + imposition * 0.03,
                physicalDmg: 0.12 + imposition * 0.03,
                imaginaryDmg: 0.12 + imposition * 0.03,
            },
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition, stack) {
        return {
            0: {
                id: "adve1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> For every defeated enemy, the wearer's  </div> 
                        <div className="buff-text">ATK</div>
                        <div>  increases by </div> 
                        <div className="buff-text"> {((0.04 + imposition * 0.01) * 100).toFixed(1)}% </div> 
                        <div> stacking up to 3 time(s). </div> 
                    </div>
                ),
                effect: {
                    atkPercent: (0.04 + imposition * 0.01) * stack,
                },
                type: "buff",
                hasStack: true,
                stacks: 3,
                hasInput: false,
            },
        }
    }
}

export default Breakfast;