import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';

import { Welt } from "../../../../../../../database/characters";

const WeltDebuffs = () => {

    const { characterBuffStates, level, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    
    const welt = new Welt();

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    useEffect(() => {

        if(level < 3 && characterBuffStates[0]) {
            updateStatesFromJson(A2, true);
            characterBuffStates[0] = false;
        }

    }, [level]);

    const A2 = {
        effect: {
            fireVulnerability: 0.12,
            physicalVulnerability: 0.12,
            lightningVulnerability: 0.12,
            iceVulnerability: 0.12,
            quantumVulnerability: 0.12,
            windVulnerability: 0.12,
            imaginaryVulnerability: 0.12,
        },
    }

    return (
        <>  
            {level >= 3 &&
                <>
                    <FormControlLabel
                        className='header'
                        control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, A2)} />}
                        label={welt.A2().name}
                    />
                    <div>{welt.A2().description}</div>
                </>
            }
        </>
    )
}

export const WeltGlobalDebuffs = (props) => {

    const {updateStatesFromJson } = useContext(globalData);

    const welt = new Welt();

    const weltBuffStates = props.buffStates;
    const setWeltBuffStates = props.setBuffStates;
    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...weltBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setWeltBuffStates(updatedStates);
    }

    useEffect(() => {

        if(weltBuffStates[0]) {
            updateStatesFromJson(A2, true);
            weltBuffStates[0] = false;
        }

        setDone(!done);
    }, [characterSwitched]);

    const A2 = {
        effect: {
            fireVulnerability: 0.12,
            physicalVulnerability: 0.12,
            lightningVulnerability: 0.12,
            iceVulnerability: 0.12,
            quantumVulnerability: 0.12,
            windVulnerability: 0.12,
            imaginaryVulnerability: 0.12,
        },
    }

    return (
        <>
            <FormControlLabel
                className='header'
                control={<Checkbox checked={weltBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, A2)} />}
                label={welt.A2().name}
            />
            <div>{welt.A2().description}</div>
        </>
    )
}

export default WeltDebuffs;