export class Sleep {
    constructor() {
        this.stats = [
            [1,  26.4,    21,     48],
            [20, 101.64,  80.85,  185],
            [20, 133.32, 106.05,  242],
            [40, 254.76, 202.65, 463],
            [40, 297,   236.25, 540],
            [50, 336.6,   267.75, 612],
            [50, 378.84, 301.35, 689],
            [60, 418.44, 332.85, 761],
            [60, 460.68, 366.45, 838],
            [70, 500.28, 397.95, 910],
            [70, 542.52, 431.55, 986],
            [80, 582.12, 463.05, 1058],
        ];
        
        this.name = "Sleep Like the Dead";
        this.path = "Hunt";
        this.id = "sleep";
        this.rarity = "fiveStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text"> CRIT DMG </div> 
                <div> by </div> 
                <div className="buff-text">{((0.3 + imposition * 0.05) * 100).toFixed(1)}%.</div>
                <div> When the wearer's </div>
                <div className="buff-text">Basic ATK or Skill</div> 
                <div> does not result in a CRIT Hit, increases their </div> 
                <div className="buff-text"> CRIT Rate </div> 
                <div> by </div>
                <div className="buff-text">{((0.36 + imposition * 0.06) * 100).toFixed(1)}%</div>
                <div> for </div>
                <div className="buff-text"> 1 turn(s). </div>
                <div> This effect can only trigger once every </div>
                <div className="buff-text"> 3 turn(s). </div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "silenceb",
            name: this.name,
            effect:{
                critDmg: 0.3 + imposition * 0.05,
            },
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "silence0",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> When the wearer's </div>
                        <div className="buff-text">Basic ATK or Skill</div> 
                        <div> does not result in a CRIT Hit, increases their </div> 
                        <div className="buff-text"> CRIT Rate </div> 
                        <div> by </div>
                        <div className="buff-text">{((0.36 + imposition * 0.08) * 100).toFixed(1)}%</div>
                        <div> for </div>
                        <div className="buff-text"> 1 turn(s). </div>
                        <div> This effect can only trigger once every </div>
                        <div className="buff-text"> 3 turn(s). </div>
                    </div>
                ),
                effect: {
                    critRate: 0.36 + imposition * 0.06,
                },
                type: "buff",
                hasStacks: false,
                hasInput: false,
            },
        }
    }
}

export default Sleep;