class Pela {
    constructor() {
        this.stats = [
            {level: 1,  atk: 74.4,   def: 63,     hp: 134, spd: 105, taunt: 100, energy: 110},
            {level: 20, atk: 145.08, def: 122.85, hp: 262, spd: 105, taunt: 100, energy: 110, iceDmg: 0.032},
            {level: 20, atk: 174.84, def: 148.05, hp: 316, spd: 105, taunt: 100, energy: 110, iceDmg: 0.032},
            {level: 40, atk: 279,    def: 236.25, hp: 504, spd: 105, taunt: 100, energy: 110, iceDmg: 0.032},
            {level: 40, atk: 308.76, def: 261.45, hp: 558, spd: 105, taunt: 100, energy: 110, atkPercent: 0.04, iceDmg: 0.064, effectHitRate: 0.04},
            {level: 50, atk: 345.96, def: 292.95, hp: 625, spd: 105, taunt: 100, energy: 110, atkPercent: 0.04, iceDmg: 0.064, effectHitRate: 0.04},
            {level: 50, atk: 375.72, def: 318.15, hp: 679, spd: 105, taunt: 100, energy: 110, atkPercent: 0.04, iceDmg: 0.112, effectHitRate: 0.04},
            {level: 60, atk: 412.92, def: 349.65, hp: 746, spd: 105, taunt: 100, energy: 110, atkPercent: 0.04, iceDmg: 0.112, effectHitRate: 0.04},
            {level: 60, atk: 442.68, def: 374.85, hp: 800, spd: 105, taunt: 100, energy: 110, atkPercent: 0.1, iceDmg: 0.112, effectHitRate: 0.04},
            {level: 70, atk: 479.88, def: 406.35, hp: 867, spd: 105, taunt: 100, energy: 110, atkPercent: 0.1, iceDmg: 0.16,  effectHitRate: 0.04},
            {level: 70, atk: 509.64, def: 431.55, hp: 921, spd: 105, taunt: 100, energy: 110, atkPercent: 0.1, iceDmg: 0.16,  effectHitRate: 0.1},
            {level: 80, atk: 546.84, def: 463.05, hp: 988, spd: 105, taunt: 100, energy: 110, atkPercent: 0.18,iceDmg: 0.224, effectHitRate: 0.1},
        ];

        this.name = "Pela";
        this.path = "Nihility";
        this.rarity = 'fourStar';
        this.element = "Ice";
        this.id = "pela";
        this.E6Buff = 0.4;
        this.E6ON = false;
    }

    setE6(bool) {
        this.E6ON = bool;
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Frost Shot",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className="Ice"> Ice DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return 1.05 + (level * 0.105);
        }else if(level < 10){
            return 1.575 + ((level - 5) * 0.1313);
        }else {
            return 2.1 + ((level - 9) * 0.105);
        }
    }

    skillDescription(level) {
        return {
            name: "Frostbite",
            description: (
                <div className="buff-description">
                    <div>Removes </div>
                    <div className="buff-text">1 buffs(s)</div>
                    <div> and deals </div>
                    <div className="Ice"> Ice DMG </div>
                    <div > equal to </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level < 6) {
            return {
                multiplier: 0.6 + (level * 0.04),
                defIgnore: 0.3 + (level * 0.01),
            }
        }else if(level < 10){
            return {
                multiplier: 0.8 + ((level - 5) * 0.05),
                defIgnore: 0.35 + ((level - 5) * 0.0125),
            }    
        }else {
            return {
                multiplier: 1 + ((level - 9) * 0.04),
                defIgnore: 0.4 + ((level - 9) * 0.01),
            }
        }
    }

    ultimateDescription(level) {
        return {
            name: "Zone Suppression",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className="Ice">Ice DMG</div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).multiplier * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to all enemies, with a  </div>
                    <div className="buff-text"> 100% base chance </div>
                    <div>  to inflict Exposed on all enemies. When Exposed, enemies' </div>
                    <div className="buff-text"> DEF </div>
                    <div>  is reduced by </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).defIgnore * 100).toFixed(1)}% </div>
                    <div> for 2 turn(s). </div>
                </div>
            ),
        }
    }

    simpleUlt(level) {
        return {
            name: "Zone Suppression",
            description: (
                <div className="buff-description">
                    <div className="buff-text"> DEF </div>
                    <div>  is reduced by </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).defIgnore * 100).toFixed(1)}% </div>
                    <div> for 2 turn(s). </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 5 + (level * 0.5);
        }else if(level < 10){
            return 7.5 + ((level - 5) * 0.6);
        }else {
            return 10 + ((level - 9) * 0.5);
        }
    }

    talentDescription(level) { 
        return {
            name: "Data Collecting",
            description: (
                <div className="buff-description">
                    <div>If the enemy is </div>
                    <div className="buff-text"> debuffed </div>
                    <div>after Pela's attack, Pela will restore </div>
                    <div className="buff-text"> {this.talent(level)} additional Energy</div>
                    <div> This effect can only be triggered 1 time per attack.</div>
                </div>
            ),
        }
    }

    simpleTech() {
        return {
            name: "Preemptive Strike",
            description: (
                <div className="buff-description">
                    <div> DEF of all enemies is lowered by 20% for 2 turn(s). </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Preemptive Strike",
            description: (
                <div className="buff-description">
                    <div> Immediately attacks the enemy. Upon entering battle, Pela deals  </div>
                    <div className="Ice"> Ice DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> 80% </div>
                    <div> of her ATK to a random enemy, with a 100% base chance of lowering the DEF of all enemies by 20% for 2 turn(s). </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Bash",
            description: (
                <div className="buff-description">
                    <div> Deals </div>
                    <div className="buff-text"> 20% </div>
                    <div> more DMG to debuffed enemies </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "The Secret Strategy",
            description: (
                <div className="buff-description">
                    <div> When Pela is on the battlefield, all allies'  </div>
                    <div className="buff-text"> Effect Hit Rate </div>
                    <div> increases by </div>
                    <div className="buff-text"> 10% </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Wipe Out",
            description: (
                <div className="buff-description">
                    <div> Using Skill to remove buff(s) increases the DMG of the next attack by </div>
                    <div className="buff-text"> 20% </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Victory Report",
            description: (
                <div className="buff-description">
                    <div> When an enemy is defeated, Pela </div>
                    <div className="buff-text"> regenerates 5 Energy. </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Adamant Charge",
            description: (
                <div className="buff-description">
                    <div> Using Skill to remove buff(s) increases SPD by 10% for 2 turn(s). </div>
                </div>
            )
        }
    }

    E3() {
        return {
            name: "Suppressive Force",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }

    E4() {
        return {
            name: "Full Analysis",
            description:(
                <div className="buff-description">
                    <div>When Skill is used, there is a 100% base chance to reduce the target enemy's Ice RES by 12% for 2 turn(s). </div>
                </div>
            )
        }
    }

    E5() {
        return {
            name: "Absolute Jeopardy",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: " Feeble Pursuit",
            description: (
                <div className="buff-description">
                    <div>When Pela attacks a debuffed enemy, she deals Additional Ice DMG equal to 40% of Pela's ATK to the enemy. </div>
                </div>
            ),
        }
    }
}


export default Pela;