class Jingliu {
    constructor() {
        this.stats = [
            {level: 1,  atk: 92.4,   def: 66,    hp: 195, spd: 96, taunt: 125, energy: 140},
            {level: 20, atk: 180.18, def: 128.7, hp: 381, spd: 96, taunt: 125, energy: 140, critDmg: 0.053},
            {level: 20, atk: 217.14, def: 155.1, hp: 459, spd: 96, taunt: 125, energy: 140, critDmg: 0.053},
            {level: 40, atk: 346.5,  def: 247.5, hp: 733, spd: 96, spdFlat: 2, taunt: 125, energy: 140, critDmg: 0.106},
            {level: 40, atk: 383.46, def: 273.9, hp: 811, spd: 96, spdFlat: 2, taunt: 125, energy: 140, critDmg: 0.106, hpPercent: 0.04},
            {level: 50, atk: 429.66, def: 306.9, hp: 908, spd: 96, spdFlat: 2, taunt: 125, energy: 140, critDmg: 0.186, hpPercent: 0.04},
            {level: 50, atk: 466.62, def: 333.3, hp: 987, spd: 96, spdFlat: 2, taunt: 125, energy: 140, critDmg: 0.186, hpPercent: 0.04},
            {level: 60, atk: 512.82, def: 366.3, hp: 1084,spd: 96, spdFlat: 2, taunt: 125, energy: 140, critDmg: 0.186, hpPercent: 0.04},
            {level: 60, atk: 549.78, def: 392.7, hp: 1162,spd: 96, spdFlat: 5, taunt: 125, energy: 140, critDmg: 0.266, hpPercent: 0.04},
            {level: 70, atk: 595.98, def: 425.7, hp: 1260,spd: 96, spdFlat: 5, taunt: 125, energy: 140, critDmg: 0.266, hpPercent: 0.04},
            {level: 70, atk: 632.94, def: 452.1, hp: 1338,spd: 96, spdFlat: 5, taunt: 125, energy: 140, critDmg: 0.266, hpPercent: 0.1},
            {level: 80, atk: 679.14, def: 485.1, hp: 1436,spd: 96, spdFlat: 9, taunt: 125, energy: 140, critDmg: 0.373, hpPercent: 0.1},
        ];

        this.name = "Jingliu";
        this.path = "Destruction";
        this.rarity = 'fiveStar';
        this.element = "Ice";
        this.id = "jingliu";
        this.E2On = false;
        this.isBeta = true;
        this.totalHpConsumed = 0;
    }

    setE2On(value) {
        this.E2On = value;
    }

    setHpConsumed(hpLost) {
        this.totalHpConsumed = hpLost;
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Lucent Moonglow",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return 1 + (level * 0.1);
        }else if(level < 10){
            return 1.5 + ((level - 5) * 0.125);
        }else {
            return 2 + ((level - 9) * 0.1);
        }
    }

    skillDescription(level) {
        return {
            name: "Transcendent Flash",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to a single enemy and obtains 1 stack(s) of Syzygy. </div>
                </div>
            ),
        }
    }

    skillAttack1(level) {
        if(level < 6) {
            return {
                single: 1.25 + (level * 0.125),
                spread: 0.62 + (level * 0.0625),
            }
        }else if(level < 10){
            return {
                single: 1.875 + ((level - 5) * 0.15625),
                spread: 0.925 + ((level - 5) * 0.08125),
            }
        }else {
            return {
                single: 2.5 + ((level - 9) * 0.125),
                spread: 1.25 + ((level - 9) * 0.0625),
            }
        }
    }

    skill1Description(level) {
        return {
            name: "Moon On Glacial River",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack1(level).single * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to a single enemy and deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack1(level).spread * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to adjacent enemies. </div>
                    <br/>
                    <div>Consumes 1 stack(s) of Syzygy. Using this ability does not consume Skill Points.</div>
                </div>
            ),
        }
    }
        
    ultimateAttack(level) {
        if(level < 6) {
            return {
                single: 1.8 + (level * 0.12),
                spread: 0.9 + (level * 0.06),
            }
        }else if(level < 10){
            return {
                single: 2.4 + ((level - 5) * 0.15),
                spread: 1.2 + ((level - 5) * 0.075),
            }
        }else {
            return {
                single: 3 + ((level - 9) * 0.12),
                spread: 1.5 + ((level - 9) * 0.06),
            }
        }
    }

    ultimateDescription(level) {
        return {
            name: "Florephemeral Dreamflux",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.ultimateAttack(level).single * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to a single enemy and deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.ultimateAttack(level).spread * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to adjacent enemies. </div>
                    <br/>
                    <div>Obtains 1 stack(s) of Syzygy after the attack concludes.</div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return {
                critRate: 0.4 + (level * 0.01),
                cap: 0.9 + (level * 0.09),
            }
        }else if(level < 10){
            return {
                critRate: 0.45 + ((level - 5) * 0.0125),
                cap: 1.35 + ((level - 5) * 0.1125),
            }
        }else {
            return {
                critRate: 0.5 + ((level - 5) * 0.01),
                cap: 1.8 + ((level - 9) * 0.09),
            }
        }
    }

    talentDescription(level) { 
        return {
            name: "Crescent Transmigration",
            description: (
                <div className="buff-description">
                    <div> When Jingliu has 2 stack(s) of Syzygy, she enters the Spectral Transmigration state and gets her Action Advanced by 100%, and increases her </div>
                    <div className="buff-text"> CRIT Rate </div>
                    <div> by </div>
                    <div className="buff-text"> {(this.talent(level).critRate * 100).toFixed(1)}%. </div>
                    <br/>
                    <div> Jingliu then gains and can only use the following Skill, Moon On Glacial River. </div>
                    <br/>
                    <div> When Jingliu uses an attack in the Spectral Transmigration state, she consumes HP from all other allies equal to 4% of their respective Max HP (this cannot reduce allies' HP to lower than 1). </div>
                    <br/>
                    <div> Jingliu's ATK increases based on 540% of the total HP consumed from all allies in this instance, capped at </div>
                    <div className="buff-text"> {(this.talent(level).cap * 100).toFixed(1)}% </div>
                    <div> of her </div>
                    <div className="buff-text"> Base ATK </div>
                    <div> until the current attack ends. </div>
                    <br/>
                    <div> Syzygy can stack up to 3 times. </div>
                    <br/>
                    <div> When Syzygy stacks become 0, Jingliu will exit the Spectral Transmigration state. </div>
                </div>
            ),
        }
    }
    
    techniqueDescription() {
        return {
            name: "Shine of Truth",
            description: (
                <div className="buff-description">
                    <div> After using this Technique, creates a dimension around Jingliu that lasts for 20 seconds, and all enemies in this dimension will become Frozen. After entering combat with enemies in the dimension, immediately regenerates 15 Energy and obtains 1 stack(s) of Syzygy, with a 100% base chance of Freezing enemy targets for 1 turn(s). While Frozen, enemy targets cannot take action, and receive Additional DMG equal to 80% of Jingliu's ATK at the start of every turn. Only 1 dimension created by allies can exist at the same time. </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Deathrealm",
            description: (
                <div className="buff-description">
                    <div> While in the Spectral Transmigration state, increases Effect RES by 35%. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Sword Champion",
            description: (
                <div className="buff-description">
                    <div> After using Transcendent Flash, the next action will be Advanced Forward by 10%. </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Frost Wraith",
            description: (
                <div className="buff-description">
                    <div> While in the Spectral Transmigration state, increases Ultimate DMG by 20%. </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Moon Crashes Tianguan Gate",
            description: (
                <div className="buff-description">
                    <div> When using her Ultimate or Enhanced Skill, Jingliu's CRIT DMG increases by 24% for 1 turn(s). If only one enemy target is attacked, the DMG originally intended for adjacent targets will also be calculated against the current target, dealing Ice DMG equal to 80% of the DMG the adjacent targets would have received. </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Crescent Shadows Qixing Dipper",
            description: (
                <div className="buff-description">
                    <div> After using Ultimate, increases the DMG of next Enhanced Skill by 80%. </div>
                </div>
            )
        }
    }

    E3() {
        return {
            name: " Halfmoon Gapes Mercurial Haze",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }
    
    E4() {
        return {
            name: " Lunarlance Shines Skyward Dome",
            description:(
                <div className="buff-description">
                    <div>While in the Spectral Transmigration state, the ATK obtained from consuming allies' HP is additionally increased by 90% of the total HP that has been consumed from the entire team. The cap for ATK increased in this way increases by 30%.</div>
                </div>
            )
        }
    }

    E5() {
        return {
            name: "Night Shades Astral Radiance",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: " Eclipse Hollows Corporeal Husk",
            description: (
                <div className="buff-description">
                    <div>When Jingliu enters the Spectral Transmigration state, obtain 1 stacks of Syzygy. While she is in the Spectral Transmigration state, her CRIT DMG increases by 50%. </div>
                </div>
            ),
        }
    }
}

export default Jingliu;