import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';
import { FuXuan } from "../../../../../../../database/characters";

const FuxuanBuffs = () => {

    const { eidolon, character, characterBuffStates, skillLevel, setCharacterBuffStates, updateStatesFromJson, hp, hpPercent, hpFlat } = useContext(globalData);
    const fuxuan = new FuXuan();

    const [prevSkillLevel, setPrevSkillLevel] = useState(skillLevel);
    const [hpLost, setHpLost] = useState(0.0);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    useEffect(() => {
        
        if(eidolon < 1 && characterBuffStates[2]) {
            updateStatesFromJson(E1Buff, true);
            characterBuffStates[2] = false;
        }

    }, [eidolon]);

    useEffect(() => {

        if(characterBuffStates[3]) {
            const tempBuff = {
                effect: {
                    hpPercent: (fuxuan.skillAttack(prevSkillLevel).hpPercent),
                    critRate: fuxuan.skillAttack(prevSkillLevel).critRate,
                }
            }
            updateStatesFromJson(tempBuff, true);
            updateStatesFromJson(skillBuff, false);
        }

        setPrevSkillLevel(skillLevel);

    }, [skillLevel]);

    useEffect(() => {

        if(characterBuffStates[0]) {
            character.setHpLost(hpLost);
            const updatedStates = [...characterBuffStates];
            setCharacterBuffStates(updatedStates);
        }

    }, [hpLost]);

    
    let skillBuff = {
        effect: {
            hpPercent: fuxuan.skillAttack(skillLevel).hpPercent,
            critRate: fuxuan.skillAttack(skillLevel).critRate,
        }
    }

    const E1Buff = {
        effect: {
            critDmg: 0.3,
        },
    }

    const handleLossChange = (event) => {
        const re = /^[0-9]*\.?[0-9]+$/;
        if (event.target.value === '' || !re.test(event.target.value)) {
            return;
        }

        if(characterBuffStates[0]) {
            if(event.target.value > ((hp * hpPercent) + hpFlat) * 1.2) {
                character.setHpLost(((hp * hpPercent) + hpFlat) * 1.2);
            } else {
                character.setHpLost(event.target.value);
            }
        }

        if(event.target.value > ((hp * hpPercent) + hpFlat) * 1.2) {
            setHpLost(((hp * hpPercent) + hpFlat) * 1.2);
        } else {
            setHpLost((event.target.value));
        }

    }

    const hpLostHandle = () => {
        const updatedStates = [...characterBuffStates];
        updatedStates[0] = !updatedStates[0];
        setCharacterBuffStates(updatedStates);

        if(updatedStates[0]) {
            character.setHpLost(hpLost);
        }else {
            character.setHpLost(0);
        }
    }

    return (
        <>

            <>
                <FormControlLabel
                className='header'
                control={<Checkbox checked={characterBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, skillBuff)} />}
                label={fuxuan.skillDescription(skillLevel).name}
                />
                <div>{fuxuan.skillSimple(skillLevel).description}</div>
            </>

            {eidolon >= 1 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, E1Buff)} />}
                    label={"(E1) " + fuxuan.E1().name}
                    />
                    <div>{fuxuan.E1().description}</div>
                </>
            }

            {eidolon >= 6 &&

                    <>
                        <FormControlLabel
                        className='header'
                        control={<Checkbox checked={characterBuffStates[0] || false} onChange={hpLostHandle} />}
                        label={"(E6) " + fuxuan.E6().name}
                        />
                        <div>{fuxuan.E6().description}</div>
                        <div style={{display: "flex", width: "60%"}}>
                            <div className="centered">Total HP Lost</div>
                            <input type="text" className="critDmg buff-input" style={{width: "60px"}} maxLength={5} value={parseInt(hpLost)} onChange={handleLossChange}/>
                        </div>
                    </>
            }

        </>
    )
}

export const FuxuanGlobalBuffs = (props) => {

    const fuxuan = new FuXuan();
    const { updateStatesFromJson } = useContext(globalData);

    const fuxuanBuffStates = props.buffStates;
    const setFuxuanBuffStates = props.setBuffStates;
    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    const [skillLevel, setSkillLevel] = useState(0);
    const [maxHp, setMaxHp] = useState(201);
    const [prevMaxHp, setPrevMaxHp] = useState(maxHp);

    useEffect(() => {

        if(fuxuanBuffStates[0]) {
            updateStatesFromJson(E1Buff, true);
            fuxuanBuffStates[0] = false;
        }
        
        if(fuxuanBuffStates[3]) {
            updateStatesFromJson(skillBuff, true);
            fuxuanBuffStates[3] = false;
        }
        
        setDone(!done);
    }, [characterSwitched]);

    const [prevSkillLevel, setPrevSkillLevel] = useState(skillLevel);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...fuxuanBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setFuxuanBuffStates(updatedStates);
    }

    useEffect(() => {

        if(fuxuanBuffStates[3]) {
            let tempBuff = {
                effect: {
                    hpFlat: (maxHp * fuxuan.skillAttack(prevSkillLevel).hpPercent),
                    critRate: fuxuan.skillAttack(prevSkillLevel).critRate,
                }
            }

            updateStatesFromJson(tempBuff, true);
            updateStatesFromJson(skillBuff, false);
        }

        setPrevSkillLevel(skillLevel);

    }, [skillLevel]);

    useEffect(() => {

        if(fuxuanBuffStates[3]) {
            let tempBuff = {
                effect: {
                    hpFlat: (prevMaxHp * fuxuan.skillAttack(skillLevel).hpPercent),
                    critRate: fuxuan.skillAttack(skillLevel).critRate,
                }
            }

            updateStatesFromJson(tempBuff, true);
            updateStatesFromJson(skillBuff, false);
        }

        setPrevMaxHp(maxHp);

    }, [maxHp]);

    
    let skillBuff = {
        effect: {
            hpFlat: (maxHp * fuxuan.skillAttack(skillLevel).hpPercent),
            critRate: fuxuan.skillAttack(skillLevel).critRate,
        }
    }

    const E1Buff = {
        effect: {
            critDmg: 0.3,
        },
    }

    const updateSkillLevel = (e) => {
        setSkillLevel(e.target.value);
    }

    const handleMaxHp = (event) => {
        const re = /^[0-9\b.]+$/;
        if ((event.target.value !== '' && !re.test(event.target.value))|| event.target.value === '.') {
            return;
        }

        setMaxHp((event.target.value));
    }

    return (
        <>
            <div style={{display: "flex", width: "80%"}}>
                <div className="centered">Max HP</div>
                <input type="text" className="critDmg buff-input" maxLength={6} value={maxHp} onChange={handleMaxHp}/>
            </div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={fuxuanBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, skillBuff)} />}
            label={fuxuan.skillDescription(skillLevel).name}
            />
            <div>{fuxuan.skillSimple(skillLevel).description}</div>
            <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                <div style={{display: "flex"}}>
                    <div>Skill Lv.</div>
                    <select className="levels buff-input" style={{width: "45%"}} value={skillLevel} onChange={updateSkillLevel}>
                        <option value={0}>1</option>
                        <option value={1}>2</option>
                        <option value={2}>3</option>
                        <option value={3}>4</option>
                        <option value={4}>5</option>
                        <option value={5}>6</option>
                        <option value={6}>7</option>
                        <option value={7}>8</option>
                        <option value={8}>9</option>
                        <option value={9}>10</option>
                        <option value={10}>11</option>
                        <option value={11}>12</option>
                    </select>
                </div>
            </div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={fuxuanBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, E1Buff)} />}
            label={"(E1) " + fuxuan.E1().name}
            />
            <div>{fuxuan.E1().description}</div>

        </>
    )
}

export default FuxuanBuffs;