import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';
import { Sushang } from "../../../../../../../database/characters";

const SushangBuffs = () => {

    const {level, eidolon, character, ultimateLevel, talentLevel, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    const sushang = new Sushang();

    const [prevTalentLevel, setPrevTalentLevel] = useState(talentLevel);
    const [prevUltimateLevel, setPrevUltimateLevel] = useState(ultimateLevel);
    const [A4Stacks, setA4Stacks] = useState(1);
    const [E6Stacks, setE6Stacks] = useState(1);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    useEffect(() => {
        if(characterBuffStates[1]) {
            const tempUltBuff = {
                effect: {
                    spdPercent: sushang.talent(prevTalentLevel),
                },
            }
            updateStatesFromJson(tempUltBuff, true);
            
            const newTempUltBuff = {
                effect: {
                    spdPercent: sushang.talent(talentLevel),
                },
            }
            updateStatesFromJson(newTempUltBuff, false);
        }
        
        setPrevTalentLevel(talentLevel);
    }, [talentLevel]);

    useEffect(() => {
        if(characterBuffStates[0]) {
            const tempUltBuff = {
                effect: {
                    atkPercent: sushang.bonusATK(prevUltimateLevel),
                },
            }
            updateStatesFromJson(tempUltBuff, true);
            
            const newTempUltBuff = {
                effect: {
                    atkPercent: sushang.bonusATK(ultimateLevel),
                },
            }
            updateStatesFromJson(newTempUltBuff, false);
        }
        
        setPrevUltimateLevel(ultimateLevel);
    }, [ultimateLevel]);

    useEffect(() => {

        if(eidolon < 4 && characterBuffStates[4]) {
            updateStatesFromJson(E4Buff, true);
            characterBuffStates[4] = false;
        }

        if(eidolon < 6 && characterBuffStates[1]) {
            updateStatesFromJson(talentBuff, true);

            const tempTalentBuff = {
                effect: {
                    spdPercent: sushang.talent(talentLevel) * 1,
                },
            }

            updateStatesFromJson(tempTalentBuff);
            setE6Stacks(1);
        }

    }, [eidolon]);

    useEffect(() => {
        
        if(level < 6 && characterBuffStates[2]) {
            character.setSwordStanceStacks(0);
            characterBuffStates[2] = false;
        }

    }, [level]);

    const ultimateBuff = {
        effect: {
            atkPercent: sushang.bonusATK(ultimateLevel),
        },
    }

    const talentBuff = {
        effect: {
            spdPercent: sushang.talent(talentLevel) * E6Stacks,
        },
    }

    const E4Buff = {
        effect: {
            BE: 0.4,
        },
    }

    const applyStanceBuff = (index) => {
        const updatedStates = [...characterBuffStates];
        updatedStates[index] = !updatedStates[index];
        if(updatedStates[index]) {
            character.setSwordStanceStacks(A4Stacks);
        }else {
            character.setSwordStanceStacks(0);
        }
        setCharacterBuffStates(updatedStates);
    }

    const updateSwordStanceStacks = (e) => {
        if(characterBuffStates[2]) {
            character.setSwordStanceStacks(e.target.value);
        }
        const updatedStates = [...characterBuffStates];
        setCharacterBuffStates(updatedStates);
        setA4Stacks(e.target.value);
    }

    const updateE6Stacks = (e) => {

        if(characterBuffStates[1]) {
            updateStatesFromJson(talentBuff, true);
        }

        if(characterBuffStates[1]) {
            const tempTalentBuff = {
                effect: {
                    spdPercent: sushang.talent(talentLevel) * e.target.value,
                },
            }
            updateStatesFromJson(tempTalentBuff, false);
        }
        setE6Stacks(e.target.value);
    }

    let talentContent = "";

    if(eidolon >= 6) {
        talentContent = (
            <>
            <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                <div style={{display: "flex"}}>
                    <div>Stacks </div>
                    <select className="levels buff-input" style={{width: "45%"}} value={E6Stacks} onChange={updateE6Stacks}>
                        <option value={1}>1</option>
                        <option value={2}>2</option>
                    </select>
                </div>
            </div>
            </>
        );
    }

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, ultimateBuff)} />}
            label={sushang.ultimateDescription(ultimateLevel).name}
            />
            <div>{sushang.bonusMultDesc(ultimateLevel)}</div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, talentBuff)} />}
            label={sushang.talentDescription(talentLevel).name}
            />
            <div>{sushang.talentDescription(talentLevel).description}</div>
            {talentContent}

            {level >= 6 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[2] || false} onChange={() => applyStanceBuff(2)} />}
                    label={"(A4) " + sushang.A4().name}
                    />
                    <div>{sushang.A4().description}</div>
                    <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                    <div style={{display: "flex"}}>
                        <div>Stacks </div>
                        <select className="levels buff-input" style={{width: "45%"}} value={A4Stacks} onChange={updateSwordStanceStacks}>
                            <option value={1}>1</option>
                            <option value={2}>2</option>
                            <option value={3}>3</option>
                            <option value={4}>4</option>
                            <option value={5}>5</option>
                            <option value={6}>6</option>
                            <option value={7}>7</option>
                            <option value={8}>8</option>
                            <option value={9}>9</option>
                            <option value={10}>10</option>
                        </select>
                    </div>
                </div>
                </>
            }

            {eidolon >= 4 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[4] || false} onChange={() => applyCharacterBuffs(4, E4Buff)} />}
                    label={"(E4) " + sushang.E4().name}
                    />
                    <div>{sushang.E4().description}</div>
                </>
            }
        </>
    )
}

export const SushangGlobalBuffs = (props) => {

    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {
        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <div>No Buffs</div>
        </>
    )
}

export default SushangBuffs;