export class MilkyWay {
    constructor() {
        this.stats = [
            [1,  26.4,    18,    53],
            [20, 101.64,  69.3, 203],
            [20, 133.32, 90.9,  267],
            [40, 254.76, 173.7, 510],
            [40, 297,    202.5, 594],
            [50, 336.6,  229.5, 673],
            [50, 378.84, 258.3, 758],
            [60, 418.44, 285.3, 837],
            [60, 460.68, 314.1, 921],
            [70, 500.28, 341.1, 1001],
            [70, 542.52, 369.9, 1085],
            [80, 582.12, 396.9, 1164],
        ];
        
        this.name = "Night on the Milky Way";
        this.path = "Erudition";
        this.id = "milkyway";
        this.rarity = "fiveStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>For every enemy on the field, increases the wearer's </div>
                <div className="buff-text"> ATK  </div> 
                <div> by </div> 
                <div className="buff-text">{((0.09 + imposition * 0.015) * 100).toFixed(1)}%.</div>
                <div> up to 5 stacks. </div>
                <div> When an enemy is inflicted with Weakness Break, the DMG dealt by the wearer increases by </div> 
                <div className="buff-text"> {((0.30 + imposition * 0.05) * 100).toFixed(1)}%.</div> 
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "silenceb",
            name: this.name,
            effect:{},
            type: "buff",
            hasStack: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition, stack) {
        return {
            0: {
                id: "silence0",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div>For every enemy on the field, increases the wearer's </div>
                        <div className="buff-text"> ATK  </div> 
                        <div> by </div> 
                        <div className="buff-text">{((0.09 + imposition * 0.015) * 100).toFixed(1)}%.</div>
                        <div> up to 5 stacks. </div>
                    </div>
                ),
                effect: {
                    atkPercent: (0.09 + imposition * 0.015) * stack,
                },
                type: "buff",
                hasStack: true,
                stacks: 5,
                hasInput: false,
            },
            1: {
                id: "silence0",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> When an enemy is inflicted with Weakness Break, the DMG dealt by the wearer increases by </div> 
                        <div className="buff-text"> {((0.30 + imposition * 0.05) * 100).toFixed(1)}%.</div> 
                    </div>
                ),
                effect: {
                    iceDmg: 0.30 + imposition * 0.05,
                    quantumDmg: 0.30 + imposition * 0.05,
                    fireDmg: 0.30 + imposition * 0.05,
                    lightningDmg: 0.30 + imposition * 0.05,
                    windDmg: 0.30 + imposition * 0.05,
                    physicalDmg: 0.30 + imposition * 0.05,
                    imaginaryDmg: 0.30 + imposition * 0.05,
                },
                type: "buff",
                hasStack: false,
                hasInput: false,
            },
        }
    }
}

export default MilkyWay;