class March {
    constructor() {
        this.stats = [
            {level: 1,  atk: 69.6,   def: 78,    hp: 144, spd: 101, taunt: 150, energy: 120},
            {level: 20, atk: 135.72, def: 152.1, hp: 281, spd: 101, taunt: 150, energy: 120, iceDmg: 0.032},
            {level: 20, atk: 163.56, def: 183.3, hp: 338, spd: 101, taunt: 150, energy: 120, iceDmg: 0.032},
            {level: 40, atk: 261,    def: 292.5, hp: 540, spd: 101, taunt: 150, energy: 120, iceDmg: 0.032, defPercent: 0.05},
            {level: 40, atk: 288.84, def: 323.7, hp: 598, spd: 101, taunt: 150, energy: 120, iceDmg: 0.064, defPercent: 0.05,  effectRes: 0.04},
            {level: 50, atk: 323.64, def: 362.7, hp: 670, spd: 101, taunt: 150, energy: 120, iceDmg: 0.064, defPercent: 0.05,  effectRes: 0.04},
            {level: 50, atk: 351.48, def: 393.9, hp: 727, spd: 101, taunt: 150, energy: 120, iceDmg: 0.112, defPercent: 0.05,  effectRes: 0.04},
            {level: 60, atk: 386.28, def: 432.9, hp: 799, spd: 101, taunt: 150, energy: 120, iceDmg: 0.112, defPercent: 0.05,  effectRes: 0.04},
            {level: 60, atk: 414.12, def: 464.1, hp: 857, spd: 101, taunt: 150, energy: 120, iceDmg: 0.16,  defPercent: 0.125,  effectRes: 0.04},
            {level: 70, atk: 448.92, def: 503.1, hp: 929, spd: 101, taunt: 150, energy: 120, iceDmg: 0.16,  defPercent: 0.125,  effectRes: 0.04},
            {level: 70, atk: 476.76, def: 534.3, hp: 986, spd: 101, taunt: 150, energy: 120, iceDmg: 0.16,  defPercent: 0.125,  effectRes: 0.1},
            {level: 80, atk: 511.56, def: 573.3, hp: 1058,spd: 101, taunt: 150, energy: 120, iceDmg: 0.224, defPercent: 0.225,  effectRes: 0.1},
        ];

        this.name = "March 7th";
        this.path = "Preservation";
        this.rarity = 'fourStar';
        this.element = "Ice";
        this.id = "march";
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "	Frigid Cold Arrow",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level == 0) {
            return {
                multiplier: 0.38,
                addition: 190,
            }
        }else if(level == 1) {
            return {
                multiplier: 0.4038,
                addition: 304,
            }
        }
        else if(level == 2) {
            return {
                multiplier: 0.4275,
                addition: 390,
            }
        }
        else if(level == 3) {
            return {
                multiplier: 0.4513,
                addition: 475,
            }
        }
        else if(level == 4) {
            return {
                multiplier: 0.475,
                addition: 532,
            }
        }
        else if(level == 5) {
            return {
                multiplier: 0.494,
                addition: 589,
            }
        }
        else if(level == 6) {
            return {
                multiplier: 0.513,
                addition: 632,
            }
        }
        else if(level == 7) {
            return {
                multiplier: 0.532,
                addition: 675,
            }
        }
        else if(level == 8) {
            return {
                multiplier: 0.551,
                addition: 717,
            }
        }
        else if(level == 9) {
            return {
                multiplier: 0.57,
                addition: 760,
            }
        }
        else if(level == 10) {
            return {
                multiplier: 0.589,
                addition: 803,
            }
        }
        else if(level == 11) {
            return {
                multiplier: 0.608,
                addition: 846,
            }
        }
    }

    skillDescription(level) {
        return {
            name: "The Power of Cuteness",
            description: (
                <div className="buff-description">
                    <div> Provides a single ally with a Shield that can absorb DMG equal to </div>
                    <div className="buff-text">{(this.skillAttack(level).multiplier * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">DEF</div>
                    <div> plus </div>
                    <div className="buff-text">{this.skillAttack(level).addition}.</div>
                    <div> for 3 turn(s). </div>
                    <div> If the ally's current HP percentage is 30% or higher, greatly increases the chance of enemies attacking that ally. </div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level < 6) {
            return {
                multiplier: 0.9 + (level * 0.06),
                additional: 0.3 + (level * 0.03),
            }
        }else if(level < 10){
            return {
                multiplier: 1.2 + ((level - 5) * 0.075),
                additional: 0.45 + ((level - 5) * 0.0375),
            }
        }else {
            return {
                multiplier: 1.5 + ((level - 9) * 0.06),
                additional: 0.6 + ((level - 5) * 0.03),
            }
        }
    }

    ultimateDescription(level) {
        return {
            name: "Glacial Cascade",
            description: (
                <div className="buff-description">
                    <div>Deals  </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.ultimateAttack(level).multiplier * 100).toFixed(1)}%</div>
                    <div>  of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to all enemies. </div>
                    <div> Hit enemies have a 50% base chance to be Frozen for 1 turn(s). While Frozen, enemies cannot take action and will receive Additional Ice DMG equal to </div>
                    <div className="buff-text">{(this.ultimateAttack(level).additional * 100).toFixed(1)}%</div>
                    <div>  of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div>  at the beginning of each turn. </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 0.5 + (level * 0.05);
        }else if(level < 10) {
            return 0.75 + ((level - 5) * 0.0625);
        }else {
            return 1 + ((level - 9) * 0.05);
        }
    }

    talentDescription(level) { 
        return {
            name: " Girl Power",
            description: (
                <div className="buff-description">
                    <div>After a Shielded ally is attacked by an enemy, March 7th immediately Counters, dealing Ice DMG equal to </div>
                    <div className="buff-text">{(this.talent(level) * 100).toFixed(1)}%</div>
                    <div>  of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> This effect can be triggered 2 time(s) each turn. </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Freezing Beauty",
            description: (
                <div className="buff-description">
                    <div> Immediately attacks the enemy. After entering battle, there is a 100% base chance to Freeze a random enemy for 1 turn(s). While Frozen, the enemy cannot take action and will take Additional Ice DMG equal to 50% of March 7th's ATK at the beginning of each turn. </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Purify",
            description: (
                <div className="buff-description">
                    <div> Skill removes 1 debuff from an ally. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Reinforce",
            description: (
                <div className="buff-description">
                    <div> The duration of the Shield generated from Skill is extended for 1 turn(s). </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Ice Spell",
            description: (
                <div className="buff-description">
                    <div> Increases Ultimate's base chance to Freeze enemies by 15%. </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Memory of You",
            description: (
                <div className="buff-description">
                    <div> Every time March 7th's Ultimate Freezes a target, she regenerates 6 Energy.</div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Memory of It",
            description: (
                <div className="buff-description">
                    <div> Upon entering battle, grants a Shield equal to 24% of March 7th's DEF plus 320 to the ally with the lowest HP percentage, lasting for 3 turn(s). </div>
                </div>
            )
        }
    }
    
    E3() {
        return {
            name: "Memory of Everything",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }
    
    E4() {
        return {
            name: " Never Forfeit Again",
            description:(
                <div className="buff-description">
                    <div>The Talent's Counter effect can be triggered 1 more time in each turn. The DMG dealt by Counter increases by an amount that is equal to 30% of March 7th's DEF.</div>
                </div>
            )
        }
    }

    E5() {
        return {
            name: "Never Forget Again",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }
    
    
    E6() {
        return {
            name: "Just Like This, Always...",
            description: (
                <div className="buff-description">
                    <div> Allies under the protection of the Shield granted by the Skill restores HP equal to 4% of their Max HP plus 106 at the beginning of each turn. </div>
                </div>
            ),
        }
    }
}

export default March;