class JingYuan {
    constructor() {
        this.stats = [
            {level: 1,  atk: 95.04,  def: 66,    hp: 158, spd: 99, taunt: 75, energy: 130},
            {level: 20, atk: 185.33, def: 128.7, hp: 309, spd: 99, taunt: 75, energy: 130, atkPercent: 0.04},
            {level: 20, atk: 223.34, def: 155.1, hp: 372, spd: 99, taunt: 75, energy: 130, atkPercent: 0.04},
            {level: 40, atk: 356.4,  def: 247.5, hp: 594, spd: 99, taunt: 75, energy: 130, atkPercent: 0.04, critRate: 0.027},
            {level: 40, atk: 394.42, def: 273.9, hp: 657, spd: 99, taunt: 75, energy: 130, atkPercent: 0.08, critRate: 0.027, defPercent: 0.05},
            {level: 50, atk: 441.94, def: 306.9, hp: 737, spd: 99, taunt: 75, energy: 130, atkPercent: 0.08, critRate: 0.027, defPercent: 0.05},
            {level: 50, atk: 479.95, def: 333.3, hp: 800, spd: 99, taunt: 75, energy: 130, atkPercent: 0.14, critRate: 0.027, defPercent: 0.05},
            {level: 60, atk: 527.47, def: 366.3, hp: 879, spd: 99, taunt: 75, energy: 130, atkPercent: 0.14, critRate: 0.027, defPercent: 0.05},
            {level: 60, atk: 565.49, def: 392.7, hp: 942, spd: 99, taunt: 75, energy: 130, atkPercent: 0.2,  critRate: 0.067, defPercent: 0.05},
            {level: 70, atk: 613.01, def: 425.7, hp: 1022,spd: 99, taunt: 75, energy: 130, atkPercent: 0.2,  critRate: 0.067, defPercent: 0.05},
            {level: 70, atk: 651.02, def: 452.1, hp: 1085,spd: 99, taunt: 75, energy: 130, atkPercent: 0.2,  critRate: 0.067, defPercent: 0.125},
            {level: 80, atk: 698.54, def: 485.1, hp: 1164,spd: 99, taunt: 75, energy: 130, atkPercent: 0.28, critRate: 0.12,  defPercent: 0.125},
        ];

        this.name = "Jing Yuan";
        this.path = "Erudition";
        this.rarity = 'fiveStar';
        this.element = "Lightning";
        this.id = "jingyuan";
        this.A2ON = false;
    }

    setA2ON(value) {
        this.A2ON = value;
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Glistening Light",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return  0.5 + (level * 0.05);
        }else if(level < 10){
            return 0.75 + ((level - 5) * 0.0625);
        }else {
            return 1 + ((level - 9) * 0.05);
        }
    }

    skillDescription(level) {
        return {
            name: "Rifting Zenith",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to all enemies and increases Lightning-Lord's Hits Per Action by 2 for the next turn. </div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level < 6) {
            return 1.2 + (level * 0.08);
        }else if(level < 10){
            return 1.6 + ((level - 5) * 0.1);
        }else {
            return 2 + ((level - 9) * 0.08);
        }
    }

    ultimateDescription(level) {
        return {
            name: "Lightbringer",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.ultimateAttack(level) * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to all enemies, and increases Lightning-Lord's Hits Per Action by 3 for the next turn. </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 0.33 + (level * 0.033);
        }else if(level < 10){
            return 0.495 + ((level - 5) * 0.0413);
        }else {
            return 0.66 + ((level - 9) * 0.033);
        }
    }

    talentDescription(level) { 
        return {
            name: "Prana Extirpated",
            description: (
                <div className="buff-description">
                    <div>Summons Lightning-Lord at the start of the battle. Lightning-Lord has 60 base SPD and 3 base Hits Per Action. </div>
                    <br/>
                    <div>When the Lightning-Lord takes action, its hits are considered as follow-up attacks, with each hit dealing </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.talent(level) * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to a random single enemy, and enemies adjacent to it also receive </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> 25% </div>
                    <br/>
                    <br/>
                    <div>The Lightning-Lord's Hits Per Action can reach a max of 10. Every time Lightning-Lord's Hits Per Action increases by 1, its SPD increases by 10.  </div>
                    <br/>
                    <div>After the Lightning-Lord's action ends, its SPD and Hits Per Action return to their base values. </div>
                    <br/>
                    <div>When Jing Yuan is knocked down, the Lightning-Lord will disappear.</div>
                    <br/>
                    <div>When Jing Yuan is affected by Crowd Control debuff, the Lightning-Lord is unable to take action.</div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Spirit Invocation",
            description: (
                <div className="buff-description">
                    <div> After the Technique is used, the Lightning-Lord's Hits Per Action in the first turn increases by 3 at the start of the next battle. </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Battalia Crush",
            description: (
                <div className="buff-description">
                    <div> If the Lightning-Lord's Hits Per Action is greater or equal to 6 in the next turn, its CRIT DMG increases by 25% for the next turn. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Savant Providence",
            description: (
                <div className="buff-description">
                    <div> At the start of the battle, immediately regenerates 15 Energy. </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "War Marshal",
            description: (
                <div className="buff-description">
                    <div> After the Skill is used, the CRIT Rate increases by 10% for 2 turn(s). </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: " Slash, Seas Split",
            description: (
                <div className="buff-description">
                    <div> When Lightning-Lord attacks, the DMG multiplier on enemies adjacent to the target enemy increases by an extra amount equal to 25% of the DMG multiplier against the target enemy. </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Swing, Skies Squashed",
            description: (
                <div className="buff-description">
                    <div> After Lightning-Lord takes action, DMG caused by Jing Yuan's Basic ATK, Skill, and Ultimate increases by 20% for 2 turn(s). </div>
                </div>
            )
        }
    }

    E3() {
        return {
            name: "Strike, Suns Subdued",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }

    
    E4() {
        return {
            name: "Spin, Stars Sieged",
            description:(
                <div className="buff-description">
                    <div>For each hit performed by the Lightning-Lord when it takes action, Jing Yuan regenerates 2 Energy.</div>
                </div>
            )
        }
    }
    
    E5() {
        return {
            name: "Stride, Spoils Seized",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }
    
    E6() {
        return {
            name: "Sweep, Souls Slain",
            description: (
                <div className="buff-description">
                    <div>Each hit performed by the Lightning-Lord when it takes action will make the target enemy Vulnerable. While Vulnerable, enemies receive 12% more DMG until the end of the Lightning-Lord's current turn, stacking up to 3 time(s).</div>
                </div>
            ),
        }
    }
}


export default JingYuan;