export class Echoes {
    constructor() {
        this.stats = [
            [1,  26.4,    18,    53],
            [20, 101.64,  69.3, 203],
            [20, 133.32, 90.9,  267],
            [40, 254.76, 173.7, 510],
            [40, 297,    202.5, 594],
            [50, 336.6,  229.5, 673],
            [50, 378.84, 258.3, 758],
            [60, 418.44, 285.3, 837],
            [60, 460.68, 314.1, 921],
            [70, 500.28, 341.1, 1001],
            [70, 542.52, 369.9, 1085],
            [80, 582.12, 396.9, 1164],
        ];
        
        this.name = "Echoes of the Coffin";
        this.path = "Abundance";
        this.id = "echoes";
        this.rarity = "fiveStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text"> ATK </div> 
                <div> by </div> 
                <div className="buff-text">{((0.24 + imposition * 0.04) * 100).toFixed(1)}%.</div>
                <div> After the wearer uses an attack, for each different enemy target the wearer hits, </div>
                <div className="buff-text"> regenerates {((3 + imposition * 1))} Energy. </div> 
                <div> Each attack can regenerate Energy up to 3 time(s) this way </div>
                <div> After the wearer uses their Ultimate, all allies gain </div>
                <div className="buff-text"> {(12 + imposition * 2)} SPD</div> 
                <div> for 1 turn. </div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "silenceb",
            name: this.name,
            effect:{
                atkPercent: 0.24 + imposition * 0.04,
            },
            type: "buff",
            hasStack: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "silence0",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> After the wearer uses their Ultimate, all allies gain </div>
                        <div className="buff-text"> {(12 + imposition * 2)} SPD</div> 
                        <div> for 1 turn. </div>
                    </div>
                ),
                effect: {
                    spdFlat: 12 + imposition * 2,
                },
                type: "buff",
                hasStack: false,
                hasInput: false,
            },
        }
    }
}

export default Echoes;