import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';
import { Gepard } from "../../../../../../../database/characters";

const GepardBuffs = () => {

    const { eidolon, level, def, defPercent, defFlat, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    const gepard = new Gepard();

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    useEffect(() => {

        if(eidolon < 4 && characterBuffStates[1]) {
            updateStatesFromJson(E4Buff, true);
            characterBuffStates[1] = false;
        }

    }, [eidolon]);

    useEffect(() => {

        if(level < 10 && characterBuffStates[0]) {
            updateStatesFromJson(A6Buff, true);
            characterBuffStates[0] = false;
        }

    }, [level]);

    const A6Buff = {
        effect: {
            atkFlat: 0.35 * (def * defPercent + defFlat),
        },
    }
    
    const E4Buff = {
        effect: {
            effectRes: 0.2,
        },
    }

    return (
        <>
        {level >= 10 &&
            <>
                <FormControlLabel
                className='header'
                control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, A6Buff)} />}
                label={"(A6) " + gepard.A6().name}
                />
                <div>{gepard.A6().description}</div>
            </>
        }

        {eidolon >= 4 &&
            <>
                <FormControlLabel
                className='header'
                control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, E4Buff)} />}
                label={"(E4) " + gepard.E4().name}
                />
                <div>{gepard.E4().description}</div>
            </>
        }

        </>
    )
}

export const GepardGlobalBuffs = (props) => {

    const gepard = new Gepard();
    const { updateStatesFromJson } = useContext(globalData);

    const gepardBuffStates = props.buffStates;
    const setGepardBuffStates = props.setBuffStates;
    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {

        if(gepardBuffStates[1]) {
            updateStatesFromJson(E4Buff, true);
            gepardBuffStates[1] = false;
        }

        setDone(!done);
    }, [characterSwitched]);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...gepardBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setGepardBuffStates(updatedStates);
    }

    const E4Buff = {
        effect: {
            effectRes: 0.2,
        },
    }

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={gepardBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, E4Buff)} />}
            label={"(E4) " + gepard.E4().name}
            />
            <div>{gepard.E4().description}</div>
        </>
    )
}

export default GepardBuffs;