import React, { useContext } from 'react';
import { globalData } from '../../../globalData';

function HookTable() {

    const levelMult = {
        1: 54.0000,
        20: 139.7703,
        40: 363.6658,
        50: 774.9041,
        60: 1640.3068,
        70: 2659.6406,
        80: 3767.5533,
    }

    const {
        character,
        fireDmg,
        atk,
        atkPercent,
        atkFlat,
        critRate,
        critDmg,
        energyRegen,
        level,
        enemyLevel,
        firePen,
        breakMultiplier,
        defReduction,
        enemyRes,
        basicAttackLevel,
        skillLevel,
        breakEffect,
        enemyToughness,
        ultimateLevel,
        basicBoost,
        eidolon,
        dotBoost,
        dotVulnerability,
        skillBoost,
        ultimateBoost,
        effectHitRate,
        fireVulnerability,
        talentLevel,
    } = useContext(globalData);

    let hookAtk = (atk * atkPercent) + atkFlat;
    let damageBoost = (1 + fireDmg);
    let critDmgBoost = (1 + critDmg);

    let basicDmgBoost = (damageBoost + basicBoost);
    let basicBaseDmg = hookAtk * character.basicAttack(basicAttackLevel);

    let skillDmgBoost = (damageBoost + skillBoost);
    let skillBaseDmg = hookAtk * character.skillAttack(skillLevel).single;

    let skillDot = hookAtk * character.skillAttack(skillLevel).dot;

    let enhancedSkillBaseDmg = hookAtk * character.skillAttack1(skillLevel).single;

    let enhacedSpreadSkillBaseDmg = hookAtk * character.skillAttack1(skillLevel).splash;

    let ultimateDmgBoost = (damageBoost + ultimateBoost);

    let ultimateBaseDmg = hookAtk * character.ultimateAttack(ultimateLevel);

    let talentAdditionalDmg = hookAtk * character.talent(talentLevel);

    let techDmg = hookAtk * 0.5;

    let resMult = (1 - (enemyRes - firePen));
    let vulnerabilityMultiplier = (1 + fireVulnerability);
    let defReductionStat = (1 - defReduction);

    if(defReductionStat < 0) {
        defReductionStat = 0;
    }
    
    let defMult = (character.stats[level]["level"] + 20)/((enemyLevel + 20) * defReductionStat + character.stats[level]["level"] + 20);
    let breakMult = breakMultiplier;

    let critRateStat = critRate;

    if(critRateStat > 1) {
        critRateStat = 1;
    }

    let maxToughnessMult = 0.5 + (enemyToughness/120);
    let breakBaseDmg = 2 * levelMult[character.stats[level]["level"]] * maxToughnessMult;
    let breakDmg = breakBaseDmg * (1 + breakEffect) * defMult * resMult * vulnerabilityMultiplier * breakMult; 
    let burnBaseDmg = 1 * levelMult[character.stats[level]["level"]];
    let burn = burnBaseDmg * (1 + breakEffect) * resMult * defMult * (vulnerabilityMultiplier + dotVulnerability) * breakMult * (damageBoost + dotBoost);

    return (
        <div className="damage-details">
            <div className='header'>Damage Details</div>
            <div className="divider"></div>
            <div className="damage-title header">Basic Attack ({basicAttackLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Basic</td>
                        <td className='tb-text'>{((basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{((basicBaseDmg * basicDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
            </table>
            <div className="damage-title header">Skill Attack ({skillLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>DMG</td>
                        <td className='tb-text'>{((skillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{((skillBaseDmg * skillDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((skillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>DoT</td>
                        <td className='tb-text'>{((skillDot * (damageBoost + dotBoost) * defMult * (vulnerabilityMultiplier + dotVulnerability) * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(((skillDot * (damageBoost + dotBoost) * defMult * (vulnerabilityMultiplier + dotVulnerability) * resMult * breakMult))).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>DoT Chance</td>
                        <td className='tb-text'>{(1 * (1 + effectHitRate) * (1 - 0.4) * 100).toFixed(1)}%</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(1 * (1 + effectHitRate) * (1 - 0.4) * 100).toFixed(1)}%</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{60}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{60}</td>
                    </tr>
                </tbody>
            </table>


            <div className="damage-title header">Enhanced Skill Attack ({skillLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    {eidolon >= 1 ?
                        <>
                            <tr>
                                <td>DMG</td>
                                <td className='tb-text'>{((enhancedSkillBaseDmg * (skillDmgBoost + 0.2) * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                                <td className='tb-text'>{((enhancedSkillBaseDmg * (skillDmgBoost + 0.2) * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                                <td className='tb-text'>{(((enhancedSkillBaseDmg * (skillDmgBoost + 0.2)  * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                            </tr>
                            <tr>
                                <td>Blast</td>
                                <td className='tb-text'>{((enhacedSpreadSkillBaseDmg * (skillDmgBoost + 0.2)  * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                                <td className='tb-text'>{((enhacedSpreadSkillBaseDmg * (skillDmgBoost + 0.2)  * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                                <td className='tb-text'>{(((enhacedSpreadSkillBaseDmg * (skillDmgBoost + 0.2)  * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                            </tr>
                        </>
                        :
                        <>
                            <tr>
                                <td>DMG</td>
                                <td className='tb-text'>{((enhancedSkillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                                <td className='tb-text'>{((enhancedSkillBaseDmg * skillDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                                <td className='tb-text'>{(((enhancedSkillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                            </tr>
                            <tr>
                                <td>Blast</td>
                                <td className='tb-text'>{((enhacedSpreadSkillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                                <td className='tb-text'>{((enhacedSpreadSkillBaseDmg * skillDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                                <td className='tb-text'>{(((enhacedSpreadSkillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                            </tr>
                        </>
                    }
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{60}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{60}</td>
                    </tr>
                    <tr>
                        <td>Break Blast</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
            </table>

        <div className="damage-title header">Ultimate Attack ({ultimateLevel - 1 + 2})</div>
        <table className="table table-striped attack-table">
            <thead>
                <tr>
                    <th scope="col"></th>
                    <th scope="col">Non-CRIT</th>
                    <th scope="col">CRIT</th>
                    <th scope="col">Average</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>DMG</td>
                    <td className='tb-text'>{((ultimateBaseDmg * ultimateDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                    <td className='tb-text'>{((ultimateBaseDmg * ultimateDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                    <td className='tb-text'>{(((ultimateBaseDmg * ultimateDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                </tr>
                <tr>
                    <td>Energy</td>
                    <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                    <td className='tb-text'>-</td>
                    <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                </tr>
                <tr>
                    <td>Break</td>
                    <td className='tb-text'>{90}</td>
                    <td className='tb-text'>-</td>
                    <td className='tb-text'>{90}</td>
                </tr>
            </tbody>
        </table>

        <div className="damage-title header">Talent Bonus DMG ({talentLevel - 1 + 2})</div>
        <table className="table table-striped attack-table">
            <thead>
                <tr>
                    <th scope="col"></th>
                    <th scope="col">Non-CRIT</th>
                    <th scope="col">CRIT</th>
                    <th scope="col">Average</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>DMG</td>
                    <td className='tb-text'>{((talentAdditionalDmg * damageBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                    <td className='tb-text'>{((talentAdditionalDmg * damageBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                    <td className='tb-text'>{(((talentAdditionalDmg * damageBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                </tr>
            </tbody>
        </table>

        <div className="damage-title header">Technique</div>
        <table className="table table-striped attack-table">
            <thead>
            <tr>
                <th scope="col"></th>
                <th scope="col">Non-CRIT</th>
                <th scope="col">CRIT</th>
                <th scope="col">Average</th>
            </tr>
            </thead>
            <tbody>
                <tr>
                    <td>DMG</td>
                    <td className='tb-text'>{((techDmg * damageBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                    <td className='tb-text'>{((techDmg * damageBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                    <td className='tb-text'>{(((techDmg * damageBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                </tr>
            </tbody>
        </table>

        <div className="damage-title header">Weakness Break</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Type</th>
                        <th scope="col">Value</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Break Damage</td>
                        <td className='tb-text'>{(breakDmg).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Burn DMG</td>
                        <td className='tb-text'>{(burn).toFixed(0)}</td>
                    </tr>
                </tbody>
            </table>
        </div>
    )
}


export default HookTable;