export class Genius {
    constructor(rarity, mainStat, type) {
        this.id = "genius";
        this.name = "Genius of Brilliant Stars";
        this.rarity = rarity;
        this.mainStat = mainStat;
        this.type = type;
        this.mainStatValue = 0;
        this.subStats = {
            0: {
                "subType": "SPD",
                "value": 0,
            },
            1: {
                "subType": "HP",
                "value": 0,
            },
            2: {
                "subType": "HP%",
                "value": 0,
            },
            3: {
                "subType": "ATK",
                "value": 0,
            },
        };
    }

    twoEffect() {
        return {
            0: {
                id: "genius20",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div>Increases </div>
                        <div className="Quantum"> Quantum DMG </div> 
                        <div> by </div> 
                        <div className="Quantum"> 10% </div>
                    </div>
                    ),
                effect: {
                    quantumDmg: 0.1,
                },
                type: "buff",
                hasStack: false,
                hasInput: false,
            },
        }
    }

    fourEffect() {
        return {
            0: {
                id: "genius40",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div>When the wearer deals DMG to the target enemy, ignores</div>
                        <div className="buff-text">10% DEF</div>
                    </div>
                ),
                effect: {
                    defIgnore: 0.1,
                },
                type: "debuff",
                hasStack: false,
                hasInput: false,
            },
            1: {
                id: "genius41",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div>If the target enemy has Quantum Weakness, the wearer additionally ignores</div>
                        <div className="buff-text">10% DEF</div>
                    </div>
                ),
                effect: {
                    defIgnore: 0.1,
                },
                type: "debuff",
                hasStack: false,
                hasInput: false,
            },
        }
    }

    // function to change mainStat
    changeMainStat(mainStat) {
        this.mainStat = mainStat;
    }

    setMainStatValue(value) {
        this.mainStatValue = value;
    }

}

