import React, { useContext } from 'react';
import { globalData } from '../../../globalData';

function ClaraTable() {

    const levelMult = {
        1: 54.0000,
        20: 139.7703,
        40: 363.6658,
        50: 774.9041,
        60: 1640.3068,
        70: 2659.6406,
        80: 3767.5533,
    }

    const {
        character,
        physicalDmg,
        atk,
        atkPercent,
        atkFlat,
        critRate,
        critDmg,
        energyRegen,
        level,
        enemyLevel,
        physicalPen,
        breakEffect,
        defReduction,
        enemyRes,
        basicAttackLevel,
        skillLevel,
        ultimateLevel,
        skillBoost,
        breakMultiplier,
        basicBoost,
        physicalVulnerability,
        enemyToughness,
        talentLevel,
        enemyMaxHP,
        followUpBoost,
        isBoss,
        dotVulnerability,
        dotBoost,
        followUpVulnerability,
        followUpCritDmg,
    } = useContext(globalData);

    let claraAtk = (atk * atkPercent) + atkFlat;
    let damageBoost = (1 + physicalDmg);
    let critDmgBoost = (1 + critDmg);
    let basicDmgBoost = damageBoost + basicBoost;
    let skillDmgBoost = damageBoost + skillBoost;
    let basicBaseDmg = claraAtk * character.basicAttack(basicAttackLevel);
    let skillBaseDmg = claraAtk * character.skillAttack(skillLevel);
    let resMult = (1 - (enemyRes - physicalPen));
    let defReductionStat = (1 - defReduction);
    let vulnerabilityMultiplier = (1 + physicalVulnerability);

    let counterBaseDmg = character.talent(talentLevel) * claraAtk;
    let enhancedCounter = (character.talent(talentLevel) + character.ultimateAttack(ultimateLevel).multiplier) * claraAtk;
    let counterDmgBoost = damageBoost + followUpBoost;

    if(level >= 10) {
        counterDmgBoost += 0.3;
    }

    if(defReductionStat < 0) {
        defReductionStat = 0;
    }
    
    let defMult = (character.stats[level]["level"] + 20)/((enemyLevel + 20) * defReductionStat + character.stats[level]["level"] + 20);
    let breakMult = breakMultiplier;
    
    let critRateStat = critRate;
    
    if(critRateStat > 1) {
        critRateStat = 1;
    }
    
    let maxToughnessMult = 0.5 + (enemyToughness/120);
    let breakBaseDmg = 2 * levelMult[character.stats[level]["level"]] * maxToughnessMult;
    let breakDmg = breakBaseDmg * (1 + breakEffect) * defMult * resMult * vulnerabilityMultiplier * breakMult;


    let bleedBaseDmg = 0;
    if(isBoss) {
        bleedBaseDmg = 0.07 * enemyMaxHP;
    }else {
        bleedBaseDmg = 0.16 * enemyMaxHP;
    }

    if(2 * levelMult[character.stats[level]["level"]] * maxToughnessMult < bleedBaseDmg) {
        bleedBaseDmg = 2 * levelMult[character.stats[level]["level"]] * maxToughnessMult;
    }

    let bleed = bleedBaseDmg * (1 + breakEffect) * resMult * defMult * (vulnerabilityMultiplier + dotVulnerability) * breakMult * (damageBoost + dotBoost);

    return (
        <div className="damage-details">
            <div className='header'>Damage Details</div>
            <div className="divider"></div>
            <div className="damage-title header">Basic Attack ({basicAttackLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Basic</td>
                        <td className='tb-text'>{(basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult).toFixed(0)}</td>
                        <td className='tb-text'>{(basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult * critDmgBoost).toFixed(0)}</td>
                        <td className='tb-text'>{((basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
            </table>
            <div className="damage-title header">Skill Attack ({skillLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col" className='tb-text-header'>Non-CRIT</th>
                        <th scope="col" className='tb-text-header'>CRIT</th>
                        <th scope="col" className='tb-text-header'>Average</th>

                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Skill</td>
                        <td className='tb-text'>{(skillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult).toFixed(0)}</td>
                        <td className='tb-text'>{(skillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult * critDmgBoost).toFixed(0)}</td>
                        <td className='tb-text'>{((skillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Marked</td>
                        <td className='tb-text'>{(2 * skillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult).toFixed(0)}</td>
                        <td className='tb-text'>{(2 * skillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult * critDmgBoost).toFixed(0)}</td>
                        <td className='tb-text'>{((2 * skillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
            </table>
            <div className="damage-title header">Ultimate Attack ({ultimateLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                </thead>
                <tbody>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Talent ({talentLevel - 1 + 2})</div>
        <table className="table table-striped attack-table">
            <thead>
            <tr>
                <th scope="col"></th>
                <th scope="col">Non-CRIT</th>
                <th scope="col">CRIT</th>
                <th scope="col">Average</th>
            </tr>
            </thead>
            <tbody>
                <tr>
                    <td>Counter</td>
                    <td className='tb-text'>{((counterBaseDmg * counterDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult)).toFixed(0)}</td>
                    <td className='tb-text'>{((counterBaseDmg * counterDmgBoost * defMult  * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult * (critDmgBoost + followUpCritDmg))).toFixed(0)}</td>
                    <td className='tb-text'>{(((counterBaseDmg * counterDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult)) * (1 + (critDmg + followUpCritDmg) * critRateStat)).toFixed(0)}</td>
                </tr>
                <tr>
                    <td>Enhanced</td>
                    <td className='tb-text'>{((enhancedCounter * counterDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult)).toFixed(0)}</td>
                    <td className='tb-text'>{((enhancedCounter * counterDmgBoost * defMult  * (vulnerabilityMultiplier + followUpVulnerability)* resMult * breakMult * (critDmgBoost + followUpCritDmg))).toFixed(0)}</td>
                    <td className='tb-text'>{(((enhancedCounter * counterDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult)) * (1 + (critDmg + followUpCritDmg) * critRateStat)).toFixed(0)}</td>
                </tr>
                <tr>
                    <td>Enhanced Blast</td>
                    <td className='tb-text'>{(0.5 * (enhancedCounter * counterDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult)).toFixed(0)}</td>
                    <td className='tb-text'>{(0.5 * (enhancedCounter * counterDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult * (critDmgBoost + followUpCritDmg))).toFixed(0)}</td>
                    <td className='tb-text'>{(0.5 * ((enhancedCounter * counterDmgBoost * defMult* (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult)) * (1 + (critDmg + followUpCritDmg) * critRateStat)).toFixed(0)}</td>
                </tr>
                <tr>
                    <td>Energy</td>
                    <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                    <td className='tb-text'>-</td>
                    <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                </tr>
                <tr>
                    <td>Break</td>
                    <td className='tb-text'>{30}</td>
                    <td className='tb-text'>-</td>
                    <td className='tb-text'>{30}</td>
                </tr>
            </tbody>
        </table>

            <div className="damage-title header">Weakness Break</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Type</th>
                        <th scope="col">Value</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Break Damage</td>
                        <td className='tb-text'>{(breakDmg).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Bleed Damage</td>
                        <td className='tb-text'>{(bleed).toFixed(0)}</td>
                    </tr>
                </tbody>
            </table>
    </div>
    )
}


export default ClaraTable;