import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';
import { Herta } from "../../../../../../../database/characters";

const HertaBuffs = () => {

    const {level, eidolon, character, ultimateLevel, skillLevel, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    const herta = new Herta();

    const [prevUltimateLevel, setPrevUltimateLevel] = useState(ultimateLevel);
    const [E1ON, setE1ON] = useState(false);
    const [stack, setStack] = useState(0);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];

        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    useEffect(() => {
        if(characterBuffStates[1]) {
            const tempUltBuff = {
                effect: {
                    critDmg: herta.ultimateAttack(prevUltimateLevel).critDmg,
                },
            }
            updateStatesFromJson(tempUltBuff, true);
            
            const newTempUltBuff = {
                effect: {
                    critDmg: herta.ultimateAttack(ultimateLevel).critDmg,
                },
            }
            updateStatesFromJson(newTempUltBuff, false);
        }
        
        setPrevUltimateLevel(ultimateLevel);
    }, [ultimateLevel]);

    useEffect(() => {

        if(level < 11 && characterBuffStates[2]) {
            updateStatesFromJson(A6Buff, true);
            characterBuffStates[2] = false;
        }

    }, [level]);

    useEffect(() => {

        if(eidolon < 1 && E1ON) {
            character.setE1ON(false);
            setE1ON(false);
            const updatedStates = [...characterBuffStates];
            setCharacterBuffStates(updatedStates);
        }

        if(eidolon < 2 && characterBuffStates[3]) {
            updateStatesFromJson(E2Buff, true);
            characterBuffStates[3] = false;
        }

        if(eidolon < 4 && characterBuffStates[4]) {
            updateStatesFromJson(E4Buff, true);
            characterBuffStates[4] = false;
        }

        if(eidolon < 6 && characterBuffStates[5]) {
            updateStatesFromJson(E6Buff, true);
            characterBuffStates[5] = false;
        }
        
    }, [eidolon]);

    const skillBuff = {
        effect: {
            iceDmg: 0.45,
            fireDmg: 0.45,
            lightningDmg: 0.45,
            physicalDmg: 0.45,
            imaginaryDmg: 0.45,
            windDmg: 0.45,
            quantumDmg: 0.45,
        },
    }

    const techBuff = {
        effect: {
            atkPercent: 0.4,
        },
    }

    const A6Buff = {
        effect: {
            iceDmg: 0.2,
            fireDmg: 0.2,
            lightningDmg: 0.2,
            physicalDmg: 0.2,
            imaginaryDmg: 0.2,
            windDmg: 0.2,
            quantumDmg: 0.2,
        },
    }

    const E2Buff = {
        effect: {
            critRate: 0.03 * stack,
        },
    }

    const E4Buff = {
        effect: {
            iceDmg: 0.1,
            fireDmg: 0.1,
            lightningDmg: 0.1,
            physicalDmg: 0.1,
            imaginaryDmg: 0.1,
            windDmg: 0.1,
            quantumDmg: 0.1,
        },
    }

    const E6Buff = {
        effect: {
            atkPercent: 0.25,
        },
    }

    const turnE1On = () => {
        setE1ON(!E1ON);
        character.setE1ON(!E1ON);
        const updatedStates = [...characterBuffStates];
        setCharacterBuffStates(updatedStates);
    }

    const updateStacks = (event) => {

        if(characterBuffStates[3]) {
            const tempUltBuff = {
                effect: {
                    critRate: 0.03 * stack,
                },
            }
            updateStatesFromJson(tempUltBuff, true);
            
            const newTempUltBuff = {
                effect: {
                    critRate: 0.03 * event.target.value,
                },
            }
            updateStatesFromJson(newTempUltBuff, false);
        }

        setStack(event.target.value);
    }

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, skillBuff)} />}
            label={herta.skillDescription(skillLevel).name}
            />
            <div>{herta.skillSimple(skillLevel).description}</div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, techBuff)} />}
            label={herta.techniqueDescription().name}
            />
            <div>{herta.techniqueDescription().description}</div>

            {level >= 11 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, A6Buff)} />}
                    label={"(A6) " + herta.A6().name}
                    />
                    <div>{herta.A6().description}</div>
                </>
            }

            {eidolon >= 1 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={E1ON || false} onChange={turnE1On} />}
                    label={"(E1) " + herta.E1().name}
                    />
                    <div>{herta.E1().description}</div>
                </>
            }

            {eidolon >= 2 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, E2Buff)} />}
                    label={"(E2) " + herta.E2().name}
                    />
                    <div>{herta.E2().description}</div>
                    <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                        <div style={{display: "flex"}}>
                            <div>Stacks</div>
                            <select className="levels buff-input" style={{width: "45%"}} value={stack} onChange={updateStacks}>
                                <option value={1}>1</option>
                                <option value={2}>2</option>
                                <option value={3}>3</option>
                                <option value={4}>4</option>
                                <option value={5}>5</option>
                            </select>
                        </div>
                    </div>
                </>
            }

            {eidolon >= 4 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[4] || false} onChange={() => applyCharacterBuffs(4, E4Buff)} />}
                    label={"(E4) " + herta.E4().name}
                    />
                    <div>{herta.E4().description}</div>
                </>
            }

            {eidolon >= 6 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[5] || false} onChange={() => applyCharacterBuffs(5, E6Buff)} />}
                    label={"(E6) " + herta.E6().name}
                    />
                    <div>{herta.E6().description}</div>
                </>
            }
        </>
    )
}

export const HertaGlobalBuffs = (props) => {

    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {
        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <div>No Buffs</div>
        </>
    )

}

export default HertaBuffs;