export class Swordplay {
    constructor() {
        this.stats = [
            [1, 21.6, 15, 43],
            [20, 83.16, 57.75, 166],
            [20, 109.08, 75.75, 218],
            [40, 208.44, 144.75,417],
            [40, 243, 168.75,  486],
            [50, 275.4, 191.25,551],
            [50, 309.96, 215.25,620],
            [60, 342.36, 237.75,685],
            [60, 376.92, 261.75,754],
            [70, 409.32, 284.25,819],
            [70, 443.88, 308.25,888],
            [80, 476.28, 330.75,953],
        ];
        
        this.name = "Swordplay";
        this.path = "Hunt";
        this.id = "swordplay";
        this.rarity = "fourStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>For each time the wearer hits the same target, DMG dealt increases by </div>
                <div className="buff-text">{((0.08 + imposition * 0.02) * 100).toFixed(1)}%</div>
                <div>, stacking up to 5 time(s). This effect will be dispelled when the wearer changes targets.</div> 
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "arrowsb",
            name: this.name,
            effect:{},
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition, stack) {
        return {
            0: {
                id: "swordplay0",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div>For each time the wearer hits the same target, DMG dealt increases by </div>
                        <div className="buff-text">{((0.08 + imposition * 0.02) * 100).toFixed(1)}%</div>
                        <div>, stacking up to 5 time(s). This effect will be dispelled when the wearer changes targets.</div> 
                    </div>
                ),
                effect: {
                    iceDmg: (0.08 + imposition * 0.02) * stack,
                    quantumDmg: (0.08 + imposition * 0.02) * stack,
                    fireDmg: (0.08 + imposition * 0.02) * stack,
                    lightningDmg: (0.08 + imposition * 0.02) * stack,
                    windDmg: (0.08 + imposition * 0.02) * stack,
                    physicalDmg: (0.08 + imposition * 0.02) * stack,
                    imaginaryDmg: (0.08 + imposition * 0.02) * stack,
                },
                type: "buff",
                hasStack: true,
                stacks: 5,
                hasInput: false,
            },
        }
    }
}

export default Swordplay;