import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';

import { Luka } from "../../../../../../../database/characters";

export const LukaDebuffs = () => {

    const { ultimateLevel, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    
    const luka = new Luka();

    const [prevUltimateLevel, setPrevUltimateLevel] = useState(ultimateLevel);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);

    }

    useEffect(() => {
        if(characterBuffStates[4]) {
            const tempUltBuff = {
                effect: {
                    physicalVulnerability: luka.ultimateAttack(prevUltimateLevel).vulnerability,
                    iceVulnerability: luka.ultimateAttack(prevUltimateLevel).vulnerability,
                    fireVulnerability: luka.ultimateAttack(prevUltimateLevel).vulnerability,
                    lightningVulnerability: luka.ultimateAttack(prevUltimateLevel).vulnerability,
                    quantumVulnerability: luka.ultimateAttack(prevUltimateLevel).vulnerability,
                    windVulnerability: luka.ultimateAttack(prevUltimateLevel).vulnerability,
                    imaginaryVulnerability: luka.ultimateAttack(prevUltimateLevel).vulnerability,
                },
            }
            updateStatesFromJson(tempUltBuff, true);
            
            const newTempUltBuff = {
                effect: {
                    physicalVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
                    iceVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
                    fireVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
                    lightningVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
                    quantumVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
                    windVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
                    imaginaryVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
                },
            }
            updateStatesFromJson(newTempUltBuff, false);
        }
        
        setPrevUltimateLevel(ultimateLevel);
    }, [ultimateLevel]);

    const ultBuff = {
        effect: {
            physicalVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
            iceVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
            fireVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
            lightningVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
            quantumVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
            windVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
            imaginaryVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
        },
    }

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[4] || false} onChange={() => applyCharacterBuffs(4, ultBuff)} />}
            label={luka.ultimateDescription(ultimateLevel).name}
            />
            <div>{luka.simpleUlt(ultimateLevel).description}</div>

        </>
    )
}

export const LukaGlobalDebuffs = (props) => {

    const sampoBuffStates = props.buffStates;
    const setSampoBuffStates = props.setBuffStates;
    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    const { updateStatesFromJson } = useContext(globalData);
    const [ultimateLevel, setUltimateLevel] = useState(0);
    const [prevUltimateLevel, setPrevUltimateLevel] = useState(ultimateLevel);

    useEffect(() => {
        if(sampoBuffStates[1]) {
            updateStatesFromJson(ultBuff, true);
            sampoBuffStates[1] = false;
        }

        setDone(!done);
    }, [characterSwitched]);

    const luka = new Luka();

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...sampoBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setSampoBuffStates(updatedStates);
    }

    const updateUltLevel = (event) => {
        setUltimateLevel(event.target.value);
    }

    const ultBuff = {
        effect: {
            physicalVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
            iceVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
            fireVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
            lightningVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
            quantumVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
            windVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
            imaginaryVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
        },
    }

    useEffect(() => {
        if(sampoBuffStates[1]) {
            const tempUltBuff = {
                effect: {
                    physicalVulnerability: luka.ultimateAttack(prevUltimateLevel).vulnerability,
                    iceVulnerability: luka.ultimateAttack(prevUltimateLevel).vulnerability,
                    fireVulnerability: luka.ultimateAttack(prevUltimateLevel).vulnerability,
                    lightningVulnerability: luka.ultimateAttack(prevUltimateLevel).vulnerability,
                    quantumVulnerability: luka.ultimateAttack(prevUltimateLevel).vulnerability,
                    windVulnerability: luka.ultimateAttack(prevUltimateLevel).vulnerability,
                    imaginaryVulnerability: luka.ultimateAttack(prevUltimateLevel).vulnerability,
                },
            }
            updateStatesFromJson(tempUltBuff, true);
            
            const newTempUltBuff = {
                effect: {
                    physicalVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
                    iceVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
                    fireVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
                    lightningVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
                    quantumVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
                    windVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
                    imaginaryVulnerability: luka.ultimateAttack(ultimateLevel).vulnerability,
                },
            }
            updateStatesFromJson(newTempUltBuff, false);
        }
        
        setPrevUltimateLevel(ultimateLevel);
    }, [ultimateLevel]);

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={sampoBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, ultBuff)} />}
            label={luka.ultimateDescription(ultimateLevel).name}
            />
            <div  className="mb-3">{luka.simpleUlt(ultimateLevel).description}</div>
            <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                <div style={{display: "flex"}}>
                    <div>Ult Level</div>
                    <select className="levels buff-input" style={{width: "45%"}} value={ultimateLevel} onChange={updateUltLevel}>
                        <option value={0}>1</option>
                        <option value={1}>2</option>
                        <option value={2}>3</option>
                        <option value={3}>4</option>
                        <option value={4}>5</option>
                        <option value={5}>6</option>
                        <option value={6}>7</option>
                        <option value={7}>8</option>
                        <option value={8}>9</option>
                        <option value={9}>10</option>
                        <option value={10}>11</option>
                        <option value={11}>12</option>
                    </select>
                </div>
            </div>
        </>
    )
}  


export default LukaDebuffs;