import React, { useContext } from 'react';
import { globalData } from '../../../globalData';

function MarchTable() {

    const levelMult = {
        1: 54.0000,
        20: 139.7703,
        40: 363.6658,
        50: 774.9041,
        60: 1640.3068,
        70: 2659.6406,
        80: 3767.5533,
    }

    const {
        character,
        iceDmg,
        atk,
        atkPercent,
        atkFlat,
        def,
        defPercent,
        defFlat,
        eidolon,
        talentLevel,
        critRate,
        critDmg,
        energyRegen,
        level,
        enemyLevel,
        icePen,
        effectHitRate,
        breakEffect,
        defReduction,
        enemyRes,
        basicAttackLevel,
        skillLevel,
        ultimateLevel,
        breakMultiplier,
        basicBoost,
        iceVulnerability,
        enemyToughness,
        ultimateBoost,
        shieldStrength,
        followUpVulnerability,
        followUpCritDmg,
    } = useContext(globalData);

    let marchAtk = (atk * atkPercent) + atkFlat;
    let marchDef = (def * defPercent) + defFlat;

    let damageBoost = (1 + iceDmg);
    let critDmgBoost = (1 + critDmg);
    let basicDmgBoost = damageBoost + basicBoost;
    let basicBaseDmg = marchAtk * character.basicAttack(basicAttackLevel);

    let skillShield = (((marchDef * character.skillAttack(skillLevel).multiplier) + character.skillAttack(skillLevel).addition));

    let talentBaseDmg = (marchAtk * character.talent(talentLevel));
    if(eidolon >= 4) {
        talentBaseDmg = (marchAtk * character.talent(talentLevel)) + (marchDef * 0.3);
    }

    let talentDmgBoost = damageBoost;
    
    let ultDmgBoost = damageBoost + ultimateBoost;
    let ultimateBaseDmg = marchAtk * character.ultimateAttack(ultimateLevel).multiplier;

    let techDmg = marchAtk * 0.5;

    let resMult = (1 - (enemyRes - icePen));
    let defReductionStat = (1 - defReduction);
    let vulnerabilityMultiplier = (1 + iceVulnerability);

    if(defReductionStat < 0) {
        defReductionStat = 0;
    }

    let defMult = (character.stats[level]["level"] + 20)/((enemyLevel + 20) * defReductionStat + character.stats[level]["level"] + 20);
    let breakMult = breakMultiplier;
    
    let critRateStat = critRate;
    
    if(critRateStat > 1) {
        critRateStat = 1;
    }
    
    let maxToughnessMult = 0.5 + (enemyToughness/120);
    let breakBaseDmg = 1 * levelMult[character.stats[level]["level"]] * maxToughnessMult;
    let breakDmg = breakBaseDmg * (1 + breakEffect) * defMult * resMult * vulnerabilityMultiplier * breakMult; 
    let freezeBaseDmg = 1 * levelMult[character.stats[level]["level"]];
    let freeze = freezeBaseDmg * (1 + breakEffect) * resMult * defMult * vulnerabilityMultiplier * breakMult;

    return (
        <div className="damage-details">
            <div className='header'>Damage Details</div>
            <div className="divider"></div>
            <div className="damage-title header">Basic Attack ({basicAttackLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Basic</td>
                        <td className='tb-text'>{(basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult).toFixed(0)}</td>
                        <td className='tb-text'>{(basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult * critDmgBoost).toFixed(0)}</td>
                        <td className='tb-text'>{((basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
                
            </table>

            <div className="damage-title header">Skill Shield ({skillLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Source</th>
                        <th scope="col" className='tb-text-header'>Amount</th>

                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Shield</td>
                        <td className='tb-text'>{(skillShield * (1 + shieldStrength)).toFixed(0)}</td>
                    </tr>
                    { eidolon >= 2 && 
                    <tr>
                        <td>E2 Shield</td>
                        <td className='tb-text'>{(((0.24 * marchDef) + 320) * (1 + shieldStrength)).toFixed(0)}</td>
                    </tr>
                    }
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Ultimate Attack ({ultimateLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Ultimate</td>
                        <td className='tb-text'>{(ultimateBaseDmg * ultDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult).toFixed(0)}</td>
                        <td className='tb-text'>{(ultimateBaseDmg * ultDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult * (critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{((ultimateBaseDmg * ultDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult) * (1 + (critDmg) * critRateStat)).toFixed(0)}</td>
                    </tr>
                    {level >= 10 ? 
                        <tr>
                            <td>Chance</td>
                            <td className='tb-text'>{((0.65 * (1 + effectHitRate) * (1 - 0.4)) * 100).toFixed(1)}%</td>
                            <td className='tb-text'>-</td>
                            <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                        </tr>
                    :
                        <tr>
                            <td>Chance</td>
                            <td className='tb-text'>{((0.5 * (1 + effectHitRate) * (1 - 0.4)) * 100).toFixed(1)}%</td>
                            <td className='tb-text'>-</td>
                            <td className='tb-text'>{((0.5 * (1 + effectHitRate) * (1 - 0.4)) * 100).toFixed(1)}%</td>
                        </tr>
                    }
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break (AOE)</td>
                        <td className='tb-text'>{60}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{60}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Talent ({talentLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Counter</td>
                        <td className='tb-text'>{(talentBaseDmg * talentDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult).toFixed(0)}</td>
                        <td className='tb-text'>{((talentBaseDmg * talentDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability)  * resMult * breakMult) * (critDmgBoost + followUpCritDmg)).toFixed(0)}</td>
                        <td className='tb-text'>{((talentBaseDmg * talentDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability)  * resMult * breakMult) * (1 + (critDmg + followUpCritDmg) * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(10 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(10 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Technique</div>
                <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Freeze DMG</td>
                        <td className='tb-text'>{((techDmg * damageBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{((techDmg * damageBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((techDmg * damageBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Freeze Chance</td>
                        <td className='tb-text'>{(1 * (1 + effectHitRate) * (0.6) * 100).toFixed(1)}%</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(1 * (1 + effectHitRate) * (0.6) * 100).toFixed(1)}%</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Weakness Break</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Type</th>
                        <th scope="col">Value</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Break Damage</td>
                        <td className='tb-text'>{(breakDmg).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Ice Damage</td>
                        <td className='tb-text'>{(freeze).toFixed(0)}</td>
                    </tr>
                </tbody>
            </table>
    </div>
    )
}


export default MarchTable;