import { globalData } from '../../globalData';
import React, { useContext, useState, useEffect } from 'react';
import { Checkbox,  FormControlLabel } from '@mui/material';

function EnemyStats() {
    const {
        enemyRes,
        enemyLevel,
        setEnemyRes,
        setEnemyLevel,
        setBreakMultiplier,
        enemyToughness,
        setEnemyToughness,
        enemyMaxHP,
        setEnemyMaxHP,
        isBoss,
        setIsBoss,
        isBroken,
        setIsBroken,
    } = useContext(globalData);

    const updateEnemyLevel = (event) => {

        const re = /^[0-9]*\.?[0-9]+$/;
        if (event.target.value === '' || !re.test(event.target.value)) {
            setEnemyLevel(1);
            return;
        }

        setEnemyLevel(parseInt(event.target.value));
    }

    const updateEnemyRes = (event) => {

        const re = /^[0-9]*\.?[0-9]+$/;
        if (event.target.value === '' || !re.test(event.target.value)) {
            setEnemyRes(0);
            return;
        }

        setEnemyRes(parseFloat(event.target.value)/100);
    }

    const updateEnemyToughness = (event) => {

        const re = /^[0-9]*\.?[0-9]+$/;
        if (event.target.value === '' || !re.test(event.target.value)) {
            setEnemyRes(0);
            return;
        }

        setEnemyToughness(parseInt(event.target.value));
    }

    const updateEnemyMaxhp = (event) => {

        const re = /^[0-9]*\.?[0-9]+$/;
        if (event.target.value === '' || !re.test(event.target.value)) {
            setEnemyRes(0);
            return;
        }

        setEnemyMaxHP(parseInt(event.target.value));
    }

    const updateWeaknessMultiplier = (event) => {
        setIsBroken(event.target.checked);
        if(event.target.checked) {
            setBreakMultiplier(1);
        } else {
            setBreakMultiplier(0.9);
        }
    }

    const updateBossStatus = (event) => {
        setIsBoss(event.target.checked);
    }
    
    return (
        <div className="enemy-set-up">
            <div style={{display: "flex", justifyContent: "space-between"}}>
                <div>Enemy Level</div>
                <input type='text' className='textInput buff-input' value={enemyLevel} onChange={updateEnemyLevel}></input>
            </div>
            <div style={{display: "flex", justifyContent: "space-between"}} className='mt-2'>
                <div>Enemy Resistance %</div>
                <input type='text' className='textInput buff-input' value={(enemyRes * 100).toFixed(0)} onChange={updateEnemyRes}></input>
            </div>
            <div style={{display: "flex", justifyContent: "space-between"}} className='mt-2'>
                <div>Enemy Toughness</div>
                <input type='text' className='textInput buff-input' value={enemyToughness} onChange={updateEnemyToughness}></input>
            </div>

            <FormControlLabel
            className='buff'
            control={<Checkbox checked={isBroken || false} onChange={updateWeaknessMultiplier} />}
            label={"Enemy Broken ?"}
            />

            <div className='muted-text mt-3'> only affects Bleed damage </div>

            <div style={{display: "flex", justifyContent: "space-between"}} className='mt-2'>
                <div>Enemy Max HP</div>
                <input type='text' className='textInput buff-input' style={{width: "30%"}} value={enemyMaxHP} onChange={updateEnemyMaxhp}></input>
            </div>


            <FormControlLabel
            className='buff'
            control={<Checkbox checked={isBoss || false} onChange={updateBossStatus} />}
            label={"Boss Enemy ?"}
            />

            
            <div className='muted-text'> - you can get enemy data from <a href='https://honkai-star-rail.fandom.com/wiki/Enemy' target='_blank' rel="noopener noreferrer"> here </a></div>
        </div>
    )
}

export default EnemyStats;