import React, { useContext } from 'react';
import { globalData } from '../../../globalData';

function ServalTable() {

    const levelMult = {
        1: 54.0000,
        20: 139.7703,
        40: 363.6658,
        50: 774.9041,
        60: 1640.3068,
        70: 2659.6406,
        80: 3767.5533,
    }

    const {
        character,
        lightningDmg,
        atk,
        atkPercent,
        atkFlat,
        critRate,
        eidolon,
        critDmg,
        energyRegen,
        level,
        enemyLevel,
        lightningPen,
        breakMultiplier,
        defReduction,
        enemyRes,
        basicAttackLevel,
        skillLevel,
        breakEffect,
        enemyToughness,
        ultimateLevel,
        basicBoost,
        skillBoost,
        ultimateBoost,
        lightningVulnerability,
        talentLevel,
        effectHitRate,
        dotBoost,
        dotVulnerability,
    } = useContext(globalData);

    let servalAtk = (atk * atkPercent) + atkFlat;
    let damageBoost = (1 + lightningDmg);
    let critDmgBoost = (1 + critDmg);

    let basicDmgBoost = (damageBoost + basicBoost);
    let basicBaseDmg = servalAtk * character.basicAttack(basicAttackLevel);

    let skillDmgBoost = (damageBoost + skillBoost);
    let skillBaseDmg = servalAtk * character.skillAttack(skillLevel).single;
    let skillSpreadDmg = servalAtk * character.skillAttack(skillLevel).splash;
    let dotBaseDmg = servalAtk * character.skillAttack(skillLevel).dot;

    let ultimateDmgBoost = (damageBoost + ultimateBoost);
    let ultimateBaseDmg = servalAtk * character.ultimateAttack(ultimateLevel);

    let talentBaseDmg = 0;
    if(character.isShocked) {
        talentBaseDmg = servalAtk * character.talent(talentLevel);
    }


    let resMult = (1 - (enemyRes - lightningPen));
    let vulnerabilityMultiplier = (1 + lightningVulnerability);
    let defReductionStat = (1 - defReduction);

    if(defReductionStat < 0) {
        defReductionStat = 0;
    }
    
    let defMult = (character.stats[level]["level"] + 20)/((enemyLevel + 20) * defReductionStat + character.stats[level]["level"] + 20);
    let breakMult = breakMultiplier;

    let critRateStat = critRate;

    let techDmg = servalAtk * 0.5;

    if(critRateStat > 1) {
        critRateStat = 1;
    }

    let maxToughnessMult = 0.5 + (enemyToughness/120);
    let breakBaseDmg = 1 * levelMult[character.stats[level]["level"]] * maxToughnessMult;
    let breakDmg = breakBaseDmg * (1 + breakEffect) * defMult * resMult * vulnerabilityMultiplier * breakMult; 
    let shockBaseDmg = 2 * levelMult[character.stats[level]["level"]];
    let shock = shockBaseDmg * (1 + breakEffect) * resMult * defMult * (vulnerabilityMultiplier + dotVulnerability) * breakMult * (damageBoost + dotBoost);

    return (
        <div className="damage-details">
            <div className='header'>Damage Details</div>
            <div className="divider"></div>
            <div className="damage-title header">Basic Attack ({basicAttackLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Basic</td>
                        <td className='tb-text'>{(((basicBaseDmg + talentBaseDmg) * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{(((basicBaseDmg + talentBaseDmg) * basicDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{((((basicBaseDmg + talentBaseDmg) * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    {eidolon >= 1 && 
                        <tr>
                            <td>E1</td>
                            <td className='tb-text'>{(0.6 * ((basicBaseDmg + talentBaseDmg) * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                            <td className='tb-text'>{(0.6 * ((basicBaseDmg + talentBaseDmg) * basicDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                            <td className='tb-text'>{(0.6 * (((basicBaseDmg + talentBaseDmg) * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                        </tr>
                    }
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
            </table>
            <div className="damage-title header">Skill Attack ({skillLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>DMG</td>
                        <td className='tb-text'>{(((skillBaseDmg + talentBaseDmg) * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{(((skillBaseDmg + talentBaseDmg) * skillDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{((((skillBaseDmg + talentBaseDmg) * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Blast</td>
                        <td className='tb-text'>{(((skillSpreadDmg + talentBaseDmg) * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{(((skillSpreadDmg + talentBaseDmg) * skillDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{((((skillSpreadDmg + talentBaseDmg) * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>DoT</td>
                        <td className='tb-text'>{((dotBaseDmg * (damageBoost + dotBoost) * defMult * (vulnerabilityMultiplier + dotVulnerability) * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(((dotBaseDmg * (damageBoost + dotBoost) * defMult * (vulnerabilityMultiplier + dotVulnerability) * resMult * breakMult))).toFixed(0)}</td>
                    </tr>
                    {level >= 3 ?
                        <tr>
                            <td>DoT Chance</td>
                            <td className='tb-text'>{(1 * (1 + effectHitRate) * (1 - 0.4) * 100).toFixed(1)}%</td>
                            <td className='tb-text'>-</td>
                            <td className='tb-text'>{(1 * (1 + effectHitRate) * (1 - 0.4) * 100).toFixed(1)}%</td>
                        </tr>
                        :
                        <tr>
                            <td>DoT Chance</td>
                            <td className='tb-text'>{(0.8 * (1 + effectHitRate) * (1 - 0.4) * 100).toFixed(1)}%</td>
                            <td className='tb-text'>-</td>
                            <td className='tb-text'>{(0.8 * (1 + effectHitRate) * (1 - 0.4) * 100).toFixed(1)}%</td>
                        </tr>
                    }
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{60}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{60}</td>
                    </tr>
                </tbody>
            </table>
        <div className="damage-title header">Ultimate Attack ({ultimateLevel - 1 + 2})</div>
        <table className="table table-striped attack-table">
            <thead>
                <tr>
                    <th scope="col"></th>
                    <th scope="col">Non-CRIT</th>
                    <th scope="col">CRIT</th>
                    <th scope="col">Average</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>DMG</td>
                    <td className='tb-text'>{(((ultimateBaseDmg + talentBaseDmg) * ultimateDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                    <td className='tb-text'>{(((ultimateBaseDmg + talentBaseDmg) * ultimateDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                    <td className='tb-text'>{((((ultimateBaseDmg + talentBaseDmg) * ultimateDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                </tr>
                <tr>
                    <td>Energy</td>
                    <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                    <td className='tb-text'>-</td>
                    <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                </tr>
                <tr>
                    <td>Break</td>
                    <td className='tb-text'>{60}</td>
                    <td className='tb-text'>-</td>
                    <td className='tb-text'>{60}</td>
                </tr>
            </tbody>
        </table>

        <div className="damage-title header">Technique</div>
        <table className="table table-striped attack-table">
            <thead>
            <tr>
                <th scope="col"></th>
                <th scope="col">Non-CRIT</th>
                <th scope="col">CRIT</th>
                <th scope="col">Average</th>
            </tr>
            </thead>
            <tbody>
                <tr>
                    <td>DMG</td>
                    <td className='tb-text'>{((techDmg * damageBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                    <td className='tb-text'>{((techDmg * damageBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                    <td className='tb-text'>{(((techDmg * damageBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                </tr>
                <tr>
                    <td>Tech Shock</td>
                    <td className='tb-text'>{((techDmg * (damageBoost + dotBoost) * defMult * (vulnerabilityMultiplier + dotVulnerability) * resMult * breakMult)).toFixed(0)}</td>
                    <td className='tb-text'>{((techDmg * (damageBoost + dotBoost) * defMult  * (vulnerabilityMultiplier + dotVulnerability)* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                    <td className='tb-text'>{(((techDmg * (damageBoost + dotBoost) * defMult * (vulnerabilityMultiplier + dotVulnerability) * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                </tr>
            </tbody>
        </table>

        <div className="damage-title header">Weakness Break</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Type</th>
                        <th scope="col">Value</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Break Damage</td>
                        <td className='tb-text'>{(breakDmg).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Shock DMG</td>
                        <td className='tb-text'>{(shock).toFixed(0)}</td>
                    </tr>
                </tbody>
            </table>
        </div>
    )
}


export default ServalTable;