export class Moment {
    constructor() {
        this.stats = [
            [1, 21.6, 27, 48],
            [20, 83.16, 103.95, 185],
            [20, 109.08, 136.35, 242],
            [40, 208.44, 260.55, 463],
            [40, 243, 303.75,  540],
            [50, 275.4, 344.25,  612],
            [50, 309.96, 387.45, 689],
            [60, 342.36, 427.95, 761],
            [60, 376.92, 471.15, 838],
            [70, 409.32, 511.65, 910],
            [70, 443.88, 554.85, 986],
            [80, 476.28, 595.35, 1058],
        ];
        
        this.name = "Moment of Victory";
        this.id = "moment";
        this.path = "Preservation";
        this.rarity = "fiveStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text"> DEF </div>
                <div> by </div>
                <div className="buff-text">{((0.24 + imposition * 0.04) * 100).toFixed(1)}% </div>
                <div> and </div>
                <div className="buff-text"> Effect Hit Rate </div>
                <div> Increases the DMG of the wearer when they use their Ultimate by </div>
                <div className="buff-text">{((0.24 + imposition * 0.04) * 100).toFixed(1)}% </div>
                <div> Increases the chance for the wearer to be attacked by enemies. When the wearer is attacked, increase their </div>
                <div className="buff-text"> DEF </div>
                <div> by </div>
                <div className="buff-text">{((0.24 + imposition * 0.04) * 100).toFixed(1)}% </div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{
                defPercent: 0.24 + imposition * 0.04,
                effectHitRate: 0.24 + imposition * 0.04,
                taunt: 2,
            },
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "adve1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div>When the wearer is attacked, increase their </div>
                        <div className="buff-text"> DEF </div>
                        <div> by </div>
                        <div className="buff-text">{((0.24 + imposition * 0.04) * 100).toFixed(1)}% </div>
                    </div>
                ),
                effect: {
                    defPercent: 0.24 + imposition * 0.04,
                },
                type: "buff",
                hasStack: false,
                hasInput: false,
            },
        }
    }
}

export default Moment;