class DanHeng {
    constructor() {
        this.stats = [
            {level: 1,  atk: 74.4,   def: 54,    hp: 120, spd: 110, taunt: 75, energy: 100},
            {level: 20, atk: 145.08, def: 105.3, hp: 234, spd: 110, taunt: 75, energy: 100, windDmg: 0.032},
            {level: 20, atk: 174.84, def: 126.9, hp: 282, spd: 110, taunt: 75, energy: 100, windDmg: 0.032},
            {level: 40, atk: 279,    def: 202.5, hp: 450, spd: 110, taunt: 75, energy: 100, windDmg: 0.032},
            {level: 40, atk: 308.76, def: 224.1, hp: 498, spd: 110, taunt: 75, energy: 100, windDmg: 0.064, atkPercent: 0.04, defPercent: 0.05},
            {level: 50, atk: 345.96, def: 251.1, hp: 558, spd: 110, taunt: 75, energy: 100, windDmg: 0.064, atkPercent: 0.04, defPercent: 0.05},
            {level: 50, atk: 375.72, def: 272.7, hp: 606, spd: 110, taunt: 75, energy: 100, windDmg: 0.112, atkPercent: 0.04, defPercent: 0.05},
            {level: 60, atk: 412.92, def: 299.7, hp: 666, spd: 110, taunt: 75, energy: 100, windDmg: 0.112, atkPercent: 0.1, defPercent: 0.05},
            {level: 60, atk: 442.68, def: 321.3, hp: 714, spd: 110, taunt: 75, energy: 100, windDmg: 0.16,  atkPercent: 0.1, defPercent: 0.05},
            {level: 70, atk: 479.88, def: 348.3, hp: 774, spd: 110, taunt: 75, energy: 100, windDmg: 0.16,  atkPercent: 0.1, defPercent: 0.05},
            {level: 70, atk: 509.64, def: 369.9, hp: 822, spd: 110, taunt: 75, energy: 100, windDmg: 0.16,  atkPercent: 0.1, defPercent: 0.125},
            {level: 80, atk: 546.84, def: 396.9, hp: 882, spd: 110, taunt: 75, energy: 100, windDmg: 0.224, atkPercent: 0.18, defPercent: 0.125},
        ];

        this.name = "Dan Heng";
        this.path = "Hunt";
        this.rarity = 'fourStar';
        this.element = "Wind";
        this.id = "danheng";
        this.slowed = false;
    }

    setSlowed(slowed) {
        this.slowed = slowed;
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Cloudlancer Art: North Wind",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className="Wind"> Wind DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return 1.3 + (level * 0.13);
        }else if(level < 10){
            return 1.95 + ((level - 5) * 0.1625);
        }else {
            return 2.6 + ((level - 9) * 0.13);
        }
    }

    skillDescription(level) {
        return {
            name: "Cloudlancer Art: Torrent",
            description: (
                <div className="buff-description">
                    <div> Deals </div>
                    <div className="Wind"> Wind DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                    <div> On a CRIT Hit, there is a 100% base chance to reduce the target's SPD by 12% for 2 turn(s). </div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(this.slowed) {
            if(level < 6) {
                return 2.4 + (level * 0.16) + (0.72 + (level * 0.048));
            }else if(level < 10){
                return 3.2 + ((level - 5) * 0.2 + (0.96 + (level * 0.06)));
            }else {
                return 4 + ((level - 9) * 0.16 + (1.2 + (level * 0.048)));
            }
        }

        if(level < 6) {
            return 2.4 + (level * 0.16);
        }else if(level < 10){
            return 3.2 + ((level - 5) * 0.2);
        }else {
            return 4 + ((level - 9) * 0.16);
        }
    }

    bonusMult(level) {
        if(level < 6) {
            return 0.72 + (level * 0.048);
        }else if(level < 10){
            return 0.96 + ((level - 5) * 0.06);
        }else {
            return 1.2 + ((level - 9) * 0.048);
        }
    }

    bonusMultDesc(level) {
        return (<>
            <div> If the enemy is Slowed, the Ultimate's DMG multiplier increases by </div>
            <div className="buff-text"> {(this.bonusMult(level) * 100).toFixed(1)}% </div>
        </>)
    }

    ultimateDescription(level) {
        return {
            name: "Ethereal Dream",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className="Wind">Wind DMG</div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.ultimateAttack(level) * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to a single enemy. </div>
                    <div> If the enemy is Slowed, the Ultimate's DMG multiplier increases by </div>
                    <div className="buff-text"> {(this.bonusMult(level) * 100).toFixed(1)}% </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 0.18 + (level * 0.018);
        }else if(level < 10){
            return 0.27 + ((level - 5) * 0.0225);
        }else {
            return 0.36 + ((level - 9) * 0.018);
        }
    }

    talentDescription(level) { 
        return {
            name: "Superiority of Reach",
            description: (
                <div className="buff-description">
                    <div>When Dan Heng is the target of an ally's Ability, his next attack's </div>
                    <div className="Wind"> Wind RES PEN </div>
                    <div> increases by </div>
                    <div className="buff-text"> {(this.talent(level) * 100).toFixed(1)}% </div>
                    <div> This effect can be triggered again after 2 turn(s). </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Splitting Spearhead",
            description: (
                <div className="buff-description">
                    <div> After Dan Heng uses his Technique, his </div>
                    <div className="buff-text"> ATK </div>
                    <div> increases by 40% at the start of the next battle for 3 turn(s). </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Hidden Dragon",
            description: (
                <div className="buff-description">
                    <div> When current HP percentage is </div>
                    <div className="buff-text"> 50% or lower</div>
                    <div> reduces the chance of being attacked by enemies. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Faster Than Light",
            description: (
                <div className="buff-description">
                    <div> After launching an attack, there is a </div>
                    <div className="buff-text"> 50% fixed chance </div>
                    <div> to increase own SPD by </div>
                    <div className="buff-text"> 20% </div>
                    <div> for 2 turn(s). </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "High Gale",
            description: (
                <div className="buff-description">
                    <div className="buff-text"> Basic ATK </div>
                    <div> deals </div>
                    <div className="buff-text"> 40% </div>
                    <div> more DMG to Slowed enemies. </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "	The Higher You Fly, the Harder You Fall",
            description: (
                <div className="buff-description">
                    <div> When the target enemy's current HP percentage is </div>
                    <div className="buff-text"> greater than or equal to 50%, CRIT Rate </div>
                    <div> increases by </div>
                    <div className="buff-text"> 12% </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Quell the Venom Octet, Quench the Vice O'Flame",
            description: (
                <div className="buff-description">
                    <div> Reduces Talent cooldown by 1 turn. </div>
                </div>
            )
        }
    }

    E3() {
        return {
            name: "Seen and Unseen",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }

    E4() {
        return {
            name: "Roaring Dragon and Soaring Sun",
            description:(
                <div className="buff-description">
                    <div>When Dan Heng uses his Ultimate to defeat an enemy, he will immediately take action again.</div>
                </div>
            )
        }
    }

    E5() {
        return {
            name: "	A Drop of Rain Feeds a Torrent",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: "The Troubled Soul Lies in Wait",
            description: (
                <div className="buff-description">
                    <div>The Slow state triggered by Skill reduces the enemy's SPD by an extra 8%. </div>
                </div>
            ),
        }
    }
}


export default DanHeng;