import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';

import { DanHengIL } from "../../../../../../../database/characters";

const DanHengILBuffs = () => {

    const {level, eidolon, skillLevel, talentLevel, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    
    const danHeng = new DanHengIL();

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    const [skillStacks, setSkillStacks] = useState(1);
    const [talentStacks, setTalentStacks] = useState(1);
    const [prevTalentLevel, setPrevTalentLevel] = useState(talentLevel);
    const [prevSkillLevel, setPrevSkillLevel] = useState(skillLevel);
    const [E6Stacks, setE6Stacks] = useState(1);


    const A6Buff = {
        effect: {
            critDmg: 0.24,
        }
    }

    const E6Buff = {
        effect: {
            imaginaryPen: 0.20 * E6Stacks,
        },
    }

    useEffect(() => {
        if(eidolon < 6 && characterBuffStates[3]) {
            updateStatesFromJson(E6Buff, true);
            characterBuffStates[3] = false;
        }
    }, [eidolon]);

    useEffect(() => {
        
        if(level < 11 && characterBuffStates[2]) {
            updateStatesFromJson(A6Buff, true);
            characterBuffStates[2] = false;
        }

    }, [level]);

    const updateE6Stacks = (e) => {
        
        if(characterBuffStates[3]) {
            updateStatesFromJson(E6Buff, true);
        }

        setE6Stacks(e.target.value);

        const tempE6Buff = {
            effect: {
                imaginaryPen: 0.20 * e.target.value,
            },
        }

        if(characterBuffStates[3]) {
            updateStatesFromJson(tempE6Buff, false);
        }
    }

    return (
        <>
            {level >= 11 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, A6Buff)} />}
                    label={"(A6) " + danHeng.A6().name}
                    />
                <div>{danHeng.A6().description}</div>
            </>
            }

            {eidolon >= 6 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, E6Buff)} />}
                    label={"(E6) " + danHeng.E6().name}
                    />
                    <div>{danHeng.E6().description}</div>
                    <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                        <div style={{display: "flex"}}>
                        <div>Stacks </div>
                        <select className="levels buff-input" style={{width: "45%"}} value={E6Stacks} onChange={updateE6Stacks}>
                            <option value={1}>1</option>
                            <option value={2}>2</option>
                            <option value={3}>3</option>
                        </select>
                        </div>
                    </div>
                </>
            }
        </>
    )
}

export const DanHengILGlobalBuffs = (props) => {

    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {
        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <div>No Buffs</div>
        </>
    )
}

export default DanHengILBuffs;