export class Mutual {
    constructor() {
        this.stats = [
            [1, 16.8,    9,     38 ],
            [20, 64.68,  34.65,  148],
            [20, 84.84, 45.45,  194],
            [40, 162.12, 86.85, 371],
            [40, 189,   101.25, 432],
            [50, 214.2,  114.75, 490],
            [50, 241.08, 129.15, 551],
            [60, 266.28, 142.65, 609],
            [60, 293.16, 157.05	, 670],
            [70, 318.36, 170.55, 728],
            [70, 345.24, 184.95, 789],
            [80, 370.44, 	198.45, 847],
        ];
        
        this.name = "Mutual Demise";
        this.path = "Destruction";
        this.id = "mutual";
        this.rarity = "threeStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>If the wearer's current </div>
                <div className="buff-text"> HP </div> 
                <div> is </div> 
                <div className="buff-text"> lower than 80%, CRIT Rate </div> 
                <div> increases by </div> 
                <div className="buff-text"> {((0.12 + imposition * 0.03) * 100).toFixed(1)}%.</div> 
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "silenceb",
            name: this.name,
            effect:{},
            type: "buff",
            hasStack: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "silence0",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div>If the wearer's current </div>
                        <div className="buff-text"> HP </div> 
                        <div> is </div> 
                        <div className="buff-text"> lower than 80%, CRIT Rate </div> 
                        <div> increases by </div> 
                        <div className="buff-text"> {((0.12 + imposition * 0.03) * 100).toFixed(1)}%.</div> 
                    </div>
                ),
                effect: {
                    critRate: 0.12 + imposition * 0.03,
                },
                type: "buff",
                hasStack: false,
                hasInput: false,
            },
        }
    }
}

export default Mutual;