import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';
import { Yukong } from "../../../../../../../database/characters";

const YukongBuffs = () => {

    const {level, eidolon, skillLevel, ultimateLevel, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    const yukong = new Yukong();

    const [prevSkillLevel, setPrevSkillLevel] = useState(skillLevel);
    const [prevUltimateLevel, setPrevUltimateLevel] = useState(ultimateLevel);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];

        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }
    
    const skillBuff = {
        effect: {
            atkPercent: yukong.skillAttack(skillLevel),
        },
    }

    useEffect(() => {
        if(characterBuffStates[0]) {
            const tempSkillBuff = {
                effect: {
                    atkPercent: yukong.skillAttack(prevSkillLevel),
                },
            }

            updateStatesFromJson(tempSkillBuff, true);
            updateStatesFromJson(skillBuff, false);
        }
        
        setPrevSkillLevel(skillLevel);
    }, [skillLevel]);

    const ultimateBuff = {
        effect: {
            critRate: yukong.ultimateBuff(ultimateLevel).critRate,
            critDmg: yukong.ultimateBuff(ultimateLevel).critDmg,
        }
    }

    useEffect(() => {
        if(characterBuffStates[1]) {
            const tempUltBuff = {
                effect: {
                    critRate: yukong.ultimateBuff(prevUltimateLevel).critRate,
                    critDmg: yukong.ultimateBuff(prevUltimateLevel).critDmg,
                },
            }
            updateStatesFromJson(tempUltBuff, true);

            const newTempUltBuff = {
                effect: {
                    critRate: yukong.ultimateBuff(ultimateLevel).critRate,
                    critDmg: yukong.ultimateBuff(ultimateLevel).critDmg,
                },
            }
            updateStatesFromJson(newTempUltBuff, false);
        }
        
        setPrevUltimateLevel(ultimateLevel);
    }, [ultimateLevel]);


    const A4Buff = {
        effect: {
            imaginaryDmg: 0.12,
        },
    }


    const E1Buff = {
        effect: {
            spdPercent: 0.1,
        },
    }

    const E4Buff = {
        effect: {
            imaginaryDmg: 0.3,
        }
    }

    useEffect(() => {

        if(eidolon < 1 && characterBuffStates[3]) {
            updateStatesFromJson(E1Buff, true);
            characterBuffStates[3] = false;
        }

        if(eidolon < 4 && characterBuffStates[4]) {
            updateStatesFromJson(E4Buff, true);
            characterBuffStates[4] = false;
        }
    }, [eidolon]);

    useEffect(() => {
        
        if(level < 6 && characterBuffStates[2]) {
            updateStatesFromJson(A4Buff, true);
            characterBuffStates[2] = false;
        }

    }, [level]);

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, skillBuff)} />}
            label={yukong.skillDescription(skillLevel).name}
            />
            <div>{yukong.skillDescription(skillLevel).description}</div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, ultimateBuff)} />}
            label={yukong.ultimateDescription(ultimateLevel).name}
            />
            <div>{yukong.ultimateDescription(ultimateLevel).description}</div>

            {level >= 6 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, A4Buff)} />}
                    label={"(A4) " + yukong.A4().name}
                    />
                    <div>{yukong.A4().description}</div>
                </>
            }

            {eidolon >= 1 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, E1Buff)} />}
                    label={"(E1) " + yukong.E1().name}
                    />
                    <div>{yukong.E1().description}</div>
                </>
            }

            {eidolon >= 4 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[4] || false} onChange={() => applyCharacterBuffs(4, E4Buff)} />}
                    label={"(E4) " + yukong.E4().name}
                    />
                    <div>{yukong.E4().description}</div>
                </>
            }
        </>
    )
}

export const YukongGlobalBuffs = (props) => {
    
    const { updateStatesFromJson } = useContext(globalData);
    const yukong = new Yukong();

    const [skillLevel, setSkillLevel] = useState(0);
    const [ultimateLevel, setUltimateLevel] = useState(0);

    const yukongBuffStates = props.buffStates;
    const setyukongBuffStates = props.setBuffStates;
    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    const [prevSkillLevel, setPrevSkillLevel] = useState(skillLevel);
    const [prevUltimateLevel, setPrevUltimateLevel] = useState(ultimateLevel);


    const updateUltLevel = (event) => {
        setUltimateLevel(event.target.value);
    }

    const updateSkillLevel = (event) => {
        setSkillLevel(event.target.value);
    }

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...yukongBuffStates];

        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setyukongBuffStates(updatedStates);
    }
    
    const skillBuff = {
        effect: {
            atkPercent: yukong.skillAttack(skillLevel),
        },
    }

    useEffect(() => {
        if(yukongBuffStates[0]) {
            const tempSkillBuff = {
                effect: {
                    atkPercent: yukong.skillAttack(prevSkillLevel),
                },
            }

            updateStatesFromJson(tempSkillBuff, true);
            updateStatesFromJson(skillBuff, false);
        }
        
        setPrevSkillLevel(skillLevel);
    }, [skillLevel]);

    const ultimateBuff = {
        effect: {
            critRate: yukong.ultimateBuff(ultimateLevel).critRate,
            critDmg: yukong.ultimateBuff(ultimateLevel).critDmg,
        }
    }

    useEffect(() => {
        if(yukongBuffStates[1]) {
            const tempUltBuff = {
                effect: {
                    critRate: yukong.ultimateBuff(prevUltimateLevel).critRate,
                    critDmg: yukong.ultimateBuff(prevUltimateLevel).critDmg,
                },
            }
            updateStatesFromJson(tempUltBuff, true);

            const newTempUltBuff = {
                effect: {
                    critRate: yukong.ultimateBuff(ultimateLevel).critRate,
                    critDmg: yukong.ultimateBuff(ultimateLevel).critDmg,
                },
            }
            updateStatesFromJson(newTempUltBuff, false);
        }
        
        setPrevUltimateLevel(ultimateLevel);
    }, [ultimateLevel]);


    const A4Buff = {
        effect: {
            imaginaryDmg: 0.12,
        },
    }


    const E1Buff = {
        effect: {
            spdPercent: 0.1,
        },
    }

    useEffect(() => {
        if(yukongBuffStates[0]) {
            updateStatesFromJson(skillBuff, true);
            yukongBuffStates[0] = false;
        }

        if(yukongBuffStates[1]) {
            updateStatesFromJson(ultimateBuff, true);
            yukongBuffStates[1] = false;
        }

        if(yukongBuffStates[2]) {
            updateStatesFromJson(A4Buff, true);
            yukongBuffStates[2] = false;
        }

        if(yukongBuffStates[3]) {
            updateStatesFromJson(E1Buff, true);
            yukongBuffStates[3] = false;
        }

        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={yukongBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, skillBuff)} />}
            label={yukong.skillDescription(skillLevel).name}
            />
            <div className="mb-3">{yukong.skillSimple(skillLevel).description}</div>
            <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                <div style={{display: "flex"}}>
                    <div>Skill Level</div>
                    <select className="levels buff-input" style={{width: "45%"}} value={skillLevel} onChange={updateSkillLevel}>
                        <option value={0}>1</option>
                        <option value={1}>2</option>
                        <option value={2}>3</option>
                        <option value={3}>4</option>
                        <option value={4}>5</option>
                        <option value={5}>6</option>
                        <option value={6}>7</option>
                        <option value={7}>8</option>
                        <option value={8}>9</option>
                        <option value={9}>10</option>
                        <option value={10}>11</option>
                        <option value={11}>12</option>
                    </select>
                </div>
            </div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={yukongBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, ultimateBuff)} />}
            label={yukong.ultimateDescription(ultimateLevel).name}
            />
            <div className="mb-3">{yukong.ultimateSimple(ultimateLevel).description}</div>
            <div style={{display: "flex"}}>
                <div>Ult Level</div>
                <select className="levels buff-input" style={{width: "15%"}} value={ultimateLevel} onChange={updateUltLevel}>
                    <option value={0}>1</option>
                    <option value={1}>2</option>
                    <option value={2}>3</option>
                    <option value={3}>4</option>
                    <option value={4}>5</option>
                    <option value={5}>6</option>
                    <option value={6}>7</option>
                    <option value={7}>8</option>
                    <option value={8}>9</option>
                    <option value={9}>10</option>
                    <option value={10}>11</option>
                    <option value={11}>12</option>
                </select>
            </div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={yukongBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, A4Buff)} />}
            label={"(A4) " + yukong.A4().name}
            />
            <div>{yukong.A4().description}</div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={yukongBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, E1Buff)} />}
            label={"(E1) " + yukong.E1().name}
            />
            <div>{yukong.E1().description}</div>

        </>
    )
}   

export default YukongBuffs;