export class Amber {
    constructor() {
        this.stats = [
            [1, 12, 12, 38],
            [20, 46.2, 46.2, 148],
            [20, 60.6, 60.6, 194],
            [40, 115.8, 115.8, 371],
            [40, 135, 135,  432],
            [50, 153, 153,  490],
            [50, 172.2, 172.2, 551],
            [60, 190.2, 190.2, 609],
            [60, 209.4, 209.4, 670],
            [70, 227.4, 227.4, 728],
            [70, 246.6, 246.6, 789],
            [80, 264.6, 264.6, 847],
        ];
        
        this.name = "Amber";
        this.path = "Preservation";
        this.id = "amber";
        this.rarity = "threeStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's</div>
                <div className="buff-text"> DEF </div>
                <div> by </div>
                <div className="buff-text">{((0.16 + imposition * 0.04) * 100).toFixed(1)}% </div>
                <div> If the wearer's current HP is </div>
                <div className="buff-text"> lower than 50% </div>
                <div> increases their </div>
                <div className="buff-text"> DEF </div>
                <div> by a further </div>
                <div className="buff-text">{((0.16 + imposition * 0.04) * 100).toFixed(1)}% </div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{
                defPercent: 0.16 + imposition * 0.04,
            },
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "adve1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> If the wearer's current HP is </div>
                        <div className="buff-text"> lower than 50% </div>
                        <div> increases their </div>
                        <div className="buff-text"> DEF </div>
                        <div> by a further </div>
                        <div className="buff-text">{((0.16 + imposition * 0.04) * 100).toFixed(1)}% </div>
                    </div>
                ),
                effect: {
                    defPercent: 0.16 + imposition * 0.04,
                },
                type: "buff",
                hasStack: false,
                hasInput: false,
            },
        }
    }
}

export default Amber;