class Herta {
    constructor() {
        this.stats = [
            {level: 1,  atk: 79.2,   def: 54,    hp: 130, spd: 100, taunt: 75, energy: 110},
            {level: 20, atk: 154.44, def: 105.3, hp: 253, spd: 100, taunt: 75, energy: 110, iceDmg: 0.032},
            {level: 20, atk: 186.12, def: 126.9, hp: 305, spd: 100, taunt: 75, energy: 110, iceDmg: 0.032},
            {level: 40, atk: 297,    def: 202.5, hp: 486, spd: 100, taunt: 75, energy: 110, iceDmg: 0.032, defPercent: 0.05},
            {level: 40, atk: 328.68, def: 224.1, hp: 538, spd: 100, taunt: 75, energy: 110, iceDmg: 0.064, defPercent: 0.05,  critRate: 0.027},
            {level: 50, atk: 368.28, def: 251.1, hp: 603, spd: 100, taunt: 75, energy: 110, iceDmg: 0.064, defPercent: 0.05,  critRate: 0.027},
            {level: 50, atk: 399.96, def: 272.7, hp: 654, spd: 100, taunt: 75, energy: 110, iceDmg: 0.112, defPercent: 0.05,  critRate: 0.027},
            {level: 60, atk: 439.56, def: 299.7, hp: 719, spd: 100, taunt: 75, energy: 110, iceDmg: 0.112, defPercent: 0.125, critRate: 0.027},
            {level: 60, atk: 471.24, def: 321.3, hp: 771, spd: 100, taunt: 75, energy: 110, iceDmg: 0.16,  defPercent: 0.125, critRate: 0.027},
            {level: 70, atk: 510.84, def: 348.3, hp: 836, spd: 100, taunt: 75, energy: 110, iceDmg: 0.16,  defPercent: 0.125, critRate: 0.027},
            {level: 70, atk: 542.52, def: 369.9, hp: 888, spd: 100, taunt: 75, energy: 110, iceDmg: 0.16,  defPercent: 0.125, critRate: 0.067},
            {level: 80, atk: 582.12, def: 396.9, hp: 953, spd: 100, taunt: 75, energy: 110, iceDmg: 0.224, defPercent: 0.225, critRate: 0.067},
        ];

        this.name = "Herta";
        this.path = "Erudition";
        this.rarity = 'fourStar';
        this.element = "Ice";
        this.id = "herta";
        this.E1ON = false;
    }

    setE1ON = (bool) => {
        this.E1ON = bool;
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "What Are You Looking At?",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return 0.5 + (level * 0.05);
        }else if(level < 10){
            return 0.75 + ((level - 5) * 0.0625);
        }else {
            return 1 + ((level - 9) * 0.05);
        }
    }

    skillDescription(level) {
        return {
            name: "One-Time Offer",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to all enemies. </div>
                    <div> If the enemy's HP percentage is 50% or higher, DMG dealt to this target increases by 20%. </div>
                </div>
            ),
        }
    }

    skillSimple(level) {
        return {
            name: "One-Time Offer",
            description: (
                <div className="buff-description">
                    <div> If the enemy's HP percentage is 50% or higher, DMG dealt to this target increases by 20%. </div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level < 6) {
            return 1.2 + (level * 0.08);
        }else if(level < 10){
            return 1.6 + ((level - 5) * 0.1);
        }else {
            return 2 + ((level - 9) * 0.08);
        }
    }

    ultimateDescription(level) {
        return {
            name: "It's Magic, I Added Some Magic",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.ultimateAttack(level) * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to all enemies. </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 0.25 + (level * 0.015);
        }else if(level < 10){
            return 0.325 + ((level - 5) * 0.0188);
        }else {
            return 0.4 + ((level - 9) * 0.015);
        }
    }

    talentDescription(level) { 
        return {
            name: "Superiority of Reach",
            description: (
                <div className="buff-description">
                    <div>When an ally's attack causes an enemy's HP percentage to fall to 50% or lower, Herta will launch a follow-up attack, dealing</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.talent(level) * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to all enemies. </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "It Can Still Be Optimized",
            description: (
                <div className="buff-description">
                    <div> After using her Technique, Herta's </div>
                    <div className="buff-text"> ATK </div>
                    <div> increases by 40% for 3 turn(s) at the beginning of the next battle. </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Efficiency",
            description: (
                <div className="buff-description">
                    <div> When Skill is used, the DMG Boost effect on target enemies increases by an extra 25%. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Puppet",
            description: (
                <div className="buff-description">
                    <div> The chance to resist Crowd Control Debuffs increases by 35%. </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Icing",
            description: (
                <div className="buff-description">
                    <div> When Ultimate is used, deals 20% more DMG to Frozen enemies. </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Kick You When You're Down",
            description: (
                <div className="buff-description">
                    <div> If the enemy's HP percentage is at 50% or less, Herta's Basic ATK deals Additional Ice DMG equal to 40% of Herta's ATK. </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Keep the Ball Rolling",
            description: (
                <div className="buff-description">
                    <div> Every time Talent is triggered, this character's CRIT Rate increases by 3%. This effect can stack up to 5 time(s). </div>
                </div>
            )
        }
    }

    E3() {
        return {
            name: "That's the Kind of Girl I Am",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }

    E4() {
        return {
            name: "Hit Where It Hurts",
            description:(
                <div className="buff-description">
                    <div>When Talent is triggered, DMG increases by 10%.</div>
                </div>
            )
        }
    }

    E5() {
        return {
            name: "Cuss Big or Cuss Nothing",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: "No One Can Betray Me",
            description: (
                <div className="buff-description">
                    <div>After using Ultimate, this character's ATK increases by 25% for 1 turn(s).</div>
                </div>
            ),
        }
    }
}


export default Herta;