import React from 'react';
import {Dialog, DialogTitle, DialogContent} from '@mui/material';
import { useContext } from "react";
import { globalData } from '../globalData';

function Changes(props) {

    const open = props.open;
    const setOpen = props.setOpen;

    const { showBeta } = useContext(globalData);

    return (
        <div className="dialog">
            <Dialog aria-label='dialog-title' open={open} onClose={() => setOpen(false)} maxWidth="sm" fullWidth>
                <DialogTitle className='dialog-container' id='dialog-title'>
                <div style={{display: "flex", justifyContent: "space-between", alignItems: "center"}}>
                    <div>Changes</div>
                    <button onClick={() => setOpen(false)} className='calc-btn' style={{width: "37px", display: "flex", backgroundColor: "red", textAlign:"center", alignItems: "center", justifyContent: "center", margin: "0"}}>&#10005;</button>
                </div>
                </DialogTitle>
                <DialogContent className='dialog-content'>
                    <div className='date'>15/9/2023</div>
                    <div className='changes'> - Fixed a bug where equipping the planar set Vonwacq would result in unexpected behaviour. Thanks Dela! </div>
                    {showBeta && 
                        <>
                            <div className='date'>12/9/2023</div>
                            <div className='changes'> - Fixed an issue with Topaz's Skill debuff applying abnormally </div>

                        </>
                    }
                    <div className='date'>10/9/2023</div>
                    {showBeta &&
                        <>
                            <div className='changes'> - Added Jingliu </div>
                            <div className='changes'> - Added Light Cone: I Shall Be My Own Sword </div>
                            <div className='changes'> - Added Topaz And Numpy </div>
                            <div className='changes'> - Added Light Cone: Worrisome, Blissful </div>
                        </>
                    }
                    <div className='changes'> - Added the ability to view upcoming content which you can check out using the gear icon in the top left of the page. </div>
                    <div className='date'>6/9/2023</div>
                    <div className='changes'> - Added Fu Xuan </div>
                    <div className='changes'> - Added Fu Lynx </div>
                    <div className='changes'> - Added Light Cone: She Already Shut Her Eyes</div>
                    <div className='date'>3/9/2023</div>
                    <div className='changes'> - Fixed Kafka's E6</div>
                    <div className='changes'> - Added high resolution images for relics/characters, thanks markee!</div>
                    <div className='changes'> - Fixed a bug where switching substats caused incorrect behaviour, again thanks to Binnyboy! </div>
                    <div className='changes'> - Fixed a bug where the vulnerability effect from Incessant Rain wasn't properly added to the calculation, thanks Binnyboy!!</div>
                    <div className='date'>2/9/2023</div>
                    <div className='changes'> - Fixed a bug where the shield bonus buff from (Knight of Purity Palace) wasn't properly calculated</div>
                    <div className='date'>1/9/2023</div>
                    <div className='changes'> - Added Trailblazer (Preservation)</div>
                    <div className='changes'> - Added Trailblazer (Destruction)</div>
                    <div className='changes'> - Added Light Cone: Solitary Healing</div>
                    <div className='date'>31/8/2023</div>
                    <div className='changes'> - Added Silver Wolf </div>
                    <div className='changes'> - Added Luka </div>
                    <div className='date'>30/8/2023</div>
                    <div className='changes'> - Updated the Introduction panel to be more helpful </div>
                    <div className='changes'> - Added Kafka </div>
                    <div className='changes'> - Added Welt Yang</div>
                    <div className='changes'> - Relics/Ornaments buffs that don't have a condition are now automatically added</div>
                    <div className='date'>29/8/2023</div>
                    <div className='changes'> - Fixed a bug where if you switch a relic or an ornament that has stacks the buff wouldn't be cleared properly</div>
                    <div className='changes'> - Fixed a bug with Fleetless and Broken Keel, which made them not stackable</div>
                    <div className='changes'> - Added Sampo</div>
                    <div className='changes'> - Fixed Pela's traces</div>
                </DialogContent>
            </Dialog>
        </div>
    )
}

export default Changes;