import React, { useContext } from 'react';
import { globalData } from '../../../globalData';

function BladeTable() {

    const levelMult = {
        1: 54.0000,
        20: 139.7703,
        40: 363.6658,
        50: 774.9041,
        60: 1640.3068,
        70: 2659.6406,
        80: 3767.5533,
    }

    const {
        character,
        windDmg,
        eidolon,
        atk,
        atkPercent,
        atkFlat,
        hp,
        hpPercent,
        hpFlat,
        critRate,
        critDmg,
        energyRegen,
        level,
        enemyLevel,
        windPen,
        breakMultiplier,
        defReduction,
        enemyRes,
        basicAttackLevel,
        breakEffect,
        talentLevel,
        enemyToughness,
        ultimateLevel,
        basicBoost,
        dotBoost,
        followUpBoost,
        followUpCritDmg,
        ultimateBoost,
        dotVulnerability,
        windVulnerability,
        followUpVulnerability,
    } = useContext(globalData);

    let bladeAtk = (atk * atkPercent) + atkFlat;
    let bladeHp = (hp * hpPercent) + hpFlat;

    let damageBoost = (1 + windDmg);
    let critDmgBoost = (1 + critDmg);

    let basicDmgBoost = (damageBoost + basicBoost);
    let basicBaseDmg = bladeAtk * character.basicAttack(basicAttackLevel);

    let basic1BaseDmg = (bladeAtk * character.basicAttack1(basicAttackLevel).singleAtk) + (bladeHp * character.basicAttack1(basicAttackLevel).singleHp);
    let basic1SplashBaseDmg = (bladeAtk * character.basicAttack1(basicAttackLevel).splashAtk) + (bladeHp * character.basicAttack1(basicAttackLevel).splashHp);

    let ultimateDmgBoost = (damageBoost + ultimateBoost);

    let ultimateBaseDmg;

    if(eidolon >= 1) {
        ultimateBaseDmg = (bladeAtk * character.ultimateAttack(ultimateLevel).singleAtk) 
        + (bladeHp * character.ultimateAttack(ultimateLevel).singleHp) 
        + (character.hpLost * character.ultimateAttack(ultimateLevel).singleTallyHp)
        + (character.hpLost * 1.5);
    } else {
        ultimateBaseDmg = (bladeAtk * character.ultimateAttack(ultimateLevel).singleAtk) + (bladeHp * character.ultimateAttack(ultimateLevel).singleHp) + (character.hpLost * character.ultimateAttack(ultimateLevel).singleTallyHp);
    }
    let ultimateSpreadBaseDmg = (bladeAtk * character.ultimateAttack(ultimateLevel).splashAtk) + (bladeHp * character.ultimateAttack(ultimateLevel).splashHp) + (character.hpLost * character.ultimateAttack(ultimateLevel).splashTallyHp);

    let followUpDmgBoost = (damageBoost + followUpBoost);

    if(level >= 10 && eidolon >= 6) {
        followUpDmgBoost = (damageBoost + followUpBoost + 0.7);
    } else if (level >= 10) {
        followUpDmgBoost = (damageBoost + followUpBoost + 0.2);
    } else if (eidolon >= 6) {
        followUpDmgBoost = (damageBoost + followUpBoost + 0.5);
    }

    let talentBaseDmg = (bladeAtk * character.talent(talentLevel).atk) + (bladeHp * character.talent(talentLevel).hp);


    let techDmg = bladeHp * 0.4;

    let resMult = (1 - (enemyRes - windPen));
    let vulnerabilityMultiplier = (1 + windVulnerability);
    let defReductionStat = (1 - defReduction);

    if(defReductionStat < 0) {
        defReductionStat = 0;
    }
    
    let defMult = (character.stats[level]["level"] + 20)/((enemyLevel + 20) * defReductionStat + character.stats[level]["level"] + 20);
    let breakMult = breakMultiplier;

    let critRateStat = critRate;

    if(critRateStat > 1) {
        critRateStat = 1;
    }

    let maxToughnessMult = 0.5 + (enemyToughness/120);
    let breakBaseDmg = 1.5 * levelMult[character.stats[level]["level"]] * maxToughnessMult;
    let breakDmg = breakBaseDmg * (1 + breakEffect) * defMult * resMult * vulnerabilityMultiplier * breakMult; 
    let windSheerBaseDmg = levelMult[character.stats[level]["level"]];
    let windSheer = windSheerBaseDmg * (1 + breakEffect) * resMult * defMult * (vulnerabilityMultiplier + dotVulnerability) * breakMult * (damageBoost + dotBoost);

    return (
        <div className="damage-details">
            <div className='header'>Damage Details</div>
            <div className="divider"></div>
            <div className="damage-title header">Basic Attack ({basicAttackLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Basic</td>
                        <td className='tb-text'>{((basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{((basicBaseDmg * basicDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Enhanced Basic ({basicAttackLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Single</td>
                        <td className='tb-text'>2x{((basic1BaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)/2).toFixed(0)}</td>
                        <td className='tb-text'>2x{((basic1BaseDmg * basicDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)/2).toFixed(0)}</td>
                        <td className='tb-text'>2x{(((basic1BaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)/2) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Blast</td>
                        <td className='tb-text'>{((basic1SplashBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{((basic1SplashBaseDmg * basicDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((basic1SplashBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{60}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{60}</td>
                    </tr>
                    <tr>
                        <td style={{fontSize: "14px"}}>Splash Break</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
            </table>

        <div className="damage-title header">Ultimate Attack ({ultimateLevel - 1 + 2})</div>
        <table className="table table-striped attack-table">
            <thead>
                <tr>
                    <th scope="col"></th>
                    <th scope="col">Non-CRIT</th>
                    <th scope="col">CRIT</th>
                    <th scope="col">Average</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>Single</td>
                    <td className='tb-text'>{((ultimateBaseDmg * ultimateDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                    <td className='tb-text'>{((ultimateBaseDmg * ultimateDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                    <td className='tb-text'>{(((ultimateBaseDmg * ultimateDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                </tr>
                <tr>
                    <td>Blast</td>
                    <td className='tb-text'>{((ultimateSpreadBaseDmg * ultimateDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                    <td className='tb-text'>{((ultimateSpreadBaseDmg * ultimateDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                    <td className='tb-text'>{(((ultimateSpreadBaseDmg * ultimateDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                </tr>
                <tr>
                    <td>Energy</td>
                    <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                    <td className='tb-text'>-</td>
                    <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                </tr>
                <tr>
                    <td>Break</td>
                    <td className='tb-text'>{60}</td>
                    <td className='tb-text'>-</td>
                    <td className='tb-text'>{60}</td>
                </tr>
            </tbody>
        </table>

        <div className="damage-title header">Talent ({talentLevel - 1 + 2})</div>
        <table className="table table-striped attack-table">
            <thead>
            <tr>
                <th scope="col"></th>
                <th scope="col">Non-CRIT</th>
                <th scope="col">CRIT</th>
                <th scope="col">Average</th>
            </tr>
            </thead>
            <tbody>
                <tr>
                    <td>AOE</td>
                    <td className='tb-text'>{((talentBaseDmg * followUpDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult)).toFixed(0)}</td>
                    <td className='tb-text'>{((talentBaseDmg * followUpDmgBoost * defMult  * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult * (critDmgBoost + followUpCritDmg))).toFixed(0)}</td>
                    <td className='tb-text'>{(((talentBaseDmg * followUpDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult)) * (1 + (critDmg + followUpCritDmg) * critRateStat)).toFixed(0)}</td>
                </tr>
                <tr>
                    <td>Energy</td>
                    <td className='tb-text'>{(10 * energyRegen).toFixed(1)}</td>
                    <td className='tb-text'>-</td>
                    <td className='tb-text'>{(10 * energyRegen).toFixed(1)}</td>
                </tr>
                <tr>
                    <td>Break</td>
                    <td className='tb-text'>{30}</td>
                    <td className='tb-text'>-</td>
                    <td className='tb-text'>{30}</td>
                </tr>
            </tbody>
        </table>

        <div className="damage-title header">Technique</div>
        <table className="table table-striped attack-table">
            <thead>
            <tr>
                <th scope="col"></th>
                <th scope="col">Non-CRIT</th>
                <th scope="col">CRIT</th>
                <th scope="col">Average</th>
            </tr>
            </thead>
            <tbody>
                <tr>
                    <td>DMG</td>
                    <td className='tb-text'>{((techDmg * damageBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                    <td className='tb-text'>{((techDmg * damageBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                    <td className='tb-text'>{(((techDmg * damageBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                </tr>
            </tbody>
        </table>

        <div className="damage-title header">Weakness Break</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Type</th>
                        <th scope="col">Value</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Break Damage</td>
                        <td className='tb-text'>{(breakDmg).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Wind Sheer 1 stack</td>
                        <td className='tb-text'>{(windSheer).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Wind Sheer 2 stack</td>
                        <td className='tb-text'>{(windSheer * 2).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Wind Sheer 3 stack</td>
                        <td className='tb-text'>{(windSheer * 3).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Wind Sheer 4 stack</td>
                        <td className='tb-text'>{(windSheer * 4).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Wind Sheer 5 stack</td>
                        <td className='tb-text'>{(windSheer * 5).toFixed(0)}</td>
                    </tr>
                </tbody>
            </table>
        </div>
    )
}


export default BladeTable;