import React, { useContext } from 'react';
import { globalData } from '../../../globalData';

function KafkaTable() {

    const levelMult = {
        1: 54.0000,
        20: 139.7703,
        40: 363.6658,
        50: 774.9041,
        60: 1640.3068,
        70: 2659.6406,
        80: 3767.5533,
    }

    const {
        character,
        lightningDmg,
        atk,
        atkPercent,
        atkFlat,
        critRate,
        critDmg,
        energyRegen,
        level,
        eidolon,
        enemyLevel,
        lightningPen,
        breakEffect,
        defReduction,
        enemyRes,
        basicAttackLevel,
        skillLevel,
        ultimateLevel,
        talentLevel,
        ultimateBoost,
        skillBoost,
        basicBoost,
        breakMultiplier,
        ultimateCritDmg,
        lightningVulnerability,
        enemyToughness,
        effectHitRate,
        dotBoost,
        dotVulnerability,
        followUpBoost,
        lightCone,
        imposition,
        followUpVulnerability,
        followUpCritDmg,
    } = useContext(globalData);

    let kafkaAtk = (atk * atkPercent) + atkFlat;
    let EHR = effectHitRate;
    
    let damageBoost = (1 + lightningDmg);
    let critDmgBoost = (1 + critDmg);
    
    let basicDmgBoost = damageBoost + basicBoost;
    let basicBaseDmg = kafkaAtk * character.basicAttack(basicAttackLevel);

    let skillDmgBoost = damageBoost + skillBoost;
    let skillBaseDmg = kafkaAtk * character.skillAttack(skillLevel).single;
    let skillSpreadBaseDmg = kafkaAtk * character.skillAttack(skillLevel).spread;

    let ultDmgBoost = damageBoost + ultimateBoost;
    let ultimateBaseDmg = kafkaAtk * character.ultimateAttack(ultimateLevel).multiplier;

    let resMult = (1 - (enemyRes - lightningPen));
    let defReductionStat = (1 - defReduction);
    let vulnerabilityMultiplier = (1 + lightningVulnerability);

    let followUpDmgBoost = damageBoost + followUpBoost;
    let talentBaseDmg = character.talent(talentLevel) * kafkaAtk;

    let ultimateDotBaseDmg = character.ultimateAttack(ultimateLevel).dot * kafkaAtk;

    if(eidolon >= 6) {
        ultimateDotBaseDmg = (character.ultimateAttack(ultimateLevel).dot + 1.56) * kafkaAtk;
    }

    let techDmg = kafkaAtk * 0.5;

    
    if(defReductionStat < 0) {
        defReductionStat = 0;
    }
    
    let defMult = (character.stats[level]["level"] + 20)/((enemyLevel + 20) * defReductionStat + character.stats[level]["level"] + 20);
    let breakMult = breakMultiplier;
    
    let critRateStat = critRate;
    
    if(critRateStat > 1) {
        critRateStat = 1;
    }

    let sigLCDmg = 0;
    if(lightCone && lightCone.id === "patience") {
        sigLCDmg = kafkaAtk * (0.6 + imposition * 0.1) * (damageBoost + dotBoost) * defMult * (vulnerabilityMultiplier + dotVulnerability) * resMult * breakMult;
    }
    
    if(level >= 10) {
        EHR += 0.35;
    }
    
    let maxToughnessMult = 0.5 + (enemyToughness/120);
    let breakBaseDmg = 1 * levelMult[character.stats[level]["level"]] * maxToughnessMult;
    let breakDmg = breakBaseDmg * (1 + breakEffect) * defMult * resMult * vulnerabilityMultiplier * breakMult; 
    let shockBaseDmg = 2 * levelMult[character.stats[level]["level"]];
    let shock = shockBaseDmg * (1 + breakEffect) * resMult * defMult * (vulnerabilityMultiplier + dotVulnerability) * breakMult * (damageBoost + dotBoost);

    return (
        <div className="damage-details">
            <div className='header'>Damage Details</div>
            <div className='muted-text' style={{textAlign: "center"}}>DoT Triggers assume Ult DoT and Sig LC if equipped</div>
            <div className="divider"></div>
            <div className="damage-title header">Basic Attack ({basicAttackLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Basic</td>
                        <td className='tb-text'>{((basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{(basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult * critDmgBoost).toFixed(0)}</td>
                        <td className='tb-text'>{((basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
            </table>
            <div className="damage-title header">Skill Attack ({skillLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col" className='tb-text-header'>Non-CRIT</th>
                        <th scope="col" className='tb-text-header'>CRIT</th>
                        <th scope="col" className='tb-text-header'>Average</th>

                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>DMG</td>
                        <td className='tb-text'>{(skillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult).toFixed(0)}</td>
                        <td className='tb-text'>{(skillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult * critDmgBoost).toFixed(0)}</td>
                        <td className='tb-text'>{((skillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>DMG Blast</td>
                        <td className='tb-text'>{(skillSpreadBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult).toFixed(0)}</td>
                        <td className='tb-text'>{(skillSpreadBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult * critDmgBoost).toFixed(0)}</td>
                        <td className='tb-text'>{((skillSpreadBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>DoT Trigger</td>
                        <td className='tb-text'>{(character.skillAttack(skillLevel).dotTrigger * ((ultimateDotBaseDmg * (damageBoost + dotBoost) * defMult * (vulnerabilityMultiplier + dotVulnerability) * resMult * breakMult) + sigLCDmg)).toFixed(0)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(character.skillAttack(skillLevel).dotTrigger * ((ultimateDotBaseDmg * (damageBoost + dotBoost) * defMult * (vulnerabilityMultiplier + dotVulnerability) * resMult * breakMult) + sigLCDmg)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{60}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{60}</td>
                    </tr>
                </tbody>
            </table>

        {lightCone && lightCone.id === "patience" &&
            <>
                <div className="damage-title header">Light Cone DoT</div>
                <table className="table table-striped attack-table">
                    <thead>
                        <tr>
                            <th scope="col">Source</th>
                            <th scope="col" className='tb-text-header'>DMG</th>

                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>Erode</td>
                            <td className='tb-text'>{(sigLCDmg).toFixed(0)}</td>
                        </tr>
                    </tbody>
                </table>
            </>
        }

            <div className="damage-title header">Ultimate Attack ({ultimateLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Ultimate</td>
                        <td className='tb-text'>{(ultimateBaseDmg * ultDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult).toFixed(0)}</td>
                        <td className='tb-text'>{(ultimateBaseDmg * ultDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult * (critDmgBoost + ultimateCritDmg)).toFixed(0)}</td>
                        <td className='tb-text'>{((ultimateBaseDmg * ultDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult) * (1 + (critDmg + ultimateCritDmg) * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Ult DoT</td>
                        <td className='tb-text'>{(ultimateDotBaseDmg * (damageBoost + dotBoost) * defMult * (vulnerabilityMultiplier + dotVulnerability) * resMult * breakMult).toFixed(0)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(ultimateDotBaseDmg * (damageBoost + dotBoost) * defMult * (vulnerabilityMultiplier + dotVulnerability) * resMult * breakMult).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>DoT Trigger</td>
                        <td className='tb-text'>{(character.ultimateAttack(ultimateLevel).dotTrigger * ((ultimateDotBaseDmg * (damageBoost + dotBoost) * defMult * (vulnerabilityMultiplier + dotVulnerability) * resMult * breakMult) + sigLCDmg)).toFixed(0)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(character.ultimateAttack(ultimateLevel).dotTrigger * ((ultimateDotBaseDmg * (damageBoost + dotBoost) * defMult * (vulnerabilityMultiplier + dotVulnerability) * resMult * breakMult) + sigLCDmg)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{60}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{60}</td>
                    </tr>
                    <tr>
                        <td>Chance</td>
                        <td className='tb-text'>{(1 * (1 + EHR) * (1 - 0.4) * 100).toFixed(1)}%</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(1 * (1 + EHR) * (1 - 0.4) * 100).toFixed(1)}%</td>
                    </tr>
                </tbody>
            </table>
            

            <div className="damage-title header">Talent DMG ({talentLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>DMG</td>
                        <td className='tb-text'>{((talentBaseDmg * followUpDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{((talentBaseDmg * followUpDmgBoost * defMult  * (vulnerabilityMultiplier + followUpVulnerability)* resMult * breakMult * (critDmgBoost + followUpCritDmg))).toFixed(0)}</td>
                        <td className='tb-text'>{(((talentBaseDmg * followUpDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult)) * (1 + (critDmg + followUpCritDmg) * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(10 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(10 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                    <tr>
                        <td>Chance</td>
                        <td className='tb-text'>{(1 * (1 + EHR) * (1 - 0.4) * 100).toFixed(1)}%</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(1 * (1 + EHR) * (1 - 0.4) * 100).toFixed(1)}%</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Technique</div>
            <table className="table table-striped attack-table">
                <thead>
                <tr>
                    <th scope="col"></th>
                    <th scope="col">Non-CRIT</th>
                    <th scope="col">CRIT</th>
                    <th scope="col">Average</th>
                </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>DMG</td>
                        <td className='tb-text'>{((techDmg * damageBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{((techDmg * damageBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((techDmg * damageBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Chance</td>
                        <td className='tb-text'>{(1 * (1 + EHR) * (1 - 0.4) * 100).toFixed(1)}%</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(1 * (1 + EHR) * (1 - 0.4) * 100).toFixed(1)}%</td>
                    </tr>
                </tbody>
            </table>


            <div className="damage-title header">Weakness Break</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Type</th>
                        <th scope="col">Value</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Break Damage</td>
                        <td className='tb-text'>{(breakDmg).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Shock DMG</td>
                        <td className='tb-text'>{(shock).toFixed(0)}</td>
                    </tr>
                </tbody>
            </table>
    </div>
    )
}


export default KafkaTable;