class Welt {
    constructor() {
        this.stats = [
            {level: 1,  atk: 84.48,  def: 69.3,   hp: 153, spd: 102, taunt: 100, energy: 120},
            {level: 20, atk: 164.74, def: 135.14, hp: 299, spd: 102, taunt: 100, energy: 120, atkPercent: 0.04},
            {level: 20, atk: 198.53, def: 162.86, hp: 360, spd: 102, taunt: 100, energy: 120, atkPercent: 0.04},
            {level: 40, atk: 316.8,  def: 259.88, hp: 574, spd: 102, taunt: 100, energy: 120, atkPercent: 0.04, imaginaryDmg: 0.032},
            {level: 40, atk: 350.59, def: 287.6,  hp: 635, spd: 102, taunt: 100, energy: 120, atkPercent: 0.08, imaginaryDmg: 0.032, effectRes: 0.04},
            {level: 50, atk: 392.83, def: 322.25, hp: 712, spd: 102, taunt: 100, energy: 120, atkPercent: 0.08, imaginaryDmg: 0.032, effectRes: 0.04},
            {level: 50, atk: 426.62, def: 349.97, hp: 773, spd: 102, taunt: 100, energy: 120, atkPercent: 0.14, imaginaryDmg: 0.032, effectRes: 0.04},
            {level: 60, atk: 468.86, def: 384.62, hp: 850, spd: 102, taunt: 100, energy: 120, atkPercent: 0.14, imaginaryDmg: 0.032, effectRes: 0.04},
            {level: 60, atk: 502.66, def: 412.34, hp: 911, spd: 102, taunt: 100, energy: 120, atkPercent: 0.2,  imaginaryDmg: 0.08,  effectRes: 0.04},
            {level: 70, atk: 544.9,  def: 446.99, hp: 988, spd: 102, taunt: 100, energy: 120, atkPercent: 0.2,  imaginaryDmg: 0.08,  effectRes: 0.04},
            {level: 70, atk: 578.69, def: 474.71, hp: 1049,spd: 102, taunt: 100, energy: 120, atkPercent: 0.2,  imaginaryDmg: 0.08,  effectRes: 0.1},
            {level: 80, atk: 620.93, def: 509.36, hp: 1125,spd: 102, taunt: 100, energy: 120, atkPercent: 0.28, imaginaryDmg: 0.144, effectRes: 0.1},
        ];

        this.name = "Welt";
        this.path = "Nihility";
        this.rarity = 'fiveStar';
        this.element = "Imaginary";
        this.id = "welt";
        this.E1ON = false;
        this.talentON = false;
    }

    setE1ON(value) {
        this.E1ON = value;
    }

    setTalentON(value) {
        this.talentON = value;
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Gravity Suppression",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return {
                multiplier: 0.36 + (level * 0.036),
                chance: 0.65 + (level * 0.01),
            }
        }else if(level < 10){
            return {
                multiplier: 0.54 + ((level - 5) * 0.045),
                chance: 0.70 + ((level - 5) * 0.0125),
            }
        }else {
            return {
                multiplier: 0.72 + ((level - 9) * 0.036),
                chance: 0.75 + ((level - 9) * 0.01),
            }
        }
    }

    skillDescription(level) {
        return {
            name: "Edge of the Void",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level).multiplier * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy, and further deals DMG for 2 extra time(s), with each time dealing </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level).multiplier * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a random enemy. </div>
                    <br/>
                    <div> On hit, there is a </div>
                    <div className="buff-text">{(this.skillAttack(level).chance * 100).toFixed(1)}%</div>
                    <div> reduce the enemy's </div>
                    <div className="buff-text">SPD</div>
                    <div> by </div>
                    <div className="buff-text">10%</div>
                    <div> for 2 turn(s). </div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level < 6) {
            return {
                multiplier: 0.9 + (level * 0.06),
                delay: 0.32 + (level * 0.008),
            }
        }else if(level < 10){
            return {
                multiplier: 1.2 + ((level - 5) * 0.075),
                delay: 0.36 + ((level - 5) * 0.01),
            }    
        }else {
            return {
                multiplier: 1.5 + ((level - 9) * 0.06),
                delay: 0.4 + ((level - 9) * 0.008),
            }
        }
    }

    ultimateDescription(level) {
        return {
            name: "Synthetic Black Hole",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).multiplier * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to all enemies, with a  </div>
                    <div className="buff-text"> 100% base chance </div>
                    <div>  for enemies hit by this ability to be Imprisoned for 1 turn. Imprisoned enemies have their </div>
                    <div className="buff-text"> actions delayed </div>
                    <div> by </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).delay * 100).toFixed(1)}% </div>
                    <div> and </div>
                    <div className="buff-text"> SPD </div>
                    <div> reduced by </div>
                    <div className="buff-text"> 10% </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 0.3 + (level * 0.03);
        }else if(level < 10){
            return 0.45 + ((level - 5) * 0.0375);
        }else {
            return 0.6 + ((level - 9) * 0.03);
        }
    }

    talentDescription(level) { 
        return {
            name: "Time Distortion",
            description: (
                <div className="buff-description">
                    <div>When hitting an enemy that is already Slowed, Welt deals </div>
                    <div className={this.element}> additional {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.talent(level) * 100).toFixed(1)}% </div>
                    <div> of his </div>
                    <div className="buff-text"> ATK </div>
                    <div> to the enemy. </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Gravitational Imprisonment",
            description: (
                <div className="buff-description">
                    <div> After using Welt's Technique, create a dimension that lasts for 15 second(s). Enemies in this dimension have their Movement SPD reduced by 50%. After entering battle with enemies in the dimension, there is a 100% base chance to Imprison the enemies for 1 turn. </div>
                    <br/>
                    <div> Imprisoned enemies have their actions delayed by 20% and SPD reduced by 10%. Only 1 dimension created by allies can exist at the same time. </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Retribution",
            description: (
                <div className="buff-description">
                    <div> When using Ultimate, there is a 100% base chance to increase the </div>
                    <div className="buff-text"> DMG received </div>
                    <div>  by the targets by </div>
                    <div className="buff-text"> 12% </div>
                    <div> for 2 turn(s). </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Judgment",
            description: (
                <div className="buff-description">
                    <div> Using Ultimate additionally regenerates 10 Energy. </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Punishment",
            description: (
                <div className="buff-description">
                    <div> Deals 20% more DMG to enemies inflicted with Weakness Break. </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Legacy of Honor",
            description: (
                <div className="buff-description">
                    <div> After Welt uses his Ultimate, his abilities are enhanced. The next 2 time(s) he uses his Basic ATK or Skill, deals Additional DMG to the target equal to 50% of his Basic ATK's DMG multiplier or 80% of his Skill's DMG multiplier respectively. </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Conflux of Stars",
            description: (
                <div className="buff-description">
                    <div> When his Talent is triggered, Welt regenerates 3 Energy. </div>
                </div>
            )
        }
    }

    E3() {
        return {
            name: "Prayer of Peace",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }

    E4() {
        return {
            name: "Appellation of Justice",
            description:(
                <div className="buff-description">
                    <div>Base chance for Skill to inflict SPD Reduction increases by 35% </div>
                </div>
            )
        }
    }

    E5() {
        return {
            name: "Power of Kindness",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: "Prospect of Glory",
            description: (
                <div className="buff-description">
                    <div>When using Skill, deals DMG for 1 extra time to a random enemy. </div>
                </div>
            ),
        }
    }
}


export default Welt;