class Topaz {
    constructor() {
        this.stats = [
            {level: 1,  atk: 84.48,  def: 56.1,   hp: 127, spd: 110, taunt: 75, energy: 130},
            {level: 20, atk: 164.74, def: 109.4,  hp: 247, spd: 110, taunt: 75, energy: 130, fireDmg: 0.032},
            {level: 20, atk: 198.53, def: 131.84, hp: 298, spd: 110, taunt: 75, energy: 130, fireDmg: 0.032},
            {level: 40, atk: 316.8,  def: 210.38, hp: 475, spd: 110, taunt: 75, energy: 130, fireDmg: 0.032, critRate: 0.027},
            {level: 40, atk: 350.59, def: 232.82, hp: 526, spd: 110, taunt: 75, energy: 130, fireDmg: 0.064, critRate: 0.027, hpPercent: 0.04},
            {level: 50, atk: 392.83, def: 260.87, hp: 589, spd: 110, taunt: 75, energy: 130, fireDmg: 0.064, critRate: 0.027, hpPercent: 0.04},
            {level: 50, atk: 426.62, def: 283.31, hp: 640, spd: 110, taunt: 75, energy: 130, fireDmg: 0.112, critRate: 0.027, hpPercent: 0.04},
            {level: 60, atk: 468.86, def: 311.36, hp: 703, spd: 110, taunt: 75, energy: 130, fireDmg: 0.112, critRate: 0.027, hpPercent: 0.04},
            {level: 60, atk: 502.66, def: 333.8,  hp: 754, spd: 110, taunt: 75, energy: 130, fireDmg: 0.16,  critRate: 0.067, hpPercent: 0.04},
            {level: 70, atk: 544.9,  def: 361.85, hp: 817, spd: 110, taunt: 75, energy: 130, fireDmg: 0.16,  critRate: 0.067, hpPercent: 0.04},
            {level: 70, atk: 578.69, def: 384.29, hp: 868, spd: 110, taunt: 75, energy: 130, fireDmg: 0.16,  critRate: 0.067, hpPercent: 0.1},
            {level: 80, atk: 620.93, def: 412.34, hp: 931, spd: 110, taunt: 75, energy: 130, fireDmg: 0.224, critRate: 0.12,  hpPercent: 0.1},
        ];

        this.name = "Topaz and Numby";
        this.path = "Hunt";
        this.rarity = 'fiveStar';
        this.element = "Fire";
        this.isBeta = true;
        this.id = "topaz";
        this.ultimateOn = false;
    }

    setUlitmateOn(value) {
        this.ultimateOn = value;
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Deficit...",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return {
                vulnerability: 0.25 + (level * 0.025),
                multiplier: 0.75 + (level * 0.075),
            }
        }else if(level < 10){
            return {
                vulnerability: 0.375 + ((level - 5) * 0.0313),
                multiplier: 1.125 + ((level - 5) * 0.0938),
            }
        }else {
            return {
                vulnerability: 0.5 + ((level - 9) * 0.025),
                multiplier: 1.5 + ((level - 9) * 0.075),
            }
        }
    }

    skillDescription(level) {
        return {
            name: "Difficulty Paying?",
            description: (
                <div className="buff-description">
                    <div>Causes a single enemy to enter the Proof of Debt state and increases the target's </div>
                    <div className="buff-text">DMG received from follow-up attacks</div>
                    <div> by </div>
                    <div className="buff-text">{(this.skillAttack(level).vulnerability * 100).toFixed(1)}%.</div>
                    <br/>
                    <div> Proof of Debt only takes effect on the most recent target it is applied to. </div>
                    <br/>
                    <div> During allies' action, if there are no enemies with Proof of Debt on the field, {this.name} will cause a random enemy to enter Proof of Debt. </div>
                    <br/>
                    <div> Numby deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level).multiplier * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to this target. Using this Skill to deal DMG is considered as launching a </div>
                    <div className="buff-text">Follow-up Attack.</div>
                </div>
            ),
        }
    }

    skillSimple(level) {
        return {
            name: "Difficulty Paying?",
            description: (
                <div className="buff-description">
                    <div>Increases the target's </div>
                    <div className="buff-text">DMG received from follow-up attacks</div>
                    <div> by </div>
                    <div className="buff-text">{(this.skillAttack(level).vulnerability * 100).toFixed(1)}%.</div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level < 6) {
            return {
                multiplier: 0.75 + (level * 0.075),
                critDmg: 0.125 + (level * 0.0125),
            }
        }else if(level < 10){
            return {
                multiplier: 1.125 + ((level - 5) * 0.0938),
                critDmg: 0.1875 + ((level - 5) * 0.0156),
            }
        }else {
            return {
                multiplier: 1.5 + ((level - 9) * 0.075),
                critDmg: 0.25 + ((level - 9) * 0.0125),
            }
        }
    }

    ultimateDescription(level) {
        return {
            name: "Turn a Profit!",
            description: (
                <div className="buff-description">
                    <div>Numby enters the Windfall Bonanza! state and its </div>
                    <div className="buff-text">DMG multiplier</div>
                    <div> increases by </div>
                    <div className="buff-text">{(this.ultimateAttack(level).multiplier * 100).toFixed(1)}%</div>
                    <div> and </div>
                    <div className="buff-text">Crit DMG</div>
                    <div> increases by </div>
                    <div className="buff-text">{(this.ultimateAttack(level).critDmg * 100).toFixed(1)}%.</div>
                    <br/>
                    <div>Also, when enemies with Proof of Debt receive attacks from Basic ATK, Skill, or Ultimate, Numby's action is Advanced Forward by 50%. After Numby uses 2 attacks, exit the Windfall Bonanza! state. </div>
                </div>
            ),
        }
    }

    ultimateSimple(level) {
        return {
            name: "Turn a Profit!",
            description: (
                <div className="buff-description">
                    <div>Numby enters the Windfall Bonanza! state and its </div>
                    <div className="buff-text">DMG multiplier</div>
                    <div> increases by </div>
                    <div className="buff-text">{(this.ultimateAttack(level).multiplier * 100).toFixed(1)}%</div>
                    <div> and </div>
                    <div className="buff-text">Crit DMG</div>
                    <div> increases by </div>
                    <div className="buff-text">{(this.ultimateAttack(level).critDmg * 100).toFixed(1)}%.</div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return  0.75 + (level * 0.075);
        }else if(level < 10){
            return 1.125 + ((level - 5) * 0.0938);
        }else {
            return 1.5 + ((level - 9) * 0.075);
        }
    }

    talentDescription(level) { 
        return {
            name: "Trotter Market!?",
            description: (
                <div className="buff-description">
                    <div>Summons Numby at the start of battle.</div>
                    <br/>
                    <div>Numby has 80 SPD by default.</div>
                    <br/>
                    <div>When taking action, Numby launches follow-up attacks on the single enemy target afflicted with Proof of Debt, dealing </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.talent(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK.</div>
                    <br/>
                    <div>When enemies afflicted with Proof of Debt receive follow-up attacks, Numby's action is Advanced Forward by 50%. </div>
                    <br/>
                    <div>The action Advance Forward effect cannot be triggered during Numby's own turn. </div>
                    <br/>
                    <div>When Topaz is downed, Numby disappears. </div>
                </div>
            ),
        }
    }

    talentSimple(level) {
        return {
            name: "Celestial Jade",
            description: (
                <div className="buff-description">
                    <div>Qingque's </div>
                    <div className="buff-text"> ATK </div>
                    <div> increases by </div>
                    <div className="buff-text"> {(this.talent(level) * 100).toFixed(1)}% </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Explicit Subsidy",
            description: (
                <div className="buff-description">
                    <div> Summons Numby when Topaz enters the overworld. Numby will automatically search for Basic Treasures and Trotters in a set radius. </div>
                    <br/>
                    <div> Using her Technique causes Topaz to regenerate 60 Energy after Numby's first attack in the next battle. </div>
                    <br/>
                    <div> After using her Technique, if Topaz is still in the team when enemies in the overworld are defeated, additionally receive a small amount of credits when obtaining credits. </div>
                    <br/>
                    <div> Only 10000 additional credits can be received every calendar day. </div>
                    <br/>
                    <div> After using her Technique and defeating enemies in Simulated Universe additionally receive a small amount of Cosmic Fragments, with a small chance to obtain 1 random Curio. </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Overdraft",
            description: (
                <div className="buff-description">
                    <div>When Topaz uses Basic ATK to deal DMG, it will be considered as a follow-up attack.</div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Financial Turmoil",
            description: (
                <div className="buff-description">
                    <div> Increases Topaz and Numby's </div>
                    <div className="buff-text"> DMG Dealt </div>
                    <div> to enemy targets with </div>
                    <div className="Fire"> Fire Weakness </div>
                    <div> by </div>
                    <div className="buff-text"> 15%.</div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Technical Adjustment",
            description: (
                <div className="buff-description">
                    <div> After Numby uses an attack while in the Windfall Bonanza! state, Topaz additionally regenerates 10 Energy. </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Future Market",
            description: (
                <div className="buff-description">
                    <div> When enemies afflicted with Proof of Debt receive follow-up attacks, they will enter the Debtor state. This can only take effect 1 time in a single action. Debtor increases CRIT DMG from follow-up attacks dealt to affected enemies by 25%, stacking up to 2 time(s). When Proof of Debt is removed, Debtor is also removed. </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Bona Fide Acquisition",
            description: (
                <div className="buff-description">
                    <div> After Numby takes action and launches an attack, Topaz regenerates 5 Energy. </div>
                </div>
            )
        }
    }

    E3() {
        return {
            name: "Seize the Big and Free the Small",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }

    E4() {
        return {
            name: "Agile Operation",
            description:(
                <div className="buff-description">
                    <div>After Numby's turn begins, Topaz's action is Advanced Forward by 20%.</div>
                </div>
            )
        }
    }
    
    E5() {
        return {
            name: "Inflationary Demand",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }
    
    E6() {
        return {
            name: "Incentive Mechanism",
            description: (
                <div className="buff-description">
                    <div>Numby's attack count while in the Windfall Bonanza!! state increases by 1, and its Fire RES PEN increases by 10% during its attacks.</div>
                </div>
            ),
        }
    }
}


export default Topaz;