export class Shared {
    constructor() {
        this.stats = [
            [1,  19.2,   18,     43],
            [20, 73.92,  69.3,  166],
            [20, 96.96,  90.9,  218],
            [40, 185.28, 173.7, 417],
            [40, 216,    202.5, 486],
            [50, 244.8,  229.5, 551],
            [50, 275.52, 258.3, 620],
            [60, 304.32, 285.3, 685],
            [60, 335.04, 314.1, 754],
            [70, 363.84, 341.1, 819],
            [70, 394.56, 369.9, 888],
            [80, 423.36, 396.9, 953],
        ];
        
        this.name = "Shared Feeling";
        this.path = "Abundance";
        this.id = "shared";
        this.rarity = "fourStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text"> Outgoing Healing </div> 
                <div> by </div> 
                <div className="buff-text">{((0.10 + imposition * 0.025) * 100).toFixed(1)}%.</div>
                <div> When using Skill </div> 
                <div className="buff-text"> regenerates {2 + imposition * 1} Energy </div>
                <div> for all allies. </div> 
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{
                OGH: 0.1 + imposition * 0.025,
            },
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                // id: "adve1",
                // name: this.name,
                // description: (
                //     <div className="buff-description">
                //         <div> increases Outgoing Healing when they use their Ultimate by </div> 
                //         <div className="buff-text">{((0.12 + imposition * 0.03) * 100).toFixed(1)}%.</div>
                //     </div>
                // ),
                // effect: {
                //     ultimateOGH: 0.12 + imposition * 0.03,
                // },
                // type: "buff",
                // hasStack: false,
                // hasInput: false,
            },
        }
    }
}

export default Shared;