export class Unreachable {
    constructor() {
        this.stats = [
            [1,  26.4,    15,    58],
            [20, 101.64,  57.75, 222],
            [20, 133.32, 75.75,  291],
            [40, 254.76, 144.75, 556],
            [40, 297,    168.75, 648],
            [50, 336.6,  191.25, 734],
            [50, 378.84, 215.25, 827],
            [60, 418.44, 237.75, 913],
            [60, 460.68, 261.75, 1005],
            [70, 500.28, 284.25, 1092],
            [70, 542.52, 308.25, 1184],
            [80, 582.12, 330.75, 1270],
        ];
        
        this.name = "The Unreachable Side";
        this.path = "Destruction";
        this.id = "unreachable";
        this.rarity = "fiveStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text"> CRIT Rate </div> 
                <div> by </div> 
                <div className="buff-text">{((0.18 + imposition * 0.03) * 100).toFixed(1)}%.</div>
                <div> and increases their </div>
                <div className="buff-text">Max HP</div>
                <div>  by </div>
                <div className="buff-text"> {((0.18 + imposition * 0.03) * 100).toFixed(1)}%.</div> 
                <div> When the wearer is attacked or consumes their own HP, their DMG increases by </div> 
                <div className="buff-text"> {((0.24 + imposition * 0.04) * 100).toFixed(1)}%.</div> 
                <div> This effect is removed after the wearer uses an attack.  </div> 
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "silenceb",
            name: this.name,
            effect:{
                critRate: 0.18 + imposition * 0.03,
                hpPercent: 0.18 + imposition * 0.03,
            },
            type: "buff",
            hasStack: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "silence0",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> When the wearer is attacked or consumes their own HP, their DMG increases by </div> 
                        <div className="buff-text"> {((0.24 + imposition * 0.04) * 100).toFixed(1)}%.</div> 
                        <div> This effect is removed after the wearer uses an attack.  </div>   
                    </div>
                ),
                effect: {
                    iceDmg: 0.24 + imposition * 0.04,
                    quantumDmg: 0.24 + imposition * 0.04,
                    fireDmg: 0.24 + imposition * 0.04,
                    lightningDmg: 0.24 + imposition * 0.04,
                    windDmg: 0.24 + imposition * 0.04,
                    physicalDmg: 0.24 + imposition * 0.04,
                    imaginaryDmg: 0.24 + imposition * 0.04,
                },
                type: "buff",
                hasStack: false,
                hasInput: false,
            },
        }
    }
}

export default Unreachable;