export class OwnSword {
    constructor() {
        this.stats = [
            [1,  26.4,   18,     53],
            [20, 101.64, 69.3,  203],
            [20, 133.32, 90.9,  267],
            [40, 254.76, 173.7, 510],
            [40, 297,    202.5, 594],
            [50, 336.6,  229.5, 673],
            [50, 378.84, 258.3, 758],
            [60, 418.44, 285.3, 837],
            [60, 460.68, 314.1, 921],
            [70, 500.28, 341.1, 1001],
            [70, 542.52, 369.9, 1085],
            [80, 582.12, 396.9, 1164],
        ];
        
        this.name = "I Shall Be My Own Sword";
        this.path = "Destruction";
        this.id = "ownsword";
        this.isBeta = true;
        this.rarity = "fiveStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text"> CRIT DMG </div> 
                <div> by </div> 
                <div className="buff-text">{((0.2 + imposition * 0.03) * 100).toFixed(1)}%.</div>
                <div> When ally gets attacked or loses HP, wearer gains 1 stack of Eclipse, up to a max of 3 stack(s). Each stack of Eclipse will increase the </div>
                <div className="buff-text"> DMG </div>
                <div> of the wearer's next attack by </div>
                <div className="buff-text"> {((0.14 + imposition * 0.025) * 100).toFixed(1)}%. </div>
                <div> When 3 stack(s) are maxed out, an additional attack will be delivered that ignores </div>
                <div className="buff-text">{((0.12 + imposition * 0.02) * 100).toFixed(1)}%.</div>
                <div> of the enemy's DEF. This effect will be dispeled after the wearer delivers their attack. </div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: this.id + "b",
            name: this.name,
            effect:{
                critDmg: 0.2 + imposition * 0.03,
            },
            type: "buff",
            hasStack: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition, stack) {
        return {
            0: {
                id: this.id + "0",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> When ally gets attacked or loses HP, wearer gains 1 stack of Eclipse, up to a max of 3 stack(s). Each stack of Eclipse will increase the </div>
                        <div className="buff-text"> DMG </div>
                        <div> of the wearer's next attack by </div>
                        <div className="buff-text"> {((0.14 + imposition * 0.025) * 100).toFixed(1)}%. </div>
                    </div>
                ),
                effect: {
                    quantumDmg: (0.14 + imposition * 0.025) * stack,
                    iceDmg: (0.14 + imposition * 0.025) * stack,
                    fireDmg: (0.14 + imposition * 0.025) * stack,
                    windDmg: (0.14 + imposition * 0.025) * stack,
                    lightningDmg: (0.14 + imposition * 0.025) * stack,
                    imaginaryDmg: (0.14 + imposition * 0.025) * stack,
                    physicalDmg: (0.14 + imposition * 0.025) * stack,
                },
                type: "buff",
                hasStack: true,
                stacks: 3,
            },
            1: {
                id: this.id + "1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> When 3 stack(s) are maxed out, an additional attack will be delivered that ignores </div>
                        <div className="buff-text">{((0.12 + imposition * 0.02) * 100).toFixed(1)}%.</div>
                        <div> of the enemy's DEF. This effect will be dispeled after the wearer delivers their attack. </div>
                    </div>
                ),
                effect: {
                    defIgnore: 0.12 + imposition * 0.02,
                },
                type: "debuff",
            },
        }
    }
}

export default OwnSword;