export class Arrows {
    constructor() {
        this.stats = [
            [1, 14.4, 12, 38],
            [20, 55.44, 46.2, 148],
            [20, 72.72, 60.6, 194],
            [40,138.96,115.8,371],
            [40,162,236.135,432],
            [50,183.6,267.153,490],
            [50,206.64,172.2,551],
            [60,228.24,190.2,609],
            [60,251.28,209.4,670],
            [70,272.88,227.4,728],
            [70,295.92,246.6,789],
            [80,317.52,264.6,847],
        ];
        
        this.name = "Arrows";
        this.path = "Hunt";
        this.id = "arrows";
        this.rarity = "threeStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>At the start of the battle, the wearer's </div>
                <div className="buff-text">CRIT Rate</div> 
                <div> increases by </div> 
                <div className="buff-text">{((0.12 + imposition * 0.03) * 100).toFixed(1)}%</div>
                <div> for </div>
                <div className="buff-text">2 turn(s).</div> 
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "arrowsb",
            name: this.name,
            effect:{},
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "arrowse1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div>At the start of the battle, the wearer's </div>
                        <div className="buff-text">CRIT Rate</div> 
                        <div> increases by </div> 
                        <div className="buff-text">{((0.12 + imposition * 0.03) * 100).toFixed(1)}%</div>
                        <div> for </div>
                        <div className="buff-text">2 turn(s).</div> 
                    </div>
                ),
                effect: {
                    critRate: 0.12 + imposition * 0.03,
                },
                type: "buff",
                hasStacks: false,
                hasInput: false,
            },
        }
    }
}

export default Arrows;