import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';
import { Tingyun } from "../../../../../../../database/characters";

const TingyunBuffs = () => {

    const {
        level, character, eidolon, skillLevel, ultimateLevel, characterBuffStates, setCharacterBuffStates, updateStatesFromJson 
    } = useContext(globalData);

    const tingyun = new Tingyun();

    const [prevSkillLevel, setPrevSkillLevel] = useState(skillLevel);
    const [prevUltimateLevel, setPrevUltimateLevel] = useState(ultimateLevel);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];

        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }
    
    const skillBuff = {
        effect: {
            atkPercent: character.skillAttack(skillLevel).atkPercent,
        },
    }

    useEffect(() => {
        if(characterBuffStates[0]) {
            const tempSkillBuff = {
                effect: {
                    atkPercent: tingyun.skillAttack(prevSkillLevel).atkPercent,
                },
            }

            updateStatesFromJson(tempSkillBuff, true);
            updateStatesFromJson(skillBuff, false);
        }
        
        setPrevSkillLevel(skillLevel);
    }, [skillLevel]);

    const ultimateBuff = {
        effect: {
            iceDmg: tingyun.ultimateAttack(ultimateLevel),
            quantumDmg: tingyun.ultimateAttack(ultimateLevel),
            fireDmg: tingyun.ultimateAttack(ultimateLevel),
            lightningDmg: tingyun.ultimateAttack(ultimateLevel),
            windDmg: tingyun.ultimateAttack(ultimateLevel),
            physicalDmg: tingyun.ultimateAttack(ultimateLevel),
            imaginaryDmg: tingyun.ultimateAttack(ultimateLevel),
        }
    }

    useEffect(() => {
        if(characterBuffStates[1]) {
            const tempUltBuff = {
                effect: {
                    iceDmg: tingyun.ultimateAttack(prevUltimateLevel),
                    quantumDmg: tingyun.ultimateAttack(prevUltimateLevel),
                    fireDmg: tingyun.ultimateAttack(prevUltimateLevel),
                    lightningDmg: tingyun.ultimateAttack(prevUltimateLevel),
                    windDmg: tingyun.ultimateAttack(prevUltimateLevel),
                    physicalDmg: tingyun.ultimateAttack(prevUltimateLevel),
                    imaginaryDmg: tingyun.ultimateAttack(prevUltimateLevel),
                },
            }
            updateStatesFromJson(tempUltBuff, true);

            const newTempUltBuff = {
                effect: {
                    iceDmg: tingyun.ultimateAttack(ultimateLevel),
                    quantumDmg: tingyun.ultimateAttack(ultimateLevel),
                    fireDmg: tingyun.ultimateAttack(ultimateLevel),
                    lightningDmg: tingyun.ultimateAttack(ultimateLevel),
                    windDmg: tingyun.ultimateAttack(ultimateLevel),
                    physicalDmg: tingyun.ultimateAttack(ultimateLevel),
                    imaginaryDmg: tingyun.ultimateAttack(ultimateLevel),
                },
            }
            updateStatesFromJson(newTempUltBuff, false);
        }
        
        setPrevUltimateLevel(ultimateLevel);
    }, [ultimateLevel]);


    const A2Buff = {
        effect: {
            spdPercent: 0.2,
        },
    }

    const A4Buff = {
        effect: {
            basicBoost: 0.4,
        },
    }


    const E1Buff = {
        effect: {
            spdPercent: 0.1,
        },
    }

    useEffect(() => {

        if(eidolon < 1 && characterBuffStates[4]) {
            updateStatesFromJson(E1Buff, true);
            characterBuffStates[4] = false;
        }

        if(eidolon < 4 && characterBuffStates[5]) {
            character.setE4On(false);
            characterBuffStates[5] = false;
        }
    }, [eidolon]);

    useEffect(() => {
        
        if(level < 3 && characterBuffStates[2]) {
            updateStatesFromJson(A2Buff, true);
            characterBuffStates[2] = false;
        }

    }, [level]);

    const turnE4On = (index) => {
        const updatedStates = [...characterBuffStates];
        updatedStates[index] = !updatedStates[index];
        character.setE4On(updatedStates[index]);
        setCharacterBuffStates(updatedStates);
    }

    const applySkillBuff = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        
        setCharacterBuffStates(updatedStates);
    }

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => applySkillBuff(0, skillBuff)} />}
            label={tingyun.skillDescription(skillLevel).name}
            />
            <div>{tingyun.skillDescription(skillLevel).description}</div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, ultimateBuff)} />}
            label={tingyun.ultimateDescription(ultimateLevel).name}
            />
            <div>{tingyun.ultimateDescription(ultimateLevel).description}</div>

            {level >= 3 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, A2Buff)} />}
                    label={"(A2) " + tingyun.A2().name}
                    />
                    <div>{tingyun.A2().description}</div>
                </>
            }

            {level >= 6 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, A4Buff)} />}
                    label={"(A4) " + tingyun.A4().name}
                    />
                    <div>{tingyun.A4().description}</div>
                </>
            }

            {eidolon >= 1 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[4] || false} onChange={() => applyCharacterBuffs(4, E1Buff)} />}
                    label={"(E1) " + tingyun.E1().name}
                    />
                    <div>{tingyun.E1().description}</div>
                </>
            }

            {eidolon >= 4 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[5] || false} onChange={() => turnE4On(5)} />}
                    label={"(E4) " + tingyun.E4().name}
                    />
                    <div>{tingyun.E4().description}</div>
                </>
            }
        </>
    )
}

export const TingyunGlobalBuffs = (props) => {
    
    const tingyun = new Tingyun();

    const {
        character,
        lightningDmg,
        atk,
        atkPercent,
        atkFlat,
        level,
        enemyLevel,
        lightningPen,
        defReduction,
        enemyRes,
        breakMultiplier,
        lightningVulnerability,
        updateStatesFromJson,
    } = useContext(globalData);

    let activeCharacterAtk = (atk * atkPercent) + atkFlat;
    let damageBoost = (1 + lightningDmg);
    let resMult = (1 - (enemyRes - lightningPen));
    let defReductionStat = (1 - defReduction);
    let vulnerabilityMultiplier = (1 + lightningVulnerability);

    if(defReductionStat < 0) {
        defReductionStat = 0;
    }

    let defMult = (character.stats[level]["level"] + 20)/((enemyLevel + 20) * defReductionStat + character.stats[level]["level"] + 20);
    let breakMult = breakMultiplier;

    const [skillLevel, setSkillLevel] = useState(0);
    const [ultimateLevel, setUltimateLevel] = useState(0);

    const tingyunBuffStates = props.buffStates;
    const setTingyunBuffStates = props.setBuffStates;
    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    const [prevSkillLevel, setPrevSkillLevel] = useState(skillLevel);
    const [prevUltimateLevel, setPrevUltimateLevel] = useState(ultimateLevel);


    const updateUltLevel = (event) => {
        setUltimateLevel(event.target.value);
    }

    const updateSkillLevel = (event) => {
        setSkillLevel(event.target.value);
    }

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...tingyunBuffStates];

        if(index === 0 && !tingyunBuffStates[0]) {
            const skillBuff = {
                effect: {
                    atkPercent: tingyun.skillAttack(skillLevel).atkPercent,
                    additionalDmg: (tingyun.skillAttack(skillLevel).additionalDmg) * ((atk * (atkPercent + tingyun.skillAttack(skillLevel).atkPercent)) + atkFlat) * damageBoost * resMult * defMult * breakMult * vulnerabilityMultiplier,
                },
            }
            
            updateStatesFromJson(skillBuff, updatedStates[index]);
        }else if(index === 0 && tingyunBuffStates[0]) {
            const skillBuff = {
                effect: {
                    atkPercent: tingyun.skillAttack(skillLevel).atkPercent,
                    additionalDmg: (tingyun.skillAttack(skillLevel).additionalDmg) * (atk * atkPercent + atkFlat) * damageBoost * resMult * defMult * breakMult * vulnerabilityMultiplier,
                },
            }
            
            updateStatesFromJson(skillBuff, updatedStates[index]);
        }else {
            updateStatesFromJson(buffValue, updatedStates[index]);
        }

        updatedStates[index] = !updatedStates[index];
        setTingyunBuffStates(updatedStates);
    }
    
    const skillBuff = {
        effect: {
            atkPercent: tingyun.skillAttack(skillLevel).atkPercent,
            additionalDmg: (tingyun.skillAttack(skillLevel).additionalDmg) * activeCharacterAtk * damageBoost * resMult * defMult * breakMult * vulnerabilityMultiplier,
        },
    }

    useEffect(() => {
        if(tingyunBuffStates[0]) {
            const tempSkillBuff = {
                effect: {
                    atkPercent: tingyun.skillAttack(prevSkillLevel).atkPercent,
                    additionalDmg: (tingyun.skillAttack(prevSkillLevel).additionalDmg) * activeCharacterAtk * damageBoost * resMult * defMult * breakMult * vulnerabilityMultiplier,
                },
            }

            updateStatesFromJson(tempSkillBuff, true);
            updateStatesFromJson(skillBuff, false);
        }
        
        setPrevSkillLevel(skillLevel);
    }, [skillLevel]);

    const ultimateBuff = {
        effect: {
            iceDmg: tingyun.ultimateAttack(ultimateLevel),
            quantumDmg: tingyun.ultimateAttack(ultimateLevel),
            fireDmg: tingyun.ultimateAttack(ultimateLevel),
            lightningDmg: tingyun.ultimateAttack(ultimateLevel),
            windDmg: tingyun.ultimateAttack(ultimateLevel),
            physicalDmg: tingyun.ultimateAttack(ultimateLevel),
            imaginaryDmg: tingyun.ultimateAttack(ultimateLevel),
        }
    }

    useEffect(() => {
        if(tingyunBuffStates[1]) {
            const tempUltBuff = {
                effect: {
                    iceDmg: tingyun.ultimateAttack(prevUltimateLevel),
                    quantumDmg: tingyun.ultimateAttack(prevUltimateLevel),
                    fireDmg: tingyun.ultimateAttack(prevUltimateLevel),
                    lightningDmg: tingyun.ultimateAttack(prevUltimateLevel),
                    windDmg: tingyun.ultimateAttack(prevUltimateLevel),
                    physicalDmg: tingyun.ultimateAttack(prevUltimateLevel),
                    imaginaryDmg: tingyun.ultimateAttack(prevUltimateLevel),
                },
            }
            updateStatesFromJson(tempUltBuff, true);

            const newTempUltBuff = {
                effect: {
                    iceDmg: tingyun.ultimateAttack(ultimateLevel),
                    quantumDmg: tingyun.ultimateAttack(ultimateLevel),
                    fireDmg: tingyun.ultimateAttack(ultimateLevel),
                    lightningDmg: tingyun.ultimateAttack(ultimateLevel),
                    windDmg: tingyun.ultimateAttack(ultimateLevel),
                    physicalDmg: tingyun.ultimateAttack(ultimateLevel),
                    imaginaryDmg: tingyun.ultimateAttack(ultimateLevel),
                },
            }
            updateStatesFromJson(newTempUltBuff, false);
        }
        
        setPrevUltimateLevel(ultimateLevel);
    }, [ultimateLevel]);

    const E1Buff = {
        effect: {
            spdPercent: 0.1,
        },
    }

    useEffect(() => {
        if(tingyunBuffStates[0]) {
            updateStatesFromJson(skillBuff, true);
            tingyunBuffStates[0] = false;
        }

        if(tingyunBuffStates[1]) {
            updateStatesFromJson(ultimateBuff, true);
            tingyunBuffStates[1] = false;
        }

        if(tingyunBuffStates[3]) {
            updateStatesFromJson(E1Buff, true);
            tingyunBuffStates[3] = false;
        }

        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={tingyunBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, skillBuff)} />}
            label={tingyun.skillDescription(skillLevel).name}
            />
            <div className="mb-3">{tingyun.skillSimple(skillLevel).description}</div>
            <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                <div style={{display: "flex"}}>
                    <div>Skill Level</div>
                    <select className="levels buff-input" style={{width: "45%"}} value={skillLevel} onChange={updateSkillLevel}>
                        <option value={0}>1</option>
                        <option value={1}>2</option>
                        <option value={2}>3</option>
                        <option value={3}>4</option>
                        <option value={4}>5</option>
                        <option value={5}>6</option>
                        <option value={6}>7</option>
                        <option value={7}>8</option>
                        <option value={8}>9</option>
                        <option value={9}>10</option>
                        <option value={10}>11</option>
                        <option value={11}>12</option>
                    </select>
                </div>
            </div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={tingyunBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, ultimateBuff)} />}
            label={tingyun.ultimateDescription(ultimateLevel).name}
            />
            <div className="mb-3">{tingyun.ultimateDescription(ultimateLevel).description}</div>
            <div style={{display: "flex"}}>
                <div>Ult Level</div>
                <select className="levels buff-input" style={{width: "15%"}} value={ultimateLevel} onChange={updateUltLevel}>
                    <option value={0}>1</option>
                    <option value={1}>2</option>
                    <option value={2}>3</option>
                    <option value={3}>4</option>
                    <option value={4}>5</option>
                    <option value={5}>6</option>
                    <option value={6}>7</option>
                    <option value={7}>8</option>
                    <option value={8}>9</option>
                    <option value={9}>10</option>
                    <option value={10}>11</option>
                    <option value={11}>12</option>
                </select>
            </div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={tingyunBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, E1Buff)} />}
            label={"(E1) " + tingyun.E1().name}
            />
            <div>{tingyun.E1().description}</div>

        </>
    )
}   

export default TingyunBuffs;