import { globalData } from '../../../../../globalData';
import React, { useContext, useState, useEffect } from 'react';

function TimingBuffs() {

    const {
        lightCone,
        effectRes,
        imposition,
        prevImposition,
        updateStatesFromJson,
    } = useContext(globalData);


    useEffect(() => {
        const buff = {
            effect: {
                effectRes: lightCone.weaponBaseEffect(prevImposition).effect.effectRes,
                OGH: lightCone.weaponBaseEffect(prevImposition).effect.OGH,
            }
        }
        updateStatesFromJson(buff, true);

        let ER = effectRes;
        ER = effectRes - lightCone.getER(prevImposition) + lightCone.getER(imposition);
        lightCone.setER(ER);

        const tempBuff = {
            effect: {
                effectRes: lightCone.weaponBaseEffect(imposition).effect.effectRes,
                OGH: lightCone.weaponBaseEffect(imposition).effect.OGH,
            }
        }
        updateStatesFromJson(tempBuff, false);

    }, [effectRes]);

    useEffect(() => {
        
        const buff = {
            effect: {
                effectRes: lightCone.weaponBaseEffect(prevImposition).effect.effectRes,
                OGH: lightCone.weaponBaseEffect(prevImposition).effect.OGH,
            }
        }
        updateStatesFromJson(buff, true);

        let ER = effectRes;
        ER = effectRes - lightCone.getER(prevImposition) + lightCone.getER(imposition);
        lightCone.setER(ER);

        const tempBuff = {
            effect: {
                effectRes: lightCone.weaponBaseEffect(imposition).effect.effectRes,
                OGH: lightCone.weaponBaseEffect(imposition).effect.OGH,
            }
        }
        updateStatesFromJson(tempBuff, false);
    }, [imposition]);

    return (
        <></>
    )
}

export default TimingBuffs;