import React, {useState, useContext, useEffect } from 'react';
import { globalData } from '../../../globalData';
import RelicSubs from './relicContentParts/relicSubStats';
import LoopIcon from '@mui/icons-material/Loop';

export function RelicHeadContent(props) {

    const flatHpStats5 = {
        0: 112.896,
        3: 231.4368,
        6: 349.9776,
        9: 468.5184,
        12: 587.0592,
        15: 705.6,
    }

    const flatHpStats4 = {
        0: 90.3168,
        3: 185.14944,
        6: 279.98208,
        9: 374.81472,
        12: 469.6474,
    }

    const {
        headPiece,
        setHpFlat,
        hpFlat,
    } = useContext(globalData);

    const [hp, setHp] = useState(0);
    const [selectedValue, setSelectedValue] = useState("0");
    const setHeadOpen = props.setHeadOpen;
    
    useEffect(() => {
        setHpFlat((prevValue) => prevValue - hp);
        if(headPiece.rarity === "fiveStar") {
            setHp(flatHpStats5[0]);
            headPiece.setMainStatValue(flatHpStats5[0]);
            setHpFlat((prevValue) => prevValue + flatHpStats5[0]);
        } else {
            setHp(flatHpStats4[0]);
            headPiece.setMainStatValue(flatHpStats4[0]);
            setHpFlat((prevValue) => prevValue + flatHpStats4[0]);
        }
        setSelectedValue("0");
    }, [headPiece]);

    const handleHpChange = (event) => {
        setHpFlat((prevValue) => prevValue - hp);
        if(headPiece.rarity === "fiveStar") {
            setHp(flatHpStats5[event.target.value]);
            headPiece.setMainStatValue(flatHpStats5[event.target.value]);
            setHpFlat((prevValue) => prevValue + flatHpStats5[event.target.value]);
        }else {
            setHp(flatHpStats4[event.target.value]);
            headPiece.setMainStatValue(flatHpStats4[event.target.value]);
            setHpFlat((prevValue) => prevValue + flatHpStats4[event.target.value]);
        }
        setSelectedValue(event.target.value);
    }

    let lastOption = "";
    let maxLevelButton = 12;
    if(headPiece && headPiece.rarity === "fiveStar") {
        lastOption = <option value="15">15</option>;
        maxLevelButton = 15;
    }

    return (
        <div className='relic-content'>
            <div className='relic-header'>
                <select name="level" id="level" className='relic-main-stat' onChange={(event) => handleHpChange(event)} value={selectedValue}>
                    <option value="0">0</option>
                    <option value="3">3</option>
                    <option value="6">6</option>
                    <option value="9">9</option>
                    <option value="12">12</option>
                    {lastOption}
                </select>
                <div className='relic-stuff'>
                    <div style={{color: "#18cd18", fontWeight: "900", fontSize: "1rem", width: "144px", paddingLeft: "5px"}}>HP</div>
                </div>
                <div style={{color: "#18cd18", fontWeight: "900", fontSize: "1.2rem"}}>{parseInt(hp)}</div>
            </div>
            <RelicSubs relicPiece={headPiece} subIndex={0} initialSubstat={"CD"}></RelicSubs>
            <RelicSubs relicPiece={headPiece} subIndex={1} initialSubstat={"ER"}></RelicSubs>
            <RelicSubs relicPiece={headPiece} subIndex={2} initialSubstat={"DEF"}></RelicSubs>
            <RelicSubs relicPiece={headPiece} subIndex={3} initialSubstat={"DEF%"}></RelicSubs>
            <div className='qol-buttons'>
                <button className='calc-btn' style={{width: "20%"}} value={maxLevelButton} onClick={(event) => handleHpChange(event)}>{maxLevelButton}</button>
                <button className='calc-btn' style={{width: "20%"}} onClick={() => setHeadOpen(true)}><LoopIcon></LoopIcon></button>
            </div>
        </div>
    )
}

