import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';

import { Blade } from "../../../../../../../database/characters";

const BladeBuffs = () => {

    const { character, hp, hpPercent, hpFlat, eidolon, skillLevel, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    
    const blade = new Blade();

    const [prevSkillLevel, setPrevSkillLevel] = useState(skillLevel);
    const [hpLost, setHpLost] = useState(0.0);
    const [E4Stacks, setE4Stacks] = useState(1);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    useEffect(() => {

        if(characterBuffStates[0]) {
            const tempBuff = {
                effect: {
                    iceDmg: blade.skillAttack(prevSkillLevel),
                    quantumDmg: blade.skillAttack(prevSkillLevel),
                    fireDmg: blade.skillAttack(prevSkillLevel),
                    lightningDmg: blade.skillAttack(prevSkillLevel),
                    windDmg: blade.skillAttack(prevSkillLevel),
                    physicalDmg: blade.skillAttack(prevSkillLevel),
                    imaginaryDmg: blade.skillAttack(prevSkillLevel),
                }
            }
            updateStatesFromJson(tempBuff, true);
            updateStatesFromJson(skillBuff, false);
        }

        setPrevSkillLevel(skillLevel);

    }, [skillLevel]);

    useEffect(() => {

        character.setHpLost(hpLost);
        const updatedStates = [...characterBuffStates];
        setCharacterBuffStates(updatedStates);

    }, [hpLost]);

    useEffect(() => {
        if(eidolon < 2 && characterBuffStates[1]) {
            updateStatesFromJson(E2Buff, true);
            characterBuffStates[1] = false;
        }

        if(eidolon < 4 && characterBuffStates[2]) {
            updateStatesFromJson(E4Buff, true);
            characterBuffStates[2] = false;
        }

    }, [eidolon]);

    const skillBuff = {
        effect: {
            iceDmg: blade.skillAttack(skillLevel),
            quantumDmg: blade.skillAttack(skillLevel),
            fireDmg: blade.skillAttack(skillLevel),
            lightningDmg: blade.skillAttack(skillLevel),
            windDmg: blade.skillAttack(skillLevel),
            physicalDmg: blade.skillAttack(skillLevel),
            imaginaryDmg: blade.skillAttack(skillLevel),
        }
    }

    const E2Buff = {
        effect: {
            critRate: 0.15,
        },
    }

    const E4Buff = {
        effect: {
            hpPercent: 0.2 * E4Stacks,
        },
    }

    const handleLossChange = (event) => {
        const re = /^[0-9]*\.?[0-9]+$/;
        if (event.target.value === '' || !re.test(event.target.value)) {
            return;
        }

        if(event.target.value > ((hp * hpPercent) + hpFlat) * 0.9) {
            setHpLost(((hp * hpPercent) + hpFlat) * 0.9);
        } else {
            setHpLost((event.target.value));
        }

    }

    const updateE4Stacks = (e) => {

        if(characterBuffStates[2]) {
            updateStatesFromJson(E4Buff, true);
        }

        if(characterBuffStates[2]) {
            const tempTalentBuff = {
                effect: {
                    hpPercent: 0.2 * e.target.value,
                },
            }
            updateStatesFromJson(tempTalentBuff, false);
        }
        setE4Stacks(e.target.value);
    }

    return (
        <>
            <div style={{display: "flex", width: "60%"}}>
                <div className="centered">Total HP Lost</div>
                <input type="text" className="critDmg buff-input" style={{width: "65px"}} maxLength={5} value={parseInt(hpLost)} onChange={handleLossChange}/>
            </div>
            
            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, skillBuff)} />}
            label={blade.skillDescription(skillLevel).name}
            />
            <div>{blade.skillSimple(skillLevel).description}</div>

            {eidolon >= 2 &&
                <>
                    <FormControlLabel
                        className='header'
                        control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, E2Buff)} />}
                        label={"(E2) " + blade.E2().name}
                    />
                    <div>{blade.E2().description}</div>
                </>
            }

            {eidolon >= 4 &&
                <>
                    <FormControlLabel
                        className='header'
                        control={<Checkbox checked={characterBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, E4Buff)} />}
                        label={"(E4) " + blade.E4().name}
                    />
                    <div>{blade.E4().description}</div>
                    <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                        <div style={{display: "flex"}}>
                            <div>Stacks </div>
                            <select className="levels buff-input" style={{width: "45%"}} value={E4Stacks} onChange={updateE4Stacks}>
                                <option value={1}>1</option>
                                <option value={2}>2</option>
                            </select>
                        </div>
                    </div>
                </>
            }
        </>
    )
}

export const BladeGlobalBuffs = (props) => {

    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {
        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <div>No Buffs</div>
        </>
    )
}

export default BladeBuffs;