import * as characterTables from "./damageDetailsParts"
import { useContext } from "react";
import { globalData } from "../globalData";

function DamageDetails() {

    const { character } = useContext(globalData);

    const characterTablesMap = {
        seele: <characterTables.SeeleTable/>,
        bronya: <characterTables.BronyaTable/>,
        yukong: <characterTables.YukongTable/>,
        pela: <characterTables.PelaTable/>,
        danheng: <characterTables.DanHengTable/>,
        sushang: <characterTables.SushangTable/>,
        danhengil: <characterTables.DanHengILTable/>,
        tingyun: <characterTables.TingyunTable/>,
        asta: <characterTables.AstaTable/>,
        natasha: <characterTables.NatashaTable/>,
        bailu: <characterTables.BailuTable/>,
        luocha: <characterTables.LuochaTable/>,
        lynx: <characterTables.LynxTable/>,
        march: <characterTables.MarchTable/>,
        gepard: <characterTables.GepardTable/>,
        fuxuan: <characterTables.FuxuanTable/>,
        arlan: <characterTables.ArlanTable/>,
        blade: <characterTables.BladeTable/>,
        clara: <characterTables.ClaraTable/>,
        hook: <characterTables.HookTable/>,
        yanqing: <characterTables.YanqingTable/>,
        herta: <characterTables.HertaTable/>,
        serval: <characterTables.ServalTable/>,
        himeko: <characterTables.HimekoTable/>,
        jingyuan: <characterTables.JingYuanTable/>,
        qingque: <characterTables.QingqueTable/>,
        sampo: <characterTables.SampoTable/>,
        welt: <characterTables.WeltTable/>,
        kafka: <characterTables.KafkaTable/>,
        luka: <characterTables.LukaTable/>,
        silverwolf: <characterTables.SilverWolfTable/>,
        trailblazerdestruction: <characterTables.PhysicalTBTable/>,
        trailblazertank: <characterTables.TrailBlazerFireTable/>,
        jingliu: <characterTables.JingliuTable/>,
        topaz: <characterTables.TopazTable/>,
    }

    return (
        <div className="calc-item">
            {characterTablesMap[character.id]}
        </div>
    )
}

export default DamageDetails;