import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';
import { Bailu } from "../../../../../../../database/characters";

const BailuBuffs = () => {

    const {level, eidolon, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    const bailu = new Bailu();


    const [stacks, setStacks] = useState(1);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    useEffect(() => {

        if(eidolon < 2 && characterBuffStates[0]) {
            updateStatesFromJson(E2Buff, true);
            characterBuffStates[0] = false;
        }

        if(eidolon < 4 && characterBuffStates[1]) {
            updateStatesFromJson(E4Buff, true);
            characterBuffStates[1] = false;
        }

    }, [eidolon]);

    useEffect(() => {
        
        if(level < 3 && characterBuffStates[2]) {
            updateStatesFromJson(A2Buff, true);
            characterBuffStates[2] = false;
        }

    }, [level]);

    const A2Buff = {
        effect: {
            hpPercent: 0.1,
        },
    }

    const E2Buff = {
        effect: {
            ERR: 0.15,
        },
    }

    const E4Buff = {
        effect: {
            quantumDmg: 0.1 * stacks,
            fireDmg: 0.1 * stacks,
            iceDmg: 0.1 * stacks,
            lightningDmg: 0.1 * stacks,
            physicalDmg: 0.1 * stacks,
            windDmg: 0.1 * stacks,
            imaginaryDmg: 0.1 * stacks,
        },
    }

    const updateStacks = (e) => {
        if(characterBuffStates[1]) {
            const tempUltBuff = {
                effect: {
                    quantumDmg: 0.1 * stacks,
                    fireDmg: 0.1 * stacks,
                    iceDmg: 0.1 * stacks,
                    lightningDmg: 0.1 * stacks,
                    physicalDmg: 0.1 * stacks,
                    windDmg: 0.1 * stacks,
                    imaginaryDmg: 0.1 * stacks,
                },
            }
            updateStatesFromJson(tempUltBuff, true);
            
            const newTempUltBuff = {
                effect: {
                    quantumDmg: 0.1 * e.target.value,
                    fireDmg: 0.1 * e.target.value,
                    iceDmg: 0.1 * e.target.value,
                    lightningDmg: 0.1 * e.target.value,
                    physicalDmg: 0.1 * e.target.value,
                    windDmg: 0.1 * e.target.value,
                    imaginaryDmg: 0.1 * e.target.value,
                },
            }
            updateStatesFromJson(newTempUltBuff, false);
        }
        setStacks(e.target.value);
    }

    return (
        <>
            {level >= 3 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, A2Buff)} />}
                    label={"(A2) " + bailu.A2().name}
                    />
                    <div>{bailu.A2().description}</div>
                </>
            }

            {eidolon >= 2 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, E2Buff)} />}
                    label={"(E2) " + bailu.E2().name}
                    />
                    <div>{bailu.E2().description}</div>
                </>
            }

            {eidolon >= 4 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, E4Buff)} />}
                    label={"(E4) " + bailu.E4().name}
                    />
                    <div>{bailu.E4().description}</div>
                    <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                    <div style={{display: "flex"}}>
                        <div>Stacks </div>
                        <select className="levels buff-input" style={{width: "45%"}} value={stacks} onChange={updateStacks}>
                            <option value={1}>1</option>
                            <option value={2}>2</option>
                            <option value={3}>3</option>
                        </select>
                    </div>
                </div>
                </>
            }

        </>
    )
}

export const BailuGlobalBuffs = (props) => {

    const bailu = new Bailu();
    const { updateStatesFromJson } = useContext(globalData);

    const bailuBuffStates = props.buffStates;
    const setBailuBuffStates = props.setBuffStates;
    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;
    const [stacks, setStacks] = useState(1);

    useEffect(() => {

        if(bailuBuffStates[1]) {
            updateStatesFromJson(E4Buff, true);
            bailuBuffStates[1] = false;
        }

        if(bailuBuffStates[2]) {
            updateStatesFromJson(A2Buff, true);
            bailuBuffStates[2] = false;
        }

        setDone(!done);
    }, [characterSwitched]);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...bailuBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setBailuBuffStates(updatedStates);
    }

    const A2Buff = {
        effect: {
            hpPercent: 0.1,
        },
    }

    const E4Buff = {
        effect: {
            quantumDmg: 0.1 * stacks,
            fireDmg: 0.1 * stacks,
            iceDmg: 0.1 * stacks,
            lightningDmg: 0.1 * stacks,
            physicalDmg: 0.1 * stacks,
            windDmg: 0.1 * stacks,
            imaginaryDmg: 0.1 * stacks,
        },
    }

    const updateStacks = (e) => {
        if(bailuBuffStates[1]) {
            const tempUltBuff = {
                effect: {
                    quantumDmg: 0.1 * stacks,
                    fireDmg: 0.1 * stacks,
                    iceDmg: 0.1 * stacks,
                    lightningDmg: 0.1 * stacks,
                    physicalDmg: 0.1 * stacks,
                    windDmg: 0.1 * stacks,
                    imaginaryDmg: 0.1 * stacks,
                },
            }
            updateStatesFromJson(tempUltBuff, true);
            
            const newTempUltBuff = {
                effect: {
                    quantumDmg: 0.1 * e.target.value,
                    fireDmg: 0.1 * e.target.value,
                    iceDmg: 0.1 * e.target.value,
                    lightningDmg: 0.1 * e.target.value,
                    physicalDmg: 0.1 * e.target.value,
                    windDmg: 0.1 * e.target.value,
                    imaginaryDmg: 0.1 * e.target.value,
                },
            }
            updateStatesFromJson(newTempUltBuff, false);
        }
        setStacks(e.target.value);
    }

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={bailuBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, A2Buff)} />}
            label={"(A2) " + bailu.A2().name}
            />
            <div>{bailu.A2().description}</div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={bailuBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, E4Buff)} />}
            label={"(E4) " + bailu.E4().name}
            />
            <div>{bailu.E4().description}</div>
            <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                <div style={{display: "flex"}}>
                    <div>Stacks </div>
                    <select className="levels buff-input" style={{width: "45%"}} value={stacks} onChange={updateStacks}>
                        <option value={1}>1</option>
                        <option value={2}>2</option>
                        <option value={3}>3</option>
                    </select>
                </div>
            </div>
        </>
    )
}

export default BailuBuffs;