export class Chorus {
    constructor() {
        this.stats = [
            [1, 14.4, 12, 38],
            [20, 55.44, 46.2, 148],
            [20, 72.72, 60.6, 194],
            [40, 138.96 ,115.8,371],
            [40, 162 ,236.135,432],
            [50, 183.6 ,267.153, 490],
            [50, 206.64,172.2, 551],
            [60, 228.24,190.2, 609],
            [60, 251.28,209.4, 670],
            [70, 272.88,227.4, 728],
            [70, 295.92,246.6, 789],
            [80, 317.52,264.6, 847],
        ];
        
        this.name = "Chorus";
        this.path = "Harmony";
        this.id = "chorus";
        this.rarity = "threeStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>After entering battle, increases the  </div>
                <div className="buff-text"> ATK </div> 
                <div> of all allies by </div> 
                <div className="buff-text">{((0.08 + imposition * 0.01) * 100).toFixed(1)}%.</div>
                <div> Effects of the same type cannot stack. </div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{},
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "adve1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> After entering battle, increases the </div>
                        <div className="buff-text"> ATK </div> 
                        <div> of all allies by </div> 
                        <div className="buff-text">{((0.08 + imposition * 0.01) * 100).toFixed(1)}%.</div>
                        <div> Effects of the same type cannot stack. </div>
                    </div>
                ),
                effect: {
                    atkPercent: (0.08 + imposition * 0.01),
                },
                type: "buff",
                hasStack: false,
                hasInput: false,
            },
        }
    }
}

export default Chorus;