export class Aeon {
    constructor() {
        this.stats = [
            [1,  24,    18,    48],
            [20, 92.4,  69.3, 185],
            [20, 121.2, 90.9,  242],
            [40, 231.6, 173.7, 463],
            [40, 270,    202.5, 540],
            [50, 306,  229.5, 612],
            [50, 344.4, 258.3, 689],
            [60, 380.4, 285.3, 761],
            [60, 418.8, 314.1, 838],
            [70, 454.8, 341.1, 910],
            [70, 493.2, 369.9, 986],
            [80, 529.2, 396.9, 1058],
        ];
        
        this.name = "On the Fall of an Aeon";
        this.path = "Destruction";
        this.id = "aeon";
        this.rarity = "fiveStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Whenever the wearer attacks, their </div>
                <div className="buff-text"> ATK  </div> 
                <div> is increased by </div> 
                <div className="buff-text">{((0.08 + imposition * 0.02) * 100).toFixed(1)}%.</div>
                <div>  in this battle. This effect can stack up to 4 time(s). When the wearer inflicts </div>
                <div className="buff-text">Weakness Break</div>
                <div> on enemies, the wearer's DMG increases by </div>
                <div className="buff-text"> {((0.12 + imposition * 0.03) * 100).toFixed(1)}%. for 2 turn(s).</div> 
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "silenceb",
            name: this.name,
            effect:{},
            type: "buff",
            hasStack: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition, stack) {
        return {
            0: {
                id: "silence0",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div>Whenever the wearer attacks, their </div>
                        <div className="buff-text"> ATK  </div> 
                        <div> is increased by </div> 
                        <div className="buff-text">{((0.08 + imposition * 0.02) * 100).toFixed(1)}%.</div>
                        <div>  in this battle. This effect can stack up to 4 time(s). </div>
                    </div>
                ),
                effect: {
                    atkPercent: (0.08 + imposition * 0.02) * stack,
                },
                type: "buff",
                hasStack: true,
                stacks: 4,
                hasInput: false,
            },
            1: {
                id: "silence1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> When the wearer inflicts </div>
                        <div className="buff-text">Weakness Break</div>
                        <div> on enemies, the wearer's DMG increases by </div>
                        <div className="buff-text"> {((0.12 + imposition * 0.03) * 100).toFixed(1)}%. for 2 turn(s).</div> 
                    </div>
                ),
                effect: {
                    iceDmg: 0.12 + imposition * 0.03,
                    quantumDmg: 0.12 + imposition * 0.03,
                    fireDmg: 0.12 + imposition * 0.03,
                    lightningDmg: 0.12 + imposition * 0.03,
                    windDmg: 0.12 + imposition * 0.03,
                    physicalDmg: 0.12 + imposition * 0.03,
                    imaginaryDmg: 0.12 + imposition * 0.03,
                },
                type: "buff",
                hasStack: false,
                hasInput: false,
            },
        }
    }
}

export default Aeon;