export class Cornucopia {
    constructor() {
        this.stats = [
            [1, 12, 12, 43],
            [20, 46.2, 46.2, 166],
            [20, 60.6, 60.6, 218],
            [40, 115.8 ,115.8,417],
            [40, 135 ,236.135,486],
            [50, 153 ,267.153, 551],
            [50, 172.2,172.2, 620],
            [60, 190.2,190.2, 685],
            [60, 209.4,209.4, 754],
            [70, 227.4,227.4, 819],
            [70, 246.6,246.6, 888],
            [80, 264.6,264.6, 953],
        ];
        
        this.name = "Cornucopia";
        this.path = "Abundance";
        this.id = "cornucopia";
        this.rarity = "threeStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>When the wearer uses their </div>
                <div className="buff-text"> Skill or Ultimate </div> 
                <div> their </div> 
                <div className="buff-text"> Outgoing Healing increases by </div> 
                <div className="buff-text">{((0.12 + imposition * 0.03) * 100).toFixed(1)}%</div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{},
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "adve1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div>When the wearer uses their </div>
                        <div className="buff-text"> Skill or Ultimate </div> 
                        <div> their </div> 
                        <div className="buff-text"> Outgoing Healing increases by </div> 
                        <div className="buff-text">{((0.12 + imposition * 0.03) * 100).toFixed(1)}%</div>
                    </div>
                ),
                effect: {
                    OGH: 0.12 + imposition * 0.03,
                },
                type: "buff",
                hasStacks: false,
                hasInput: false,
            },
        }
    }
}

export default Cornucopia;