class Blade {
    constructor() {
        this.stats = [
            {level: 1,  atk: 73.92,  def: 66,    hp: 185, spd: 97, taunt: 125, energy: 130},
            {level: 20, atk: 144.14, def: 128.7, hp: 360, spd: 97, taunt: 125, energy: 130, atkPercent: 0.04},
            {level: 20, atk: 173.71, def: 155.1, hp: 434, spd: 97, taunt: 125, energy: 130, atkPercent: 0.04},
            {level: 40, atk: 277.2,  def: 247.5, hp: 693, spd: 97, critRate: 0.027, taunt: 125, energy: 130, hpPercent: 0.04},
            {level: 40, atk: 306.77, def: 273.9, hp: 767, spd: 97, critRate: 0.027, taunt: 125, energy: 130, hpPercent: 0.08, effectRes: 0.04},
            {level: 50, atk: 343.73, def: 306.9, hp: 859, spd: 97, critRate: 0.027, taunt: 125, energy: 130, hpPercent: 0.08, effectRes: 0.04},
            {level: 50, atk: 373.3,  def: 333.3, hp: 933, spd: 97, critRate: 0.027,  taunt: 125, energy: 130, hpPercent: 0.14, effectRes: 0.04},
            {level: 60, atk: 410.26, def: 366.3, hp: 1026,spd: 97, critRate: 0.027,  taunt: 125, energy: 130, hpPercent: 0.14, effectRes: 0.04},
            {level: 60, atk: 439.82, def: 392.7, hp: 1100,spd: 97, critRate: 0.067,  taunt: 125, energy: 130, hpPercent: 0.2,  effectRes: 0.04},
            {level: 70, atk: 476.78, def: 425.7, hp: 1192,spd: 97, critRate: 0.067,  taunt: 125, energy: 130, hpPercent: 0.2,  effectRes: 0.04},
            {level: 70, atk: 506.35, def: 452.1, hp: 1266,spd: 97, critRate: 0.067,  taunt: 125, energy: 130, hpPercent: 0.2,  effectRes: 0.1},
            {level: 80, atk: 543.31, def: 485.1, hp: 1358,spd: 97, critRate: 0.12, taunt: 125, energy: 130, hpPercent: 0.28, effectRes: 0.1},
        ];

        this.name = "Blade";
        this.path = "Destruction";
        this.rarity = 'fiveStar';
        this.element = "Wind";
        this.id = "blade";
        this.hpLost = 0;
    }

    setHpLost(hpLost) {
        this.hpLost = hpLost;
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicAttack1(level) {
        return {
            singleAtk: 0.2 + (level * 0.04),
            singleHp: 0.5 + (level * 0.1),
            splashAtk: 0.08 + (level * 0.016),
            splashHp: 0.2 + (level * 0.04),   
        }
    }

    basicDescription(level) {
        return {
            name: "Shard Sword",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    basic1Description(level) {
        return {
            name: "Forest of Swords",
            description: (
                <div className="buff-description">
                    <div>Consumes HP equal to </div>
                    <div className="buff-text"> 10% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> Max HP </div>
                    <div> and deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to the sum of </div>
                    <div className="buff-text">{(this.basicAttack1(level).singleAtk * 100).toFixed(1)}%</div>
                    <div> of his </div>
                    <div className="buff-text">ATK</div>
                    <div> and </div>
                    <div className="buff-text">{(this.basicAttack1(level).singleHp * 100).toFixed(1)}%</div>
                    <div> of his </div>
                    <div className="buff-text">Max HP</div>
                    <div> to a single enemy. </div>
                    <div> In addition, deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to the sum of </div>
                    <div className="buff-text">{(this.basicAttack1(level).splashAtk * 100).toFixed(1)}%</div>
                    <div> of his </div>
                    <div className="buff-text">ATK</div>
                    <div> and </div>
                    <div className="buff-text">{(this.basicAttack1(level).splashHp * 100).toFixed(1)}%</div>
                    <div> of his </div>
                    <div className="buff-text">Max HP</div>
                    <div> to adjacent targets. </div>
                    <div> If Blade's current HP is insufficient, his HP will be reduced to 1 when using Forest of Swords. Forest of Swords cannot regenerate Skill Points. </div>
                </div>
            ),
        }
    }


    skillAttack(level) {
        if(level < 6) {
            return 0.12 + (level * 0.028);
        }else if(level < 10){
            return 0.26 + ((level - 5) * 0.035);
        }else {
            return 0.4 + ((level - 9) * 0.028);
        }
    }

    skillDescription(level) {
        return {
            name: "Hellscape",
            description: (
                <div className="buff-description">
                    <div>Consumes </div>
                    <div className="buff-text"> HP </div>
                    <div> equal to </div>
                    <div className="buff-text"> 30% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> Max HP </div>
                    <div> to enter the Hellscape state. </div>
                    <div> When Hellscape is active, his Skill cannot be used, his DMG dealt increases by </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                    <div> and his Basic ATK Shard Sword is enhanced to Forest of Swords for 3 turn(s). If Blade's current HP is insufficient, his HP will be reduced to 1 when he uses his Skill. This Skill does not regenerate Energy. Using this Skill does not end the current turn. </div>
                </div>
            ),
        }
    }

    skillSimple(level) {
        return {
            name: "Hellscape",
            description: (
                <div className="buff-description">
                    <div>  DMG dealt increases by </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                </div>
            ),
        }
    }
        

    ultimateAttack(level) {
        if(level < 6) {
            return {
                singleAtk: 0.24 + (level * 0.016),
                singleHp: 0.6 + (level * 0.04),
                singleTallyHp: 0.6 + (level * 0.04),
                splashAtk: 0.096 + (level * 0.0064),
                splashHp: 0.24 + (level * 0.016),
                splashTallyHp: 0.24 + (level * 0.016),
            }
        }else if(level < 10){
            return {
                singleAtk: 0.32 + ((level - 5) * 0.02),
                singleHp: 0.8 + ((level - 5) * 0.05),
                singleTallyHp: 0.8 + ((level - 5) * 0.05),
                splashAtk: 0.128 + ((level - 5) * 0.008),
                splashHp: 0.32 + ((level - 5) * 0.02),
                splashTallyHp: 0.32 + ((level - 5) * 0.02),
            }
        }else {
            return {
                singleAtk: 0.4 + ((level - 9) * 0.016),
                singleHp: 1 + ((level - 9) * 0.04),
                singleTallyHp: 1 + ((level - 9) * 0.04),
                splashAtk: 0.16 + ((level - 9) * 0.0064),
                splashHp: 0.4 + ((level - 9) * 0.016),
                splashTallyHp: 0.4 + ((level - 9) * 0.016),
            }
        }
    }

    ultimateDescription(level) {
        return {
            name: "Death Sentence",
            description: (
                <div className="buff-description">
                    <div>Sets Blade's current </div>
                    <div className={this.element}>HP</div>
                    <div> to 50% </div>
                    <div> of his Max HP </div>
                    <div> and deals </div>
                    <div className={this.element}>{this.element} DMG</div>
                    <div> to a single enemy equal to the sum of </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).singleAtk * 100).toFixed(1)}% </div>
                    <div> of his </div>
                    <div className="buff-text">ATK</div>
                    <div>, </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).singleHp * 100).toFixed(1)}% </div>
                    <div> of his </div>
                    <div className="buff-text">Max HP</div>
                    <div> and </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).singleTallyHp * 100).toFixed(1)}% </div>
                    <div> of the tally of Blade's HP loss in the current battle.</div>
                    <div> At the same time deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> to adjacent targets equal to the sum of </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).splashAtk * 100).toFixed(1)}% </div>
                    <div> of his </div>
                    <div className="buff-text">ATK</div>
                    <div>, </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).splashHp * 100).toFixed(1)}% </div>
                    <div> of his </div>
                    <div className="buff-text">Max HP</div>
                    <div> and </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).splashTallyHp * 100).toFixed(1)}% </div>
                    <div> of the tally of his HP loss in the current battle. The tally of Blade's HP loss in the current battle is capped at </div>
                    <div className="buff-text"> 90% </div>
                    <div> of his Max HP. This value will be reset and re-accumulated after his Ultimate has been used. </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return {
                atk: 0.22 + (level * 0.022),
                hp: 0.55 + (level * 0.055),
            }
        }else if(level < 10){
            return {
                atk: 0.33 + ((level - 5) * 0.0275),
                hp: 0.825 + ((level - 5) * 0.0688),
            }
        }else {
            return {
                atk: 0.44 + ((level - 9) * 0.022),
                hp: 1.1 + ((level - 9) * 0.055),
            }
        }
    }

    talentDescription(level) { 
        return {
            name: "Shuhu's Gift",
            description: (
                <div className="buff-description">
                    <div> When Blade sustains DMG or consumes his HP, he gains 1 stack of Charge, stacking up to 5 times. A max of 1 Charge stack can be gained every time he is attacked. </div>
                    <div> When Charge stack reaches maximum, immediately launches a follow-up attack on all enemies, dealing </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.talent(level).atk * 100).toFixed(1)}% </div>
                    <div> of Blade's </div>
                    <div className="buff-text">ATK</div>
                    <div> plus </div>
                    <div className="buff-text"> {(this.talent(level).hp * 100).toFixed(1)}% </div>
                    <div> of his </div>
                    <div className="buff-text">Max HP.</div>
                    <div> At the same time, restores Blade's HP by 25% of his Max HP. After the follow-up attack, all Charges are consumed. </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Karma Wind",
            description: (
                <div className="buff-description">
                    <div> Immediately attacks the enemy. After entering combat, consumes 20% of Blade's Max HP while dealing Wind DMG equal to 40% of his Max HP to all enemies. If Blade's current HP is insufficient, his HP will be reduced to 1 when this Technique is used. </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Vita Infinita",
            description: (
                <div className="buff-description">
                    <div> When Blade's current HP is at 50% of Max HP or lower, Incoming Healing increases by 20%. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Neverending Deaths",
            description: (
                <div className="buff-description">
                    <div> If Blade hits a Weakness Broken enemy after using Forest of Swords, he will restore HP equal to 5% of his Max HP plus 100. </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Cyclone of Destruction",
            description: (
                <div className="buff-description">
                    <div> DMG dealt by Talent's follow-up attack increases by 20%. </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Blade Cuts the Deepest in Hell",
            description: (
                <div className="buff-description">
                    <div> Blade's Ultimate deals additionally increased DMG to a single enemy target, with the increased amount equal to 150% of the tally of Blade's HP loss in the current battle. The tally of Blade's HP loss in the current battle is capped at 90% of his Max HP. The tally value will be reset and re-accumulated after his Ultimate has been used. </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Ten Thousand Sorrows From One Broken Dream",
            description: (
                <div className="buff-description">
                    <div> When Blade is in the Hellscape state, his CRIT Rate increases by 15%. </div>
                </div>
            )
        }
    }

    E3() {
        return {
            name: "Hardened Blade Bleeds Coldest Shade",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }
    
    E4() {
        return {
            name: "Rejected by Death, Infected With Life",
            description:(
                <div className="buff-description">
                    <div>When Blade's current HP drops to 50% or lower of his Max HP, increases his Max HP by 20%. Stacks up to 2 time(s).</div>
                </div>
            )
        }
    }

    E5() {
        return {
            name: "Death By Ten Lords' Gaze",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: "Reborn Into an Empty Husk",
            description: (
                <div className="buff-description">
                    <div>The maximum number of Charge stacks is reduced to 4. The DMG of the follow-up attack triggered by Blade's Talent additionally increases by 50% of his Max HP. </div>
                </div>
            ),
        }
    }
}

export default Blade;