export class Moles {
    constructor() {
        this.stats = [
            [1,  21.6,   12,     48],
            [20, 83.16,  46.2,  185],
            [20, 109.08,  60.6,  242],
            [40, 208.44, 115.8, 463],
            [40, 243,    135, 540],
            [50, 275.4,  153, 612],
            [50, 309.96, 172.2, 689],
            [60, 342.36, 190.2, 761],
            [60, 376.92, 209.4, 838],
            [70, 409.32, 227.4, 910],
            [70, 443.88, 246.6, 986],
            [80, 476.28, 264.6, 1058],
        ];
        
        this.name = "The Moles Welcome You";
        this.path = "Destruction";
        this.id = "moles";
        this.rarity = "fourStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>When the wearer uses </div>
                <div className="buff-text"> Basic ATK, Skill, or Ultimate </div> 
                <div> to attack enemies, the wearer gains one stack of Mischievous. Each stack increases the wearer's </div> 
                <div className="buff-text"> ATK </div> 
                <div> by </div> 
                <div className="buff-text"> {((0.12 + imposition * 0.03) * 100).toFixed(1)}%.</div> 
                <div> stacks up to 3 times </div> 
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "silenceb",
            name: this.name,
            effect:{},
            type: "buff",
            hasStack: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition, stack) {
        return {
            0: {
                id: "silence0",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div>When the wearer uses </div>
                        <div className="buff-text"> Basic ATK, Skill, or Ultimate </div> 
                        <div> to attack enemies, the wearer gains one stack of Mischievous. Each stack increases the wearer's </div> 
                        <div className="buff-text"> ATK </div> 
                        <div> by </div> 
                        <div className="buff-text"> {((0.12 + imposition * 0.03) * 100).toFixed(1)}%.</div> 
                    </div>
                ),
                effect: {
                    atkPercent: (0.12 + imposition * 0.03) * stack,
                },
                type: "buff",
                hasStack: true,
                stacks: 3,
                hasInput: false,
            },
        }
    }
}

export default Moles;