class Seele {
    constructor() {
        this.stats = [
            {level: 1,  atk: 87.12,  def: 49.5,   hp: 127, spd: 115, taunt: 75, energy: 120},
            {level: 20, atk: 169.88, def: 96.53,  hp: 247, spd: 115, taunt: 75, energy: 120},
            {level: 20, atk: 204.73, def: 116.33, hp: 298, spd: 115, taunt: 75, energy: 120, atkPercent: 0.04},
            {level: 40, atk: 326.7,  def: 185.63, hp: 475, spd: 115, critDmg: 0.053, taunt: 75, energy: 120, atkPercent: 0.04},
            {level: 40, atk: 361.55, def: 205.43, hp: 526, spd: 115, critDmg: 0.053, taunt: 75, energy: 120, atkPercent: 0.08, defPercent: 0.05},
            {level: 50, atk: 405.11, def: 230.18, hp: 589, spd: 115, critDmg: 0.053, taunt: 75, energy: 120, atkPercent: 0.08, defPercent: 0.05},
            {level: 50, atk: 439.96, def: 249.98, hp: 640, spd: 115, critDmg: 0.053, taunt: 75, energy: 120, atkPercent: 0.14, defPercent: 0.05},
            {level: 60, atk: 483.52, def: 274.73, hp: 703, spd: 115, critDmg: 0.053, taunt: 75, energy: 120, atkPercent: 0.14, defPercent: 0.05},
            {level: 60, atk: 518.36, def: 294.53, hp: 754, spd: 115, critDmg: 0.133, taunt: 75, energy: 120, atkPercent: 0.20, defPercent: 0.05},
            {level: 70, atk: 561.92, def: 319.28, hp: 817, spd: 115, critDmg: 0.133, taunt: 75, energy: 120, atkPercent: 0.20, defPercent: 0.05},
            {level: 70, atk: 596.77, def: 339.08, hp: 868, spd: 115, critDmg: 0.133, taunt: 75, energy: 120, atkPercent: 0.20, defPercent: 0.125},
            {level: 80, atk: 640.33, def: 363.83, hp: 931, spd: 115, critDmg: 0.24,  taunt: 75, energy: 120, atkPercent: 0.28, defPercent: 0.125},
        ];

        this.name = "Seele";
        this.path = "Hunt";
        this.rarity = 'fiveStar';
        this.element = "Quantum";
        this.id = "seele";
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Thwack",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className="Quantum"> Quantum DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of Seele's </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return 1.1 + (level * 0.11);
        }else if(level < 10){
            return 1.65 + ((level - 5) * 0.1375);
        }else {
            return 2.2 + ((level - 9) * 0.11);
        }
    }

    skillDescription(level) {
        return {
            name: "Sheathed Blade",
            description: (
                <div className="buff-description">
                    <div>Increases Seele's SPD by </div>
                    <div className="buff-text">25%</div>
                    <div> for </div>
                    <div className="buff-text">2</div>
                    <div> turn(s). </div>
                    <div> and deals </div>
                    <div className="Quantum"> Quantum DMG </div>
                    <div > equal to </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                    <div> of Seele's </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillSimple(level) {
        return {
            name: "Sheathed Blade",
            description: (
                <div className="buff-description">
                    <div>Increases Seele's SPD by </div>
                    <div className="buff-text">25%</div>
                    <div> for </div>
                    <div className="buff-text">2</div>
                    <div> turn(s). </div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level < 6) {
            return 2.55 + (level * 0.17);
        }else if(level < 10){
            return 3.4 + ((level - 5) * 0.2125);
        }else {
            return 4.25 + ((level - 9) * 0.17);
        }
    }

    ultimateDescription(level) {
        return {
            name: "Butterfly Flurry",
            description: (
                <div className="buff-description">
                    <div>Seele enters the buffed state and deals </div>
                    <div className="Quantum">Quantum DMG</div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.ultimateAttack(level) * 100).toFixed(1)}% </div>
                    <div> of Seele's </div>
                    <div className="buff-text"> ATK </div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 0.4 + (level * 0.04);
        }else if(level < 10){
            return 0.6 + ((level - 5) * 0.05);
        }else {
            return 0.8 + ((level - 9) * 0.04);
        }
    }

    talentDescription(level) { 
        return {
            name: "Resurgence",
            description: (
                <div className="buff-description">
                    <div>Enters the buffed state upon defeating an enemy with </div>
                    <div className="buff-text"> Basic ATK, Skill, or Ultimate </div>
                    <div>, and receives an extra turn. While in the buffed state, the </div>
                    <div className="Quantum"> DMG </div>
                    <div> of Seele's attacks increases by </div>
                    <div className="Quantum"> {(this.talent(level) * 100).toFixed(1)}% </div>
                    <div> for </div>
                    <div className="buff-text"> 1 </div>
                    <div> turn(s). Enemies defeated in the extra turn provided by "Resurgence" will not trigger another "Resurgence." </div>
                </div>
            ),
        }
    }

    talentSimple(level) {
        return {
            name: "Resurgence",
            description: (
                <div className="buff-description">
                    <div>The </div>
                    <div className="Quantum"> DMG </div>
                    <div> of Seele's attacks increases by </div>
                    <div className="Quantum"> {(this.talent(level) * 100).toFixed(1)}% </div>
                    <div> for </div>
                    <div className="buff-text"> 1 </div>
                    <div> turn</div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Phantom Illusion",
            description: (
                <div className="buff-description">
                    <div> After using her Technique, Seele gains Stealth for </div>
                    <div className="buff-text"> 20 second(s). </div>
                    <div> While Stealth is active, Seele cannot be detected by enemies. And when entering battle by attacking enemies, Seele will immediately enter the buffed state. </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Nightshade",
            description: (
                <div className="buff-description">
                    <div> When current HP percentage is </div>
                    <div className="buff-text"> 50% or lower</div>
                    <div> reduces the chance of being attacked by enemies. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Lacerate",
            description: (
                <div className="buff-description">
                    <div> While Seele is in the buffed state, her </div>
                    <div className="Quantum"> Quantum RES PEN </div>
                    <div> increases by </div>
                    <div className="buff-text"> 20% </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Rippling Waves",
            description: (
                <div className="buff-description">
                    <div> After using a </div>
                    <div className="buff-text"> Basic ATK </div>
                    <div> Seele's next action will be Advanced Forward by </div>
                    <div className="buff-text"> 20% </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Extirpating Slash",
            description: (
                <div className="buff-description">
                    <div> When dealing DMG to an enemy whose HP percentage is </div>
                    <div className="buff-text"> 80% or lower CRIT Rate </div>
                    <div> increases by </div>
                    <div className="buff-text"> 15% </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Dancing Butterfly",
            description: (
                <div className="buff-description">
                    <div> The SPD Boost effect of Seele's Skill can stack up to </div>
                    <div className="buff-text"> 2 time(s). </div>
                </div>
            )
        }
    }

    E3() {
        return {
            name: "Dazzled",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }

    E4() {
        return {
            name: "A Glimpse",
            description:(
                <div className="buff-description">
                    <div>Seele regenerates  </div>
                    <div className="buff-text"> 15 Energy </div>
                    <div> when she defeats an enemy. </div>
                </div>
            )
        }
    }

    E5() {
        return {
            name: "Keen and Sharp",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: "Separation",
            description: (
                <div className="buff-description">
                    <div>After Seele uses her Ultimate, inflict the target enemy with Butterfly Flurry for 1 turn(s). Enemies suffering from Butterfly Flurry will take Additional </div>
                    <div className="Quantum"> Quantum DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> 15%</div>
                    <div> of Seele's Ultimate DMG every time they are attacked. If the target enemy is defeated by the Butterfly Flurry DMG triggered by other allies' attacks, Seele's Talent will not be triggered. When Seele is knocked down, the Butterfly Flurry inflicted on the enemies will be removed.  </div>
                </div>
            ),
        }
    }
}


export default Seele;