import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';
import { Seele } from "../../../../../../../database/characters";

const SeeleBuffs = () => {

    const {level, eidolon, skillLevel, talentLevel, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    const seele = new Seele();

    const [prevTalentLevel, setPrevTalentLevel] = useState(talentLevel);
    const [prevLevel, setPrevLevel] = useState(level);
    const [E2Stacks, setE2Stacks] = useState(1);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    useEffect(() => {
        if(characterBuffStates[0]) {
            const tempUltBuff = {
                effect: {
                    quantumDmg: seele.talent(prevTalentLevel),
                    fireDmg: seele.talent(prevTalentLevel),
                    iceDmg: seele.talent(prevTalentLevel),
                    lightningDmg: seele.talent(prevTalentLevel),
                    physicalDmg: seele.talent(prevTalentLevel),
                    imaginaryDmg: seele.talent(prevTalentLevel),
                    windDmg: seele.talent(prevTalentLevel),
                },
            }
            updateStatesFromJson(tempUltBuff, true);
            
            const newTempUltBuff = {
                effect: {
                    quantumDmg: seele.talent(talentLevel),
                    fireDmg: seele.talent(talentLevel),
                    iceDmg: seele.talent(talentLevel),
                    lightningDmg: seele.talent(talentLevel),
                    physicalDmg: seele.talent(talentLevel),
                    imaginaryDmg: seele.talent(talentLevel),
                    windDmg: seele.talent(talentLevel),
                },
            }
            updateStatesFromJson(newTempUltBuff, false);
        }
        
        setPrevTalentLevel(talentLevel);
    }, [talentLevel]);

    useEffect(() => {

        if(eidolon < 1 && characterBuffStates[4]) {
            updateStatesFromJson(E1Buff, true);
            characterBuffStates[4] = false;
        }

        if(eidolon < 2 && characterBuffStates[1]) {
            updateStatesFromJson(skillBuff, true);

            const tempTalentBuff = {
                effect: {
                    spdPercent: 0.25 * 1,
                },
            }

            updateStatesFromJson(tempTalentBuff);
            setE2Stacks(1);
        }

    }, [eidolon]);

    useEffect(() => {
        
        if(level < 6 && characterBuffStates[0] && prevLevel >= 6) {
            
            const oldTalentBuff = {
                effect: {
                    quantumDmg: seele.talent(talentLevel),
                    fireDmg: seele.talent(talentLevel),
                    iceDmg: seele.talent(talentLevel),
                    lightningDmg: seele.talent(talentLevel),
                    physicalDmg: seele.talent(talentLevel),
                    imaginaryDmg: seele.talent(talentLevel),
                    windDmg: seele.talent(talentLevel),
                    quantumPen: 0.2,
                },
            }

            updateStatesFromJson(oldTalentBuff, true);
            updateStatesFromJson(talentBuff, false);
        }

        if(level >= 6 && characterBuffStates[0] && prevLevel < 6) {

            const newTalentBuff = {
                effect: {
                    quantumPen: 0.2,
                },
            }

            updateStatesFromJson(newTalentBuff, false);
        }
        setPrevLevel(level);

    }, [level]);


    let talentBuff = {
        effect: {
            quantumDmg: seele.talent(talentLevel),
            fireDmg: seele.talent(talentLevel),
            iceDmg: seele.talent(talentLevel),
            lightningDmg: seele.talent(talentLevel),
            physicalDmg: seele.talent(talentLevel),
            imaginaryDmg: seele.talent(talentLevel),
            windDmg: seele.talent(talentLevel),
        },
    }

    if(level >= 6) {
        talentBuff = {
            effect: {
                ...talentBuff.effect,
                quantumPen: 0.2,
            },
        }
    }

    const skillBuff = {
        effect: {
            spdPercent: 0.25 * E2Stacks,
        },
    }

    const E1Buff = {
        effect: {
            critRate: 0.15,
        },
    }

    const updateE2Stacks = (e) => {

        if(characterBuffStates[1]) {
            updateStatesFromJson(skillBuff, true);
        }

        if(characterBuffStates[1]) {
            const tempSkillBuff = {
                effect: {
                    spdPercent: 0.25 * e.target.value,
                },
            }
            updateStatesFromJson(tempSkillBuff, false);
        }
        setE2Stacks(e.target.value);
    }

    let skillContent = "";

    if(eidolon >= 2) {
        skillContent = (
            <>
            <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                <div style={{display: "flex"}}>
                    <div>Stacks </div>
                    <select className="levels buff-input" style={{width: "45%"}} value={E2Stacks} onChange={updateE2Stacks}>
                        <option value={1}>1</option>
                        <option value={2}>2</option>
                    </select>
                </div>
            </div>
            </>
        );
    }

    return (
        <>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, skillBuff)} />}
            label={seele.skillDescription(skillLevel).name}
            />
            <div>{seele.skillSimple(skillLevel).description}</div>
            {skillContent}

            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, talentBuff)} />}
            label={seele.talentDescription(talentLevel).name}
            />
            {level >= 6 ? 
                <div className="buff-description">
                    <br></br>
                    <div>{seele.talentSimple(talentLevel).description}, additionally Seele's </div>
                    <div className="buff-text"> Quantum RES PEN </div>
                    <div> increases by </div>
                    <div className="buff-text"> 20% </div>
                </div>
            :
                <div>{seele.talentSimple(talentLevel).description}</div>
            }

            {eidolon >= 1 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[4] || false} onChange={() => applyCharacterBuffs(4, E1Buff)} />}
                    label={"(E1) " + seele.E1().name}
                    />
                    <div>{seele.E1().description}</div>
                </>
            }
        </>
    )
}

export const SeeleGlobalBuffs = (props) => {

    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {
        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <div>No Buffs</div>
        </>
    )
}

export default SeeleBuffs;