export class Something {
    constructor() {
        this.stats = [
            [1,  26.4,    18,    53],
            [20, 101.64,  69.3, 203],
            [20, 133.32, 90.9,  267],
            [40, 254.76, 173.7, 510],
            [40, 297,    202.5, 594],
            [50, 336.6,  229.5, 673],
            [50, 378.84, 258.3, 758],
            [60, 418.44, 285.3, 837],
            [60, 460.68, 314.1, 921],
            [70, 500.28, 341.1, 1001],
            [70, 542.52, 369.9, 1085],
            [80, 582.12, 396.9, 1164],
        ];
        
        this.name = "Something Irreplaceable";
        this.path = "Destruction";
        this.id = "something";
        this.rarity = "fiveStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text"> ATK  </div> 
                <div> by </div> 
                <div className="buff-text">{((0.24 + imposition * 0.04) * 100).toFixed(1)}%.</div>
                <div> When the wearer defeats an enemy or is hit, immediately restores HP equal to </div>
                <div className="buff-text">{((0.08 + imposition * 0.01) * 100).toFixed(1)}%.</div>
                <div>  of the wearer's ATK </div>
                <div> At the same time, the wearer's DMG is increased by  </div> 
                <div className="buff-text"> {((0.24 + imposition * 0.04) * 100).toFixed(1)}%.</div> 
                <div> until the end of their next turn. This effect cannot stack and can only trigger 1 time per turn.  </div> 
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "silenceb",
            name: this.name,
            effect:{
                atkPercent: 0.24 + imposition * 0.04,
            },
            type: "buff",
            hasStack: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition, stack) {
        return {
            0: {
                id: "silence0",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> When the wearer defeats an enemy or is hit, the wearer's DMG is increased by  </div> 
                        <div className="buff-text"> {((0.24 + imposition * 0.04) * 100).toFixed(1)}%.</div> 
                        <div> until the end of their next turn. This effect cannot stack and can only trigger 1 time per turn.  </div>  
                    </div>
                ),
                effect: {
                    iceDmg: 0.24 + imposition * 0.04,
                    quantumDmg: 0.24 + imposition * 0.04,
                    fireDmg: 0.24 + imposition * 0.04,
                    lightningDmg: 0.24 + imposition * 0.04,
                    windDmg: 0.24 + imposition * 0.04,
                    physicalDmg: 0.24 + imposition * 0.04,
                    imaginaryDmg: 0.24 + imposition * 0.04,
                },
                type: "buff",
                hasStack: false,
                hasInput: false,
            },
        }
    }
}

export default Something;