import React, { useContext } from 'react';
import { globalData } from '../../../globalData';

function DanHengILTable() {

    const levelMult = {
        1: 54.0000,
        20: 139.7703,
        40: 363.6658,
        50: 774.9041,
        60: 1640.3068,
        70: 2659.6406,
        80: 3767.5533,
    }

    const {
        character,
        imaginaryDmg,
        atk,
        atkPercent,
        atkFlat,
        critRate,
        critDmg,
        eidolon,
        energyRegen,
        level,
        enemyLevel,
        imaginaryPen,
        breakEffect,
        defReduction,
        enemyRes,
        basicAttackLevel,
        ultimateLevel,
        imaginaryVulnerability,
        ultimateBoost,
        breakMultiplier,
        basicBoost,
        ultimateCritDmg,
        enemyToughness,
        talentLevel,
        skillLevel,
    } = useContext(globalData);

    let danHengAtk = (atk * atkPercent) + atkFlat;
    let damageBoost = (1 + imaginaryDmg);
    let critDmgBoost = (1 + critDmg);

    let resMult = (1 - (enemyRes - imaginaryPen));
    let defReductionStat = (1 - defReduction);
    let vulnerabilityMultiplier = (1 + imaginaryVulnerability);

    if(defReductionStat < 0) {
        defReductionStat = 0;
    }

    let defMult = (character.stats[level]["level"] + 20)/((enemyLevel + 20) * defReductionStat + character.stats[level]["level"] + 20);
    let breakMult = breakMultiplier;
    
    let critRateStat = critRate;
    
    if(critRateStat > 1) {
        critRateStat = 1;
    }

    let basicBaseDmg = danHengAtk * character.basicAttack(basicAttackLevel);
    let basicDmgBoost = damageBoost + basicBoost;

    let basic1BaseDmg = danHengAtk * character.basicAttack1(basicAttackLevel)/3;
    let basic2BaseDmgSingle = danHengAtk * (character.basicAttack2(basicAttackLevel).single - character.basicAttack1(basicAttackLevel))/2;
    let basic2BaseDmgSplash = danHengAtk * character.basicAttack2(basicAttackLevel).splash/2;

    let basic3BaseDmgSplash = danHengAtk * (character.basicAttack3(basicAttackLevel).splash - character.basicAttack2(basicAttackLevel).splash)/2;

    let ultDmgBoost = damageBoost + ultimateBoost;

    let techDmg = danHengAtk * 1.2;

    let basic1FinalDmg = 0;
    let basic1FinalDmgCrit = 0;
    let basic2FinalDmgSpread = 0;
    let basic2FinalDmgSingle = 0;
    let basic2FinalDmgSingleCrit = 0;
    let basic2FinalDmgSpreadCrit = 0;
    let basic3FinalDmgSingle = 0;
    let basic3FinalDmgSingleCrit = 0;
    let basic3FinalDmgSpread = 0;
    let basic3FinalDmgSpreadCrit = 0;
    let ultimateFinalDmgSingle = 0;
    let ultimateFinalDmgSpread = 0;

    if(eidolon >= 1) {
        let basic1FinalDmg1 = basic1BaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult;
        let basic1FinalDmg2 = basic1BaseDmg * (basicDmgBoost + 2 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;
        let basic1FinalDmg3 = basic1BaseDmg * (basicDmgBoost + 4 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;

        basic1FinalDmg = basic1FinalDmg1 + basic1FinalDmg2 + basic1FinalDmg3;

        let basic1FinalDmg1Crit = basic1BaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult * critDmgBoost;
        let basic1FinalDmg2Crit = basic1BaseDmg * (basicDmgBoost + 2 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult *  breakMult * critDmgBoost;
        let basic1FinalDmg3Crit = basic1BaseDmg * (basicDmgBoost + 4 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult *  breakMult * critDmgBoost;

        basic1FinalDmgCrit = basic1FinalDmg1Crit + basic1FinalDmg2Crit + basic1FinalDmg3Crit;


        let basic2FinalDmg1Single = basic2BaseDmgSingle * (basicDmgBoost + 6 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;
        let basic2FinalDmg2Single = basic2BaseDmgSingle * (basicDmgBoost + 8 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;

        basic2FinalDmgSingle = basic1FinalDmg + basic2FinalDmg1Single + basic2FinalDmg2Single;


        let basic2FinalDmg1Spread = basic2BaseDmgSplash * (basicDmgBoost + 6 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;
        let basic2FinalDmg2Spread = basic2BaseDmgSplash * (basicDmgBoost + 8 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;

        basic2FinalDmgSpread = basic2FinalDmg1Spread + basic2FinalDmg2Spread;


        let basic2FinalDmg1SingleCrit = basic2BaseDmgSingle * (basicDmgBoost + 6 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult * (critDmgBoost + character.skillAttack(skillLevel));
        let basic2FinalDmg2SingleCrit = basic2BaseDmgSingle * (basicDmgBoost + 8 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult * (critDmgBoost + 2 * character.skillAttack(skillLevel));

        basic2FinalDmgSingleCrit = basic1FinalDmgCrit + basic2FinalDmg1SingleCrit + basic2FinalDmg2SingleCrit;


        let basic2FinalDmg1SpreadCrit = basic2BaseDmgSplash * (basicDmgBoost + 6 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult * (critDmgBoost + character.skillAttack(skillLevel));
        let basic2FinalDmg2SpreadCrit = basic2BaseDmgSplash * (basicDmgBoost + 8 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult * (critDmgBoost + 2 * character.skillAttack(skillLevel));

        basic2FinalDmgSpreadCrit = basic2FinalDmg1SpreadCrit + basic2FinalDmg2SpreadCrit;

        let basic3FinalDmg1Single = basic2BaseDmgSingle * (basicDmgBoost + 10 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;
        let basic3FinalDmg2Single = basic2BaseDmgSingle * (basicDmgBoost + 10 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;

        basic3FinalDmgSingle = basic2FinalDmgSingle + basic3FinalDmg1Single + basic3FinalDmg2Single;

        let basic3FinalDmg1SingleCrit = basic2BaseDmgSingle * (basicDmgBoost + 10 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult * (critDmgBoost + 3 * character.skillAttack(skillLevel));
        let basic3FinalDmg2SingleCrit = basic2BaseDmgSingle * (basicDmgBoost + 10 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult * (critDmgBoost + 4 * character.skillAttack(skillLevel));

        basic3FinalDmgSingleCrit = basic2FinalDmgSingleCrit + basic3FinalDmg1SingleCrit + basic3FinalDmg2SingleCrit;

        let basic3FinalDmg1Spread = basic3BaseDmgSplash * (basicDmgBoost + 10 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;
        let basic3FinalDmg2Spread = basic3BaseDmgSplash * (basicDmgBoost + 10 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;

        basic3FinalDmgSpread = basic2FinalDmg1Spread + basic3FinalDmg1Spread + basic3FinalDmg2Spread;

        let basic3FinalDmg1SpreadCrit = basic3BaseDmgSplash * (basicDmgBoost + 10 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult * (critDmgBoost + 3 * character.skillAttack(skillLevel));
        let basic3FinalDmg2SpreadCrit = basic3BaseDmgSplash * (basicDmgBoost + 10 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult * (critDmgBoost + 4 * character.skillAttack(skillLevel));

        basic3FinalDmgSpreadCrit = basic2FinalDmg1SpreadCrit + basic3FinalDmg1SpreadCrit + basic3FinalDmg2SpreadCrit;

        let ultimateHit1BaseDmgSingle = danHengAtk * character.ultimateAttack(ultimateLevel).single * 0.3 * (ultDmgBoost) * defMult * vulnerabilityMultiplier * resMult * breakMult;
        let ultimateHit2BaseDmgSingle = danHengAtk * character.ultimateAttack(ultimateLevel).single * 0.3 * (ultDmgBoost + 2 * character.talent(talentLevel))  * defMult * vulnerabilityMultiplier * resMult * breakMult;
        let ultimateHit3BaseDmgSingle = danHengAtk * character.ultimateAttack(ultimateLevel).single * 0.4 * (ultDmgBoost + 4 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;
    
        let ultimateHit1BaseDmgSpread = danHengAtk * character.ultimateAttack(ultimateLevel).splash * 0.3 * (ultDmgBoost) * defMult * vulnerabilityMultiplier * resMult * breakMult;
        let ultimateHit2BaseDmgSpread = danHengAtk * character.ultimateAttack(ultimateLevel).splash * 0.3 * (ultDmgBoost + 2 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;
        let ultimateHit3BaseDmgSpread = danHengAtk * character.ultimateAttack(ultimateLevel).splash * 0.4 * (ultDmgBoost + 4 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;

        ultimateFinalDmgSingle = ultimateHit1BaseDmgSingle + ultimateHit2BaseDmgSingle + ultimateHit3BaseDmgSingle;
        ultimateFinalDmgSpread = ultimateHit1BaseDmgSpread + ultimateHit2BaseDmgSpread + ultimateHit3BaseDmgSpread;

    }else {
        let basic1FinalDmg1 = basic1BaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult;
        let basic1FinalDmg2 = basic1BaseDmg * (basicDmgBoost + character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;
        let basic1FinalDmg3 = basic1BaseDmg * (basicDmgBoost + 2 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;

        basic1FinalDmg = basic1FinalDmg1 + basic1FinalDmg2 + basic1FinalDmg3;

        let basic1FinalDmg1Crit = basic1BaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult * critDmgBoost;
        let basic1FinalDmg2Crit = basic1BaseDmg * (basicDmgBoost + character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult *  breakMult * critDmgBoost;
        let basic1FinalDmg3Crit = basic1BaseDmg * (basicDmgBoost + 2 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult *  breakMult * critDmgBoost;

        basic1FinalDmgCrit = basic1FinalDmg1Crit + basic1FinalDmg2Crit + basic1FinalDmg3Crit;


        let basic2FinalDmg1Single = basic2BaseDmgSingle * (basicDmgBoost + 3 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;
        let basic2FinalDmg2Single = basic2BaseDmgSingle * (basicDmgBoost + 4 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;

        basic2FinalDmgSingle = basic1FinalDmg + basic2FinalDmg1Single + basic2FinalDmg2Single;


        let basic2FinalDmg1Spread = basic2BaseDmgSplash * (basicDmgBoost + 3 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;
        let basic2FinalDmg2Spread = basic2BaseDmgSplash * (basicDmgBoost + 4 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;

        basic2FinalDmgSpread = basic2FinalDmg1Spread + basic2FinalDmg2Spread;


        let basic2FinalDmg1SingleCrit = basic2BaseDmgSingle * (basicDmgBoost + 3 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult * (critDmgBoost + character.skillAttack(skillLevel));
        let basic2FinalDmg2SingleCrit = basic2BaseDmgSingle * (basicDmgBoost + 4 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult * (critDmgBoost + 2 * character.skillAttack(skillLevel));

        basic2FinalDmgSingleCrit = basic1FinalDmgCrit + basic2FinalDmg1SingleCrit + basic2FinalDmg2SingleCrit;


        let basic2FinalDmg1SpreadCrit = basic2BaseDmgSplash * (basicDmgBoost + 3 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult * (critDmgBoost + character.skillAttack(skillLevel));
        let basic2FinalDmg2SpreadCrit = basic2BaseDmgSplash * (basicDmgBoost + 4 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult * (critDmgBoost + 2 * character.skillAttack(skillLevel));

        basic2FinalDmgSpreadCrit = basic2FinalDmg1SpreadCrit + basic2FinalDmg2SpreadCrit;

        let basic3FinalDmg1Single = basic2BaseDmgSingle * (basicDmgBoost + 5 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;
        let basic3FinalDmg2Single = basic2BaseDmgSingle * (basicDmgBoost + 6 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;

        basic3FinalDmgSingle = basic2FinalDmgSingle + basic3FinalDmg1Single + basic3FinalDmg2Single;

        let basic3FinalDmg1SingleCrit = basic2BaseDmgSingle * (basicDmgBoost + 5 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult * (critDmgBoost + 3 * character.skillAttack(skillLevel));
        let basic3FinalDmg2SingleCrit = basic2BaseDmgSingle * (basicDmgBoost + 6 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult * (critDmgBoost + 4 * character.skillAttack(skillLevel));

        basic3FinalDmgSingleCrit = basic2FinalDmgSingleCrit + basic3FinalDmg1SingleCrit + basic3FinalDmg2SingleCrit;

        let basic3FinalDmg1Spread = basic3BaseDmgSplash * (basicDmgBoost + 5 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;
        let basic3FinalDmg2Spread = basic3BaseDmgSplash * (basicDmgBoost + 6 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;

        basic3FinalDmgSpread = basic2FinalDmg1Spread + basic3FinalDmg1Spread + basic3FinalDmg2Spread;

        let basic3FinalDmg1SpreadCrit = basic3BaseDmgSplash * (basicDmgBoost + 5 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult * (critDmgBoost + 3 * character.skillAttack(skillLevel));
        let basic3FinalDmg2SpreadCrit = basic3BaseDmgSplash * (basicDmgBoost + 6 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult * (critDmgBoost + 4 * character.skillAttack(skillLevel));

        basic3FinalDmgSpreadCrit = basic2FinalDmg1SpreadCrit + basic3FinalDmg1SpreadCrit + basic3FinalDmg2SpreadCrit;

        let ultimateHit1BaseDmgSingle = danHengAtk * character.ultimateAttack(ultimateLevel).single * 0.3 * (ultDmgBoost) * defMult * vulnerabilityMultiplier * resMult * breakMult;
        let ultimateHit2BaseDmgSingle = danHengAtk * character.ultimateAttack(ultimateLevel).single * 0.3 * (ultDmgBoost + 1 * character.talent(talentLevel))  * defMult * vulnerabilityMultiplier * resMult * breakMult;
        let ultimateHit3BaseDmgSingle = danHengAtk * character.ultimateAttack(ultimateLevel).single * 0.4 * (ultDmgBoost + 2 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;
    
        let ultimateHit1BaseDmgSpread = danHengAtk * character.ultimateAttack(ultimateLevel).splash * 0.3 * (ultDmgBoost) * defMult * vulnerabilityMultiplier * resMult * breakMult;
        let ultimateHit2BaseDmgSpread = danHengAtk * character.ultimateAttack(ultimateLevel).splash * 0.3 * (ultDmgBoost + 1 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;
        let ultimateHit3BaseDmgSpread = danHengAtk * character.ultimateAttack(ultimateLevel).splash * 0.4 * (ultDmgBoost + 2 * character.talent(talentLevel)) * defMult * vulnerabilityMultiplier * resMult * breakMult;

        ultimateFinalDmgSingle = ultimateHit1BaseDmgSingle + ultimateHit2BaseDmgSingle + ultimateHit3BaseDmgSingle;
        ultimateFinalDmgSpread = ultimateHit1BaseDmgSpread + ultimateHit2BaseDmgSpread + ultimateHit3BaseDmgSpread;
    }
    
    let maxToughnessMult = 0.5 + (enemyToughness/120);
    let breakBaseDmg = 0.5 * levelMult[character.stats[level]["level"]] * maxToughnessMult;
    let breakDmg = breakBaseDmg * (1 + breakEffect) * defMult * resMult * vulnerabilityMultiplier * breakMult; 
    let delay = 0.3 * (1 + breakEffect);

    return (
        <div className="damage-details">
            <div className='header'>Damage Details</div>
            <div className="divider"></div>
            <div className="damage-title header">Basic Attack ({basicAttackLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Basic</td>
                        <td className='tb-text'>{(basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult).toFixed(0)}</td>
                        <td className='tb-text'>{(basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult * critDmgBoost).toFixed(0)}</td>
                        <td className='tb-text'>{((basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Basic Attack lv1 ({basicAttackLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Basic</td>
                        <td className='tb-text'>{(basic1FinalDmg).toFixed(0)}</td>
                        <td className='tb-text'>{(basic1FinalDmgCrit).toFixed(0)}</td>
                        <td className='tb-text'>{((basic1FinalDmg) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{60}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{60}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Basic Attack lv2 ({basicAttackLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Single</td>
                        <td className='tb-text'>{(basic2FinalDmgSingle).toFixed(0)}</td>
                        <td className='tb-text'>{(basic2FinalDmgSingleCrit).toFixed(0)}</td>
                        <td className='tb-text'>{((basic2FinalDmgSingle) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Blast</td>
                        <td className='tb-text'>{(basic2FinalDmgSpread).toFixed(0)}</td>
                        <td className='tb-text'>{(basic2FinalDmgSpreadCrit).toFixed(0)}</td>
                        <td className='tb-text'>{((basic2FinalDmgSpread) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(35 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(35 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break Single</td>
                        <td className='tb-text'>{90}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{90}</td>
                    </tr>
                    <tr>
                        <td>Break Blast</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Basic Attack lv3 ({basicAttackLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Single</td>
                        <td className='tb-text'>{(basic3FinalDmgSingle).toFixed(0)}</td>
                        <td className='tb-text'>{(basic3FinalDmgSingleCrit).toFixed(0)}</td>
                        <td className='tb-text'>{((basic3FinalDmgSingle) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Blast</td>
                        <td className='tb-text'>{(basic3FinalDmgSpread).toFixed(0)}</td>
                        <td className='tb-text'>{(basic3FinalDmgSpreadCrit).toFixed(0)}</td>
                        <td className='tb-text'>{((basic3FinalDmgSpread) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(40 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(40 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break Single</td>
                        <td className='tb-text'>{120}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{120}</td>
                    </tr>
                    <tr>
                        <td>Break Blast</td>
                        <td className='tb-text'>{60}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{60}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Ultimate Attack ({ultimateLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Single</td>
                        <td className='tb-text'>{(ultimateFinalDmgSingle).toFixed(0)}</td>
                        <td className='tb-text'>{(ultimateFinalDmgSingle * (critDmgBoost + ultimateCritDmg)).toFixed(0)}</td>
                        <td className='tb-text'>{((ultimateFinalDmgSingle) * (1 + (critDmg + ultimateCritDmg) * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Blast</td>
                        <td className='tb-text'>{(ultimateFinalDmgSpread).toFixed(0)}</td>
                        <td className='tb-text'>{(ultimateFinalDmgSpread * (critDmgBoost + ultimateCritDmg)).toFixed(0)}</td>
                        <td className='tb-text'>{((ultimateFinalDmgSpread) * (1 + (critDmg + ultimateCritDmg) * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{60}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{60}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Technique</div>
                <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>DMG</td>
                        <td className='tb-text'>{((techDmg * damageBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{((techDmg * damageBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((techDmg * damageBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Weakness Break</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Type</th>
                        <th scope="col">Value</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Break Damage</td>
                        <td className='tb-text'>{(breakDmg).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Action Delay</td>
                        <td className='tb-text'>{(delay * 100).toFixed(0)}%</td>
                    </tr>
                </tbody>
            </table>
    </div>
    )
}


export default DanHengILTable;