import "./calculator.css"

import CharacterDetails from "./calculatorParts/characterDetails"
import DamageDetails from "./calculatorParts/damageDetails"
import SetUp from "./calculatorParts/setUp";
import GlobalBuffs from "./calculatorParts/globalBuffs";
import { DataStateProvider } from './globalData';
import Intro from "./navbar/introduction";
import Credits from "./navbar/credits"
import Changes from "./navbar/changelog";
import Options from "./navbar/options";
import CoffeeWidget from "./navbar/buyCoffeeWidget";
import { useState } from "react";
import { Settings } from "@mui/icons-material";
import LocalCafeIcon from '@mui/icons-material/LocalCafe';

function MainCalculator() {

  const [introOpen, setIntroOpen] = useState(false);
  const [changesOpen, setChangesOpen] = useState(false);
  const [creditsOpen, setCreditsOpen] = useState(false);
  const [optionsOpen, setOptionsOpen] = useState(false);
  const [buyCoffeeOpen, setBuyCoffeeOpen] = useState(false);

  return (
    <DataStateProvider>
      <div className="main-container">
        <div className="navbar-container">
            <button onClick={() => setOptionsOpen(true)}  className="calc-btn" aria-label="Settings" style={{width: "30px", margin: "0", height: "30px", borderRadius: "0", border: "0", padding: "0"}}><Settings></Settings></button>
            <Options open={optionsOpen} setOpen={setOptionsOpen}></Options>
            <button onClick={() => setIntroOpen(true)} className="calc-btn" style={{width: "100px", margin: "0", height: "30px", borderRadius: "0", border: "0", textAlign: "center", padding: "0"}}> Introduction </button>
            <Intro open={introOpen} setOpen={setIntroOpen}></Intro>
            <button onClick={() => setChangesOpen(true)} className="calc-btn" style={{width: "80px", margin: "0", height: "30px", borderRadius: "0", border: "0", padding: "0"}}> Changes </button>
            <Changes open={changesOpen} setOpen={setChangesOpen}></Changes>
            <button onClick={() => setCreditsOpen(true)}  className="calc-btn" style={{width: "70px", margin: "0", height: "30px", borderRadius: "0", border: "0", padding: "0"}}> Credits </button>
            <Credits open={creditsOpen} setOpen={setCreditsOpen}></Credits>
            <button onClick={() => setBuyCoffeeOpen(true)}  className="calc-btn buyCoffee"> Buy Me A Coffee <LocalCafeIcon style={{color: "yellow", fontSize: "20px"}}></LocalCafeIcon> </button>
            <CoffeeWidget open={buyCoffeeOpen} setOpen={setBuyCoffeeOpen}></CoffeeWidget>
        </div>
        <div className="calc-container">
          <CharacterDetails></CharacterDetails>
          <SetUp></SetUp>
          <GlobalBuffs></GlobalBuffs>
          <DamageDetails></DamageDetails>
        </div>
      </div>
    </DataStateProvider>
  );
}

export default MainCalculator;