class SilverWolf {
    constructor() {
        this.stats = [
            {level: 1,  atk: 87.12,  def: 62.7,   hp: 143, spd: 107, taunt: 100, energy: 110},
            {level: 20, atk: 169.88, def: 122.26, hp: 278, spd: 107, taunt: 100, energy: 110, atkPercent: 0.04},
            {level: 20, atk: 204.73, def: 147.34, hp: 335, spd: 107, taunt: 100, energy: 110, atkPercent: 0.04},
            {level: 40, atk: 326.7,  def: 235.12, hp: 535, spd: 107, taunt: 100, energy: 110, atkPercent: 0.04, effectHitRate: 0.04},
            {level: 40, atk: 361.55, def: 260.2,  hp: 592, spd: 107, taunt: 100, energy: 110, atkPercent: 0.08, effectHitRate: 0.04, quantumDmg: 0.032},
            {level: 50, atk: 405.11, def: 291.55, hp: 663, spd: 107, taunt: 100, energy: 110, atkPercent: 0.08, effectHitRate: 0.04, quantumDmg: 0.032},
            {level: 50, atk: 439.96, def: 316.63, hp: 720, spd: 107, taunt: 100, energy: 110, atkPercent: 0.14, effectHitRate: 0.04, quantumDmg: 0.032},
            {level: 60, atk: 483.52, def: 347.98, hp: 791, spd: 107, taunt: 100, energy: 110, atkPercent: 0.14, effectHitRate: 0.04, quantumDmg: 0.032},
            {level: 60, atk: 518.36, def: 373.06, hp: 848, spd: 107, taunt: 100, energy: 110, atkPercent: 0.2,  effectHitRate: 0.1,  quantumDmg: 0.032},
            {level: 70, atk: 561.92, def: 404.41, hp: 920, spd: 107, taunt: 100, energy: 110, atkPercent: 0.2,  effectHitRate: 0.1,  quantumDmg: 0.032},
            {level: 70, atk: 596.77, def: 429.49, hp: 977, spd: 107, taunt: 100, energy: 110, atkPercent: 0.2,  effectHitRate: 0.1,  quantumDmg: 0.032},
            {level: 80, atk: 640.33, def: 460.84, hp: 1048,spd: 107, taunt: 100, energy: 110, atkPercent: 0.28, effectHitRate: 0.18, quantumDmg: 0.08},
        ];

        this.name = "Silver Wolf";
        this.path = "Nihility";
        this.rarity = 'fiveStar';
        this.element = "Quantum";
        this.id = "silverwolf";
        this.E4ON = false;
        this.E4Stacks = 0;
    }

    setE4Stacks(value) {
        this.E4Stacks = value;
    }

    setE4(value) {
        this.E4ON = value;
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "System Warning",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level == 0) {
            return {
                chance: 0.75,
                resPen: 0.075,
                multiplier: 0.98,
            }
        } else if(level == 1) {
            return {
                chance: 0.76,
                resPen: 0.0775,
                multiplier: 1.078,
            }
        }else if(level == 2) {
            return {
                chance: 0.77,
                resPen: 0.08,
                multiplier: 1.176,
            }
        }else if(level == 3) {
            return {
                chance: 0.78,
                resPen: 0.0825,
                multiplier: 1.274,
            }
        }else if(level == 4) {
            return {
                chance: 0.79,
                resPen: 0.085,
                multiplier: 1.372,
            }
        } else if(level == 5){
            return {
                chance: 0.8,
                resPen: 0.0875,
                multiplier: 1.47,
            }
        }else if(level == 6){
            return {
                chance: 0.8125,
                resPen: 0.0906,
                multiplier: 1.5925,
            }
        }else if(level == 7){
            return {
                chance: 0.825,
                resPen: 0.0938,
                multiplier: 1.715,
            }
        }else if(level == 8){
            return {
                chance: 0.8375,
                resPen: 0.0969,
                multiplier: 1.8375,
            }
        }else if(level == 9){
            return {
                chance: 0.85,
                resPen: 0.1,
                multiplier: 1.96,
            }
        }else if(level == 10){
            return {
                chance: 0.86,
                resPen: 0.1025,
                multiplier: 2.058,
            }
        }else if(level == 11) {
            return {
                chance: 0.87,
                resPen: 0.105,
                multiplier: 2.156,
            }
        }
    }

    skillDescription(level) {
        return {
            name: "Allow Changes?",
            description: (
                <div className="buff-description">
                    <div>There is a </div>
                    <div className="buff-text">{(this.skillAttack(level).chance * 100).toFixed(1)}% </div>
                    <div> base chance to add 1 Weakness of an on-field character's Type to the target enemy. This also reduces the enemy's </div>
                    <div className="buff-text"> DMG RES </div>
                    <div> to that Weakness Type by </div>
                    <div className="buff-text"> 20% </div>
                    <div> for 2 turn(s). </div>
                    <br/>
                    <div> If the enemy already has that Type Weakness, the effect of DMG RES reduction to that Weakness Type will not be triggered. </div>
                    <br/>
                    <div> Each enemy can only have 1 Weakness implanted by Silver Wolf. </div>
                    <br/>
                    <div> When Silver Wolf implants another Weakness to the target, only the most recent implanted Weakness will be kept. </div>
                    <br/>
                    <div> In addition, there is a 100% base chance to further reduce the </div>
                    <div className="buff-text"> All-Type RES </div>
                    <div> of the enemy by </div>
                    <div className="buff-text">{(this.skillAttack(level).resPen * 100).toFixed(1)}% </div>
                    <div> for 2 turn(s). </div>
                    <br/>
                    <div> Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level).multiplier * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a this enemy. </div>
                </div>
            ),
        }
    }

    skillSimple(level) {
        return {
            name: "Allow Changes?",
            description: (
                <div className="buff-description">
                    <div> Reduce the </div>
                    <div className="buff-text"> All-Type RES </div>
                    <div> of the enemy by </div>
                    <div className="buff-text">{(this.skillAttack(level).resPen * 100).toFixed(1)}% </div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level < 6) {
            return {
                chance: 0.85 + (level * 0.0152),
                defIgnore: 0.36 + (level * 0.009),
                multiplier: 2.28 + (level * 0.152),
            }
        }else if(level < 10){
            return {
                chance: 0.925 + ((level - 5) * 0.0188),
                defIgnore: 0.405 + ((level - 5) * 0.0113),
                multiplier: 3.04 + ((level - 5) * 0.19),
            }
        }else {
            return {
                chance: 1 + ((level - 9) * 0.015),
                defIgnore: 0.45 + ((level - 9) * 0.009),
                multiplier: 3.8 + ((level - 9) * 0.152),
            }
        }
    }

    ultimateDescription(level) {
        return {
            name: "User Banned",
            description: (
                <div className="buff-description">
                    <div>There's a </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).chance * 100).toFixed(1)}% </div>
                    <div> base chance to decrease the target enemy's </div>
                    <div className="buff-text"> DEF </div>
                    <div> by </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).defIgnore * 100).toFixed(1)}% </div>
                    <div> for 3 turn(s). </div>
                    <br/>
                    <div> And at the same time, deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).multiplier * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to the target enemy. </div>
                </div>
            ),
        }
    }

    ultimateSimple(level) {
        return {
            name: "User Banned",
            description: (
                <div className="buff-description">
                    <div> Decrease the target enemy's </div>
                    <div className="buff-text"> DEF </div>
                    <div> by </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).defIgnore * 100).toFixed(1)}%. </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return {
                atk: 0.05 + (level * 0.005),
                defIgnore: 0.04 + (level * 0.004),
                spd: 0.03 + (level * 0.003),
                chance: 0.60 + (level * 0.012),
            }
        }else if(level < 10){
            return {
                atk: 0.075 + ((level - 5) * 0.0063),
                defIgnore: 0.06 + ((level - 5) * 0.005),
                spd: 0.045 + ((level - 5) * 0.0038),
                chance: 0.66 + ((level - 5) * 0.015),
            }
        }else {
            return {
                atk: 0.10 + ((level - 9) * 0.005),
                defIgnore: 0.08 + ((level - 9) * 0.004),
                spd: 0.06 + ((level - 9) * 0.003),
                chance: 0.72 + ((level - 9) * 0.012),
            }
        }
    }

    talentDescription(level) { 
        return {
            name: "Awaiting System Response...",
            description: (
                <div className="buff-description">
                    <div>Silver Wolf can create three types of Bugs: </div>
                    <br/>
                    <div> reduce ATK by </div>
                    <div className="buff-text"> {(this.talent(level).atk * 100).toFixed(1)}% </div>
                    <div>, reduce DEF by </div>
                    <div className="buff-text"> {(this.talent(level).defIgnore * 100).toFixed(1)}% </div>
                    <div>, and reduce SPD by </div>
                    <div className="buff-text"> {(this.talent(level).spd * 100).toFixed(1)}% </div>
                    <br/>
                    <div> Every time Silver Wolf attacks, she has a </div>
                    <div className="buff-text"> {(this.talent(level).chance * 100).toFixed(1)}% </div>
                    <div> base chance to implant a random Bug that lasts for 3 turn(s) in an enemy target. </div>
                </div>
            ),
        }
    }

    talentSimple(level) {
        return {
            name: "Awaiting System Response...",
            description: (
                <div className="buff-description">
                    <div>Reduce DEF by </div>
                    <div className="buff-text"> {(this.talent(level).defIgnore * 100).toFixed(1)}% </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Force Quit Program",
            description: (
                <div className="buff-description">
                    <div> Immediately attacks the enemy. After entering battle, deals Quantum DMG equal to 80% of Silver Wolf's ATK to all enemies, and ignores Weakness Types and reduces Toughness from all enemies. Enemies with their Weakness Broken in this way will trigger the Quantum Weakness Break effect. </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Generate",
            description: (
                <div className="buff-description">
                    <div> Bug's duration is extended for 1 turn(s). Every time an enemy is inflicted with Weakness Break, Silver Wolf has a 65% base chance of implanting a random Bug in the enemy. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Inject",
            description: (
                <div className="buff-description">
                    <div> The duration of the Weakness implanted by Silver Wolf's Skill increases by 1 turn(s). </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Side Note",
            description: (
                <div className="buff-description">
                    <div>If there are 3 or more debuff(s) affecting the enemy when the Skill is used, then the Skill decreases the enemy's All-Type RES by an additional 3%. </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Social Engineering",
            description: (
                <div className="buff-description">
                    <div> After using her Ultimate to attack enemies, Silver Wolf regenerates 7 Energy for every debuff that the target enemy currently has. This effect can be triggered up to 5 time(s) in each use of her Ultimate. </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Zombie Network",
            description: (
                <div className="buff-description">
                    <div> When an enemy enters battle, reduces their Effect RES by 20%. </div>
                </div>
            )
        }
    }

    E3() {
        return {
            name: "Payload",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }

    E4() {
        return {
            name: "Bounce Attack",
            description:(
                <div className="buff-description">
                    <div>After using her Ultimate to attack enemies, deals Additional Quantum DMG equal to 20% of Silver Wolf's ATK for every debuff currently on the enemy target. This effect can be triggered for a maximum of 5 time(s) during each use of her Ultimate.</div>
                </div>
            )
        }
    }

    E5() {
        return {
            name: "Brute Force Attack",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: "Overlay Network",
            description: (
                <div className="buff-description">
                    <div>For every debuff the target enemy has, the DMG dealt by Silver Wolf increases by 20%, up to a limit of 100%.</div>
                </div>
            ),
        }
    }
}


export default SilverWolf;