class Kafka {
    constructor() {
        this.stats = [
            {level: 1,  atk: 92.4,   def: 66,    hp: 148, spd: 100, taunt: 100, energy: 120},
            {level: 20, atk: 180.18, def: 128.7, hp: 288, spd: 100, taunt: 100, energy: 120, atkPercent: 0.04},
            {level: 20, atk: 217.14, def: 155.1, hp: 347, spd: 100, taunt: 100, energy: 120, atkPercent: 0.04},
            {level: 40, atk: 346.5,  def: 247.5, hp: 554, spd: 100, taunt: 100, energy: 120, atkPercent: 0.04, effectHitRate: 0.04},
            {level: 40, atk: 383.46, def: 273.9, hp: 614, spd: 100, taunt: 100, energy: 120, atkPercent: 0.08, effectHitRate: 0.04, effectRes: 0.04},
            {level: 50, atk: 429.66, def: 306.9, hp: 687, spd: 100, taunt: 100, energy: 120, atkPercent: 0.08, effectHitRate: 0.04, effectRes: 0.04},
            {level: 50, atk: 466.62, def: 333.3, hp: 747, spd: 100, taunt: 100, energy: 120, atkPercent: 0.14, effectHitRate: 0.04, effectRes: 0.04},
            {level: 60, atk: 512.82, def: 366.3, hp: 821, spd: 100, taunt: 100, energy: 120, atkPercent: 0.14, effectHitRate: 0.04, effectRes: 0.04},
            {level: 60, atk: 549.78, def: 392.7, hp: 880, spd: 100, taunt: 100, energy: 120, atkPercent: 0.2,  effectHitRate: 0.1,  effectRes: 0.04},
            {level: 70, atk: 595.98, def: 425.7, hp: 954, spd: 100, taunt: 100, energy: 120, atkPercent: 0.2,  effectHitRate: 0.1,  effectRes: 0.04},
            {level: 70, atk: 632.94, def: 452.1, hp: 1013,spd: 100, taunt: 100, energy: 120, atkPercent: 0.2,  effectHitRate: 0.1,  effectRes: 0.1},
            {level: 80, atk: 679.14, def: 485.1, hp: 1087,spd: 100, taunt: 100, energy: 120, atkPercent: 0.28, effectHitRate: 0.18, effectRes: 0.1},
        ];

        this.name = "Kafka";
        this.path = "Nihility";
        this.rarity = 'fiveStar';
        this.element = "Lightning";
        this.id = "kafka";
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Midnight Tumult",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level == 0) {
            return {
                single: 0.8,
                spread: 0.30,
                dotTrigger: 0.6,
            }
        } else if(level == 1) {
            return {
                single: 0.88,
                spread: 0.33,
                dotTrigger: 0.615,
            }
        }else if(level == 2) {
            return {
                single: 0.96,
                spread: 0.36,
                dotTrigger: 0.645,
            }
        }else if(level == 3) {
            return {
                single: 1.04,
                spread: 0.39,
                dotTrigger: 0.645,
            }
        }else if(level == 4) {
            return {
                single: 1.12,
                spread: 0.42,
                dotTrigger: 0.66,
            }
        } else if(level == 5){
            return {
                single: 1.2,
                spread: 0.45,
                dotTrigger: 0.675,
            }
        }else if(level == 6){
            return {
                single: 1.3,
                spread: 0.4875,
                dotTrigger: 0.6938,
            }
        }else if(level == 7){
            return {
                single: 1.4,
                spread: 0.525,
                dotTrigger: 0.7125,
            }
        }else if(level == 8){
            return {
                single: 1.5,
                spread: 0.5625,
                dotTrigger: 0.7313,
            }
        }else if(level == 9){
            return {
                single: 1.6,
                spread: 0.60,
                dotTrigger: 0.75,
            }
        }else if(level == 10){
            return {
                single: 1.68,
                spread: 0.63,
                dotTrigger: 0.765,
            }
        }else if(level == 11) {
            return {
                single: 1.76,
                spread: 0.66,
                dotTrigger: 0.78,
            }
        }
    }

    skillDescription(level) {
        return {
            name: "Caressing Moonlight",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level).single * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a target enemy, and deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level).spread * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to enemies adjacent to it. </div>
                    <br/>
                    <div> If the target enemy is currently receiving DoT, all DoTs currently placed on that enemy immediately produce DMG equal to </div>
                    <div className="buff-text">{(this.skillAttack(level).dotTrigger * 100).toFixed(1)}%</div>
                    <div> of their original DMG </div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level == 0) {
            return {
                multiplier: 0.48,
                dotTrigger: 0.8,
                dot: 1.16,
            }
        } else if(level == 1) {
            return {
                multiplier: 0.512,
                dotTrigger: 0.82,
                dot: 1.2688,
            }
        }else if(level == 2) {
            return {
                multiplier: 0.544,
                dotTrigger: 0.84,
                dot: 1.3775,
            }
        }else if(level == 3) {
            return {
                multiplier: 0.576,
                dotTrigger: 0.86,
                dot: 1.4863,
            }
        }else if(level == 4) {
            return {
                multiplier: 0.608,
                dotTrigger: 0.88,
                dot: 1.595,
            }
        } else if(level == 5){
            return {
                multiplier: 0.64,
                dotTrigger: 0.9,
                dot: 1.7581,
            }
        }else if(level == 6){
            return {
                multiplier: 0.68,
                dotTrigger: 0.925,
                dot: 1.9756,
            }
        }else if(level == 7){
            return {
                multiplier: 0.72,
                dotTrigger: 0.95,
                dot: 2.2475,
            }
        }else if(level == 8){
            return {
                multiplier: 0.76,
                dotTrigger: 0.975,
                dot: 2.5738,
            }
        }else if(level == 9){
            return {
                multiplier: 0.8,
                dotTrigger: 1,
                dot: 2.9,
            }
        }else if(level == 10){
            return {
                multiplier: 0.832,
                dotTrigger: 1.02,
                dot: 3.0414,
            }
        }else if(level == 11) {
            return {
                multiplier: 0.864,
                dotTrigger: 1.04,
                dot: 3.1828,
            }
        }
    }

    ultimateDescription(level) {
        return {
            name: "Synthetic Black Hole",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).multiplier * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to all enemies, with a  </div>
                    <div className="buff-text"> 100% base chance </div>
                    <div>  for enemies  to become Shocked and immediately take DMG from their current Shock state, equal to </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).dotTrigger * 100).toFixed(1)}% </div>
                    <div> of its original DMG. Shock lasts for 2 turn(s). </div>
                    <br/>
                    <div> While Shocked, enemies receive </div>
                    <div className="buff-text"> {this.element} DoT </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).dot * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> at the beginning of each turn. </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 0.42 + (level * 0.098);
        }else if(level < 10){
            return 0.91 + ((level - 5) * 0.1225);
        }else {
            return 1.4 + ((level - 9) * 0.098);
        }
    }

    talentDescription(level) { 
        return {
            name: "Gentle but Cruel",
            description: (
                <div className="buff-description">
                    <div>After an ally of Kafka's uses Basic ATK on an enemy target, Kafka immediately launches </div>
                    <div className="buff-text"> 1 Follow Up attack</div>
                    <div> and dealing </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.talent(level) * 100).toFixed(1)}% </div>
                    <div> of her </div>
                    <div className="buff-text">ATK</div>
                    <div> to that target. with a 100% base chance to inflict Shock equivalent to that applied by her Ultimate to the attacked enemy target, lasting for 2 turns. This effect can only be triggered 1 time per turn. </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Mercy Is Not Forgiveness",
            description: (
                <div className="buff-description">
                    <div> Immediately attacks all enemies within a set range. After entering battle, deals Lightning DMG equal to 50% of Kafka's ATK to all enemies, with a 100% base chance to inflict Shock equivalent to that applied by her Ultimate on every enemy target for 2 turn(s). </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Torture",
            description: (
                <div className="buff-description">
                    <div> When the Ultimate is used, enemy targets will now receive DMG immediately from all currently applied DoT sources instead of just receiving DMG immediately from the currently applied Shock state. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Plunder",
            description: (
                <div className="buff-description">
                    <div> If an enemy is defeated while Shocked, Kafka additionally regenerates 5 Energy. </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Thorns",
            description: (
                <div className="buff-description">
                    <div> The base chance for target enemies to get Shocked by the Ultimate, the Technique, or the Talent-triggered follow-up attack increases by 30%. </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Da Capo",
            description: (
                <div className="buff-description">
                    <div> When the Talent triggers a follow-up attack, there is a 100% base chance to increase the DoT received by the target by 30% for 2 turn(s). </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Fortississimo",
            description: (
                <div className="buff-description">
                    <div> While Kafka is on the field, DoT dealt by all allies increases by 25%. </div>
                </div>
            )
        }
    }

    E3() {
        return {
            name: "Capriccio",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }

    E4() {
        return {
            name: "Recitativo",
            description:(
                <div className="buff-description">
                    <div>When an enemy target takes DMG from the Shock status inflicted by Kafka, Kafka additionally regenerates 2 Energy.</div>
                </div>
            )
        }
    }

    E5() {
        return {
            name: "Doloroso",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: "Leggiero",
            description: (
                <div className="buff-description">
                    <div>The Shock inflicted on the enemy target by the Ultimate, the Technique, or the Talent-triggered follow-up attack has a DMG multiplier increase of 156% and lasts 1 turn(s) longer.</div>
                </div>
            ),
        }
    }
}


export default Kafka;