export class Birth {
    constructor() {
        this.stats = [
            [1,  21.6,   15,     43],
            [20, 83.16,  57.75,  166],
            [20, 109.08,  75.75,  218],
            [40, 208.44, 144.75, 417],
            [40, 243,    168.75, 486],
            [50, 275.4,  191.25, 551],
            [50, 309.96, 215.25, 620],
            [60, 342.36, 237.75, 685],
            [60, 376.92, 261.75, 754],
            [70, 409.32, 284.25, 819],
            [70, 443.88, 308.25, 888],
            [80, 476.28, 330.75, 953],
        ];
        
        this.name = "The Birth of the Self";
        this.path = "Erudition";
        this.id = "birth";
        this.rarity = "fourStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases DMG dealt by the wearer's </div>
                <div className="buff-text"> follow-up attacks </div> 
                <div> by </div> 
                <div className="buff-text">{((0.24 + imposition * 0.06) * 100).toFixed(1)}%,</div>
                <div> If the current HP of the target enemy is </div> 
                <div className="buff-text"> below or equal to 50% </div> 
                <div> increases DMG dealt by </div> 
                <div className="buff-text"> follow-up attacks </div> 
                <div> by an extra </div> 
                <div className="buff-text"> {((0.24 + imposition * 0.06) * 100).toFixed(1)}% </div> 
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{
                followUpBoost: 0.24 + imposition * 0.06,
            },
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "adve1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> If the current HP of the target enemy is </div> 
                        <div className="buff-text"> below or equal to 50% </div> 
                        <div> increases DMG dealt by </div> 
                        <div className="buff-text"> follow-up attacks </div> 
                        <div> by an extra </div> 
                        <div className="buff-text"> {((0.24 + imposition * 0.06) * 100).toFixed(1)}% </div> 
                    </div>
                ),
                effect: {
                    followUpBoost: 0.24 + imposition * 0.06,
                },
                type: "buff",
                hasStack: false,
                hasInput: false,
            },
        }
    }
}

export default Birth;