import add from '../../../assets/add.webp';
import { globalData } from '../../globalData';
import { useContext, useState, useEffect } from 'react';
import SelectionMenu from './optionsSelect';
import DeleteIcon from '@mui/icons-material/Delete';

function Party() {

    const {
        teamMate1,
        teamMate2,
        teamMate3,
        setTeamMate1,
        setTeamMate2,
        setTeamMate3,
        character,
        chars,
        partyMemberAdded,
        characterDoneSwitch1,
        switchTeamMate1,
        characterDoneSwitch2,
        switchTeamMate2,
        characterDoneSwitch3,
        switchTeamMate3,
        showBeta,
    } = useContext(globalData);

    const [party, setParty] = useState([]);
    const [images, setImages] = useState("");
    const [character1Image, setCharacter1Image] = useState("");
    const [character2Image, setCharacter2Image] = useState("");
    const [character3Image, setCharacter3Image] = useState("");
    const [open, setOpen] = useState(false);
    const [open2, setOpen2] = useState(false);
    const [open3, setOpen3] = useState(false);
    const [name1, setName1] = useState("");
    const [name2, setName2] = useState("");
    const [name3, setName3] = useState("");
    const [filteredChars, setFilteredChars] = useState([]);

    function loadAllImg(imageName) {
        return import(`../../../assets/characterImages/${imageName}.webp`).then((image) => {
            return image.default;
        });
    }

    function loadTeam1Img (imageName) {
        import(`../../../assets/characterImages/${imageName}.webp`).then((image) => setCharacter1Image(image.default));
    }

    function loadTeam2Img (imageName) {
        import(`../../../assets/characterImages/${imageName}.webp`).then((image) => setCharacter2Image(image.default));
    }

    function loadTeam3Img (imageName) {
        import(`../../../assets/characterImages/${imageName}.webp`).then((image) => setCharacter3Image(image.default));
    }

    const handleMember1 = (event) => {
        setTeamMate1(event.target.dataset.id);
        setName1(event.target.dataset.id);
    }

    const handleMember2 = (event) => {
        setTeamMate2(event.target.dataset.id);
        setName2(event.target.dataset.id);
    }

    const handleMember3 = (event) => {
        setTeamMate3(event.target.dataset.id);
        setName3(event.target.dataset.id);
    }

    useEffect(() => {
        const newParty = [];
        if(teamMate1) {
            newParty.push(teamMate1.name);
        }
        if(teamMate2) {
            newParty.push(teamMate2.name);
        }
        if(teamMate3) {
            newParty.push(teamMate3.name);
        }
        setParty(newParty);
    }, [partyMemberAdded]);

    useEffect(() => {
        const newChars = chars.filter((char) => {
            if(showBeta) {
                return !party.includes(char.name) && char.name !== character.name;
            }else {
                return !party.includes(char.name) && char.name !== character.name && !char.isBeta;
            }
        });
        setFilteredChars(newChars);
    }, [party, character, chars]);

    useEffect(() => {
        const loadAllImages = async () => {
          const initial = Array(filteredChars.length).fill("");
          setImages(initial);
    
          const promises = filteredChars.map((char, index) => {
            return loadAllImg(char.id, index);
          });
    
          const loadedImages = await Promise.all(promises);
          setImages(loadedImages);
          
        };
    
        loadAllImages();
    }, [filteredChars]);

    useEffect(() => {
        if(teamMate1) {
            loadTeam1Img(teamMate1.id);
        }else {
            setCharacter1Image(add);
        }
        if(teamMate2) {
            loadTeam2Img(teamMate2.id);
        }else {
            setCharacter2Image(add);
        }
        if(teamMate3) {
            loadTeam3Img(teamMate3.id);
        }else {
            setCharacter3Image(add);
        }
    }, [teamMate1, teamMate2, teamMate3]);
    
    useEffect(() => {
        switchTeamMate1(name1);
    }, [characterDoneSwitch1]);

    useEffect(() => {
        switchTeamMate2(name2);
    }, [characterDoneSwitch2]);

    useEffect(() => {
        switchTeamMate3(name3);
    }, [characterDoneSwitch3]);

    return (
        <div className='party-container'>
            <div className='centered' style={{justifyContent: "flex-start"}}>
                <img className={teamMate1 ? `party-member ${teamMate1.rarity}` : "party-member"} src={character1Image} alt='' width={70} height={70} onClick={() => setOpen(true)}></img>
                <SelectionMenu text="Choose first party member" handleSelection={handleMember1} characterList={filteredChars} images={images} open={open} setOpen={setOpen}></SelectionMenu>
                {teamMate1 && 
                    <DeleteIcon onClick={(e) => handleMember1(e)} className='mt-2' style={{color: "red", cursor: "pointer"}}></DeleteIcon>
                }
            </div>
            <div className='centered' style={{justifyContent: "flex-start"}}>
                <img className={teamMate2 ? `party-member ${teamMate2.rarity}` : "party-member"} src={character2Image} alt='' width={70} height={70} onClick={() => setOpen2(true)}></img>
                <SelectionMenu text="Choose second party member" handleSelection={handleMember2} characterList={filteredChars} images={images} open={open2} setOpen={setOpen2}></SelectionMenu>
                {teamMate2 && 
                    <DeleteIcon onClick={(e) => handleMember2(e)} className='mt-2' style={{color: "red", cursor: "pointer"}}></DeleteIcon>
                }
            </div>
            <div className='centered' style={{justifyContent: "flex-start"}}>
                <img className={teamMate3 ? `party-member ${teamMate3.rarity}` : "party-member"} src={character3Image} alt='' width={70} height={70} onClick={() => setOpen3(true)}></img>
                <SelectionMenu text="Choose third party member" handleSelection={handleMember3} characterList={filteredChars} images={images} open={open3} setOpen={setOpen3}></SelectionMenu>
                {teamMate3 && 
                    <DeleteIcon onClick={(e) => handleMember3(e)} className='mt-2' style={{color: "red", cursor: "pointer"}}></DeleteIcon>
                }
            </div>
        </div>
    )
}

export default Party;