import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';
import { Luocha } from "../../../../../../../database/characters";

const LuochaBuffs = () => {

    const { eidolon, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    const luocha = new Luocha();

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    useEffect(() => {

        if(eidolon < 2 && characterBuffStates[0]) {
            updateStatesFromJson(E2Buff, true);
            characterBuffStates[0] = false;
        }
        
        if(eidolon < 1 && characterBuffStates[2]) {
            updateStatesFromJson(E1Buff, true);
            characterBuffStates[2] = false;
        }

        if(eidolon < 6 && characterBuffStates[1]) {
            updateStatesFromJson(E6Buff, true);
            characterBuffStates[1] = false;
        }

    }, [eidolon]);

    const E1Buff = {
        effect: {
            atkPercent: 0.2,
        },
    }

    const E2Buff = {
        effect: {
            OGH: 0.3,
        },
    }

    const E6Buff = {
        effect: {
            quantumPen: 0.2,
            firePen: 0.2,
            icePen: 0.2,
            lightningPen: 0.2,
            physicalPen: 0.2,
            windPen: 0.2,
            imaginaryPen: 0.2,
        },
    }

    return (
        <>
            {eidolon >= 1 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, E1Buff)} />}
                    label={"(E1) " + luocha.E1().name}
                    />
                    <div>{luocha.E1().description}</div>
                </>
            }

            {eidolon >= 2 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, E2Buff)} />}
                    label={"(E2) " + luocha.E2().name}
                    />
                    <div>{luocha.E2().description}</div>
                </>
            }

            {eidolon >= 6 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, E6Buff)} />}
                    label={"(E6) " + luocha.E6().name}
                    />
                    <div>{luocha.E6().description}</div>
                </>
            }

        </>
    )
}

export const LuochaGlobalBuffs = (props) => {

    const luocha = new Luocha();
    const { updateStatesFromJson } = useContext(globalData);

    const luochaBuffStates = props.buffStates;
    const setLuochaBuffStates = props.setBuffStates;
    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {

        if(luochaBuffStates[1]) {
            updateStatesFromJson(E6Buff, true);
            luochaBuffStates[1] = false;
        }

        if(luochaBuffStates[2]) {
            updateStatesFromJson(E1Buff, true);
            luochaBuffStates[2] = false;
        }

        setDone(!done);
    }, [characterSwitched]);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...luochaBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setLuochaBuffStates(updatedStates);
    }


    const E1Buff = {
        effect: {
            atkPercent: 0.2,
        },
    }

    const E6Buff = {
        effect: {
            quantumPen: 0.2,
            firePen: 0.2,
            icePen: 0.2,
            lightningPen: 0.2,
            physicalPen: 0.2,
            windPen: 0.2,
            imaginaryPen: 0.2,
        },
    }

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={luochaBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, E1Buff)} />}
            label={"(E1) " + luocha.E1().name}
            />
            <div>{luocha.E1().description}</div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={luochaBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, E6Buff)} />}
            label={"(E6) " + luocha.E6().name}
            />
            <div>{luocha.E6().description}</div>

        </>
    )
}

export default LuochaBuffs;