import React, { useContext, useState, useEffect } from 'react';
import EnemyStats from "./setUpParts/enemyStats";
import { globalData } from '../globalData';
import SelectionMenu from "./setUpParts/optionsSelect";
import ConeDetails from './setUpParts/lightConeDetails';
import nullCone from "../../assets/lcImages/nullCone.webp";
import ArrowForwardIosSharpIcon from '@mui/icons-material/ArrowForwardIosSharp';
import {Accordion, AccordionDetails, Typography, AccordionSummary} from '@mui/material';
import Relics from './setUpParts/relics';
import Party from './setUpParts/party';
function SetUp() {

    const [open, setOpen] = useState(false);

    const {
        cones,
        setLightCone,
        lightCone,
        character,
        showBeta,
    } = useContext(globalData);

    const initial = Array(cones.length).fill("");
    const [images, setImages] = useState(initial);
    const [filteredCones, setFilteredCones] = useState([]);


    const handleSelection = (event) => {
        setLightCone(event.target.dataset.id);
    }

    const [coneImage, setConeImage] = useState("");
    
    function loadImg (imageName) {
        import(`../../assets/lcImages/${imageName}.webp`).then((image) => setConeImage(image.default));
    }

    async function loadAllImg(imageName, index) {
        return import(`../../assets/lcImages/${imageName}.webp`).then((image) => {
          return image.default;
          });
    }

    useEffect(() => {
        const loadAllImages = async () => {
          const initial = Array(filteredCones.length).fill("");
          setImages(initial);
    
          const promises = filteredCones.map((cone, index) => {
            return loadAllImg(cone.id, index);
          });
    
          const loadedImages = await Promise.all(promises);
          setImages(loadedImages);
          
        };
    
        loadAllImages();
    }, [filteredCones]);

    useEffect(() => {
        if(lightCone) {
            loadImg(lightCone.id);
        }else {
            setConeImage(nullCone);
        }
    }, [lightCone]);

    useEffect(() => {
        const newCones = cones.filter((cone) => {
            if(showBeta) {    
                return cone.path === character.path;
            }else {
                return cone.path === character.path && !cone.isBeta;
            }
        });
        setFilteredCones(newCones);
    }, [character, cones]);


    return (
        <div className="calc-item">
            <div className='header' style={{marginBottom: "21px", marginTop: "14px"}}>Setup</div>
            <Accordion disableGutters style={{width: 95 + "%", backgroundColor: "#3f465a", color: "white"}}>
                <AccordionSummary  expandIcon={<ArrowForwardIosSharpIcon  sx={{ fontSize: '0.9rem' }}/>}> <Typography>Light Cone</Typography></AccordionSummary>
                <AccordionDetails style={{backgroundColor: "rgb(42, 46, 55)"}}>
                    <div  className='centered'>
                        <SelectionMenu images={images} text={"Select The Light Cone"} characterList={filteredCones} handleSelection={handleSelection} open={open} setOpen={setOpen}></SelectionMenu>
                        <img src={coneImage} width={125} alt="" onClick={() => setOpen(true)} style={{cursor: "pointer"}}></img>
                    </div>
                    <ConeDetails></ConeDetails>
                </AccordionDetails>
            </Accordion>
            <Accordion disableGutters style={{width: 95 + "%", backgroundColor: "#3f465a", color: "white"}}>
                <AccordionSummary expandIcon={<ArrowForwardIosSharpIcon sx={{ fontSize: '0.9rem' }}/>} > <Typography>Relics</Typography> </AccordionSummary>
                <AccordionDetails style={{backgroundColor: "rgb(42, 46, 55)"}}>
                    <Relics></Relics>
                </AccordionDetails>
            </Accordion>
            <Accordion disableGutters style={{width: 95 + "%", backgroundColor: "#3f465a", color: "white"}}>
                <AccordionSummary  expandIcon={<ArrowForwardIosSharpIcon sx={{ fontSize: '0.9rem' }}/>} > <Typography>Enemy Setup</Typography> </AccordionSummary>
                <AccordionDetails style={{backgroundColor: "rgb(42, 46, 55)"}}>
                    <EnemyStats></EnemyStats>
                </AccordionDetails>
            </Accordion>

            <div className='header'>Party Setup</div>
            <Party></Party>
        </div>
    )
}

export default SetUp;