class Yanqing {
    constructor() {
        this.stats = [
            {level: 1,  atk: 92.4,   def: 56.1,   hp: 121, spd: 102, taunt: 75,    energy: 140},
            {level: 20, atk: 180.18, def: 109.4,  hp: 237, spd: 102, taunt: 75,    energy: 140, atkPercent: 0.04},
            {level: 20, atk: 217.14, def: 131.84, hp: 285, spd: 102, taunt: 75,    energy: 140, atkPercent: 0.04},
            {level: 40, atk: 346.5,  def: 210.38, hp: 455, spd: 102, iceDmg: 0.032, taunt: 75, energy: 140, atkPercent: 0.04},
            {level: 40, atk: 383.46, def: 232.82, hp: 504, spd: 102, iceDmg: 0.032, taunt: 75, energy: 140, atkPercent: 0.08, hpPercent: 0.04},
            {level: 50, atk: 429.66, def: 260.87, hp: 565, spd: 102, iceDmg: 0.032, taunt: 75, energy: 140, atkPercent: 0.08, hpPercent: 0.04},
            {level: 50, atk: 466.62, def: 283.31, hp: 613, spd: 102, iceDmg: 0.032, taunt: 75, energy: 140, atkPercent: 0.14, hpPercent: 0.04},
            {level: 60, atk: 512.82, def: 311.36, hp: 674, spd: 102, iceDmg: 0.032, taunt: 75, energy: 140, atkPercent: 0.14, hpPercent: 0.04},
            {level: 60, atk: 549.78, def: 333.8,  hp: 723, spd: 102, iceDmg: 0.08,  taunt: 75, energy: 140, atkPercent: 0.2,  hpPercent: 0.04},
            {level: 70, atk: 595.98, def: 361.85, hp: 783, spd: 102, iceDmg: 0.08,  taunt: 75, energy: 140, atkPercent: 0.2, hpPercent: 0.04},
            {level: 70, atk: 632.94, def: 384.29, hp: 832, spd: 102, iceDmg: 0.08,  taunt: 75, energy: 140, atkPercent: 0.2,  hpPercent: 0.1},
            {level: 80, atk: 679.14, def: 412.34, hp: 893, spd: 102, iceDmg: 0.144, taunt: 75, energy: 140, atkPercent: 0.28, hpPercent: 0.1},
        ];

        this.name = "Yanqing";
        this.path = "Hunt";
        this.rarity = 'fiveStar';
        this.element = "Ice";
        this.id = "yanqing";
        this.E1ON = false;
        this.A2ON = false;
    }

    setE1ON(bool) {
        this.E1ON = bool;
    }

    setA2ON(bool) {
        this.A2ON = bool;
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Frost Thorn",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return 1.1 + (level * 0.11);
        }else if(level < 10){
            return 1.65 + ((level - 5) * 0.1375);
        }else {
            return 2.2 + ((level - 9) * 0.11);
        }
    }

    skillDescription(level) {
        return {
            name: "Darting Ironthorn",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> and activates Soulsteel Sync for 1 turn. </div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level < 6) {
            return {
                multiplier: 2.1 + (level * 0.14),
                critDmg: 0.3 + (level * 0.02),
            }
        }else if(level < 10){
            return {
                multiplier: 2.8 + ((level - 5) * 0.175),
                critDmg: 0.4 + ((level - 5) * 0.025),
            }
        }else {
            return {
                multiplier: 3.5 + ((level - 9) * 0.14),
                critDmg: 0.5 + ((level - 9) * 0.02),
            }
        }
    }

    ultimateDescription(level) {
        return {
            name: "Amidst the Raining Bliss",
            description: (
                <div className="buff-description">
                    <div>Increases Yanqing's </div>
                    <div className="buff-text">CRIT Rate</div>
                    <div> by </div>
                    <div className="buff-text"> 60% </div>
                    <div> When Soulsteel Sync is active, increases Yanqing's </div>
                    <div className="buff-text"> CRIT DMG </div>
                    <div> by an extra </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).critDmg * 100).toFixed(1)}% </div>
                    <div> This buff lasts for one turn. Afterwards, deals </div>
                    <div className={this.element}>{this.element} DMG</div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).multiplier * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    ultimate1(level) {
        return {
            name: "Amidst the Raining Bliss",
            description: (
                <div className="buff-description">
                    <div> Increase </div>
                    <div className="buff-text">CRIT Rate</div>
                    <div> by </div>
                    <div className="buff-text"> 60% </div>
                </div>
            ),
        }
    }

    ultimate2(level) {
        return {
            name: "Amidst the Raining Bliss",
            description: (
                <div className="buff-description">
                    <div> When Soulsteel Sync is active, increases Yanqing's </div>
                    <div className="buff-text"> CRIT DMG </div>
                    <div> by an extra </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).critDmg * 100).toFixed(1)}% </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return {
                critRate: 0.15 + (level * 0.005),
                critDmg: 0.15 + (level * 0.015),
                baseChance: 0.5 + (level * 0.01),
                additionalMultiplier: 0.25 + (level * 0.025),
            }
        }else if(level < 10){
            return {
                critRate: 0.175 + ((level - 5) * 0.0063),
                critDmg: 0.225 + ((level - 5) * 0.0188),
                baseChance: 0.55 + ((level - 5) * 0.0125),
                additionalMultiplier: 0.375 + ((level - 5) * 0.0313),
            }
        }else {
            return {
                critRate: 0.2 + ((level - 9) * 0.005),
                critDmg: 0.3 + ((level - 9) * 0.015),
                baseChance: 0.6 + ((level - 9) * 0.01),
                additionalMultiplier: 0.5 + ((level - 9) * 0.025),
            }
        }
    }

    talentDescription(level) { 
        return {
            name: "One With the Sword",
            description: (
                <div className="buff-description">
                    <div> When Soulsteel Sync is active, Yanqing is less likely to be attacked by enemies. Yanqing's </div>
                    <div className="buff-text"> CRIT Rate </div>
                    <div> increases by </div>
                    <div className="buff-text"> {(this.talent(level).critRate * 100).toFixed(1)}% </div>
                    <div> and his </div>
                    <div className="buff-text"> CRIT DMG </div>
                    <div> increases by </div>
                    <div className="buff-text"> {(this.talent(level).critDmg * 100).toFixed(1)}% </div>
                    <div> After Yanqing attacks an enemy, there is a  </div>
                    <div className="buff-text"> {(this.talent(level).baseChance * 100).toFixed(1)}% </div>
                    <div> fixed chance to perform a follow-up attack, dealing </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.talent(level).additionalMultiplier * 100).toFixed(1)}% </div>
                    <div> of Yanqing's </div>
                    <div className="buff-text"> ATK </div>
                    <div> to the enemy, which has a 65% base chance to Freeze the enemy for 1 turn. The Frozen target cannot take action and receives </div>
                    <div className="buff-text"> Additional Ice DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.talent(level).additionalMultiplier * 100).toFixed(1)}% </div>
                    <div> of Yanqing's </div>
                    <div className="buff-text"> ATK </div>
                    <div> at the beginning of each turn. When Yanqing receives DMG, the Soulsteel Sync effect will disappear. </div>
                </div>
            ),
        }
    }

    talentSimple(level) {
        return {
            name: "One With the Sword",
            description: (
                <div className="buff-description">
                    <div className="buff-text"> CRIT Rate </div>
                    <div> increases by </div>
                    <div className="buff-text"> {(this.talent(level).critRate * 100).toFixed(1)}% </div>
                    <div> and his </div>
                    <div className="buff-text"> CRIT DMG </div>
                    <div> increases by </div>
                    <div className="buff-text"> {(this.talent(level).critDmg * 100).toFixed(1)}% </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "The One True Sword",
            description: (
                <div className="buff-description">
                    <div> After using his Technique, at the start of the next battle, Yanqing deals 30% more DMG for 2 turn(s) to enemies whose current HP is 50% or higher. </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Icing on the Kick",
            description: (
                <div className="buff-description">
                    <div> When Yanqing attacks, deals Additional Ice DMG equal to 30% of Yanqing's ATK to enemies with Ice Weakness. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Frost Favors the Brave",
            description: (
                <div className="buff-description">
                    <div> When Soulsteel Sync is active, Effect RES increases by 20%. </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Gentle Blade",
            description: (
                <div className="buff-description">
                    <div> When a CRIT Hit is triggered, increases SPD by 10% for 2 turn(s). </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Svelte Saber",
            description: (
                <div className="buff-description">
                    <div> When Yanqing attacks a Frozen enemy, he deals Additional Ice DMG equal to 60% of his ATK. </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Supine Serenade",
            description: (
                <div className="buff-description">
                    <div> When Soulsteel Sync is active, Energy Regeneration Rate increases by an extra 10%. </div>
                </div>
            )
        }
    }

    E3() {
        return {
            name: "Sword Savant",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }

    E4() {
        return {
            name: "Searing Sting",
            description:(
                <div className="buff-description">
                    <div>When the current HP percentage is 80% or higher, Ice RES PEN increases by 12%. </div>
                </div>
            )
        }
    }

    E5() {
        return {
            name: "Surging Strife",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: "Swift Swoop",
            description: (
                <div className="buff-description">
                    <div>If the buffs from Soulsteel Sync or the Ultimate are in effect when an enemy is defeated, the duration of these buffs is extended by 1 turn. </div>
                </div>
            ),
        }
    }
}

export default Yanqing;