export class Brighter {
    constructor() {
        this.stats = [
            [1,  28.8,     18,   48],
            [20, 110.88, 69.3,  185],
            [20, 145.44, 90.9,  242],
            [40, 277.92, 173.7, 463],
            [40, 324,    202.5, 540],
            [50, 367.2,  229.5, 612],
            [50, 413.28, 258.3, 689],
            [60, 456.48, 285.3, 761],
            [60, 502.56, 314.1, 838],
            [70, 545.76, 341.1, 910],
            [70, 591.84, 369.9, 986],
            [80, 635.04, 396.9, 1058],
        ];
        
        this.name = "Brighter Than the Sun";
        this.path = "Destruction";
        this.id = "brighter";
        this.rarity = "fiveStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text"> CRIT Rate </div> 
                <div> by </div> 
                <div className="buff-text">{((0.18 + imposition * 0.03) * 100).toFixed(1)}%.</div>
                <div> When the wearer uses their Basic ATK, they will gain 1 stack of Dragon's Call for 2 turns. Each stack of Dragon's Call increases the wearer's </div>
                <div className="buff-text">ATK</div>
                <div> by </div>
                <div className="buff-text"> {((0.18 + imposition * 0.03) * 100).toFixed(1)}%. for 2 turn(s).</div>
                <div> and </div>
                <div className="buff-text">Energy Regeneration Rate</div>
                <div> by </div>
                <div className="buff-text">{((0.06 + imposition * 0.01) * 100).toFixed(1)}%.</div>
                <div> Dragon's Call can be stacked up to 2 times. </div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: this.id + "b",
            name: this.name,
            effect:{
                critRate: 0.18 + imposition * 0.03,
            },
            type: "buff",
            hasStack: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition, stack) {
        return {
            0: {
                id: this.id + "0",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div>Each stack of Dragon's Call increases the wearer's </div>
                        <div className="buff-text">ATK</div>
                        <div> by </div>
                        <div className="buff-text"> {((0.18 + imposition * 0.03) * 100).toFixed(1)}%. for 2 turn(s).</div>
                        <div> and </div>
                        <div className="buff-text">Energy Regeneration Rate</div>
                        <div> by </div>
                        <div className="buff-text">{((0.06 + imposition * 0.01) * 100).toFixed(1)}%.</div>
                        <div> Dragon's Call can be stacked up to 2 times. </div>
                    </div>
                ),
                effect: {
                    atkPercent: (0.18 + imposition * 0.03) * stack,
                    ERR: (0.06 + imposition * 0.01) * stack,
                },
                type: "buff",
                hasStack: true,
                stacks: 2,
                hasInput: false,
            },
        }
    }
}

export default Brighter;