import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';

import { Bronya } from "../../../../../../../database/characters";

const BronyaBuffs = () => {

    const {level, eidolon, critDmg, skillLevel, ultimateLevel, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    const bronya = new Bronya();

    const [prevSkillLevel, setPrevSkillLevel] = useState(skillLevel);
    const [prevUltimateLevel, setPrevUltimateLevel] = useState(ultimateLevel);
    const [calculatedCritDmg, setCalculatedCritDmg] = useState(0);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];

        if(index === 1 && updatedStates[1]) {
            buffValue.effect.critDmg = calculatedCritDmg;
            updateStatesFromJson(buffValue, updatedStates[index]);
        } else {
            updateStatesFromJson(buffValue, updatedStates[index]);
        }
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
        
        if(index === 1) {
            setCalculatedCritDmg(buffValue.effect.critDmg);
        }
    }
    
    const skillBuff = {
        effect: {
            iceDmg: bronya.skillAttack(skillLevel),
            quantumDmg: bronya.skillAttack(skillLevel),
            fireDmg: bronya.skillAttack(skillLevel),
            lightningDmg: bronya.skillAttack(skillLevel),
            windDmg: bronya.skillAttack(skillLevel),
            physicalDmg: bronya.skillAttack(skillLevel),
            imaginaryDmg: bronya.skillAttack(skillLevel),
        },
    }

    useEffect(() => {
        if(characterBuffStates[0]) {
            const tempSkillBuff = {
                effect: {
                    iceDmg: bronya.skillAttack(prevSkillLevel),
                    quantumDmg: bronya.skillAttack(prevSkillLevel),
                    fireDmg: bronya.skillAttack(prevSkillLevel),
                    lightningDmg: bronya.skillAttack(prevSkillLevel),
                    windDmg: bronya.skillAttack(prevSkillLevel),
                    physicalDmg: bronya.skillAttack(prevSkillLevel),
                    imaginaryDmg: bronya.skillAttack(prevSkillLevel),
                },
            }

            updateStatesFromJson(tempSkillBuff, true);
            updateStatesFromJson(skillBuff, false);
        }
        
        setPrevSkillLevel(skillLevel);
    }, [skillLevel]);

    const ultimateBuff = {
        effect: {
            atkPercent: bronya.ultimateBuff(ultimateLevel).atk,
            critDmg: (bronya.ultimateBuff(ultimateLevel).critDmg * critDmg) + bronya.ultimateBuff(ultimateLevel).bonusCritDmg,
        }
    }

    useEffect(() => {
        var tempCalc;
        if(characterBuffStates[1]) {
            const tempUltBuff = {
                effect: {
                    atkPercent: bronya.ultimateBuff(prevUltimateLevel).atk,
                    critDmg: calculatedCritDmg,
                },
            }
            updateStatesFromJson(tempUltBuff, true);
            
            tempCalc = (bronya.ultimateBuff(ultimateLevel).critDmg * (critDmg - calculatedCritDmg)) + bronya.ultimateBuff(ultimateLevel).bonusCritDmg;
            const newTempUltBuff = {
                effect: {
                    atkPercent: bronya.ultimateBuff(ultimateLevel).atk,
                    critDmg: tempCalc,
                },
            }
            updateStatesFromJson(newTempUltBuff, false);
        }
        
        setCalculatedCritDmg(tempCalc);
        setPrevUltimateLevel(ultimateLevel);
    }, [ultimateLevel]);


    useEffect(() => {
        var tempCalc;
        if(characterBuffStates[1]) {
            const tempUltBuff = {
                effect: {
                    atkPercent: bronya.ultimateBuff(prevUltimateLevel).atk,
                    critDmg: calculatedCritDmg,
                },
            }
            updateStatesFromJson(tempUltBuff, true);
            
            tempCalc = (bronya.ultimateBuff(ultimateLevel).critDmg * (critDmg - calculatedCritDmg)) + bronya.ultimateBuff(ultimateLevel).bonusCritDmg;
            const newTempUltBuff = {
                effect: {
                    atkPercent: bronya.ultimateBuff(prevUltimateLevel).atk,
                    critDmg: tempCalc,
                },
            }
            updateStatesFromJson(newTempUltBuff, false);
        }

        setCalculatedCritDmg(tempCalc);
    }, [critDmg]);
    
    const techBuff = {
        effect: {
            atkPercent: 0.15,
        },
    }


    const A4Buff = {
        effect: {
            defPercent: 0.20,
        },
    }

    const A6Buff = {
        effect: {
            iceDmg: 0.1,
            quantumDmg: 0.1,
            fireDmg: 0.1,
            lightningDmg: 0.1,
            windDmg: 0.1,
            physicalDmg: 0.1,
            imaginaryDmg: 0.1,
        },
    }


    const E2Buff = {
        effect: {
            spdPercent: 0.3,
        },
    }

    useEffect(() => {

        if(eidolon < 2 && characterBuffStates[5]) {
            updateStatesFromJson(E2Buff, true);
            characterBuffStates[5] = false;
        }

    }, [eidolon]);

    useEffect(() => {
        
        if(level < 6 && characterBuffStates[3]) {
            updateStatesFromJson(A4Buff, true);
            characterBuffStates[3] = false;
        }

        if(level < 11 && characterBuffStates[4]) {
            updateStatesFromJson(A6Buff, true);
            characterBuffStates[4] = false;
        }
        

    }, [level]);

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, skillBuff)} />}
            label={bronya.skillDescription(skillLevel).name}
            />
            <div>{bronya.skillDescription(skillLevel).description}</div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, ultimateBuff)} />}
            label={bronya.ultimateDescription(ultimateLevel).name}
            />
            <div>{bronya.ultimateDescription(ultimateLevel).description}</div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, techBuff)} />}
            label={bronya.techniqueDescription().name}
            />
            <div>{bronya.techniqueDescription().description}</div>

            {level >= 6 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, A4Buff)} />}
                    label={"(A4) " + bronya.A4().name}
                    />
                    <div>{bronya.A4().description}</div>
                </>
            }

            {level >= 11 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[4] || false} onChange={() => applyCharacterBuffs(4, A6Buff)} />}
                    label={"(A6) " + bronya.A6().name}
                    />
                <div>{bronya.A6().description}</div>
            </>
            }

            {eidolon >= 2 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[5] || false} onChange={() => applyCharacterBuffs(5, E2Buff)} />}
                    label={"(E2) " + bronya.E2().name}
                    />
                    <div>{bronya.E2().description}</div>
                </>
            }
        </>
    )
}

export const BronyaGlobalBuffs = (props) => {
    const { updateStatesFromJson } = useContext(globalData);
    const bronya = new Bronya();

    const [skillLevel, setSkillLevel] = useState(0);
    const [ultimateLevel, setUltimateLevel] = useState(0);
    const [critDmg, setCritDmg] = useState(0.5);

    const bronyBuffStates = props.buffStates;
    const setBronyaBuffStates = props.setBuffStates;
    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    const [prevSkillLevel, setPrevSkillLevel] = useState(skillLevel);
    const [prevUltimateLevel, setPrevUltimateLevel] = useState(ultimateLevel);
    const [calculatedCritDmg, setCalculatedCritDmg] = useState(0);

    const updateUltLevel = (event) => {
        setUltimateLevel(event.target.value);
    }

    const updateSkillLevel = (event) => {
        setSkillLevel(event.target.value);
    }
    
    const handleCritDmgChange = (event) => {
        const re = /^[0-9\b.]+$/;
        if ((event.target.value !== '' && !re.test(event.target.value))|| event.target.value === '.') {
            return;
        }

        setCritDmg((event.target.value / 100));
    }

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...bronyBuffStates];

        if(index === 1 && updatedStates[1]) {
            buffValue.effect.critDmg = calculatedCritDmg;
            updateStatesFromJson(buffValue, updatedStates[index]);
        } else {
            updateStatesFromJson(buffValue, updatedStates[index]);
        }
        updatedStates[index] = !updatedStates[index];
        setBronyaBuffStates(updatedStates);
        
        if(index === 1) {
            setCalculatedCritDmg(buffValue.effect.critDmg);
        }
    }
    
    const skillBuff = {
        effect: {
            iceDmg: bronya.skillAttack(skillLevel),
            quantumDmg: bronya.skillAttack(skillLevel),
            fireDmg: bronya.skillAttack(skillLevel),
            lightningDmg: bronya.skillAttack(skillLevel),
            windDmg: bronya.skillAttack(skillLevel),
            physicalDmg: bronya.skillAttack(skillLevel),
            imaginaryDmg: bronya.skillAttack(skillLevel),
        },
    }

    useEffect(() => {
        if(bronyBuffStates[0]) {
            const tempSkillBuff = {
                effect: {
                    iceDmg: bronya.skillAttack(prevSkillLevel),
                    quantumDmg: bronya.skillAttack(prevSkillLevel),
                    fireDmg: bronya.skillAttack(prevSkillLevel),
                    lightningDmg: bronya.skillAttack(prevSkillLevel),
                    windDmg: bronya.skillAttack(prevSkillLevel),
                    physicalDmg: bronya.skillAttack(prevSkillLevel),
                    imaginaryDmg: bronya.skillAttack(prevSkillLevel),
                },
            }

            updateStatesFromJson(tempSkillBuff, true);
            updateStatesFromJson(skillBuff, false);
        }
        
        setPrevSkillLevel(skillLevel);
    }, [skillLevel]);

    const ultimateBuff = {
        effect: {
            atkPercent: bronya.ultimateBuff(ultimateLevel).atk,
            critDmg: (bronya.ultimateBuff(ultimateLevel).critDmg * critDmg) + bronya.ultimateBuff(ultimateLevel).bonusCritDmg,
        }
    }

    useEffect(() => {
        var tempCalc;
        if(bronyBuffStates[1]) {
            const tempUltBuff = {
                effect: {
                    atkPercent: bronya.ultimateBuff(prevUltimateLevel).atk,
                    critDmg: calculatedCritDmg,
                },
            }
            updateStatesFromJson(tempUltBuff, true);
            
            tempCalc = (bronya.ultimateBuff(ultimateLevel).critDmg *  critDmg) + bronya.ultimateBuff(ultimateLevel).bonusCritDmg;
            const newTempUltBuff = {
                effect: {
                    atkPercent: bronya.ultimateBuff(ultimateLevel).atk,
                    critDmg: tempCalc,
                },
            }
            updateStatesFromJson(newTempUltBuff, false);
        }
        
        setCalculatedCritDmg(tempCalc);
        setPrevUltimateLevel(ultimateLevel);
    }, [ultimateLevel]);


    useEffect(() => {
        var tempCalc;
        if(bronyBuffStates[1]) {
            const tempUltBuff = {
                effect: {
                    atkPercent: bronya.ultimateBuff(prevUltimateLevel).atk,
                    critDmg: calculatedCritDmg,
                },
            }
            updateStatesFromJson(tempUltBuff, true);
            
            tempCalc = (bronya.ultimateBuff(ultimateLevel).critDmg * critDmg) + bronya.ultimateBuff(ultimateLevel).bonusCritDmg;
            const newTempUltBuff = {
                effect: {
                    atkPercent: bronya.ultimateBuff(prevUltimateLevel).atk,
                    critDmg: tempCalc,
                },
            }
            updateStatesFromJson(newTempUltBuff, false);
        }

        setCalculatedCritDmg(tempCalc);
    }, [critDmg]);
    
    const techBuff = {
        effect: {
            atkPercent: 0.15,
        },
    }


    const A4Buff = {
        effect: {
            defPercent: 0.20,
        },
    }

    const E2Buff = {
        effect: {
            spdPercent: 0.3,
        },
    }

    const A6Buff = {
        effect: {
            iceDmg: 0.1,
            quantumDmg: 0.1,
            fireDmg: 0.1,
            lightningDmg: 0.1,
            windDmg: 0.1,
            physicalDmg: 0.1,
            imaginaryDmg: 0.1,
        },
    }

    useEffect(() => {
        if(bronyBuffStates[0]) {
            updateStatesFromJson(skillBuff, true);
            bronyBuffStates[0] = false;
        }
        if(bronyBuffStates[1]) {
            updateStatesFromJson(ultimateBuff, true);
            bronyBuffStates[1] = false;
        }
        if(bronyBuffStates[2]) {
            updateStatesFromJson(techBuff, true);
            bronyBuffStates[2] = false;
        }
        if(bronyBuffStates[3]) {
            updateStatesFromJson(A4Buff, true);
            bronyBuffStates[3] = false;
        }
        if(bronyBuffStates[4]) {
            updateStatesFromJson(A6Buff, true);
            bronyBuffStates[4] = false;
        }
        if(bronyBuffStates[5]) {
            updateStatesFromJson(E2Buff, true);
            bronyBuffStates[5] = false;
        }
        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={bronyBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, skillBuff)} />}
            label={bronya.skillDescription(skillLevel).name}
            />
            <div className="mb-3">{bronya.skillSimple(skillLevel).description}</div>
            <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                <div style={{display: "flex"}}>
                    <div>Skill Level</div>
                    <select className="levels buff-input" style={{width: "45%"}} value={skillLevel} onChange={updateSkillLevel}>
                        <option value={0}>1</option>
                        <option value={1}>2</option>
                        <option value={2}>3</option>
                        <option value={3}>4</option>
                        <option value={4}>5</option>
                        <option value={5}>6</option>
                        <option value={6}>7</option>
                        <option value={7}>8</option>
                        <option value={8}>9</option>
                        <option value={9}>10</option>
                        <option value={10}>11</option>
                        <option value={11}>12</option>
                    </select>
                </div>
            </div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={bronyBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, ultimateBuff)} />}
            label={bronya.ultimateDescription(ultimateLevel).name}
            />
            <div className="mb-3">{bronya.ultimateDescription(ultimateLevel).description}</div>
            <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                <div style={{display: "flex"}}>
                    <div>Ult Level</div>
                    <select className="levels buff-input" style={{width: "45%"}} value={ultimateLevel} onChange={updateUltLevel}>
                        <option value={0}>1</option>
                        <option value={1}>2</option>
                        <option value={2}>3</option>
                        <option value={3}>4</option>
                        <option value={4}>5</option>
                        <option value={5}>6</option>
                        <option value={6}>7</option>
                        <option value={7}>8</option>
                        <option value={8}>9</option>
                        <option value={9}>10</option>
                        <option value={10}>11</option>
                        <option value={11}>12</option>
                    </select>
                </div>
                <div style={{display: "flex", width: "40%"}}>
                    <div className="centered">Crit Dmg</div>
                    <input type="text" className="critDmg buff-input" maxLength={3} value={(critDmg * 100).toFixed(0)} onChange={handleCritDmgChange}/>
                    <div>%</div>
                </div>
            </div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={bronyBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, techBuff)} />}
            label={bronya.techniqueDescription().name}
            />
            <div>{bronya.techniqueDescription().description}</div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={bronyBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, A4Buff)} />}
            label={"(A4) " + bronya.A4().name}
            />
            <div>{bronya.A4().description}</div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={bronyBuffStates[4] || false} onChange={() => applyCharacterBuffs(4, A6Buff)} />}
            label={"(A6) " + bronya.A6().name}
            />
            <div>{bronya.A6().description}</div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={bronyBuffStates[5] || false} onChange={() => applyCharacterBuffs(5, E2Buff)} />}
            label={"(E2) " + bronya.E2().name}
            />
            <div>{bronya.E2().description}</div>
        </>
    )
}   

export default BronyaBuffs;