import React from 'react';
import {Dialog, DialogTitle, DialogContent} from '@mui/material';

function Credits(props) {

    const open = props.open;
    const setOpen = props.setOpen;

    return (
        <div className="dialog">
            <Dialog aria-label='dialog-title' open={open} onClose={() => setOpen(false)} maxWidth="lg" fullWidth>
                <DialogTitle className='dialog-container' id='dialog-title'> 
                <div style={{display: "flex", justifyContent: "space-between", alignItems: "center"}}>
                    <div>Credits</div>
                    <button onClick={() => setOpen(false)} className='calc-btn' style={{width: "37px", display: "flex", backgroundColor: "red", textAlign:"center", alignItems: "center", justifyContent: "center", margin: "0"}}>&#10005;</button>
                </div>
                </DialogTitle>
                <DialogContent className='dialog-content'>
                    <div className='changes'> - Special thanks to <a href='https://honkai-star-rail.fandom.com/wiki/Honkai:_Star_Rail_Wiki' target='_blank' rel="noopener noreferrer">Honkai Star Rail Fandom</a>, all images and assets are collected from 
                    their website, as a well as the damage formulas.</div>
                    <div className='changes'> - Another special thanks to <a href='https://hsr.honeyhunterworld.com/?lang=EN' target='_blank' rel="noopener noreferrer">Honey Hunter World</a>, as character/light cone/relic level data are collected from their website.</div>
                    <div className='changes'> - Another special thanks to <a href='https://hsr.yatta.top/en' target='_blank' rel="noopener noreferrer">Yatta Project</a>, some data was collected from their website too.</div>
                    <div className='changes'> - Another special thanks to <a href='https://starrailstation.com/en' target='_blank' rel="noopener noreferrer">Star Rail Station</a>, some images were collected from their website.</div>
                    <div className='changes'> - Huge thanks to <a href='https://www.reddit.com/user/Ronqueroc' target='_blank' rel="noopener noreferrer">Ronqueroc</a>, as i took a lot of inspiration from their amazing and easy to use genshin calculator</div>
                    <div className='changes'> - Huge thanks to </div> 
                    <div style={{display: "flex", flexWrap: "wrap", alignItems: "center", justifyContent: "center"}}>
                        <div className='changes Imaginary'>Ryodan</div> 
                        <div className='changes Quantum'>Binnyboy</div> 
                        <div className='changes Quantum'>Dela</div> 
                    </div>
                    <div className='changes'> for testing and bug reports!</div>
                    <div className='changes'> - And finally thank you so much for using my calculator!!</div>
                </DialogContent>
            </Dialog>
        </div>
    )
}

export default Credits;