class Serval {
    constructor() {
        this.stats = [
            {level: 1,  atk: 88.8,   def: 51,     hp: 125, spd: 104, taunt: 75, energy: 100},
            {level: 20, atk: 173.16, def: 99.45,  hp: 243, spd: 104, taunt: 75, energy: 100, critRate: 0.027},
            {level: 20, atk: 208.68, def: 119.85, hp: 293, spd: 104, taunt: 75, energy: 100, critRate: 0.027},
            {level: 40, atk: 333,    def: 191.25, hp: 468, spd: 104, taunt: 75, energy: 100, critRate: 0.027},
            {level: 40, atk: 368.52, def: 211.65, hp: 518, spd: 104, taunt: 75, energy: 100, critRate: 0.054, effectHitRate: 0.04,  effectRes: 0.04},
            {level: 50, atk: 412.92, def: 237.15, hp: 580, spd: 104, taunt: 75, energy: 100, critRate: 0.054, effectHitRate: 0.04,  effectRes: 0.04},
            {level: 50, atk: 448.44, def: 257.55, hp: 630, spd: 104, taunt: 75, energy: 100, critRate: 0.094, effectHitRate: 0.04,  effectRes: 0.04},
            {level: 60, atk: 492.84, def: 283.05, hp: 693, spd: 104, taunt: 75, energy: 100, critRate: 0.094, effectHitRate: 0.1, effectRes: 0.04},
            {level: 60, atk: 528.36, def: 303.45, hp: 743, spd: 104, taunt: 75, energy: 100, critRate: 0.134,  effectHitRate: 0.1, effectRes: 0.04},
            {level: 70, atk: 572.76, def: 328.95, hp: 805, spd: 104, taunt: 75, energy: 100, critRate: 0.134,  effectHitRate: 0.1, effectRes: 0.04},
            {level: 70, atk: 608.28, def: 349.35, hp: 855, spd: 104, taunt: 75, energy: 100, critRate: 0.134,  effectHitRate: 0.1, effectRes: 0.1},
            {level: 80, atk: 652.68, def: 374.85, hp: 917, spd: 104, taunt: 75, energy: 100, critRate: 0.187, effectHitRate: 0.18, effectRes: 0.1},
        ];

        this.name = "Serval";
        this.path = "Erudition";
        this.rarity = 'fourStar';
        this.element = "Lightning";
        this.id = "serval";
        this.isShocked = false;
    }

    setIsShocked(bool) {
        this.isShocked = bool;
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Roaring Thunderclap",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return {
                single: 0.7 + (level * 0.07),
                splash: 0.3 + (level * 0.03),
                dot: 0.4 + (level * 0.04) + (level - 4)  * 0.02,
            }
        }else if(level < 10){
            return {
                single: 1.05 + ((level - 5)  * 0.08),
                splash: 0.45 + ((level - 5)  * 0.04),
                dot: 0.62 + ((level - 5)  * 0.08) + (level - 6)  * 0.02,
            }
        }else {
            return {
                single: 1.4 + ((level - 9)  * 0.07),
                splash: 0.6 + ((level - 9)  * 0.03),
                dot: 1.04 + ((level - 9)  * 0.052),
            }
        }
    }

    skillDescription(level) {
        return {
            name: "Lightning Flash",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level).single * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy and </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level).splash * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to all enemies adjacent to it, with a 80% base chance for enemies hit to become Shocked for 2 turn(s). </div>
                    <br/>
                    <div>While Shocked, enemies take </div>
                    <div className={this.element}> {this.element} DoT </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level).dot * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> at the beginning of each turn. </div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level < 6) {
            return 1.08 + (level * 0.07);
        }else if(level < 10){
            return 1.44 + ((level - 5) * 0.09);
        }else {
            return 1.8 + ((level - 9) * 0.07);
        }
    }

    ultimateDescription(level) {
        return {
            name: "Here Comes the Mechanical Fever",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.ultimateAttack(level) * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to all enemies. Enemies already Shocked will extend the duration of their Shock state by 2 turn(s).</div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 0.36 + (level * 0.03);
        }else if(level < 10){
            return 0.54 + ((level - 5) * 0.04);
        }else {
            return 0.72 + ((level - 9) * 0.03);
        }
    }

    talentDescription(level) { 
        return {
            name: "Galvanic Chords",
            description: (
                <div className="buff-description">
                    <div>After Serval attacks, deals </div>
                    <div className={this.element}> additional {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.talent(level) * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to all Shocked enemies. </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Good Night, Belobog",
            description: (
                <div className="buff-description">
                    <div> Immediately attacks the enemy. After entering battle, deals Lightning DMG equal to 50% of Serval's ATK to a random enemy, with a 100% base chance for all enemies to become Shocked for 3 turn(s). </div>
                    <br/>
                    <div> While Shocked, enemies will take Lightning DoT equal to 50% of Serval's ATK at the beginning of each turn. </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Rock 'n' Roll",
            description: (
                <div className="buff-description">
                    <div> Skill has a 20% increased base chance to Shock enemies. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "String Vibration",
            description: (
                <div className="buff-description">
                    <div> At the start of the battle, immediately regenerates 15 Energy. </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Mania",
            description: (
                <div className="buff-description">
                    <div> Upon defeating an enemy, ATK is increased by 20% for 2 turn(s). </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Echo Chamber",
            description: (
                <div className="buff-description">
                    <div> Basic ATK deals Lightning DMG equal to 60% of the Basic ATK's DMG to a random enemy adjacent to the target of the Basic ATK. </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Encore!",
            description: (
                <div className="buff-description">
                    <div> Every time Serval's Talent is triggered to deal Additional DMG, she regenerates 4 Energy. </div>
                </div>
            )
        }
    }

    E3() {
        return {
            name: "Listen, the Heartbeat of the Gears",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }

    E4() {
        return {
            name: "Make Some Noise!",
            description:(
                <div className="buff-description">
                    <div>Ultimate has a 100% base chance to apply Shock to any enemies not currently Shocked. This Shock has the same effects as the one applied by Skill.</div>
                </div>
            )
        }
    }

    E5() {
        return {
            name: "Belobog's Loudest Roar!",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: "This Song Rocks to Heaven!",
            description: (
                <div className="buff-description">
                    <div>Serval deals 30% more DMG to Shocked enemies.</div>
                </div>
            ),
        }
    }
}


export default Serval;