export class AlreadyShut {
    constructor() {
        this.stats = [
            [1, 19.2, 24, 58],
            [20, 73.92, 92.4, 222],
            [20, 96.96, 121.2, 291],
            [40, 185.28, 231.6, 556],
            [40, 216, 270,  648],
            [50, 244.8, 306,  734],
            [50, 275.52, 344.4, 827],
            [60, 304.32, 380.4, 913],
            [60, 335.04, 418.8, 1005],
            [70, 363.84, 454.8, 1092],
            [70, 394.56, 493.2, 1184],
            [80, 423.36, 529.2, 1270],
        ];
        
        this.name = "She Already Shut Her Eyes";
        this.id = "alreadyshut";
        this.path = "Preservation";
        this.rarity = "fiveStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text"> Max HP </div>
                <div> by </div>
                <div className="buff-text">{((0.24 + imposition * 0.04) * 100).toFixed(1)}% </div>
                <div> and </div>
                <div className="buff-text"> Energy Regeneration Rate </div>
                <div> by </div>
                <div className="buff-text">{((0.12 + imposition * 0.02) * 100).toFixed(1)}%. </div>
                <div> When the wearer's HP is reduced, all allies' DMG dealt increases by </div>
                <div className="buff-text">{((0.09 + imposition * 0.015) * 100).toFixed(1)}% </div>
                <div> At the start of every wave, restores HP to all allies equal to </div>
                <div className="buff-text">{((0.8 + imposition * 0.05) * 100).toFixed(1)}% </div>
                <div> of the respective HP difference between each characters' Max HP and their current HP. </div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{
                hpPercent: 0.24 + imposition * 0.04,
                ERR: 0.12 + imposition * 0.02,
            },
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "adve1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> When the wearer's HP is reduced, all allies' DMG dealt increases by </div>
                        <div className="buff-text">{((0.09 + imposition * 0.015) * 100).toFixed(1)}% </div>
                    </div>
                ),
                effect: {
                    iceDmg: 0.09 + imposition * 0.015,
                    quantumDmg: 0.09 + imposition * 0.015,
                    fireDmg: 0.09 + imposition * 0.015,
                    lightningDmg: 0.09 + imposition * 0.015,
                    windDmg: 0.09 + imposition * 0.015,
                    physicalDmg: 0.09 + imposition * 0.015,
                    imaginaryDmg: 0.09 + imposition * 0.015,
                },
                type: "buff",
                hasStack: false,
                hasInput: false,
            },
        }
    }
}

export default AlreadyShut;