export class PastAndFuture {
    constructor() {
        this.stats = [
            [1,  19.2,   18,     43],
            [20, 73.92,  69.3,  166],
            [20, 96.96,  90.9,  218],
            [40, 185.28, 173.7, 417],
            [40, 216,    202.5, 486],
            [50, 244.8,  229.5, 551],
            [50, 275.52, 258.3, 620],
            [60, 304.32, 285.3, 685],
            [60, 335.04, 314.1, 754],
            [70, 363.84, 341.1, 819],
            [70, 394.56, 369.9, 888],
            [80, 423.36, 396.9, 953],
        ];
        
        this.name = "Past and Future";
        this.path = "Harmony";
        this.id = "pastandfuture";
        this.rarity = "fourStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>When the wearer uses their </div>
                <div className="buff-text"> Skill </div> 
                <div> he next ally taking action (except the wearer) deals </div> 
                <div className="buff-text">{((0.16 + imposition * 0.04) * 100).toFixed(1)}% increased DMG for 1 turn(s).</div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{},
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "adve1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div>When the wearer uses their </div>
                        <div className="buff-text"> Skill </div> 
                        <div> he next ally taking action (except the wearer) deals </div> 
                        <div className="buff-text">{((0.16 + imposition * 0.04) * 100).toFixed(1)}% increased DMG for 1 turn(s).</div>
                    </div>
                ),
                effect: {
                    iceDmg: 0.16 + imposition * 0.04,
                    quantumDmg: 0.16 + imposition * 0.04,
                    fireDmg: 0.16 + imposition * 0.04,
                    lightningDmg: 0.16 + imposition * 0.04,
                    windDmg: 0.16 + imposition * 0.04,
                    physicalDmg: 0.16 + imposition * 0.04,
                    imaginaryDmg: 0.16 + imposition * 0.04,
                },
                type: "buff",
                hasStack: false,
                hasInput: false,
            },
        }
    }
}

export default PastAndFuture;