import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';
import { DanHeng } from "../../../../../../../database/characters";

const DanHengBuffs = () => {

    const {level, eidolon, character, ultimateLevel, talentLevel, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    const danHeng = new DanHeng();

    const [prevTalentLevel, setPrevTalentLevel] = useState(talentLevel);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];

        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    const changeUltMultiplier = (event) => {
        if(event.target.checked) {
            character.setSlowed(event.target.checked);
        }else {
            character.setSlowed(event.target.checked);
        }

        const updatedStates = [...characterBuffStates];
        updatedStates[0] = !updatedStates[0];
        setCharacterBuffStates(updatedStates);
    }

    useEffect(() => {
        if(characterBuffStates[1]) {
            const tempUltBuff = {
                effect: {
                    windPen: danHeng.talent(prevTalentLevel),
                },
            }
            updateStatesFromJson(tempUltBuff, true);
            
            const newTempUltBuff = {
                effect: {
                    windPen: danHeng.talent(talentLevel),
                },
            }
            console.log(newTempUltBuff);
            updateStatesFromJson(newTempUltBuff, false);
        }
        
        setPrevTalentLevel(talentLevel);
    }, [talentLevel]);

    const talentBuff = {
        effect: {
            windPen: danHeng.talent(talentLevel),
        },
    }

    const techBuff = {
        effect: {
            atkPercent: 0.4,
        },
    }

    const A6Buff = {
        effect: {
            basicBoost: 0.4,
        },
    }


    const E1Buff = {
        effect: {
            critRate: 0.12,
        },
    }

    useEffect(() => {

        if(eidolon < 1 && characterBuffStates[4]) {
            updateStatesFromJson(E1Buff, true);
            characterBuffStates[4] = false;
        }
    }, [eidolon]);

    useEffect(() => {
        
        if(level < 11 && characterBuffStates[3]) {
            updateStatesFromJson(A6Buff, true);
            characterBuffStates[3] = false;
        }

    }, [level]);

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[0] || false} onChange={(e) => changeUltMultiplier(e)} />}
            label={danHeng.ultimateDescription(ultimateLevel).name}
            />
            <div>{danHeng.bonusMultDesc(ultimateLevel)}</div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, talentBuff)} />}
            label={danHeng.talentDescription(talentLevel).name}
            />
            <div>{danHeng.talentDescription(talentLevel).description}</div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, techBuff)} />}
            label={danHeng.techniqueDescription().name}
            />
            <div>{danHeng.techniqueDescription().description}</div>

            {level >= 11 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, A6Buff)} />}
                    label={"(A6) " + danHeng.A6().name}
                    />
                    <div>{danHeng.A6().description}</div>
                </>
            }

            {eidolon >= 1 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[4] || false} onChange={() => applyCharacterBuffs(4, E1Buff)} />}
                    label={"(E1) " + danHeng.E1().name}
                    />
                    <div>{danHeng.E1().description}</div>
                </>
            }
        </>
    )
}

export const DanHengGlobalBuffs = (props) => {

    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {
        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <div>No Buffs</div>
        </>
    )
}

export default DanHengBuffs;