class Sushang {
    constructor() {
        this.stats = [
            {level: 1,  atk: 76.8,   def: 57,    hp: 125, spd: 107, taunt: 75, energy: 120},
            {level: 20, atk: 149.76, def: 111.15,hp: 243, spd: 107, taunt: 75, energy: 120, atkPercent: 0.04},
            {level: 20, atk: 180.48, def: 133.95,hp: 293, spd: 107, taunt: 75, energy: 120, atkPercent: 0.04},
            {level: 40, atk: 288,    def: 213.75, hp: 468, spd: 107, taunt: 75, energy: 120, atkPercent: 0.08},
            {level: 40, atk: 318.72, def: 236.55, hp: 518, spd: 107, taunt: 75, energy: 120, atkPercent: 0.08, hpPercent: 0.04, defPercent: 0.05},
            {level: 50, atk: 357.12, def: 265.05, hp: 580, spd: 107, taunt: 75, energy: 120, atkPercent: 0.14, hpPercent: 0.04, defPercent: 0.05},
            {level: 50, atk: 387.84, def: 287.85, hp: 630, spd: 107, taunt: 75, energy: 120, atkPercent: 0.14, hpPercent: 0.04, defPercent: 0.05},
            {level: 60, atk: 426.24, def: 316.35, hp: 693, spd: 107, taunt: 75, energy: 120, atkPercent: 0.14, hpPercent: 0.1, defPercent: 0.05},
            {level: 60, atk: 456.96, def: 339.15, hp: 743, spd: 107, taunt: 75, energy: 120, atkPercent: 0.2,  hpPercent: 0.1, defPercent: 0.05},
            {level: 70, atk: 495.36, def: 367.65, hp: 805, spd: 107, taunt: 75, energy: 120, atkPercent: 0.2,  hpPercent: 0.1, defPercent: 0.05},
            {level: 70, atk: 526.08, def: 390.45, hp: 855, spd: 107, taunt: 75, energy: 120, atkPercent: 0.2,  hpPercent: 0.1, defPercent: 0.125},
            {level: 80, atk: 564.48, def: 418.95, hp: 917, spd: 107, taunt: 75, energy: 120, atkPercent: 0.28, hpPercent: 0.18, defPercent: 0.125},
        ];

        this.name = "Sushang";
        this.path = "Hunt";
        this.rarity = 'fourStar';
        this.element = "Physical";
        this.id = "sushang";
        this.swordStanceStacks = 0;
    }

    setSwordStanceStacks(stacks) {
        this.swordStanceStacks = stacks;
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Cloudfencer Art: Starshine",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return 1.05 + (level * 0.105);
        }else if(level < 10){
            return 1.575 + ((level - 5) * 0.1313);
        }else {
            return 2.1 + ((level - 9) * 0.105);
        }
    }

    swordStance(level) {
        if(level < 6) {
            return 0.5 + (level * 0.05);
        }else if(level < 10){
            return 0.75 + ((level - 5) * 0.0625);
        }else {
            return 1 + ((level - 9) * 0.05);
        }
    }

    skillDescription(level) {
        return {
            name: "Cloudfencer Art: Mountainfall",
            description: (
                <div className="buff-description">
                    <div> Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                    <div> In addition, there is a 33% chance to trigger Sword Stance on the final hit, dealing Additional </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.swordStance(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                    <div> If the enemy is inflicted with Weakness Break, Sword Stance is guaranteed to trigger. </div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level < 6) {
            return 1.92 + (level * 0.128);
        }else if(level < 10){
            return 2.56 + ((level - 5) * 0.16);
        }else {
            return 3.2 + ((level - 9) * 0.128);
        }
    }

    
    bonusATK(level) {
        if(level < 6) {
            return 0.18 + (level * 0.012);
        }else if(level < 10){
            return 0.24 + ((level - 5) * 0.015);
        }else {
            return 0.3 + ((level - 9) * 0.012);
        }
    }

    bonusMultDesc(level) {
        return (<div className="buff-description">
            <div> Sushang's ATK increases by </div>
            <div className="buff-text"> {(this.bonusATK(level) * 100).toFixed(1)}% </div>
            <div> after casting her ultimate </div>
        </div>)
    }

    ultimateDescription(level) {
        return {
            name: "Shape of Taixu: Dawn Herald",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.ultimateAttack(level) * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to a single enemy. </div>
                    <div> and she immediately takes action again. In addition, Sushang's ATK increases by </div>
                    <div className="buff-text"> {(this.bonusATK(level) * 100).toFixed(1)}% </div>
                    <div> and using her Skill has 2 extra chances to trigger Sword Stance for 2 turn(s). Sword Stance triggered from the extra chances deals 50% of the original DMG. </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 0.15 + (level * 0.005);
        }else if(level < 10){
            return 0.175 + ((level - 5) * 0.0063);
        }else {
            return 0.2 + ((level - 9) * 0.005);
        }
    }

    talentDescription(level) { 
        return {
            name: "Dancing Blade",
            description: (
                <div className="buff-description">
                    <div>When an enemy has their Weakness Broken on the field, Sushang's SPD increases by </div>
                    <div className="buff-text"> {(this.talent(level) * 100).toFixed(1)}% </div>
                    <div> for 2 turn(s). </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Splitting Spearhead",
            description: (
                <div className="buff-description">
                    <div> Immediately attacks the enemy. Upon entering battle, Sushang deals </div>
                    <div className={this.element}> Physical DMG </div>
                    <div> equal to 80% of her ATK to all enemies. </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Guileless",
            description: (
                <div className="buff-description">
                    <div> When current HP percentage is </div>
                    <div className="buff-text"> 50% or lower</div>
                    <div> reduces the chance of being attacked by enemies. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Riposte",
            description: (
                <div className="buff-description">
                    <div> For every Sword Stance triggered, the DMG dealt by Sword Stance increases by </div>
                    <div className="buff-text"> 2.5%</div>
                    <div> Stacks up to </div>
                    <div className="buff-text"> 10 </div>
                    <div> time(s). </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Vanquisher",
            description: (
                <div className="buff-description">
                    <div> After using </div>
                    <div className="buff-text"> Basic ATK or Skill </div>
                    <div> if there are enemies on the field with Weakness Break, Sushang's action is Advanced Forward by  </div>
                    <div className="buff-text"> 15% </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Cut With Ease",
            description: (
                <div className="buff-description">
                    <div> After using Skill against a Weakness Broken enemy, regenerates </div>
                    <div className="buff-text"> 1 Skill Point</div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Refine in Toil",
            description: (
                <div className="buff-description">
                    <div> After Sword Stance is triggered, the DMG taken by Sushang is reduced by 20% for 1 turn. </div>
                </div>
            )
        }
    }

    E3() {
        return {
            name: "Rise From Fame",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }
    
    E4() {
        return {
            name: "Cleave With Heart",
            description:(
                <div className="buff-description">
                    <div>Sushang's Break Effect increases by 40%.</div>
                </div>
            )
        }
    }
    
    E5() {
        return {
            name: "Seen and Unseen",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: "Dwell Like Water",
            description: (
                <div className="buff-description">
                    <div>Talent's SPD Boost is stackable and can stack up to 2 times. Additionally, after entering battle, Sushang immediately gains 1 stack of her Talent's SPD Boost. </div>
                </div>
            ),
        }
    }
}


export default Sushang;