import React, {useState, useContext, useEffect } from 'react';
import { globalData } from '../../globalData';
import head from "../../../assets/relics/Head.webp";
import body from "../../../assets/relics/Body.webp";
import hands from "../../../assets/relics/Hands.webp";
import feet from "../../../assets/relics/Feet.webp";
import rope from "../../../assets/relics/Rope.webp";
import sphere from "../../../assets/relics/Sphere.webp";
import RelicsSelection from './relicsParts/relicsSelect';
import * as RelicsPieces from './relicsParts/relicPieces';

function Relics() {

    const {
        headPieces,
        handsPieces,
        bodyPieces,
        feetPieces,
        ropePieces,
        spherePieces,
        headPiece,
        handsPiece,
        bodyPiece,
        feetPiece,
        spherePiece,
        ropePiece,
        setHeadPiece,
        setHandsPiece,
        setBodyPiece,
        setFeetPiece,
        setSpherePiece,
        setRopePiece,
    } = useContext(globalData);

    const [toggleState, setToggleState] = useState(0);

    const [headImages, setHeadImages] = useState("");
    const [handsImages, setHandsImages] = useState("");
    const [bodyImages, setBodyImages] = useState("");
    const [feetImages, setFeetImages] = useState("");
    const [ropeImages, setRopeImages] = useState("");
    const [sphereImages, setSphereImages] = useState("");


    const [headOpen, setHeadOpen] = useState(false);
    const [handsOpen, setHandsOpen] = useState(false);
    const [bodyOpen, setBodyOpen] = useState(false);
    const [feetOpen, setFeetOpen] = useState(false);
    const [ropeOpen, setRopeOpen] = useState(false);
    const [sphereOpen, setSphereOpen] = useState(false);
    
    const [headImage, setHeadImage] = useState("");
    const [handsImage, setHandsImage] = useState("");
    const [bodyImage, setBodyImage] = useState("");
    const [feetImage, setFeetImage] = useState("");
    const [sphereImage, setSphereImage] = useState("");
    const [ropeImage, setRopeImage] = useState("");

    const toggleTab = (index) => {
        if(toggleState === index) {
            setToggleState(0);
            return;
        }
        setToggleState(index);
    };
    
    const handleHeadSelection = (event) => {
        setHeadPiece(event.target.dataset.id, event.target.dataset.rarity, event.target.dataset.type);
    }
    
    const handleHandsSelection = (event) => {
        setHandsPiece(event.target.dataset.id, event.target.dataset.rarity, event.target.dataset.type);
    }

    const handleBodySelection = (event) => {
        setBodyPiece(event.target.dataset.id, event.target.dataset.rarity, event.target.dataset.type, "CritRate");
    }

    const handleFeetSelection = (event) => {
        setFeetPiece(event.target.dataset.id, event.target.dataset.rarity, event.target.dataset.type, "SPD");
    }

    const handleSphereSelection = (event) => {
        setSpherePiece(event.target.dataset.id, event.target.dataset.rarity, event.target.dataset.type, "HP%");
    }

    const handleRopeSelection = (event) => {
        setRopePiece(event.target.dataset.id, event.target.dataset.rarity, event.target.dataset.type, "HP%");
    }

    /*
    *   Load all images for relic pieces selection menus
    */
    async function loadAllImg(imageName, piece) {
        return import(`../../../assets/relics/${piece}/${imageName}.webp`).then((image) => {
            return image.default;
        });
    }
    
    useEffect(() => {
        const loadAllImages = async () => {
          const initial = Array(headPieces.length).fill("");
          setHeadImages(initial);
    
          const promises = headPieces.map((relic) => {
            return loadAllImg(relic.id, relic.type);
          });
    
          const loadedImages = await Promise.all(promises);
          setHeadImages(loadedImages);
          
        };
    
        loadAllImages();
    }, [headPieces]);

    useEffect(() => {
        const loadAllImages = async () => {
          const initial = Array(handsPieces.length).fill("");
          setHandsImages(initial);
    
          const promises = handsPieces.map((relic) => {
            return loadAllImg(relic.id, relic.type);
          });
    
          const loadedImages = await Promise.all(promises);
          setHandsImages(loadedImages);
          
        };
    
        loadAllImages();
    }, [handsPieces]);


    useEffect(() => {
        const loadAllImages = async () => {
          const initial = Array(bodyPieces.length).fill("");
          setBodyImages(initial);
    
          const promises = bodyPieces.map((relic) => {
            return loadAllImg(relic.id, relic.type);
          });
    
          const loadedImages = await Promise.all(promises);
          setBodyImages(loadedImages);
          
        };
    
        loadAllImages();
    }, [bodyPieces]);


    useEffect(() => {
        const loadAllImages = async () => {
          const initial = Array(feetPieces.length).fill("");
          setFeetImages(initial);
    
          const promises = feetPieces.map((relic) => {
            return loadAllImg(relic.id, relic.type);
          });
    
          const loadedImages = await Promise.all(promises);
          setFeetImages(loadedImages);
          
        };
    
        loadAllImages();
    }, [feetPieces]);


    useEffect(() => {
        const loadAllImages = async () => {
            const initial = Array(spherePieces.length).fill("");
            setSphereImages(initial);

            const promises = spherePieces.map((relic) => {
                return loadAllImg(relic.id, relic.type);
            });

            const loadedImages = await Promise.all(promises);
            setSphereImages(loadedImages);

        };

        loadAllImages();
    }, [spherePieces]);

    useEffect(() => {
        const loadAllImages = async () => {
            const initial = Array(ropePieces.length).fill("");
            setRopeImages(initial);

            const promises = ropePieces.map((relic) => {
                return loadAllImg(relic.id, relic.type);
            });

            const loadedImages = await Promise.all(promises);
            setRopeImages(loadedImages);

        };

        loadAllImages();
    }, [ropePieces]);


    /* 
    *   load relic images
    */
    function loadHeadImg (imageName) {
        import(`../../../assets/relics/head/${imageName}.webp`).then((image) => setHeadImage(image.default));
    }

    function loadHandsImg (imageName) {
        import(`../../../assets/relics/hands/${imageName}.webp`).then((image) => setHandsImage(image.default));
    }

    function loadBodyImg (imageName) {
        import(`../../../assets/relics/body/${imageName}.webp`).then((image) => setBodyImage(image.default));
    }

    function loadFeetImg (imageName) {
        import(`../../../assets/relics/feet/${imageName}.webp`).then((image) => setFeetImage(image.default));
    }

    function loadSphereImg (imageName) {
        import(`../../../assets/relics/sphere/${imageName}.webp`).then((image) => setSphereImage(image.default));
    }

    function loadRopeImg (imageName) {
        import(`../../../assets/relics/rope/${imageName}.webp`).then((image) => setRopeImage(image.default));
    }
    
    useEffect(() => {
        if(headPiece) {
            loadHeadImg(headPiece.id, headPiece.type);
        }else {
            setHeadImage(head);
        }
    }, [headPiece]);

    
    useEffect(() => {
        if(handsPiece) {
            loadHandsImg(handsPiece.id);
        }else {
            setHandsImage(hands);
        }
    }, [handsPiece]);


    useEffect(() => {
        if(bodyPiece) {
            loadBodyImg(bodyPiece.id);
        }else {
            setBodyImage(body);
        }
    }, [bodyPiece]);

    useEffect(() => {
        if(feetPiece) {
            loadFeetImg(feetPiece.id);
        }else {
            setFeetImage(feet);
        }
    }, [feetPiece]);

    useEffect(() => {
        if(spherePiece) {
            loadSphereImg(spherePiece.id);
        }else {
            setSphereImage(sphere);
        }
    }, [spherePiece]);

    useEffect(() => {
        if(ropePiece) {
            loadRopeImg(ropePiece.id);
        }else {
            setRopeImage(rope);
        }
    }, [ropePiece]);

    /* 
    *   displaying the right content based on selection
    *   for each relic piece
    */
    let headContent = null;
    let headImg = <img src={head} className={toggleState === 1 ? "tabs active-tabs fiveStar" : "tabs"} onClick={() => toggleTab(1)} alt=""></img>;
    if(headPiece) {
        headContent = (
            <RelicsPieces.RelicHeadContent setHeadOpen={setHeadOpen}></RelicsPieces.RelicHeadContent>
        )
        headImg = (
            <img src={headImage} className={toggleState === 1 ? `tabs active-tabs ${headPiece.rarity}` : `tabs ${headPiece.rarity}`} onClick={() => toggleTab(1)} alt=""></img>
        )
    }

    let handsContent = null;
    let handsImg = <img src={hands} className={toggleState === 2 ? "tabs active-tabs" : "tabs"} onClick={() => toggleTab(2)} alt=""></img>;
    if(handsPiece) {
        handsContent = (
            <RelicsPieces.RelicHandsContent setHandsOpen={setHandsOpen}></RelicsPieces.RelicHandsContent>
        )
        handsImg = (
            <img src={handsImage} className={toggleState === 2 ? `tabs active-tabs ${handsPiece.rarity}` : `tabs ${handsPiece.rarity}`} onClick={() => toggleTab(2)} alt=""></img>
        )
    }

    let bodyContent = null;
    let bodyImg = <img src={body} className={toggleState === 3 ? "tabs active-tabs" : "tabs"} onClick={() => toggleTab(3)} alt=""></img>;
    if(bodyPiece) {
        bodyContent = (
            <RelicsPieces.RelicBodyContent setBodyOpen={setBodyOpen}></RelicsPieces.RelicBodyContent>
        )
        bodyImg = (
            <img src={bodyImage} className={toggleState === 3 ? `tabs active-tabs ${bodyPiece.rarity}` : `tabs ${bodyPiece.rarity}`} onClick={() => toggleTab(3)} alt=""></img>
        )
    }

    let feetContent = null;
    let feetImg = <img src={feet} className={toggleState === 4 ? "tabs active-tabs" : "tabs"} onClick={() => toggleTab(4)} alt=""></img>;
    if(feetPiece) {
        feetContent = (
            <RelicsPieces.RelicFeetContent setFeetOpen={setFeetOpen}></RelicsPieces.RelicFeetContent>
        )
        feetImg = (
            <img src={feetImage} className={toggleState === 4 ? `tabs active-tabs ${feetPiece.rarity}` : `tabs ${feetPiece.rarity}`} onClick={() => toggleTab(4)} alt=""></img>
        )
    }

    let sphereContent = null;
    let sphereImg = <img src={sphere} className={toggleState === 5 ? "tabs active-tabs" : "tabs"} onClick={() => toggleTab(5)} alt=""></img>;
    if(spherePiece) {
        sphereContent = (
            <RelicsPieces.RelicSphereContent setSphereOpen={setSphereOpen}></RelicsPieces.RelicSphereContent>
        )
        sphereImg = (
            <img src={sphereImage} className={toggleState === 5 ? `tabs active-tabs ${spherePiece.rarity}` : `tabs ${spherePiece.rarity}`} onClick={() => toggleTab(5)} alt=""></img>
        )
    }

    let ropeContent = null;
    let ropeImg = <img src={rope} className={toggleState === 6 ? "tabs active-tabs" : "tabs"} onClick={() => toggleTab(6)} alt=""></img>;
    if(ropePiece) {
        ropeContent = (
            <RelicsPieces.RelicRopeContent setRopeOpen={setRopeOpen}></RelicsPieces.RelicRopeContent>
        )
        ropeImg = (
            <img src={ropeImage} className={toggleState === 6 ? `tabs active-tabs ${ropePiece.rarity}` : `tabs ${ropePiece.rarity}`} onClick={() => toggleTab(6)} alt=""></img>
        )
    }

    return (
        <div className="relics-container">
            <div className='bloc-tabs'>
                {headImg}
                {handsImg}
                {bodyImg}
                {feetImg}
                {sphereImg}
                {ropeImg}
            </div>

            <div className='content-tabs'>
                <div className={toggleState === 1 ? "content active-content" : "content"}>
                    <div className={headPiece ? `centered hide` : "centered"}>
                        <button className='calc-btn' onClick={() => setHeadOpen(true)}>Select</button>
                    </div>
                    <RelicsSelection text={"Select Head Piece"} relicList={headPieces} images={headImages} open={headOpen} setOpen={setHeadOpen} handleSelection={handleHeadSelection}></RelicsSelection>
                    {headContent}
                </div>

                <div className={toggleState === 2 ? "content active-content" : "content"}>
                    <div className={handsPiece ? `centered hide` : "centered"}>
                        <button className='calc-btn' onClick={() => setHandsOpen(true)}>Select</button>
                    </div>
                    <RelicsSelection text={"Select Hands Piece"} relicList={handsPieces} images={handsImages} open={handsOpen} setOpen={setHandsOpen} handleSelection={handleHandsSelection}></RelicsSelection>
                    {handsContent}
                </div>

                <div className={toggleState === 3 ? "content active-content" : "content"}>
                    <div className={bodyPiece ? `centered hide` : "centered"}>
                        <button className='calc-btn' onClick={() => setBodyOpen(true)}>Select</button>
                    </div>
                    <RelicsSelection text={"Select Body Piece"} relicList={bodyPieces} images={bodyImages} open={bodyOpen} setOpen={setBodyOpen} handleSelection={handleBodySelection}></RelicsSelection>
                    {bodyContent}
                </div>

                <div className={toggleState === 4 ? "content active-content" : "content"}>
                    <div className={feetPiece ? `centered hide` : "centered"}>
                        <button className='calc-btn' onClick={() => setFeetOpen(true)}>Select</button>
                    </div>
                    <RelicsSelection text={"Select Feet Piece"} relicList={feetPieces} images={feetImages} open={feetOpen} setOpen={setFeetOpen} handleSelection={handleFeetSelection}></RelicsSelection>
                    {feetContent}
                </div>

                <div className={toggleState === 5 ? "content active-content" : "content"}>
                    <div className={spherePiece ? `centered hide` : "centered"}>
                        <button className='calc-btn' onClick={() => setSphereOpen(true)}>Select</button>
                    </div>
                    <RelicsSelection text={"Select Shpere Piece"} relicList={spherePieces} images={sphereImages} open={sphereOpen} setOpen={setSphereOpen} handleSelection={handleSphereSelection}></RelicsSelection>
                    {sphereContent}
                </div>

                <div className={toggleState === 6 ? "content active-content" : "content"}>
                    <div className={ropePiece ? `centered hide` : "centered"}>
                        <button className='calc-btn' onClick={() => setRopeOpen(true)}>Select</button>
                    </div>
                    <RelicsSelection text={"Select Rope Piece"} relicList={ropePieces} images={ropeImages} open={ropeOpen} setOpen={setRopeOpen} handleSelection={handleRopeSelection}></RelicsSelection>
                    {ropeContent}
                </div>
            </div>

        </div>
    )
}

export default Relics;