class Gepard {
    constructor() {
        this.stats = [
            {level: 1,  atk: 73.92,  def: 89.1,   hp: 190,  spd: 92, taunt: 150, energy: 100},
            {level: 20, atk: 144.14, def: 173.75, hp: 371,  spd: 92, taunt: 150, energy: 100, iceDmg: 0.032},
            {level: 20, atk: 173.71, def: 209.39, hp: 447,  spd: 92, taunt: 150, energy: 100, iceDmg: 0.032},
            {level: 40, atk: 277.2,  def: 334.13, hp: 713,  spd: 92, taunt: 150, energy: 100, iceDmg: 0.032, effectRes: 0.04},
            {level: 40, atk: 306.77, def: 369.77, hp: 789,  spd: 92, taunt: 150, energy: 100, iceDmg: 0.064, effectRes: 0.04, defPercent: 0.05},
            {level: 50, atk: 343.73, def: 414.32, hp: 884,  spd: 92, taunt: 150, energy: 100, iceDmg: 0.064, effectRes: 0.04, defPercent: 0.05},
            {level: 50, atk: 373.3,  def: 449.96, hp: 960,  spd: 92, taunt: 150, energy: 100, iceDmg: 0.112, effectRes: 0.04, defPercent: 0.05},
            {level: 60, atk: 410.26, def: 494.51, hp: 1055, spd: 92, taunt: 150, energy: 100, iceDmg: 0.112, effectRes: 0.04, defPercent: 0.05},
            {level: 60, atk: 439.82, def: 530.15, hp: 1131, spd: 92, taunt: 150, energy: 100, iceDmg: 0.16,  effectRes: 0.1,  defPercent: 0.05},
            {level: 70, atk: 476.78, def: 574.7,  hp: 1226, spd: 92, taunt: 150, energy: 100, iceDmg: 0.16,  effectRes: 0.1,  defPercent: 0.05},
            {level: 70, atk: 506.35, def: 610.34, hp: 1302, spd: 92, taunt: 150, energy: 100, iceDmg: 0.16,  effectRes: 0.1,  defPercent: 0.05},
            {level: 80, atk: 543.31, def: 654.89, hp: 1397, spd: 92, taunt: 150, energy: 100, iceDmg: 0.224, effectRes: 0.18, defPercent: 0.125},
        ];

        this.name = "Gepard";
        this.path = "Preservation";
        this.rarity = 'fiveStar';
        this.element = "Ice";
        this.id = "gepard";
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Fist of Conviction",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return {
                multiplier: 1 + (level * 0.1),
                additional: 0.3 + (level * 0.03),
            }
        }else if(level < 10){
            return {
                multiplier: 1.5 + ((level - 5) * 0.125),
                additional: 0.45 + ((level - 5) * 0.0375),
            }
        }else {
            return {
                multiplier: 2 + ((level - 9) * 0.1),
                additional: 0.6 + ((level - 5) * 0.03),
            }
        }
    }

    skillDescription(level) {
        return {
            name: "Daunting Smite",
            description: (
                <div className="buff-description">
                    <div> Deals  </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level).multiplier * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy, </div>
                    <div> with a 65% base chance to Freeze the enemy for 1 turn(s). While Frozen, the enemy cannot take action and will take Additional Ice DMG equal to  </div>
                    <div className="buff-text">{(this.skillAttack(level).additional * 100).toFixed(1)}%</div>
                    <div> If the ally's current HP percentage is 30% or higher, greatly increases the chance of enemies attacking that ally. </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> at the beginning of each turn. </div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level == 0) {
            return {
                multiplier: 0.3,
                addition: 150,
            }
        }else if(level == 1) {
            return {
                multiplier: 0.3188,
                addition: 240,
            }
        }
        else if(level == 2) {
            return {
                multiplier: 0.3375,
                addition: 308,
            }
        }
        else if(level == 3) {
            return {
                multiplier: 0.3563,
                addition: 375,
            }
        }
        else if(level == 4) {
            return {
                multiplier: 0.375,
                addition: 420,
            }
        }
        else if(level == 5) {
            return {
                multiplier: 0.39,
                addition: 465,
            }
        }
        else if(level == 6) {
            return {
                multiplier: 0.405,
                addition: 499,
            }
        }
        else if(level == 7) {
            return {
                multiplier: 0.42,
                addition: 533,
            }
        }
        else if(level == 8) {
            return {
                multiplier: 0.435,
                addition: 566,
            }
        }
        else if(level == 9) {
            return {
                multiplier: 0.45,
                addition: 600,
            }
        }
        else if(level == 10) {
            return {
                multiplier: 0.465,
                addition: 634,
            }
        }
        else if(level == 11) {
            return {
                multiplier: 0.48,
                addition: 668,
            }
        }
    }

    ultimateDescription(level) {
        return {
            name: "Enduring Bulwark",
            description: (
                <div className="buff-description">
                    <div>Applies a Shield to all allies, absorbing DMG equal to </div>
                    <div className="buff-text">{(this.ultimateAttack(level).multiplier * 100).toFixed(1)}%</div>
                    <div>  of {this.name}'s </div>
                    <div className="buff-text"> DEF </div>
                    <div> plus </div>
                    <div className="buff-text">{this.ultimateAttack(level).addition}</div>
                    <div> for 3 turn(s). </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 0.25 + (level * 0.025);
        }else if(level < 10) {
            return 0.375 + ((level - 5) * 0.0313);
        }else {
            return 0.5 + ((level - 9) * 0.025);
        }
    }

    talentDescription(level) { 
        return {
            name: "Unyielding Will",
            description: (
                <div className="buff-description">
                    <div>When struck with a killing blow, instead of becoming knocked down, Gepard's HP immediately restores to </div>
                    <div className="buff-text">{(this.talent(level) * 100).toFixed(1)}%</div>
                    <div>  of {this.name}'s </div>
                    <div className="buff-text"> Max HP </div>
                    <div> This effect can only trigger once per battle. </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Comradery",
            description: (
                <div className="buff-description">
                    <div> After Gepard uses his Technique, when the next battle begins, a Shield will be applied to all allies, absorbing DMG equal to 24% of Gepard's DEF plus 150 for 2 turn(s). </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Integrity",
            description: (
                <div className="buff-description">
                    <div> Gepard has a higher chance to be attacked by enemies. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Commander",
            description: (
                <div className="buff-description">
                    <div> When "Unyielding Will" is triggered, Gepard's Energy will be restored to 100%. </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Fighting Spirit",
            description: (
                <div className="buff-description">
                    <div> Gepard's ATK increases by 35% of his current DEF. This effect will refresh at the start of each turn. </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Due Diligence",
            description: (
                <div className="buff-description">
                    <div> When using Skill, increases the base chance to Freeze target enemy by 35%. </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Lingering Cold",
            description: (
                <div className="buff-description">
                    <div> After an enemy Frozen by Skill is unfrozen, their SPD is reduced by 20% for 1 turn(s). </div>
                </div>
            )
        }
    }
    
    E3() {
        return {
            name: "Never Surrender",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }
    
    E4() {
        return {
            name: "Faith Moves Mountains",
            description:(
                <div className="buff-description">
                    <div>When Gepard is in battle, all allies' Effect RES increases by 20%.</div>
                </div>
            )
        }
    }

    E5() {
        return {
            name: "Cold Iron Fist",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }
    
    
    E6() {
        return {
            name: "Unyielding Resolve",
            description: (
                <div className="buff-description">
                    <div> When his Talent is triggered, Gepard immediately takes action again and restores extra HP equal to 50% of his Max HP. </div>
                </div>
            ),
        }
    }
}

export default Gepard;