export class Yukong {
    constructor() {
        this.stats = [
            {level: 1,  atk: 81.6,   def: 51,     hp: 125,  spd: 107, taunt: 100, energy: 130},
            {level: 20, atk: 159.12, def: 99.45,  hp: 243,  spd: 107, taunt: 100, energy: 130, imaginaryDmg: 0.032},
            {level: 20, atk: 191.76, def: 119.85, hp: 293,  spd: 107, taunt: 100, energy: 130, imaginaryDmg: 0.032},
            {level: 40, atk: 306,    def: 191.25, hp: 468,  spd: 107, taunt: 100, energy: 130, imaginaryDmg: 0.032},
            {level: 40, atk: 338.64, def: 211.65, hp: 518,  spd: 107, taunt: 100, energy: 130, imaginaryDmg: 0.064, hpPercent: 0.04, atkPercent: 0.04},
            {level: 50, atk: 379.44, def: 237.15, hp: 580,  spd: 107, taunt: 100, energy: 130, imaginaryDmg: 0.064, hpPercent: 0.04, atkPercent: 0.04},
            {level: 50, atk: 412.08, def: 257.55, hp: 630,  spd: 107, taunt: 100, energy: 130, imaginaryDmg: 0.112, hpPercent: 0.04, atkPercent: 0.04},
            {level: 60, atk: 452.88, def: 283.05, hp: 693,  spd: 107, taunt: 100, energy: 130, imaginaryDmg: 0.112, hpPercent: 0.04, atkPercent: 0.04},
            {level: 60, atk: 485.52, def: 303.45, hp: 743,  spd: 107, taunt: 100, energy: 130, imaginaryDmg: 0.16,  hpPercent: 0.1,  atkPercent: 0.04},
            {level: 70, atk: 526.32, def: 328.95, hp: 805,  spd: 107, taunt: 100, energy: 130, imaginaryDmg: 0.16,  hpPercent: 0.1,  atkPercent: 0.04},
            {level: 70, atk: 558.96, def: 349.35, hp: 855,  spd: 107, taunt: 100, energy: 130, imaginaryDmg: 0.16,  hpPercent: 0.1,  atkPercent: 0.1},
            {level: 80, atk: 599.76, def: 374.85, hp: 917,  spd: 107, taunt: 100, energy: 130, imaginaryDmg: 0.224, hpPercent: 0.18, atkPercent: 0.1},
        ];

        this.name = "Yukong";
        this.path = "Harmony";
        this.rarity = 'fourStar';
        this.element = "Imaginary";
        this.id = "yukong";
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Arrowslinger",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of Yukong's </div>
                    <div className="buff-text">ATK</div>
                    <div> as </div>
                    <div className="Imaginary">Imaginary DMG</div>
                    <div> to a target enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return 0.4 + (level * 0.04);
        }else if(level < 10){
            return 0.6 + ((level - 5) * 0.05);
        }else {
            return 0.8 + ((level - 9) * 0.05);
        }
    }

    skillDescription(level) {
        return {
            name: "Emboldening Salvo",
            description: (
                <div className="buff-description">
                    <div>Obtains </div>
                    <div className="buff-text">2 stack(s)</div>
                    <div> of "Roaring Bowstrings" (to a maximum of 2 stacks). When "Roaring Bowstrings" is active, the  </div>
                    <div className="buff-text"> ATK </div>
                    <div> of all allies increases by </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                    <div>, and every time an ally's turn ends, Yukong loses 1 stack of "Roaring Bowstrings." When it's the turn where Yukong gains "Roaring Bowstrings" by using Skill, "Roaring Bowstrings" will not be removed. </div>
                </div>
            ),
        }
    }

    skillSimple(level) {
        return {
            name: "Emboldening Salvo",
            description: (
                <div className="buff-description">
                    <div className="buff-text"> ATK </div>
                    <div> of all allies increases by </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                </div>
            ),
        }
    }

    ultimateBuff(level) {
        if(level < 6) {
            return {
                critRate: 0.21 + (level * 0.007),
                critDmg: 0.39 + (level * 0.026),
                multiplier: 2.28 + (level * 0.152),
            };
        }else if(level < 10){
            return {
                critRate: 0.245 + ((level - 5) * 0.0088),
                critDmg: 0.52 + ((level - 5) * 0.0325),
                multiplier: 3.04 + ((level - 5) * 0.19),
            };
        }else {
            return {
                critRate: 0.28 + ((level - 9) * 0.007),
                critDmg: 0.65 + ((level - 9) * 0.026),
                multiplier: 3.8 + ((level - 9) * 0.152),
            };
        }
    }

    ultimateDescription(level) {
        return {
            name: "Diving Kestrel",
            description: (
                <div className="buff-description">
                    <div>If "Roaring Bowstrings" is active on Yukong when her Ultimate is used, additionally increases all allies' </div>
                    <div className="buff-text"> CRIT Rate </div>
                    <div> by </div>
                    <div className="buff-text">{(this.ultimateBuff(level).critRate * 100).toFixed(1)}%</div>
                    <div> and </div>
                    <div className="buff-text">CRIT DMG</div>
                    <div> by </div>
                    <div className="buff-text"> {(this.ultimateBuff(level).critDmg * 100).toFixed(1)}%. </div>
                    <div> At the same time, deals </div>
                    <div className="Imaginary"> Imaginary DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.ultimateBuff(level).multiplier * 100).toFixed(1)}%. </div>
                    <div> of Yukong's ATK to a single enemy. </div>
                </div>
            ),
        }
    }

    ultimateSimple(level) {
        return {
            name: "Diving Kestrel",
            description: (
                <div className="buff-description">
                    <div> Increases all allies' </div>
                    <div className="buff-text"> CRIT Rate </div>
                    <div> by </div>
                    <div className="buff-text">{(this.ultimateBuff(level).critRate * 100).toFixed(1)}%</div>
                    <div> and </div>
                    <div className="buff-text">CRIT DMG</div>
                    <div> by </div>
                    <div className="buff-text"> {(this.ultimateBuff(level).critDmg * 100).toFixed(1)}%. </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 0.4 + (level * 0.04);
        }else if(level < 10){
            return 0.6 + ((level - 5) * 0.05);
        }else {
            return 0.8 + ((level - 9) * 0.04);
        }
    }

    talentDescription(level) { 
        return {
            name: "Seven Layers, One Arrow",
            description: (
                <div className="buff-description">
                    <div>Basic ATK additionally deals Imaginary DMG equal to </div>
                    <div className="buff-text"> {(this.talent(level) * 100).toFixed(1)}%.</div>
                    <div> of Yukong's ATK, and increases the Toughness-Reducing DMG of this attack by </div>
                    <div className="buff-text"> 100% </div>
                    <div> This effect can be triggered again in </div>
                    <div> 1 turn(s). </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Chasing the Wind",
            description: (
                <div className="buff-description">
                    <div> After using her Technique, Yukong enters Sprint mode for </div>
                    <div className="buff-text"> 20 seconds </div>
                    <div>. In Sprint mode, her Movement SPD increases by  </div>
                    <div className="buff-text"> 35% </div>
                    <div> and Yukong gains </div>
                    <div className="buff-text">2 stack(s)</div>
                    <div> of "Roaring Bowstrings" when she enters battle by attacking enemies. </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Archerion",
            description: (
                <div className="buff-description">
                    <div>Yukong can resist 1 debuff application for 1 time. This effect can be triggered again in  </div>
                    <div className="buff-text"> 2 turn(s).</div>
                </div>
            ),
        };
    }

    A4() {
        return {
            name: "Bowmaster",
            description: (
                <div className="buff-description">
                    <div>When Yukong is on the field, </div>
                    <div className="Imaginary"> Imaginary DMG </div>
                    <div> dealt by all allies increases by </div>
                    <div className="buff-text"> 12%.</div>
                </div>
            ),
        };
    }

    A6() {
        return {
            name: "Majestas",
            description: (
                <div className="buff-description">
                    <div>When Roaring Bowstrings is active, Yukong regenerates </div>
                    <div className="buff-text"> 2 additional Energy </div>
                    <div> every time an ally takes action. </div>
                </div>
            ),
        };
    }

    E1() {
        return {
            name: "Aerial Marshal",
            description: (
                <div className="buff-description">
                    <div>At the start of battle, increases the </div>
                    <div className="buff-text"> SPD  </div>
                    <div> of all allies by </div>
                    <div className="buff-text"> 10% </div>
                    <div> for </div>
                    <div className="buff-text"> 2 turn(s). </div>
                </div>
            ),
        }
    }

    E2() {
        return {
            name: "Skyward Command",
            description: (
                <div className="buff-description">
                    <div>When any ally's current energy is equal to its energy limit, Yukong regenerates an additional 5 energy.
                        This effect can only be triggered once for each ally. The trigger count is reset after Yukong casts her Ultimate.</div>
                </div>
            ),
        }
    }

    E3() {
        return {
            name: "Torrential Fusillade",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }
    

    E4() {
        return {
            name: "Zephyrean Echoes",
            description: (
                <div className="buff-description">
                    <div>When "Roaring Bowstrings" is active, Yukong deals </div>
                    <div className="buff-text"> 30% </div>
                    <div>more DMG to enemies. </div>
                </div>
            ),
        }
    }


    E5() {
        return {
            name: "August Deadshot",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: "Bowstring Thunderclap",
            description: (
                <div className="buff-description">
                    <div>When Yukong uses her Ultimate, she immediately gains 1 stack(s) of "Roaring Bowstrings." </div>
                </div>
            ),
        }
    }

}

export default Yukong;