class Hook {
    constructor() {
        this.stats = [
            {level: 1,  atk: 84,    def: 48,    hp: 182, spd: 94, taunt: 125, energy: 120},
            {level: 20, atk: 163.8, def: 93.6,  hp: 356, spd: 94, taunt: 125, energy: 120, atkPercent: 0.04},
            {level: 20, atk: 197.4, def: 112.8, hp: 429, spd: 94, taunt: 125, energy: 120, atkPercent: 0.04},
            {level: 40, atk: 315,   def: 180,   hp: 684, spd: 94, hpPercent: 0.04, taunt: 125, energy: 120, atkPercent: 0.04},
            {level: 40, atk: 348.6, def: 199.2, hp: 757, spd: 94, hpPercent: 0.04, taunt: 125, energy: 120, atkPercent: 0.08, critDmg: 0.053},
            {level: 50, atk: 390.6, def: 223.2, hp: 848, spd: 94, hpPercent: 0.04, taunt: 125, energy: 120, atkPercent: 0.08, critDmg: 0.053},
            {level: 50, atk: 424.2, def: 242.4, hp: 921, spd: 94, hpPercent: 0.04, taunt: 125, energy: 120, atkPercent: 0.14, critDmg: 0.053},
            {level: 60, atk: 466.2, def: 266.4, hp: 1012,spd: 94, hpPercent: 0.04, taunt: 125, energy: 120, atkPercent: 0.14, critDmg: 0.053},
            {level: 60, atk: 499.8, def: 285.6, hp: 1085,spd: 94, hpPercent: 0.1,  taunt: 125, energy: 120, atkPercent: 0.2,  critDmg: 0.53},
            {level: 70, atk: 541.8, def: 309.6, hp: 1176,spd: 94, hpPercent: 0.1,  taunt: 125, energy: 120, atkPercent: 0.2,  critDmg: 0.53},
            {level: 70, atk: 575.4, def: 328.8, hp: 1249,spd: 94, hpPercent: 0.1,  taunt: 125, energy: 120, atkPercent: 0.2,  critDmg: 0.133},
            {level: 80, atk: 617.4, def: 352.8, hp: 1341,spd: 94, hpPercent: 0.18,  taunt: 125, energy: 120, atkPercent: 0.28, critDmg: 0.133},
        ];

        this.name = "Hook";
        this.path = "Destruction";
        this.rarity = 'fourStar';
        this.element = "Fire";
        this.id = "hook";
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Hehe! Don't Get Burned!",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return {
                single: 1.2 + (level * 0.12),
                dot: 0.25 + (level * 0.025),
            }
        }else if(level < 10){
            return {
                single: 1.8 + ((level - 5) * 0.15),
                dot: 0.3875 + ((level - 5) * 0.05),
            }
        }else {
            return {
                single: 2.4 + ((level - 9) * 0.12),
                dot: 0.65 + ((level - 9) * 0.0325),
            }
        }
    }

    skillAttack1(level) {
        if(level < 6) {
            return {
                single: 1.4  + (level * 0.14),
                splash: 0.4 + (level * 0.04),
                dot: 0.25 + (level * 0.025),
            }
        }else if(level < 10){
            return {
                single: 2.1 + ((level - 5) * 0.175),
                splash: 0.6 + ((level - 5) * 0.05),
                dot: 0.3875 + ((level - 5) * 0.05),
            }
        }else {
            return {
                single: 2.8 + ((level - 9) * 0.14),
                splash: 0.8 + ((level - 9) * 0.04),
                dot: 0.65 + ((level - 9) * 0.0325),
            }
        }
    }

    skillDescription(level) {
        return {
            name: "Hey! Remember Hook?",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level).single * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                    <div> In addition, there is a 100% base chance to inflict Burn for 2 turn(s). When afflicted with Burn, enemies will take </div>
                    <div className={this.element}> {this.element} DoT </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level).dot * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> at the beginning of each turn. </div>
                </div>
            ),
        }
    }

    skill1Description(level) {
        return {
            name: "Hey! Remember Hook?",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack1(level).single * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to a single with a 100% base chance to Burn them for 2 turn(s). Additionally, deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack1(level).spread * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to adjacent enemies. </div>
                    <br/>
                    <div>When afflicted with Burn, enemies will take </div>
                    <div className={this.element}> {this.element} DoT </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack1(level).dot * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> at the beginning of each turn. </div>
                </div>
            ),
        }
    }
        

    ultimateAttack(level) {
        if(level < 6) {
            return 2.4 + (level * 0.16);
        }else if(level < 10){
            return 3.2 + ((level - 5) * 0.2);
        }else {
            return 4 + ((level - 9) * 0.16);
        }
    }

    ultimateDescription(level) {
        return {
            name: "Boom! Here Comes the Fire!",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}>{this.element} DMG</div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.ultimateAttack(level) * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> After using Ultimate, the next Skill to be used is Enhanced, which deals DMG to a single enemy and enemies adjacent to it. </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 0.5 + (level * 0.05);
        }else if(level < 10){
            return 0.75 + ((level - 5) * 0.0625);
        }else {
            return 1 + ((level - 9) * 0.05);
        }
    }

    talentDescription(level) { 
        return {
            name: "Ha! Oil to the Flames!",
            description: (
                <div className="buff-description">
                    <div> When attacking a target afflicted with Burn, deals Additional </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.talent(level) * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> and regenerates 5 extra Energy. </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Ack! Look at This Mess!",
            description: (
                <div className="buff-description">
                    <div> Immediately attacks the enemy. Upon entering battle, Hook deals Fire DMG equal to 50% of her ATK to a random enemy. In addition, there is a 100% base chance to inflict Burn on every enemy for 3 turn(s). When afflicted with Burn, enemies will take Fire DoT equal to 50% of Hook's ATK at the beginning of each turn. </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Innocence",
            description: (
                <div className="buff-description">
                    <div> Hook restores HP equal to 5% of her Max HP whenever her Talent is triggered. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Naivete",
            description: (
                <div className="buff-description">
                    <div> The chance to resist Crowd Control Debuffs increases by 35%. </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Playing With Fire",
            description: (
                <div className="buff-description">
                    <div> When using her Ultimate, Hook has her action Advanced Forward by 20% and Hook additionally regenerates 5 Energy. </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Early to Bed, Early to Rise",
            description: (
                <div className="buff-description">
                    <div> Enhanced Skill deals 20% increased DMG. </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Happy Tummy, Happy Body",
            description: (
                <div className="buff-description">
                    <div> Extends the duration of Burn caused by Skill by 1 turn(s). </div>
                </div>
            )
        }
    }

    E3() {
        return {
            name: "Don't Be Picky, Nothing's Icky",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }

    E4() {
        return {
            name: "It's Okay to Not Know",
            description:(
                <div className="buff-description">
                    <div>When Talent is triggered, there is a 100% base chance to Burn enemies adjacent to the target enemy, equivalent to that of Skill. </div>
                </div>
            )
        }
    }

    E5() {
        return {
            name: "Let the Moles' Deeds Be Known",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: "Always Ready to Punch and Kick",
            description: (
                <div className="buff-description">
                    <div>Hook deals 20% more DMG to enemies afflicted with Burn. </div>
                </div>
            ),
        }
    }
}

export default Hook;