export class Solitary {
    constructor() {
        this.stats = [
            [1,  24,    18,     48],
            [20, 92.4,  69.3,  185],
            [20, 121.2, 90.9,  242],
            [40, 231.6, 173.7, 463],
            [40, 270,   202.5, 540],
            [50, 306,   229.5, 612],
            [50, 344.4, 258.3, 689],
            [60, 380.4, 285.3, 761],
            [60, 418.8, 314.1, 838],
            [70, 454.8, 341.1, 910],
            [70, 493.2, 369.9, 986],
            [80, 529.2, 396.9, 1058],
        ];
        
        this.name = "Solitary Healing";
        this.path = "Nihility";
        this.id = "solitaryhealing";
        this.rarity = "fiveStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text"> Break Effect </div>
                <div className="buff-text">{((0.20 + imposition * 0.05) * 100).toFixed(1)}%. </div>
                <div> When the wearer uses their Ultimate, increases </div>
                <div className="buff-text"> DoT Dealt </div>
                <div> by the wearer by </div>
                <div className="buff-text">{((0.24 + imposition * 0.06) * 100).toFixed(1)}% </div>
                <div> lasting for 2 turn(s). When a target enemy suffering from DoT imposed by the wearer is defeated, regenerates </div>
                <div className="buff-text">{4 + imposition * 1} Energy</div>
                <div> for the wearer. </div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{
                BE: 0.20 + imposition * 0.05,
            },
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "adve1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> When the wearer uses their Ultimate, increases </div>
                        <div className="buff-text"> DoT Dealt </div>
                        <div> by the wearer by </div>
                        <div className="buff-text">{((0.24 + imposition * 0.06) * 100).toFixed(1)}% </div>
                    </div>
                ),
                effect: {
                    dotBoost: 0.24 + imposition * 0.06,
                },
                type: "buff",
                hasStack: false,
                hasInput: false,
            },
        }
    }
}

export default Solitary;