export class Incessant {
    constructor() {
        this.stats = [
            [1,  26.4,      21,   48],
            [20, 101.64, 80.85,  185],
            [20, 133.32, 106.05, 242],
            [40, 254.76, 202.65, 463],
            [40, 297,    236.25, 540],
            [50, 336.6,  267.75, 612],
            [50, 378.84, 301.35, 689],
            [60, 418.44, 332.85, 761],
            [60, 460.68, 366.45, 838],
            [70, 500.28, 397.95, 910],
            [70, 542.52, 431.55, 986],
            [80, 582.12, 463.05, 1058],
        ];
        
        this.name = "Incessant Rain";
        this.path = "Nihility";
        this.id = "incessant";
        this.rarity = "fiveStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text"> Effect Hit Rate </div>
                <div> by </div>
                <div className="buff-text">{((0.24 + imposition * 0.04) * 100).toFixed(1)}%. </div>
                <div> When the wearer deals DMG to an enemy that currently has 3 or more debuffs, increases the wearer's </div>
                <div className="buff-text"> CRIT Rate </div>
                <div> by </div>
                <div className="buff-text">{((0.12 + imposition * 0.02) * 100).toFixed(1)}%. </div>
                <div>  After the wearer uses their </div>
                <div className="buff-text"> Basic ATK, Skill, or Ultimate </div>
                <div>  there is a 100% base chance to implant Aether Code on a random hit target that does not yet have it. Targets with Aether Code receive </div>
                <div className="buff-text">{((0.12 + imposition * 0.02) * 100).toFixed(1)}%. </div>
                <div> increased DMG for 1 turn. </div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{
                effectHitRate: 0.24 + imposition * 0.04,
            },
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "ins0",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> When the wearer deals DMG to an enemy that currently has 3 or more debuffs, increases the wearer's </div>
                        <div className="buff-text"> CRIT Rate </div>
                        <div> by </div>
                        <div className="buff-text">{((0.12 + imposition * 0.02) * 100).toFixed(1)}%. </div>
                    </div>
                ),
                effect: {
                    critRate: 0.12 + imposition * 0.02,
                },
                type: "buff",
                hasStack: false,
                hasInput: false,
            },
            1: {
                id: "ins1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div>  After the wearer uses their </div>
                        <div className="buff-text"> Basic ATK, Skill, or Ultimate </div>
                        <div>  there is a 100% base chance to implant Aether Code on a random hit target that does not yet have it. Targets with Aether Code receive </div>
                        <div className="buff-text">{((0.12 + imposition * 0.02) * 100).toFixed(1)}%. </div>
                        <div> increased DMG for 1 turn. </div>
                    </div>
                ),
                effect: {
                    iceVulnerability: 0.12 + imposition * 0.02,
                    fireVulnerability: 0.12 + imposition * 0.02,
                    lightningVulnerability: 0.12 + imposition * 0.02,
                    physicalVulnerability: 0.12 + imposition * 0.02,
                    quantumVulnerability: 0.12 + imposition * 0.02,
                    imaginaryVulnerability: 0.12 + imposition * 0.02,
                    windVulnerability: 0.12 + imposition * 0.02,
                },
                type: "debuff",
                hasStack: false,
                hasInput: false,
            },
        }
    }
}

export default Incessant;