class Asta {
    constructor() {
        this.stats = [
            {level: 1,  atk: 69.6,   def: 63,     hp: 139, spd: 106, taunt: 100, energy: 120},
            {level: 20, atk: 135.72, def: 122.85, hp: 271, spd: 106, taunt: 100, energy: 120, fireDmg: 0.032},
            {level: 20, atk: 163.56, def: 148.05, hp: 327, spd: 106, taunt: 100, energy: 120, fireDmg: 0.032},
            {level: 40, atk: 261,    def: 236.25, hp: 522, spd: 106, taunt: 100, energy: 120, fireDmg: 0.032, defPercent: 0.05},
            {level: 40, atk: 288.84, def: 261.45, hp: 578, spd: 106, taunt: 100, energy: 120, fireDmg: 0.064, critRate: 0.027, defPercent: 0.05},
            {level: 50, atk: 323.64, def: 292.95, hp: 647, spd: 106, taunt: 100, energy: 120, fireDmg: 0.064, critRate: 0.027, defPercent: 0.05},
            {level: 50, atk: 351.48, def: 318.15, hp: 703, spd: 106, taunt: 100, energy: 120, fireDmg: 0.112, critRate: 0.027, defPercent: 0.05},
            {level: 60, atk: 386.28, def: 349.65, hp: 773, spd: 106, taunt: 100, energy: 120, fireDmg: 0.112, critRate: 0.027, defPercent: 0.05},
            {level: 60, atk: 414.12, def: 374.85, hp: 828, spd: 106, taunt: 100, energy: 120, fireDmg: 0.16,  critRate: 0.027, defPercent: 0.125},
            {level: 70, atk: 448.92, def: 406.35, hp: 898, spd: 106, taunt: 100, energy: 120, fireDmg: 0.16,  critRate: 0.027, defPercent: 0.125},
            {level: 70, atk: 476.76, def: 431.55, hp: 954, spd: 106, taunt: 100, energy: 120, fireDmg: 0.16,  critRate: 0.027, defPercent: 0.125},
            {level: 80, atk: 511.56, def: 463.05, hp: 1023,spd: 106, taunt: 100, energy: 120, fireDmg: 0.224, critRate: 0.067, defPercent: 0.225},
        ];

        this.name = "Asta";
        this.path = "Harmony";
        this.rarity = 'fourStar';
        this.element = "Fire";
        this.id = "asta";
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Spectrum Beam",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return 0.25 + (level * 0.025);
        }else if(level < 10){
            return  0.375 + ((level - 5) * 0.0313);
        }else {
            return 0.5 + ((level - 9) * 0.025);
        }
    }

    skillDescription(level) {
        return {
            name: "Meteor Storm",
            description: (
                <div className="buff-description">
                    <div> Deals Fire DMG equal to </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%,</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                    <div> and further deals DMG for 4 extra times, with each time dealing Fire DMG equal to </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a random enemy. </div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level < 6) {
            return 36 + (level * 1.5);
        }else if(level < 10){
            return 43 + ((level - 5) * 1.5);
        }else {
            return 50 + ((level - 9) * 1.5);
        }
    }

    ultimateDescription(level) {
        return {
            name: "Astral Blessing",
            description: (
                <div className="buff-description">
                    <div>Increases </div>
                    <div className="buff-text"> SPD </div>
                    <div> of the all allies by </div>
                    <div className="buff-text"> {this.ultimateAttack(level)} </div>
                    <div> for 2 turn(s). </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 0.07 + (level * 0.007);
        }else if(level < 10){
            return 0.105 + ((level - 5) * 0.0088);
        }else {
            return 0.14 + ((level - 9) * 0.007);
        }
    }

    talentDescription(level) { 
        return {
            name: "Astrometry",
            description: (
                <div className="buff-description">
                    <div>Gains 1 stack of Charging for every different enemy hit by Asta plus an extra stack if the enemy hit has Fire Weakness. For every stack of Charging Asta has, all allies' </div>
                    <div className="buff-text"> ATK </div>
                    <div> increases by </div>
                    <div className="buff-text"> {(this.talent(level) * 100).toFixed(1)}% </div>
                    <div> up to 5 time(s). Starting from her second turn, Asta's Charging stack count is reduced by 3 at the beginning of every turn. </div>
                </div>
            ),
        }
    }

    talentSimple(level){
        return {
            name: "Astrometry",
            description: (
                <div className="buff-description">
                    <div>All allies' </div>
                    <div className="buff-text"> ATK </div>
                    <div> increases by </div>
                    <div className="buff-text"> {(this.talent(level) * 100).toFixed(1)}% </div>
                    <div> up to 5 time(s). </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Miracle Flash",
            description: (
                <div className="buff-description">
                    <div> Immediately attacks the enemy. After entering battle, deals Fire DMG equal to 50% of Asta's ATK to all enemies. </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Sparks",
            description: (
                <div className="buff-description">
                    <div> Asta's Basic ATK has a 80% base chance to Burn an enemy target for 3 turn(s). Burned enemies take Fire DoT equal to 50% of DMG dealt by Asta's Basic ATK at the start of each turn. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Ignite",
            description: (
                <div className="buff-description">
                    <div> When Asta is on the field, all allies'  </div>
                    <div className="Fire"> Fire </div>
                    <div> DMG increases by </div>
                    <div className="buff-text"> 18% </div>
                    <div>.</div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Constellation",
            description: (
                <div className="buff-description">
                    <div> Asta's DEF increases by 6% for every current Charging stack she possesses. </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Star Sings Sans Verses or Vocals",
            description: (
                <div className="buff-description">
                    <div> When using Skill, deals DMG for 1 extra time to a random enemy. </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Moon Speaks in Wax and Wane",
            description: (
                <div className="buff-description">
                    <div> After using her Ultimate, Asta's Charging stacks will not be reduced in the next turn. </div>
                </div>
            )
        }
    }
    
    E3() {
        return {
            name: "Meteor Showers for Wish and Want",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }
    
    E4() {
        return {
            name: "Aurora Basks in Beauty and Bliss",
            description:(
                <div className="buff-description">
                    <div>Asta's Energy Regeneration Rate increases by 15% when she has 2 or more Charging stacks.</div>
                </div>
            )
        }
    }
    
    E5() {
        return {
            name: "Nebula Secludes in Runes and Riddles",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: " Galaxy Dreams in Calm and Comfort",
            description: (
                <div className="buff-description">
                    <div> Charging stack(s) lost in each turn is reduced by 1. </div>
                </div>
            ),
        }
    }
}

export default Asta;