import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';

import { Himeko } from "../../../../../../../database/characters";

const HimekoDebuffs = () => {

    const { characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    
    const himeko = new Himeko();

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    const techBuff = {
        effect: {
            fireVulnerability: 0.1,
        },
    }

    return (
        <>  
            <FormControlLabel
                className='header'
                control={<Checkbox checked={characterBuffStates[4] || false} onChange={() => applyCharacterBuffs(4, techBuff)} />}
                label={himeko.techniqueDescription().name}
            />
            <div>{himeko.techSimple().description}</div>
        </>
    )
}

export const HimekoGlobalDebuffs = (props) => {

    const {updateStatesFromJson } = useContext(globalData);

    const himeko = new Himeko();

    const himekoBuffStates = props.buffStates;
    const setHimekoBuffStates = props.setBuffStates;
    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...himekoBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setHimekoBuffStates(updatedStates);
    }

    useEffect(() => {

        if(himekoBuffStates[0]) {
            updateStatesFromJson(techBuff, true);
            himekoBuffStates[0] = false;
        }

        setDone(!done);
    }, [characterSwitched]);

    const techBuff = {
        effect: {
            fireVulnerability: 0.1,
        },
    }

    return (
        <>
            <FormControlLabel
                className='header'
                control={<Checkbox checked={himekoBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, techBuff)} />}
                label={himeko.techniqueDescription().name}
            />
            <div>{himeko.techSimple().description}</div>
        </>
    )
}

export default HimekoDebuffs;