import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';

import {  Serval } from "../../../../../../../database/characters";

const ServalBuffs = () => {

    const { character, eidolon, level, characterBuffStates, talentLevel, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    
    const serval = new Serval();

    const [isShocked, setIsShocked] = useState(false);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    useEffect(() => {

        if(eidolon < 6 && characterBuffStates[2]) {
            updateStatesFromJson(E6Buff, true);
            characterBuffStates[2] = false;
        }

    }, [eidolon]);

    useEffect(() => {

        if(level < 10 && characterBuffStates[1]) {
            updateStatesFromJson(A6Buff, true);
            characterBuffStates[1] = false;
        }

    }, [level]);

    const A6Buff = {
        effect: {
            atkPercent: 0.2,
        }
    }

    const E6Buff = {
        effect: {
            quantumDmg: 0.3,
            fireDmg: 0.3,
            lightningDmg: 0.3,
            windDmg: 0.3,
            physicalDmg: 0.3,
            imaginaryDmg: 0.3,
            iceDmg: 0.3,
        }
    }

    const turnShockedOn = () => {
        setIsShocked(!isShocked);
        character.setIsShocked(!isShocked);
        const updatedStates = [...characterBuffStates];
        setCharacterBuffStates(updatedStates);
    }

    return (
        <>  

            <>
                <FormControlLabel
                className='header'
                control={<Checkbox checked={isShocked || false} onChange={turnShockedOn} />}
                label={serval.talentDescription(talentLevel).name}
                />
                <div>{serval.talentDescription(talentLevel).description}</div>
            </>

            {level >= 10 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, A6Buff)} />}
                    label={"(A6) " + serval.A6().name}
                    />
                    <div>{serval.A6().description}</div>
                </>
            }

            {eidolon >= 6 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, E6Buff)} />}
                    label={"(E6) " + serval.E6().name}
                    />
                    <div>{serval.E6().description}</div>
                </>
            }

        </>
    )
}

export const ServalGlobalBuffs = (props) => {

    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {
        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <div>No Buffs</div>
        </>
    )
}

export default ServalBuffs;