import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';
import { Topaz } from "../../../../../../../database/characters";

const TopazBuffs = () => {

    const {level, eidolon, character, ultimateLevel, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    const topaz = new Topaz();

    const [characterUltimateOn, setCharacterUltimateOn] = useState(false);
    const [E1Stacks, setE1Stacks] = useState(1);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    useEffect(() => {
        
        if(level < 6 && characterBuffStates[0]) {
            updateStatesFromJson(A4Buff, true);
            characterBuffStates[0] = false;
        }

    }, [level]);

    useEffect(() => {

        if(eidolon < 1 && characterBuffStates[1]) {
            updateStatesFromJson(E1Buff, true);
            characterBuffStates[1] = false;
            setE1Stacks(1);
        }

    }, [eidolon]);

    const A4Buff = {
        effect: {
            quantumDmg: 0.15,
            fireDmg: 0.15,
            iceDmg: 0.15,
            lightningDmg: 0.15,
            physicalDmg: 0.15,
            imaginaryDmg: 0.15,
            windDmg: 0.15,
        }
    }

    const E1Buff = {
        effect: {
            followUpCritDmg: 0.25 * E1Stacks,
        }
    }

    const turnUltimateOn = (e) => {
        character.setUlitmateOn(e.target.checked);
        setCharacterUltimateOn(e.target.checked);
        const updatedStates = [...characterBuffStates];
        setCharacterBuffStates(updatedStates);
    }

    const updateE1Stacks = (e) => {

        if(characterBuffStates[1]) {
            updateStatesFromJson(E1Buff, true);
        }

        if(characterBuffStates[1]) {
            const tempTalentBuff = {
                effect: {
                    followUpCritDmg: 0.25 * e.target.value,
                },
            }
            updateStatesFromJson(tempTalentBuff, false);
        }
        setE1Stacks(e.target.value);
    }

    return (
        <>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={characterUltimateOn || false} onChange={turnUltimateOn} />}
            label={topaz.ultimateDescription(ultimateLevel).name}
            />
            <div>{topaz.ultimateSimple(ultimateLevel).description}</div>
        
            {level >= 6 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, A4Buff)} />}
                    label={"(A4) " + topaz.A4().name}
                    />
                    <div>{topaz.A4().description}</div>
                </>
            }

            {eidolon >= 1 &&
                <>
                    <FormControlLabel
                        className='header'
                        control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, E1Buff)} />}
                        label={"(E1) " + topaz.E1().name}
                    />
                    <div>{topaz.E1().description}</div>
                    <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                        <div style={{display: "flex"}}>
                            <div>Stacks </div>
                            <select className="levels buff-input" style={{width: "45%"}} value={E1Stacks} onChange={updateE1Stacks}>
                                <option value={1}>1</option>
                                <option value={2}>2</option>
                            </select>
                        </div>
                    </div>
                </>
            }

        </>
    )
}

export const TopazGlobalBuffs = (props) => {

    const topazBuffStates = props.buffStates;
    const setTopazBuffStates = props.setBuffStates;
    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {

        if(topazBuffStates[1]) {
            updateStatesFromJson(E1Buff, true);
            topazBuffStates[1] = false;
        }

        setDone(!done);
    }, [characterSwitched]);

    const { updateStatesFromJson } = useContext(globalData);
    const topaz = new Topaz();

    const [E1Stacks, setE1Stacks] = useState(1);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...topazBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setTopazBuffStates(updatedStates);
    }

    const E1Buff = {
        effect: {
            followUpCritDmg: 0.25 * E1Stacks,
        }
    }

    const updateE1Stacks = (e) => {

        if(topazBuffStates[1]) {
            updateStatesFromJson(E1Buff, true);
        }

        if(topazBuffStates[1]) {
            const tempTalentBuff = {
                effect: {
                    followUpCritDmg: 0.25 * e.target.value,
                },
            }
            updateStatesFromJson(tempTalentBuff, false);
        }
        setE1Stacks(e.target.value);
    }

    return (
        <>

            <FormControlLabel
                className='header'
                control={<Checkbox checked={topazBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, E1Buff)} />}
                label={"(E1) " + topaz.E1().name}
            />
            <div>{topaz.E1().description}</div>
            <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                <div style={{display: "flex"}}>
                    <div>Stacks </div>
                    <select className="levels buff-input" style={{width: "45%"}} value={E1Stacks} onChange={updateE1Stacks}>
                        <option value={1}>1</option>
                        <option value={2}>2</option>
                    </select>
                </div>
            </div>

        </>
    )
}

export default TopazBuffs;