export class TimeWaits {
    constructor() {
        this.stats = [
            [1, 21.6, 21, 58],
            [20, 83.16, 80.85, 222],
            [20, 109.08, 106.05, 291],
            [40, 208.44, 202.65,556],
            [40, 243, 236.25,  648],
            [50, 275.4, 267.75,734],
            [50, 309.96, 301.35,827],
            [60, 342.36, 332.85,913],
            [60, 376.92, 366.45,1005],
            [70, 409.32, 397.95,1092],
            [70, 443.88, 431.55,1184],
            [80, 476.28, 463.05,1270],
        ];
        
        this.name = "Time Waits for No One";
        this.path = "Abundance";
        this.id = "timewaits";
        this.rarity = "fiveStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>	Increases the wearer's </div>
                <div className="buff-text"> Max HP </div> 
                <div> by </div> 
                <div className="buff-text">{((0.18 + imposition * 0.03) * 100).toFixed(1)}%.</div>
                <div> and </div>
                <div className="buff-text"> Outgoing Healing </div> 
                <div> by </div> 
                <div className="buff-text">{((0.12 + imposition * 0.02) * 100).toFixed(1)}%.</div>
                <div> When the wearer heals allies, record the amount of Outgoing Healing. When any ally launches an attack, a random attacked enemy takes Additional DMG equal to </div>
                <div className="buff-text">{((0.36 + imposition * 0.06) * 100).toFixed(1)}%.</div>
                <div> of the recorded Outgoing Healing value. The type of this Additional DMG is of the same Type as the wearer's. This Additional DMG is not affected by other buffs, and can only occur 1 time per turn. </div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "silenceb",
            name: this.name,
            effect:{
                hpPercent: 0.18 + imposition * 0.03,
                OGH: 0.12 + imposition * 0.02,
            },
            type: "buff",
            hasStack: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                // id: "silence0",
                // name: this.name,
                // description: (
                //     <div className="buff-description">
                //         <div> After the wearer uses their Ultimate, all allies gain </div>
                //         <div className="buff-text"> {(12 + imposition * 2)}.</div> 
                //         <div> for 1 turn. </div>
                //     </div>
                // ),
                // effect: {
                //     atkPercent: 12 + imposition * 2,
                // },
                // type: "buff",
                // hasStack: false,
                // hasInput: false,
            },
        }
    }
}

export default TimeWaits;