export class Patience {
    constructor() {
        this.stats = [
            [1,  26.4,    21,     48],
            [20, 101.64,  80.85,  185],
            [20, 133.32, 106.05,  242],
            [40, 254.76, 202.65, 463],
            [40, 297,   236.25, 540],
            [50, 336.6,   267.75, 612],
            [50, 378.84, 301.35, 689],
            [60, 418.44, 332.85, 761],
            [60, 460.68, 366.45, 838],
            [70, 500.28, 397.95, 910],
            [70, 542.52, 431.55, 986],
            [80, 582.12, 463.05, 1058],
        ];
        
        this.name = "Patience Is All You Need";
        this.path = "Nihility";
        this.id = "patience";
        this.rarity = "fiveStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases </div>
                <div className="buff-text"> DMG </div>
                <div> dealt by the wearer by </div>
                <div className="buff-text">{((0.24 + imposition * 0.04) * 100).toFixed(1)}%. </div>
                <div> After every attack launched by wearer, their </div>
                <div className="buff-text"> SPD </div>
                <div className="buff-text">{((0.048 + imposition * 0.008) * 100).toFixed(1)}% </div>
                <div> , stacking up to 3 times. </div>
                <div> If the wearer hits an enemy target that is not afflicted by Erode, there is a </div>
                <div className="buff-text"> 100% base chance </div>
                <div> to inflict Erode to the target. Enemies afflicted with Erode are also considered to be Shocked and will receive Lightning DoT at the start of each turn equal to </div>
                <div className="buff-text">{((0.6 + imposition * 0.1) * 100).toFixed(1)}% </div>
                <div> of the wearer's ATK, lasting for 1 turn(s). </div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{
                iceDmg: 0.24 + imposition * 0.04,
                quantumDmg: 0.24 + imposition * 0.04,
                fireDmg: 0.24 + imposition * 0.04,
                lightningDmg: 0.24 + imposition * 0.04,
                windDmg: 0.24 + imposition * 0.04,
                physicalDmg: 0.24 + imposition * 0.04,
                imaginaryDmg: 0.24 + imposition * 0.04,
            },
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition, stack) {
        return {
            0: {
                id: "adve1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> After every attack launched by wearer, their </div>
                        <div className="buff-text"> SPD </div>
                        <div className="buff-text">{((0.048 + imposition * 0.008) * 100).toFixed(1)}% </div>
                        <div> , stacking up to 3 times. </div>
                    </div>
                ),
                effect: {
                    spdPercent: (0.048 + imposition * 0.008) * stack,
                },
                type: "buff",
                hasStack: true,
                stacks: 3,
                hasInput: false,
            },
        }
    }
}

export default Patience;