import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';

import { JingYuan } from "../../../../../../../database/characters";

const JingYuanBuffs = () => {

    const { character, eidolon, level, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    
    const jing = new JingYuan();
    const [stacks, setStacks] = useState(1);
    const [A2ON, setA2ON] = useState(false);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    useEffect(() => {

        if(eidolon < 2 && characterBuffStates[2]) {
            updateStatesFromJson(E2Buff, true);
            characterBuffStates[2] = false;
        }

        if(eidolon < 6 && characterBuffStates[3]) {
            updateStatesFromJson(E6Buff, true);
            characterBuffStates[3] = false;
        }

    }, [eidolon]);

    useEffect(() => {

        if(level < 3 && A2ON) {
            character.setA2ON(false);
            setA2ON(false);
            const updatedStates = [...characterBuffStates];
            setCharacterBuffStates(updatedStates);
        }

        if(level < 10 && characterBuffStates[1]) {
            updateStatesFromJson(A6Buff, true);
            characterBuffStates[1] = false;
        }

    }, [level]);


    const A6Buff = {
        effect: {
            critRate: 0.1,
        }
    }

    const E2Buff = {
        effect: {
            skillBoost: 0.2,
            basicBoost: 0.2,
            ultimateBoost: 0.2,
        },
    }

    const E6Buff = {
        effect: {
            lightningVulnerability: 0.12 * stacks,
            fireVulnerability: 0.12 * stacks,
            iceVulnerability: 0.12 * stacks,
            windVulnerability: 0.12 * stacks,
            qunatumVulnerability: 0.12 * stacks,
            physicalVulnerability: 0.12 * stacks,
            imaginaryVulnerability: 0.12 * stacks,
        },
    }

    const setA2On = () => {
        character.setA2ON(!A2ON);
        setA2ON(!A2ON);
        const updatedStates = [...characterBuffStates];
        setCharacterBuffStates(updatedStates);
    }

    const updateStacks = (e) => {
        if(characterBuffStates[3]) {
            updateStatesFromJson(E6Buff, true);
            const tempE6Buff = {
                effect: {
                    lightningVulnerability: 0.12 * e.target.value,
                    fireVulnerability: 0.12 * e.target.value,
                    iceVulnerability: 0.12 * e.target.value,
                    windVulnerability: 0.12 * e.target.value,
                    qunatumVulnerability: 0.12 * e.target.value,
                    physicalVulnerability: 0.12 * e.target.value,
                    imaginaryVulnerability: 0.12 * e.target.value,
                },
            }
            updateStatesFromJson(tempE6Buff, false);
        }

        setStacks(e.target.value);
    }

    return (
        <>  

            {level >= 3 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={A2ON || false} onChange={setA2On} />}
                    label={"(A2) " + jing.A2().name}
                    />
                    <div>{jing.A2().description}</div>
                </>
            }

            {level >= 10 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[1] || false} onChange={() => applyCharacterBuffs(1, A6Buff)} />}
                    label={"(A6) " + jing.A6().name}
                    />
                    <div>{jing.A6().description}</div>
                </>
            }


            {eidolon >= 2 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, E2Buff)} />}
                    label={"(E2) " + jing.E2().name}
                    />
                    <div>{jing.E2().description}</div>
                </>
            }

            {eidolon >= 6 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, E6Buff)} />}
                    label={"(E6) " + jing.E6().name}
                    />
                    <div>{jing.E6().description}</div>
                    <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                    <div style={{display: "flex"}}>
                        <div>Stacks </div>
                            <select className="levels buff-input" style={{width: "45%"}} value={stacks} onChange={updateStacks}>
                                <option value={1}>1</option>
                                <option value={2}>2</option>
                                <option value={3}>3</option>
                            </select>
                        </div>
                    </div>
                </>
            }

        </>
    )
}

export const JingYuanGlobalBuffs = (props) => {

    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {
        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <div>No Buffs</div>
        </>
    )
}

export default JingYuanBuffs;