export class Geniuses {
    constructor() {
        this.stats = [
            [1, 21.6,    18,     38],
            [20, 83.16,  69.3,  148],
            [20, 109.08, 90.9,  194],
            [40, 208.44, 173.7, 371],
            [40, 243,    202.5, 432],
            [50, 275.4,  229.5, 490],
            [50, 309.96, 258.3, 551],
            [60, 342.36, 285.3, 609],
            [60, 376.92, 314.1, 670],
            [70, 409.32, 341.1, 728],
            [70, 443.88, 369.9, 789],
            [80, 476.28, 396.9, 847],
        ];
        
        this.name = "Geniuses' Repose";
        this.path = "Erudition";
        this.id = "geniuses";
        this.rarity = "fourStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text"> ATK  </div> 
                <div> by </div> 
                <div className="buff-text"> {((0.16 + imposition * 0.04) * 100).toFixed(1)}% </div> 
                <div> When the wearer defeats an enemy, the wearer's </div> 
                <div className="buff-text">CRIT DMG</div>
                <div> increases by </div> 
                <div className="buff-text"> {((0.24 + imposition * 0.06) * 100).toFixed(1)}% </div> 
                <div> for 3 turn(s). </div> 
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{
                atkPercent: 0.16 + imposition * 0.04,
            },
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "adve1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> When the wearer defeats an enemy, the wearer's </div> 
                        <div className="buff-text">CRIT DMG</div>
                        <div> increases by </div> 
                        <div className="buff-text"> {((0.24 + imposition * 0.06) * 100).toFixed(1)}% </div> 
                        <div> for 3 turn(s). </div>  
                    </div>
                ),
                effect: {
                    critDmg: 0.24 + imposition * 0.06,
                },
                type: "buff",
                hasStack: false,
                hasInput: false,
            },
        }
    }
}

export default Geniuses;