class Clara {
    constructor() {
        this.stats = [
            {level: 1,  atk: 100.32, def: 66,    hp: 169, spd: 90, taunt: 125, energy: 110},
            {level: 20, atk: 195.62, def: 128.7, hp: 329, spd: 90, taunt: 125, energy: 110, atkPercent: 0.04},
            {level: 20, atk: 235.75, def: 155.1, hp: 397, spd: 90, taunt: 125, energy: 110, atkPercent: 0.04},
            {level: 40, atk: 376.2,  def: 247.5, hp: 634, spd: 90, physicalDmg: 0.032, taunt: 125, energy: 110, atkPercent: 0.04},
            {level: 40, atk: 416.33, def: 273.9, hp: 701, spd: 90, physicalDmg: 0.032, taunt: 125, energy: 110, atkPercent: 0.08, hpPercent: 0.04},
            {level: 50, atk: 466.49, def: 306.9, hp: 786, spd: 90, physicalDmg: 0.032, taunt: 125, energy: 110, atkPercent: 0.08, hpPercent: 0.04},
            {level: 50, atk: 506.62, def: 333.3, hp: 853, spd: 90, physicalDmg: 0.032, taunt: 125, energy: 110, atkPercent: 0.14, hpPercent: 0.04},
            {level: 60, atk: 556.78, def: 366.3, hp: 938, spd: 90, physicalDmg: 0.032, taunt: 125, energy: 110, atkPercent: 0.14, hpPercent: 0.04},
            {level: 60, atk: 596.9,  def: 392.7, hp: 1005,spd: 90, physicalDmg: 0.08, taunt: 125, energy: 110, atkPercent: 0.2,  hpPercent: 0.04},
            {level: 70, atk: 647.06, def: 425.7, hp: 1090,spd: 90, physicalDmg: 0.08, taunt: 125, energy: 110, atkPercent: 0.2,  hpPercent: 0.04},
            {level: 70, atk: 687.19, def: 452.1, hp: 1157,spd: 90, physicalDmg: 0.08, taunt: 125, energy: 110, atkPercent: 0.2,  hpPercent: 0.1},
            {level: 80, atk: 737.35, def: 485.1, hp: 1242,spd: 90, physicalDmg: 0.144,  taunt: 125, energy: 110, atkPercent: 0.28, hpPercent: 0.1},
        ];

        this.name = "Clara";
        this.path = "Destruction";
        this.rarity = 'fiveStar';
        this.element = "Physical";
        this.id = "clara";
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "I Want to Help",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return 0.6 + (level * 0.06);
        }else if(level < 10){
            return 0.9 + ((level - 5) * 0.075);
        }else {
            return 1.2 + ((level - 9) * 0.06);
        }
    }

    skillDescription(level) {
        return {
            name: "Svarog Watches Over You",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a all enemies, and additionally deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to enemies marked by Svarog with a Mark of Counter. All Marks of Counter will be removed after this Skill is used. </div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level < 6) {
            return {
                dmgReduction: 0.15 + (level * 0.01),
                multiplier: 0.96 + (level * 0.064),
            }
        }else if(level < 10){
            return {
                dmgReduction: 0.2 + ((level - 5) * 0.0125),
                multiplier: 1.28 + ((level - 5) * 0.08),
            }
        }else {
            return {
                dmgReduction: 0.25 + ((level - 9) * 0.01),
                multiplier: 1.6 + ((level - 9) * 0.08),
            }
        }
    }

    ultimateDescription(level) {
        return {
            name: "Promise, Not Command",
            description: (
                <div className="buff-description">
                    <div>After Clara uses Ultimate, DMG dealt to her is reduced by an extra </div>
                    <div className="buff-text">{(this.ultimateAttack(level).dmgReduction * 100).toFixed(1)}%</div>
                    <div> and she has greatly increased chances of being attacked by enemies for 2 turn(s).</div>
                    <div> In addition, Svarog's Counter is enhanced. When an ally is attacked, Svarog immediately launches a Counter, and its DMG multiplier against the enemy increases by </div>
                    <div className="buff-text">{(this.ultimateAttack(level).multiplier * 100).toFixed(1)}%, </div>
                    <div> Enemies adjacent to it take 50% of the DMG dealt to the target enemy. Enhanced Counter(s) can take effect 2 time(s). </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 0.8 + (level * 0.08);
        }else if(level < 10){
            return 1.2 + ((level - 5) * 0.1);
        }else {
            return 1.6 + ((level - 9) * 0.08);
        }
    }

    talentDescription(level) { 
        return {
            name: "Because We're Family",
            description: (
                <div className="buff-description">
                    <div> Under the protection of Svarog, DMG taken by Clara when hit by enemy attacks is reduced by 10%. Svarog will mark enemies who attack Clara with his Mark of Counter and retaliate with a Counter, dealing </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.talent(level) * 100).toFixed(1)}%</div>
                    <div> of Clara's </div>
                    <div className="buff-text">ATK</div>
                    <div>.</div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: " A Small Price for Victory",
            description: (
                <div className="buff-description">
                    <div> Immediately attacks the enemy. Upon entering battle, the chance Clara will be attacked by enemies increases for 2 turn(s). </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Kinship",
            description: (
                <div className="buff-description">
                    <div> When attacked, this character has a 35% fixed chance to remove a debuff placed on them. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Under Protection",
            description: (
                <div className="buff-description">
                    <div> The chance to resist Crowd Control Debuffs increases by 35%. </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Revenge",
            description: (
                <div className="buff-description">
                    <div> Increases Svarog's Counter DMG by 30%. </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "A Tall Figure",
            description: (
                <div className="buff-description">
                    <div> Using Skill will not remove Marks of Counter on the enemy. </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "A Tight Embrace",
            description: (
                <div className="buff-description">
                    <div> After using the Ultimate, ATK increases by 30% for 2 turn(s). </div>
                </div>
            )
        }
    }

    E3() {
        return {
            name: "Cold Steel Armor",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }
    
    E4() {
        return {
            name: "Family's Warmth",
            description:(
                <div className="buff-description">
                    <div>After Clara is hit, the DMG taken by Clara is reduced by 30%. This effect lasts until the start of her next turn.</div>
                </div>
            )
        }
    }
    
    E5() {
        return {
            name: "A Small Promise",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: "Long Company",
            description: (
                <div className="buff-description">
                    <div>After other allies are hit, Svarog also has a 50% fixed chance to trigger a Counter on the attacker and mark them with a Mark of Counter. When using Ultimate, the number of Enhanced Counters increases by 1. </div>
                </div>
            ),
        }
    }
}

export default Clara;