export class PostOp {
    constructor() {
        this.stats = [
            [1,  19.2,   15,    48 ],
            [20, 73.92,  57.75,  185],
            [20, 96.96,  75.75,  242],
            [40, 185.28, 144.75, 463],
            [40, 216,    168.75, 540],
            [50, 244.8,  191.25, 612],
            [50, 275.52, 215.25, 689],
            [60, 304.32, 237.75, 761],
            [60, 335.04, 261.75, 838],
            [70, 363.84, 284.25, 910],
            [70, 394.56, 308.25, 986],
            [80, 423.36, 330.75, 1058],
        ];
        
        this.name = "Post-Op Conversation";
        this.path = "Abundance";
        this.id = "postop";
        this.rarity = "fourStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Icreases the wearer's </div>
                <div className="buff-text"> Energy Regeneration Rate </div> 
                <div> by </div> 
                <div className="buff-text">{((0.08 + imposition * 0.02) * 100).toFixed(1)}%.</div>
                <div> increases Outgoing Healing when they use their Ultimate by </div> 
                <div className="buff-text">{((0.12 + imposition * 0.03) * 100).toFixed(1)}%.</div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{
                ERR: 0.08 + imposition * 0.02,
            },
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "adve1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> increases Outgoing Healing when they use their Ultimate by </div> 
                        <div className="buff-text">{((0.12 + imposition * 0.03) * 100).toFixed(1)}%.</div>
                    </div>
                ),
                effect: {
                    ultimateOGH: 0.12 + imposition * 0.03,
                },
                type: "buff",
                hasStack: false,
                hasInput: false,
            },
        }
    }
}

export default PostOp;