import { globalData } from '../../../../../globalData';
import React, { useContext, useState, useEffect } from 'react';
import { Incessant } from '../../../../../database/lightCones';
import { Checkbox,  FormControlLabel } from '@mui/material';

export function GlobalInessantBuffs(props) {

    let incessant = new Incessant();
    const [imposition, setImposition] = useState(0);
    const buffStates = props.buffStates;
    const setBuffStates = props.setBuffStates;

    const {
        updateStatesFromJson,
        isChecked,
        lightCone,
    } = useContext(globalData);

    useEffect(() => {
        if(lightCone) {
            if(lightCone.id === incessant.id) {
                if(buffStates[0]) {
                    updateStatesFromJson(buff, true);
                }
                
                setImposition(0);
                // fill buff states with false
                const updatedStates = [...buffStates];
                for(let i = 0; i < updatedStates.length; i++) {
                    updatedStates[i] = false;
                }
                setBuffStates(updatedStates);
            }
        }else {
            setImposition(0);
            // fill buff states with false
            const updatedStates = [...buffStates];
            for(let i = 0; i < updatedStates.length; i++) {
                updatedStates[i] = false;
            }
            setBuffStates(updatedStates);
        }
    }, [isChecked])

    const buff = {
        effect: {
            fireVulnerability: 0.12 + imposition * 0.02,
            iceVulnerability: 0.12 + imposition * 0.02,
            lightningVulnerability: 0.12 + imposition * 0.02,
            physicalVulnerability: 0.12 + imposition * 0.02,
            quantumVulnerability: 0.12 + imposition * 0.02,
            imaginaryVulnerability: 0.12 + imposition * 0.02,
            windVulnerability: 0.12 + imposition * 0.02,
        }
    }

    const [activeBuff, setActiveBuff] = useState(buff);
    
    const ApplyLCBuffFromCheckBox = (index, buffValue) => {
        const updatedStates = [...buffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setBuffStates(updatedStates);
    }

    useEffect(() => {

        if(buffStates[0]) {
            updateStatesFromJson(activeBuff, true);
            updateStatesFromJson(buff, false);
        }

        setActiveBuff(buff);
    }, [imposition])

    const handleSelectImposition = (event) => {
        setImposition(event.target.selectedIndex);
    }

    if(lightCone && incessant.id === lightCone.id) {
        return null;
    }

    return (
        <div>
            <div className='centered header' style={{textAlign: "center"}}>{incessant.name}</div>
            <FormControlLabel
                className='buff'
                control={<Checkbox checked={buffStates[0] || false} onChange={() => ApplyLCBuffFromCheckBox(0, buff)} />}
                label={incessant.weaponEffect(imposition)[1].description}
            />
            <div style={{display: "flex", width: "100%", justifyContent: "space-between"}}>
                <div  style={{display: "flex"}}>
                    <div>Imposition:</div>
                    <select name="imp" id="imp" className='levels buff-input' style={{width: "100%"}} value={imposition + 1} onChange={(e) => handleSelectImposition(e)}>
                        <option value="1">1</option>
                        <option value="2">2</option>
                        <option value="3">3</option>
                        <option value="4">4</option>
                        <option value="5">5</option>
                    </select>
                </div>
            </div>
            
        </div>
    )
}