export class Resolution {
    constructor() {
        this.stats = [
            [1, 21.6, 15, 43],
            [20, 83.16, 57.75, 166],
            [20, 109.08, 75.75, 218],
            [40, 208.44, 144.75,417],
            [40, 243, 168.75,  486],
            [50, 275.4, 191.25,551],
            [50, 309.96, 215.25,620],
            [60, 342.36, 237.75,685],
            [60, 376.92, 261.75,754],
            [70, 409.32, 284.25,819],
            [70, 443.88, 308.25,888],
            [80, 476.28, 330.75,953],
        ];
        
        this.name = "Resolution Shines As Pearls of Sweat";
        this.path = "Nihility";
        this.id = "resolution";
        this.rarity = "fourStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>When the wearer hits an enemy and if the hit enemy is not already Ensnared, then there is a  </div>
                <div className="buff-text">{((0.6 + imposition * 0.1) * 100).toFixed(1)}% </div>
                <div> base chance to Ensnare the hit enemy. Ensnared enemies' </div>
                <div className="buff-text">DEF</div>
                <div> decreases by </div>
                <div className="buff-text">{((0.12 + imposition * 0.01) * 100).toFixed(1)}% </div>
                <div> for 1 turn. </div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{},
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "adve1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div>When the wearer hits an enemy and if the hit enemy is not already Ensnared, then there is a  </div>
                        <div className="buff-text">{((0.6 + imposition * 0.1) * 100).toFixed(1)}% </div>
                        <div> base chance to Ensnare the hit enemy. Ensnared enemies' </div>
                        <div className="buff-text">DEF</div>
                        <div> decreases by </div>
                        <div className="buff-text">{((0.12 + imposition * 0.01) * 100).toFixed(1)}% </div>
                        <div> for 1 turn. </div>
                    </div>
                ),
                effect: {
                    defIgnore: 0.12 + imposition * 0.01,
                },
                type: "debuff",
                hasStack: false,
                hasInput: false,
            },
        }
    }
}

export default Resolution;