import React from 'react';
import {Dialog, DialogTitle, DialogContent} from '@mui/material';
import { useState, useEffect, useContext } from "react";
import { globalData } from "../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';

function Options(props) {

    const open = props.open;
    const setOpen = props.setOpen;

    const { showBeta, setShowBeta} = useContext(globalData);

    const updateBetaContent = () => {
        setShowBeta(!showBeta);
        // refresh the page
        window.location.reload();
    }

    useEffect(() => {
        if(localStorage.getItem("SHOW_BETA") === "true") {
            setShowBeta(true);
        } else {
            setShowBeta(false);
        }
    }, []);

    useEffect(() => {
        window.localStorage.setItem("SHOW_BETA", showBeta);
    }, [showBeta]);
    
    return (
        <div className="dialog">
            <Dialog aria-label='dialog-title' open={open} onClose={() => setOpen(false)} maxWidth="xs" fullWidth>
                <DialogTitle className='dialog-container' id='dialog-title'> 
                <div style={{display: "flex", justifyContent: "space-between", alignItems: "center"}}>
                    <div>Settings</div>
                    <button onClick={() => setOpen(false)} className='calc-btn' style={{width: "37px", display: "flex", backgroundColor: "red", textAlign:"center", alignItems: "center", justifyContent: "center", margin: "0"}}>&#10005;</button>
                </div>
                </DialogTitle>
                <DialogContent className='dialog-content'>
                    <div className='mt-4' style={{display: "flex", justifyContent: "space-between", alignItems: "center"}}>
                        <div className='date'>Show Upcoming Content</div>
                        <FormControlLabel
                        className='header'
                        control={<Checkbox checked={ showBeta || false} onChange={updateBetaContent} />}
                        label=""
                        />
                    </div>
                    <div className='required' style={{margin: 0, fontSize: "16px"}}>This will reload the page</div>
                </DialogContent>
            </Dialog>
        </div>
    )
}

export default Options;