import Buffs from "./globalBuffsParts/buffs";
import Debuffs from "./globalBuffsParts/debuffs";
import { useState } from "react";

function GlobalBuffs() {

    const [toogleState, setToggleState] = useState(1);

    const toggleTab = (index) => {
        setToggleState(index);
    };
    

    return (
        <div className="calc-item">
            <div className="buffs-tabs-container">
                <button className={toogleState === 1 ? "buffs-tabs active-buffs-tabs" : "buffs-tabs"} onClick={() => toggleTab(1)}>Buffs</button>
                <button className={toogleState === 2 ? "buffs-tabs active-buffs-tabs" : "buffs-tabs"} onClick={() => toggleTab(2)}>Debuffs</button>
            </div>

            <div className="buff-content-tabs">
                <div className={toogleState === 1 ? "buff-content  active-buff-content" : "buff-content"}>
                    <Buffs></Buffs>
                </div>
                <div className={toogleState === 2 ? "buff-content  active-buff-content" : "buff-content"}>
                    <Debuffs></Debuffs>
                </div>
            </div>
        </div>
    )
}

export default GlobalBuffs;