import { globalData } from '../../../globalData';
import React, { useContext, useState, useEffect } from 'react';
import { Checkbox,  FormControlLabel } from '@mui/material';
import { Messenger } from '../../../database/relics';

function GlobalRelicBuffs() {

    const relics = [
        new Messenger,
    ]

    const initialState = Array(10).fill("1");
    const [stackStates, setStackStates] = useState(initialState);

    const specialRelics = {}

    const {
        globalRelicBuffStates,
        setGlobalRelicBuffStates,
        updateStatesFromJson,
        piecesCount,
    } = useContext(globalData);

    useEffect(() => {
        if(piecesCount) {
            for(let i = 0; i < relics.length; i++) {
                if(piecesCount[relics[i].id] && piecesCount[relics[i].id] === 4 && globalRelicBuffStates[i]) {
                    let x = relics[i].fourEffect(stackStates[i]);
                    updateStatesFromJson(x[0], true);
                    const updatedStates = [...globalRelicBuffStates];
                    updatedStates[i] = false;
                    setGlobalRelicBuffStates(updatedStates);
                }
            }
        }
    }, [piecesCount]);

    const ApplyRelicBuffFromCheckBox = (index, buffValue) => {
        const updatedStates = [...globalRelicBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setGlobalRelicBuffStates(updatedStates);
    }

    const updateSelectedStates = (e, index, lc, buffID) => {
        // const updatedStates = [...stackStates];
        // updatedStates[index] = e.target.value;
        
        // if(globalRelicBuffStates[index] === true) {
        //     const json = lc.weaponEffect(globalLCImpositionStates[index], stackStates[index]);
        //     let foundObject = null;
        //     for (const key in json) {
        //         if (json.hasOwnProperty(key) && json[key].id === buffID) {
        //             foundObject = json[key];
        //             break;
        //         }
        //     }
        //     updateStatesFromJson(foundObject, true);

        //     const json1 = lc.weaponEffect(globalLCImpositionStates[index], updatedStates[index]);
        //     foundObject = null;
        //     for (const key in json1) {
        //         if (json1.hasOwnProperty(key) && json1[key].id === buffID) {
        //             foundObject = json1[key];
        //             break;
        //         }
        //     }
        //     updateStatesFromJson(foundObject, false);
        // }

        // setStackStates(updatedStates);
    }

    return (
        <div>
            {relics.map((relic, idx) => {
                
                if(piecesCount[relic.id] && piecesCount[relic.id] === 4) {
                    return "";
                }

                return (
                    <div key={idx}>
                        <div className='header centered' style={{textAlign: "center"}}>{relic.name}</div>
                        {
                        Object.entries(relic.fourEffect()).map(([buffName, buffValue], index) => {
                            let optionsContent = "";
                            if (buffValue.type === 'buff') {
                                if(buffValue.hasStack) {
    
                                    const length = buffValue.stacks;
                                    const options = Array.from({ length }, (_, index) => index + 1);
                                    optionsContent = (
                                        <div style={{display: "flex", alignItems: "flex-end", justifyContent: "flex-end"}}>
                                            <div>Stacks:</div>
                                            <select className='levels buff-input' style={{width: "100%"}} name={buffValue.id} id={buffValue.id} key={buffValue.id} value={stackStates[idx]} onChange={(e) => updateSelectedStates(e, idx, relic, buffValue.id)}>
                                                {options.map((item) => {
                                                    return <option key={item} value={item}>{item}</option>
                                                })}
                                            </select>
                                        </div>
                                    );

                                    const json = relic.fourEffect(stackStates[idx]);
                                    // find object with ID
                                    let foundObject = null;
                                    for (const key in json) {
                                        if (json.hasOwnProperty(key) && json[key].id === buffValue.id) {
                                            foundObject = json[key];
                                            break;
                                        }
                                    }
    
                                    buffValue = foundObject;
                                }
    
                                return (
                                    <div key={buffName}>
                                        <FormControlLabel
                                            className='buff'
                                            key={buffName}
                                            control={<Checkbox checked={globalRelicBuffStates[index + idx] || false} onChange={() => ApplyRelicBuffFromCheckBox(idx, buffValue)} />}
                                            label={buffValue.description}
                                        />
                                        {optionsContent}
                                    </div>
                                );
                            }
                        })}
                    </div>
                )
            })}
        </div>

    )
}

export default GlobalRelicBuffs;