class Qingque {
    constructor() {
        this.stats = [
            {level: 1,  atk: 88.8,   def: 60,  hp: 139, spd: 98, taunt: 75, energy: 140},
            {level: 20, atk: 173.16, def: 117, hp: 271, spd: 98, taunt: 75, energy: 140, atkPercent: 0.04},
            {level: 20, atk: 208.68, def: 141, hp: 327, spd: 98, taunt: 75, energy: 140, atkPercent: 0.04},
            {level: 40, atk: 333,    def: 225, hp: 522, spd: 98, taunt: 75, energy: 140, atkPercent: 0.04, quantumDmg: 0.032},
            {level: 40, atk: 368.52, def: 249, hp: 578, spd: 98, taunt: 75, energy: 140, atkPercent: 0.08, quantumDmg: 0.032, defPercent: 0.05},
            {level: 50, atk: 412.92, def: 279, hp: 647, spd: 98, taunt: 75, energy: 140, atkPercent: 0.08, quantumDmg: 0.032, defPercent: 0.05},
            {level: 50, atk: 448.44, def: 303, hp: 703, spd: 98, taunt: 75, energy: 140, atkPercent: 0.14, quantumDmg: 0.032, defPercent: 0.05},
            {level: 60, atk: 492.84, def: 333, hp: 773, spd: 98, taunt: 75, energy: 140, atkPercent: 0.14, quantumDmg: 0.032, defPercent: 0.05},
            {level: 60, atk: 528.36, def: 357, hp: 828, spd: 98, taunt: 75, energy: 140, atkPercent: 0.2,  quantumDmg: 0.08,  defPercent: 0.05},
            {level: 70, atk: 572.76, def: 387, hp: 898, spd: 98, taunt: 75, energy: 140, atkPercent: 0.2,  quantumDmg: 0.08,  defPercent: 0.05},
            {level: 70, atk: 608.28, def: 411, hp: 954, spd: 98, taunt: 75, energy: 140, atkPercent: 0.2,  quantumDmg: 0.08,  defPercent: 0.125},
            {level: 80, atk: 652.68, def: 441, hp: 1023,spd: 98, taunt: 75, energy: 140, atkPercent: 0.28, quantumDmg: 0.144, defPercent: 0.125},
        ];

        this.name = "Qingque";
        this.path = "Erudition";
        this.rarity = 'fourStar';
        this.element = "Quantum";
        this.id = "qingque";
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Flower Pick",
            description: (
                <div className="buff-description">
                    <div>Tosses 1 jade tile from the suit with the fewest tiles in hand to deal </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    basicAttack1(level) {
        return 1.2 + (level * 0.24);
    }

    basic1Description(level) {
        return {
            name: "Cherry on Top!",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack1(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy, and deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to enemies adjacent to it. </div>
                    <br/>
                    <div>"Cherry on Top!" cannot recover Skill Points.</div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return  0.14 + (level * 0.014);
        }else if(level < 10){
            return 0.21 + ((level - 5) * 0.0175);
        }else {
            return 0.28 + ((level - 9) * 0.014);
        }
    }

    skillDescription(level) {
        return {
            name: "A Scoop of Moon",
            description: (
                <div className="buff-description">
                    <div>Immediately draws 2 jade tile(s) and increases </div>
                    <div className={this.element}> DMG </div>
                    <div> by </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                    <div> until the end of the current turn. This effect can stack up to 4 time(s). The turn will not end after this Skill is used. </div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level < 6) {
            return 1.2 + (level * 0.08);
        }else if(level < 10){
            return 1.6 + ((level - 5) * 0.1);
        }else {
            return 2 + ((level - 9) * 0.08);
        }
    }

    ultimateDescription(level) {
        return {
            name: "A Quartet? Woo-hoo!",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.ultimateAttack(level) * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to all enemies, and obtains 4 jade tiles of the same suit. </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 0.36 + (level * 0.036);
        }else if(level < 10){
            return 0.54 + ((level - 5) * 0.045);
        }else {
            return 0.72 + ((level - 9) * 0.036);
        }
    }

    talentDescription(level) { 
        return {
            name: "Celestial Jade",
            description: (
                <div className="buff-description">
                    <div>When an ally's turn starts, </div>
                    <br/>
                    <div>Qingque randomly draws 1 tile from 3 different suits and can hold up to 4 tiles at one time. </div>
                    <br/>
                    <div>If Qingque starts her turn with 4 tiles of the same suit, she consumes all tiles to enter the "Hidden Hand" state. </div>
                    <br/>
                    <div>While in this state, Qingque cannot use her Skill again. At the same time, Qingque's </div>
                    <div className="buff-text"> ATK </div>
                    <div> increases by </div>
                    <div className="buff-text"> {(this.talent(level) * 100).toFixed(1)}% </div>
                    <div> and her </div>
                    <div className="buff-text"> basic ATK "Flower Pick" </div>
                    <div> is enhanced, becoming </div>
                    <div className="buff-text"> "Cherry on Top!" </div>
                    <div> The "Hidden Hand" state ends after using </div>
                    <div className="buff-text"> "Cherry on Top!" </div>
                </div>
            ),
        }
    }

    talentSimple(level) {
        return {
            name: "Celestial Jade",
            description: (
                <div className="buff-description">
                    <div>Qingque's </div>
                    <div className="buff-text"> ATK </div>
                    <div> increases by </div>
                    <div className="buff-text"> {(this.talent(level) * 100).toFixed(1)}% </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Game Solitaire",
            description: (
                <div className="buff-description">
                    <div> After using Technique, Qingque draws 2 jade tile(s) when the battle starts. </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Tile Battle",
            description: (
                <div className="buff-description">
                    <div>Restores 1 Skill Point when using the Skill. This effect can only be triggered 1 time per battle.</div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Bide Time",
            description: (
                <div className="buff-description">
                    <div> Using the Skill increases </div>
                    <div className="buff-text"> DMG Boost </div>
                    <div> effect of attacks by an extra </div>
                    <div className="buff-text"> 10%.</div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Winning Hand",
            description: (
                <div className="buff-description">
                    <div> Qingque's </div>
                    <div className="buff-text"> SPD </div>
                    <div> increases by </div>
                    <div className="buff-text"> 10% </div>
                    <div> for 1 turn(s) after using the Enhanced Basic ATK. </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Rise Through the Tiles",
            description: (
                <div className="buff-description">
                    <div> Ultimate deals </div>
                    <div className="buff-text"> 10% </div>
                    <div> more DMG. </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Sleep on the Tiles",
            description: (
                <div className="buff-description">
                    <div> Every time Draw Tile is triggered, Qingque immediately regenerates 1 Energy. </div>
                </div>
            )
        }
    }

    E3() {
        return {
            name: "Read Between the Tiles",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }

    
    E4() {
        return {
            name: "Right on the Tiles",
            description:(
                <div className="buff-description">
                    <div>After this character's Skill is used, there is a 24% fixed chance to gain Autarky, which lasts until the end of the current turn. With Autarky, using Basic ATK or Enhanced Basic ATK immediately launches 1 follow-up attack on the same target, dealing Quantum DMG equal to 100% of the previous Basic ATK (or Enhanced Basic ATK)'s DMG.</div>
                    <br/>
                    <div className="muted-text">Fun Fact: most players quit using their skill before the autarky proc, keep using that skill until you're out!</div>
                </div>
            )
        }
    }
    
    E5() {
        return {
            name: "Gambit for the Tiles",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }
    
    E6() {
        return {
            name: " Prevail Beyond the Tiles",
            description: (
                <div className="buff-description">
                    <div>Recovers 1 Skill Point after using Enhanced Basic ATK.</div>
                </div>
            ),
        }
    }
}


export default Qingque;