    const setCharacter = (name) => {
        const factory = new factories.CharacterFactory();
        characterRef.current = factory.getInstance(name);
        resetState();
    }

    const setLightCone = (name) => {

        if(lightConeRef.current !== null) {

            if(lightConeRef.current.id === name) {
                return;
            }

            updateStatesFromJson(lightConeRef.current.weaponBaseEffect(imposition, selectedStatesLC[0]), true);
            let y = lightConeRef.current.weaponEffect(imposition, selectedStatesLC[0]);
            lcBuffStates.map((buff, index) => {
                if(buff === true) {
                    updateStatesFromJson(y[index], true);
                }
                return 0;
            });
        }
        
        const factory = new factories.LightConeFactory();
        lightConeRef.current = factory.getInstance(name);

        if(lightConeRef.current.id === "peaceful") {
            lightConeRef.current.setMaxEnergy(energy);
        }

        if(lightConeRef.current.id === "perfecttiming") {
            lightConeRef.current.setER(effectRes + lightConeRef.current.getER(imposition));
        }
        
        setConeRank(0);
        setImposition(0);
        updateStatesFromJson(lightConeRef.current.weaponBaseEffect(imposition, selectedStatesLC[0]), false);
        

        let numberOfBuffs = Object.keys(lightConeRef.current.weaponEffect).length;
        const initial = Array(numberOfBuffs).fill(false);
        setIsLCSwitched(!isLCSwitched);
        setLcBuffStates(initial);
    }

    const updatePiecesBuffs = (updatedArray) => {
        let tempPiecesCount = {};
        updatedArray.forEach(piece => {
            if(tempPiecesCount[piece.id]) {
                tempPiecesCount[piece.id] += 1;
            } else {
                tempPiecesCount[piece.id] = 1;
            }    
        });

        for(const [key, value] of Object.entries(tempPiecesCount)) {
            if(value === 2 && piecesCount[key] < 2) {
                const factory = new factories.RelicFactory();
                const relic = factory.getInstance(key, "fiveStar", "HP", "head");
                const buff = relic.twoEffect();
                updateStatesFromJson(buff[0], false);
            }

            if(value < 4 && piecesCount[key] === 4) {
                
                const factory = new factories.RelicFactory();
                const relic = factory.getInstance(key, "fiveStar", "HP", "head");
                for(let i = 0; i < relicBuffStates.length; i++) {
                    if(relicBuffStates[i] === true) {
                        const buffsToRemove = relic.fourEffect(selectStates[i]);
                        updateStatesFromJson(buffsToRemove[i], true);
                    }
                }
                
                setRelicBuffStates(initialState);
                
            }else if(value < 2 && piecesCount[key] === 2) { 
                const factory = new factories.RelicFactory();
                const relic = factory.getInstance(key, "fiveStar", "HP", "head");
                const buffsToRemove = relic.twoEffect();
                updateStatesFromJson(buffsToRemove[0], true);
            }
        }
    }

    const updatePlanarBuffs = (updatedArray) => {
        let tempPlanarCount = {};
        updatedArray.forEach(piece => {
            if(tempPlanarCount[piece.id]) {
                tempPlanarCount[piece.id] += 1;
            } else {
                tempPlanarCount[piece.id] = 1;
            }    
        });
        
        for(const [key, value] of Object.entries(tempPlanarCount)) {

            if(value === 2) {
                const factory = new factories.PlanerFactory();
                ornamentPiece.current = factory.getInstance(key, "fiveStar", "HP", "head");
            }

            if(value === 2 && ornamentsCount[key] < 2) {
                const factory = new factories.PlanerFactory();
                const pieces = factory.getInstance(key, "fiveStar", "HP", "head");
                const buffs = pieces.planerEffect(ornamentStackStates[0]);
                updateStatesFromJson(buffs[0], false);
            }

            if(value < 2 && ornamentsCount[key] === 2) {

                updateStatesFromJson(ornamentPiece.current.planerEffect(ornamentStackStates[0])[0], true);
                
                for(let i = 1; i < planarBuffStates.length; i++) {
                    const buffsToRemove = ornamentPiece.current.planerEffect(ornamentStackStates[i]);
                    if(planarBuffStates[i] === true) {
                        updateStatesFromJson(buffsToRemove[i], true);
                    }
                }
                
                ornamentPiece.current = null;
                setPlanarBuffStates(initialState);
            }
        }
    }

