export class InTheNight {
    constructor() {
        this.stats = [
            [1,  26.4,    21,     48],
            [20, 101.64,  80.85,  185],
            [20, 133.32, 106.05,  242],
            [40, 254.76, 202.65, 463],
            [40, 297,   236.25, 540],
            [50, 336.6,   267.75, 612],
            [50, 378.84, 301.35, 689],
            [60, 418.44, 332.85, 761],
            [60, 460.68, 366.45, 838],
            [70, 500.28, 397.95, 910],
            [70, 542.52, 431.55, 986],
            [80, 582.12, 463.05, 1058],
        ];
        
        this.name = "In the Night";
        this.path = "Hunt";
        this.id = "night";
        this.rarity = "fiveStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text"> CRIT Rate </div> 
                <div> by </div> 
                <div className="buff-text">{((0.18 + imposition * 0.03) * 100).toFixed(1)}%.</div>
                <div> While the wearer is in battle, for every 10 SPD that exceeds 100, the DMG of the wearer's </div>
                <div className="buff-text">Basic ATK and Skill</div> 
                <div> is increased by </div> 
                <div className="buff-text"> {((0.06 + imposition * 0.01) * 100).toFixed(1)}%.</div> 
                <div> and the </div>
                <div className="buff-text">CRIT DMG</div>
                <div> of their Ultimate is increased by </div>
                <div className="buff-text"> {((0.12 + imposition * 0.02) * 100).toFixed(1)}%.</div> 
                <div> This effect can stack up to </div>
                <div className="buff-text"> 6 turn(s). </div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "silenceb",
            name: this.name,
            effect:{
                critRate: 0.18 + imposition * 0.03,
            },
            type: "buff",
            hasStack: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition, stack) {
        return {
            0: {
                id: "silence0",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> While the wearer is in battle, for every 10 SPD that exceeds 100, the DMG of the wearer's </div>
                        <div className="buff-text">Basic ATK and Skill</div> 
                        <div> is increased by </div> 
                        <div className="buff-text"> {((0.06 + imposition * 0.01) * 100).toFixed(1)}%.</div> 
                        <div> and the </div>
                        <div className="buff-text">CRIT DMG</div>
                        <div> of their Ultimate is increased by </div>
                        <div className="buff-text"> {((0.12 + imposition * 0.02) * 100).toFixed(1)}%.</div> 
                        <div> This effect can stack up to </div>
                        <div className="buff-text"> 6 turn(s). </div>
                    </div>
                ),
                effect: {
                    basicBoost: (0.06 + imposition * 0.01) * stack,
                    skillBoost: (0.06 + imposition * 0.01) * stack,
                    ultimateCritDmg: (0.12 + imposition * 0.02) * stack,
                },
                type: "buff",
                hasStack: true,
                stacks: 6,
                hasInput: false,
            },
        }
    }
}

export default InTheNight;