export class Memories {
    constructor() {
        this.stats = [
            [1,  19.2,   18,    43 ],
            [20, 73.92,  69.3,  166],
            [20, 96.96,  90.9,  218],
            [40, 185.28, 173.7, 417],
            [40, 216,    202.5, 486],
            [50, 244.8,  229.5, 551],
            [50, 275.52, 258.3, 620],
            [60, 304.32, 285.3, 685],
            [60, 335.04, 314.1, 754],
            [70, 363.84, 341.1, 819],
            [70, 394.56, 369.9, 888],
            [80, 423.36, 396.9, 953],
        ];
        
        this.name = "Memories of the Past";
        this.path = "Harmony";
        this.id = "memories";
        this.rarity = "fourStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text"> Break Effect </div> 
                <div> by </div> 
                <div className="buff-text">{((0.28 + imposition * 0.07) * 100).toFixed(1)}%.</div>
                <div> When the wearer attacks, additionally </div>
                <div className="buff-text">regenerates {4 + imposition * 1} Energy.</div>
                <div> This effect can only be triggered 1 time per turn. </div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{
                BE: (0.28 + imposition * 0.07),
            },
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                // id: "adve1",
                // name: this.name,
                // description: (
                //     <div className="buff-description">
                //         <div> After entering battle, increases the </div>
                //         <div className="buff-text"> ATK </div> 
                //         <div> of all allies by </div> 
                //         <div className="buff-text">{((0.08 + imposition * 0.01) * 100).toFixed(1)}%.</div>
                //         <div> Effects of the same type cannot stack. </div>
                //     </div>
                // ),
                // effect: {
                //     atkPercent: (0.08 + imposition * 0.01),
                // },
                // type: "buff",
                // hasStack: false,
                // hasInput: false,
            },
        }
    }
}

export default Memories;