export class RelicFactory {
    getInstance(name, rarity, mainStat, type) {
        switch(name) {
            case 'genius':
                return new relics.Genius(rarity, mainStat, type);
            case 'band':
                return new relics.Band(rarity, mainStat, type);
            case 'champion':
                return new relics.Champion(rarity, mainStat, type);
            case 'eagle':
                return new relics.Eagle(rarity, mainStat, type);
            case 'firesmith':
                return new relics.Firesmith(rarity, mainStat, type);
            case 'guard':
                return new relics.Guard(rarity, mainStat, type);
            case 'hunter':
                return new relics.Hunter(rarity, mainStat, type);
            case 'knight':
                return new relics.Knight(rarity, mainStat, type);
            case 'longevous':
                return new relics.Longevous(rarity, mainStat, type);
            case 'messenger':
                return new relics.Messenger(rarity, mainStat, type);
            case 'musketeer':
                return new relics.Musketeer(rarity, mainStat, type);
            case 'passerby':
                return new relics.Passerby(rarity, mainStat, type);
            case 'thief':
                return new relics.Thief(rarity, mainStat, type);
            case 'wastelander':
                return new relics.Wastelander(rarity, mainStat, type);
            default:
                return null;
        }
    }
}

