import { useEffect } from "react";

export const MarchGlobalBuffs = (props) => {

    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {
        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <div>No Buffs</div>
        </>
    )
}

export default MarchGlobalBuffs;