import React, { useContext } from 'react';
import { globalData } from '../../../globalData';

function JingliuTable() {

    const levelMult = {
        1: 54.0000,
        20: 139.7703,
        40: 363.6658,
        50: 774.9041,
        60: 1640.3068,
        70: 2659.6406,
        80: 3767.5533,
    }

    const {
        character,
        iceDmg,
        atk,
        atkPercent,
        atkFlat,
        critRate,
        critDmg,
        energyRegen,
        level,
        enemyLevel,
        icePen,
        breakMultiplier,
        defReduction,
        enemyRes,
        basicAttackLevel,
        skillLevel,
        breakEffect,
        enemyToughness,
        ultimateLevel,
        basicBoost,
        eidolon,
        skillBoost,
        ultimateBoost,
        effectHitRate,
        iceVulnerability,
    } = useContext(globalData);

    let jingliuAtk = (atk * atkPercent) + atkFlat;
    let damageBoost = (1 + iceDmg);
    let critDmgBoost = (1 + critDmg);

    let basicDmgBoost = (damageBoost + basicBoost);
    let basicBaseDmg = jingliuAtk * character.basicAttack(basicAttackLevel);

    let skillDmgBoost = (damageBoost + skillBoost);
    let skillBaseDmg = jingliuAtk * character.skillAttack(skillLevel);

    let enhancedSkillDmgBoost = (damageBoost + skillBoost);

    if(character.E2On) {
        enhancedSkillDmgBoost += 0.8;
    }

    let enhancedSkillBaseDmg = jingliuAtk * character.skillAttack1(skillLevel).single;
    let enhacedSpreadSkillBaseDmg = jingliuAtk * character.skillAttack1(skillLevel).spread;

    let ultimateDmgBoost = (damageBoost + ultimateBoost);
    let ultimateBaseDmg = jingliuAtk * character.ultimateAttack(ultimateLevel).single;
    let ultimateSpreadBaseDmg = jingliuAtk * character.ultimateAttack(ultimateLevel).spread;

    let techDmg = jingliuAtk * 0.8;

    let resMult = (1 - (enemyRes - icePen));
    let vulnerabilityMultiplier = (1 + iceVulnerability);
    let defReductionStat = (1 - defReduction);

    if(defReductionStat < 0) {
        defReductionStat = 0;
    }
    
    let defMult = (character.stats[level]["level"] + 20)/((enemyLevel + 20) * defReductionStat + character.stats[level]["level"] + 20);
    let breakMult = breakMultiplier;

    let critRateStat = critRate;

    let enhancedSkillDmg = enhancedSkillBaseDmg * enhancedSkillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult;
    let enhancedSkillSpreadDmg = enhacedSpreadSkillBaseDmg * enhancedSkillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult;

    let ultimateDmg = ultimateBaseDmg * ultimateDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult;
    let ultimateSpreadDmg = ultimateSpreadBaseDmg * ultimateDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult

    if(critRateStat > 1) {
        critRateStat = 1;
    }

    let maxToughnessMult = 0.5 + (enemyToughness/120);
    let breakBaseDmg = 1 * levelMult[character.stats[level]["level"]] * maxToughnessMult;
    let breakDmg = breakBaseDmg * (1 + breakEffect) * defMult * resMult * vulnerabilityMultiplier * breakMult; 
    let freezeBaseDmg = 1 * levelMult[character.stats[level]["level"]];
    let freeze = freezeBaseDmg * (1 + breakEffect) * resMult * defMult * vulnerabilityMultiplier * breakMult;

    return (
        <div className="damage-details">
            <div className='header'>Damage Details</div>
            <div className="divider"></div>
            <div className="damage-title header">Basic Attack ({basicAttackLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Basic</td>
                        <td className='tb-text'>{((basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{((basicBaseDmg * basicDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Skill Attack ({skillLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>DMG</td>
                        <td className='tb-text'>{((skillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{((skillBaseDmg * skillDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((skillBaseDmg * skillDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{60}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{60}</td>
                    </tr>
                </tbody>
            </table>


            <div className="damage-title header">Enhanced Skill Attack ({skillLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>DMG</td>
                        <td className='tb-text'>{((enhancedSkillDmg)).toFixed(0)}</td>
                        <td className='tb-text'>{((enhancedSkillDmg * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((enhancedSkillDmg)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Blast</td>
                        <td className='tb-text'>{((enhancedSkillSpreadDmg)).toFixed(0)}</td>
                        <td className='tb-text'>{((enhancedSkillSpreadDmg * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((enhancedSkillSpreadDmg)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    {eidolon >= 1 && 
                        <tr>
                            <td>E1 DMG</td>
                            <td className='tb-text'>{((enhancedSkillDmg + 1.6 * enhancedSkillSpreadDmg)).toFixed(0)}</td>
                            <td className='tb-text'>{((enhancedSkillDmg + 1.6 * enhancedSkillSpreadDmg * (critDmgBoost))).toFixed(0)}</td>
                            <td className='tb-text'>{(((enhancedSkillDmg + 1.6 * enhancedSkillSpreadDmg)) * (1 + (critDmg) * critRateStat)).toFixed(0)}</td>
                        </tr>
                    }
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{60}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{60}</td>
                    </tr>
                    <tr>
                        <td>Break Blast</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
            </table>

        <div className="damage-title header">Ultimate Attack ({ultimateLevel - 1 + 2})</div>
        <table className="table table-striped attack-table">
            <thead>
                <tr>
                    <th scope="col"></th>
                    <th scope="col">Non-CRIT</th>
                    <th scope="col">CRIT</th>
                    <th scope="col">Average</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>DMG</td>
                    <td className='tb-text'>{(ultimateDmg).toFixed(0)}</td>
                    <td className='tb-text'>{(ultimateDmg * critDmgBoost).toFixed(0)}</td>
                    <td className='tb-text'>{(ultimateDmg * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                </tr>
                <tr>
                    <td>Blast</td>
                    <td className='tb-text'>{(ultimateSpreadDmg).toFixed(0)}</td>
                    <td className='tb-text'>{(ultimateSpreadDmg * critDmgBoost).toFixed(0)}</td>
                    <td className='tb-text'>{(ultimateSpreadDmg * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                </tr>
                {eidolon >= 1 && 
                    <tr>
                        <td>E1 DMG</td>
                        <td className='tb-text'>{((ultimateDmg + 1.6 * ultimateSpreadDmg)).toFixed(0)}</td>
                        <td className='tb-text'>{((ultimateDmg + 1.6 * ultimateSpreadDmg * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((ultimateDmg + 1.6 * ultimateSpreadDmg)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                }
                <tr>
                    <td>Energy</td>
                    <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                    <td className='tb-text'>-</td>
                    <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                </tr>
                <tr>
                    <td>Break</td>
                    <td className='tb-text'>{60}</td>
                    <td className='tb-text'>-</td>
                    <td className='tb-text'>{60}</td>
                </tr>
                <tr>
                    <td>Break Blast</td>
                    <td className='tb-text'>{60}</td>
                    <td className='tb-text'>-</td>
                    <td className='tb-text'>{60}</td>
                </tr>
            </tbody>
        </table>

        <div className="damage-title header">Technique</div>
        <table className="table table-striped attack-table">
            <thead>
            <tr>
                <th scope="col"></th>
                <th scope="col">Non-CRIT</th>
                <th scope="col">CRIT</th>
                <th scope="col">Average</th>
            </tr>
            </thead>
            <tbody>
                <tr>
                    <td>Freeze DMG</td>
                    <td className='tb-text'>{((techDmg * damageBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                    <td className='tb-text'>{((techDmg * damageBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                    <td className='tb-text'>{(((techDmg * damageBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                </tr>
                <tr>
                    <td>Freeze Chance</td>
                    <td className='tb-text'>{(1 * (1 + effectHitRate) * (0.6) * 100).toFixed(0)}%</td>
                    <td className='tb-text'>-</td>
                    <td className='tb-text'>{(1 * (1 + effectHitRate) * (0.6) * 100).toFixed(0)}%</td>
                </tr>
            </tbody>
        </table>

        <div className="damage-title header">Weakness Break</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Type</th>
                        <th scope="col">Value</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Break Damage</td>
                        <td className='tb-text'>{(breakDmg).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Ice Damage</td>
                        <td className='tb-text'>{(freeze).toFixed(0)}</td>
                    </tr>
                </tbody>
            </table>
        </div>
    )
}


export default JingliuTable;