import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';
import { Lynx } from "../../../../../../../database/characters";

const LynxBuffs = () => {

    const { eidolon, characterBuffStates, skillLevel, setCharacterBuffStates, updateStatesFromJson, hp, hpPercent, hpFlat } = useContext(globalData);
    const lynx = new Lynx();

    const [prevSkillLevel, setPrevSkillLevel] = useState(skillLevel);
    const [prevEidolon, setPrevEidolon] = useState(eidolon);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);
    }

    useEffect(() => {

        if(eidolon < 4 && characterBuffStates[0]) {
            updateStatesFromJson(E4Buff, true);
            characterBuffStates[0] = false;
        }
        
        if(eidolon < 1 && characterBuffStates[2]) {
            updateStatesFromJson(E1Buff, true);
            characterBuffStates[2] = false;
        }

        if(eidolon < 6 && characterBuffStates[3] && prevEidolon == 6) {
            let skillBuff = {
                effect: {
                    hpPercent: lynx.skillAttack(skillLevel).hpPercent + 0.06,
                    hpFlat: lynx.skillAttack(skillLevel).hpAddition,
                }
            }
            updateStatesFromJson(skillBuff, true);

            let tempSkillBuff = {
                effect: {
                    hpPercent: lynx.skillAttack(skillLevel).hpPercent,
                    hpFlat: lynx.skillAttack(skillLevel).hpAddition,
                }
            }

            updateStatesFromJson(tempSkillBuff, false);

        }

        if(eidolon >= 6 && characterBuffStates[3]) {
            let tempSkillBuff = {
                effect: {
                    hpPercent: lynx.skillAttack(skillLevel).hpPercent,
                    hpFlat: lynx.skillAttack(skillLevel).hpAddition,
                }
            }

            updateStatesFromJson(tempSkillBuff, true);

            let skillBuff = {
                effect: {
                    hpPercent: lynx.skillAttack(skillLevel).hpPercent + 0.06,
                    hpFlat: lynx.skillAttack(skillLevel).hpAddition,
                }
            }

            updateStatesFromJson(skillBuff, false);
        }

        setPrevEidolon(eidolon);

    }, [eidolon]);

    useEffect(() => {

        if(characterBuffStates[3]) {
            const tempBuff = {
                effect: {
                    hpPercent: (lynx.skillAttack(prevSkillLevel).hpPercent),
                    hpFlat: lynx.skillAttack(prevSkillLevel).hpAddition,
                }
            }
            updateStatesFromJson(tempBuff, true);
            updateStatesFromJson(skillBuff, false);
        }

        setPrevSkillLevel(skillLevel);

    }, [skillLevel]);

    
    let skillBuff = {
        effect: {
            hpPercent: lynx.skillAttack(skillLevel).hpPercent,
            hpFlat: lynx.skillAttack(skillLevel).hpAddition,
        }
    }

    if(eidolon >= 6) {
        skillBuff = {
            effect: {
                hpPercent: lynx.skillAttack(skillLevel).hpPercent + 0.06,
                hpFlat: lynx.skillAttack(skillLevel).hpAddition,
            }
        }
    }

    const E1Buff = {
        effect: {
            OGH: 0.2,
        },
    }

    const E4Buff = {
        effect: {
            atkFlat: 0.03 * (hp * hpPercent + hpFlat),
        },
    }

    return (
        <>

            <>
                <FormControlLabel
                className='header'
                control={<Checkbox checked={characterBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, skillBuff)} />}
                label={lynx.skillDescription(skillLevel).name}
                />
                <div>{lynx.skillSimple(skillLevel).description}</div>
            </>

            {eidolon >= 1 && 
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, E1Buff)} />}
                    label={"(E1) " + lynx.E1().name}
                    />
                    <div>{lynx.E1().description}</div>
                </>
            }

            {eidolon >= 4 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, E4Buff)} />}
                    label={"(E4) " + lynx.E4().name}
                    />
                    <div>{lynx.E4().description}</div>
                </>
            }

        </>
    )
}

export const LynxGlobalBuffs = (props) => {

    const lynx = new Lynx();
    const { updateStatesFromJson } = useContext(globalData);

    const lynxBuffStates = props.buffStates;
    const setLynxBuffStates = props.setBuffStates;
    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    const [skillLevel, setSkillLevel] = useState(0);
    const [maxHp, setMaxHp] = useState(144);
    const [prevMaxHp, setPrevMaxHp] = useState(maxHp);
    const [E6, setE6] = useState(false);

    useEffect(() => {

        if(lynxBuffStates[0]) {
            updateStatesFromJson(E4Buff, true);
            lynxBuffStates[0] = false;
        }
        
        if(lynxBuffStates[3]) {
            updateStatesFromJson(skillBuff, true);
            lynxBuffStates[3] = false;
        }
        
        setDone(!done);
    }, [characterSwitched]);

    const [prevSkillLevel, setPrevSkillLevel] = useState(skillLevel);

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...lynxBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setLynxBuffStates(updatedStates);
    }

    useEffect(() => {

        if(lynxBuffStates[3]) {
            let tempBuff = {
                effect: {
                    hpFlat: lynx.skillAttack(prevSkillLevel).hpAddition + (maxHp * lynx.skillAttack(prevSkillLevel).hpPercent),
                }
            }

            if(E6) {
                tempBuff = {
                    effect: {
                        hpFlat: lynx.skillAttack(prevSkillLevel).hpAddition + (maxHp * lynx.skillAttack(prevSkillLevel).hpPercent) + (maxHp * 0.06),
                    }
                }
            }
            updateStatesFromJson(tempBuff, true);
            updateStatesFromJson(skillBuff, false);
        }

        setPrevSkillLevel(skillLevel);

    }, [skillLevel]);

    useEffect(() => {

        if(lynxBuffStates[3]) {
            let tempBuff = {
                effect: {
                    hpFlat: lynx.skillAttack(skillLevel).hpAddition + (prevMaxHp * lynx.skillAttack(skillLevel).hpPercent),
                }
            }
            
            if(E6) {
                tempBuff = {
                    effect: {
                        hpFlat: lynx.skillAttack(skillLevel).hpAddition + (prevMaxHp * lynx.skillAttack(skillLevel).hpPercent) +(prevMaxHp * 0.06),
                    }
                }
            }

            updateStatesFromJson(tempBuff, true);
            updateStatesFromJson(skillBuff, false);
        }

        if(lynxBuffStates[0]) {
            const oldE4Buff = {
                effect: {
                    atkFlat: 0.03 * prevMaxHp,
                },
            }
            updateStatesFromJson(oldE4Buff, true);
            updateStatesFromJson(E4Buff, false);
        }
        

        setPrevMaxHp(maxHp);

    }, [maxHp]);

    
    let skillBuff = {
        effect: {
            hpFlat: lynx.skillAttack(skillLevel).hpAddition + (maxHp * lynx.skillAttack(skillLevel).hpPercent),
        }
    }

    if(E6) {
        skillBuff = {
            effect: {
                hpFlat: lynx.skillAttack(skillLevel).hpAddition + (maxHp * lynx.skillAttack(skillLevel).hpPercent) +(maxHp * 0.06),
            }
        }
    }

    const E4Buff = {
        effect: {
            atkFlat: 0.03 * maxHp,
        },
    }

    const updateSkillLevel = (e) => {
        setSkillLevel(e.target.value);
    }

    const handleMaxHp = (event) => {
        const re = /^[0-9]*\.?[0-9]+$/;
        if (event.target.value === '' || !re.test(event.target.value)) {
            return;
        }


        setMaxHp((event.target.value));
    }

    const turnE6On = (event) => {
        setE6(event.target.checked);

        if(event.target.checked && lynxBuffStates[3]) {
            let tskillBuff = {
                effect: {
                    hpFlat: lynx.skillAttack(skillLevel).hpAddition + (maxHp * lynx.skillAttack(skillLevel).hpPercent),
                }
            }
            updateStatesFromJson(tskillBuff, true);

            let sskillBuff = {
                effect: {
                    hpFlat: lynx.skillAttack(skillLevel).hpAddition + (maxHp * lynx.skillAttack(skillLevel).hpPercent) +(maxHp * 0.06),
                }
            }
            updateStatesFromJson(sskillBuff, false);

        }else if (lynxBuffStates[3]) {
            let sskillBuff = {
                effect: {
                    hpFlat: lynx.skillAttack(skillLevel).hpAddition + (maxHp * lynx.skillAttack(skillLevel).hpPercent) +(maxHp * 0.06),
                }
            }
            updateStatesFromJson(sskillBuff, true);

            let tskillBuff = {
                effect: {
                    hpFlat: lynx.skillAttack(skillLevel).hpAddition + (maxHp * lynx.skillAttack(skillLevel).hpPercent),
                }
            }
            updateStatesFromJson(tskillBuff, false);
        }

    }

    return (
        <>
            <div style={{display: "flex", width: "80%"}}>
                <div className="centered">Max HP</div>
                <input type="text" className="critDmg buff-input" maxLength={6} value={maxHp} onChange={handleMaxHp}/>
            </div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={lynxBuffStates[3] || false} onChange={() => applyCharacterBuffs(3, skillBuff)} />}
            label={lynx.skillDescription(skillLevel).name}
            />
            <div>{lynx.skillSimple(skillLevel).description}</div>
            <div style={{display: "flex", alignItems: "center", whiteSpace: "nowrap", justifyContent: "space-between", width: "100%"}}>
                <div style={{display: "flex"}}>
                    <div>Skill Lv.</div>
                    <select className="levels buff-input" style={{width: "45%"}} value={skillLevel} onChange={updateSkillLevel}>
                        <option value={0}>1</option>
                        <option value={1}>2</option>
                        <option value={2}>3</option>
                        <option value={3}>4</option>
                        <option value={4}>5</option>
                        <option value={5}>6</option>
                        <option value={6}>7</option>
                        <option value={7}>8</option>
                        <option value={8}>9</option>
                        <option value={9}>10</option>
                        <option value={10}>11</option>
                        <option value={11}>12</option>
                    </select>
                </div>
                <FormControlLabel
                className='header'
                control={<Checkbox checked={E6 || false} onChange={turnE6On} />}
                label="E6 (skill Buff +6%)"
                />
            </div>

            <FormControlLabel
            className='header'
            control={<Checkbox checked={lynxBuffStates[0] || false} onChange={() => applyCharacterBuffs(0, E4Buff)} />}
            label={"(E4) " + lynx.E4().name}
            />
            <div>{lynx.E4().description}</div>

        </>
    )
}

export default LynxBuffs;