import React, { useContext } from 'react';
import { globalData } from '../../../globalData';

function NatashaTable() {

    const levelMult = {
        1: 54.0000,
        20: 139.7703,
        40: 363.6658,
        50: 774.9041,
        60: 1640.3068,
        70: 2659.6406,
        80: 3767.5533,
    }

    const {
        character,
        physicalDmg,
        atk,
        atkPercent,
        atkFlat,
        hp,
        eidolon,
        hpPercent,
        hpFlat,
        critRate,
        critDmg,
        energyRegen,
        level,
        enemyLevel,
        physicalPen,
        breakEffect,
        defReduction,
        enemyRes,
        basicAttackLevel,
        skillLevel,
        ultimateLevel,
        breakMultiplier,
        basicBoost,
        outgoingHealing,
        physicalVulnerability,
        enemyToughness,
        enemyMaxHP,
        isBoss,
        dotBoost,
        effectHitRate,
        dotVulnerability,
    } = useContext(globalData);

    let natashaAtk = (atk * atkPercent) + atkFlat;
    let damageBoost = (1 + physicalDmg);
    let critDmgBoost = (1 + critDmg);
    let basicDmgBoost = damageBoost + basicBoost;
    let basicBaseDmg = natashaAtk * character.basicAttack(basicAttackLevel);

    let natashaHp = (hp * hpPercent) + hpFlat;
    let skillHealing = (((natashaHp * character.skillAttack(skillLevel).multiplier) + character.skillAttack(skillLevel).addition) * (1 + outgoingHealing));
    let conSkillHealing = (((natashaHp * character.skillAttack(skillLevel).continousMult) + character.skillAttack(skillLevel).continousAddition) * (1 + outgoingHealing));

    let ultimateHealing = (((natashaHp * character.ultimateAttack(ultimateLevel).multiplier) + character.ultimateAttack(ultimateLevel).addition) * (1 + outgoingHealing));
    let conUltimateHealing = (((natashaHp * 0.06) + 160) * (1 + outgoingHealing)); 

    let techDmg = natashaAtk * 0.8;

    let resMult = (1 - (enemyRes - physicalPen));
    let defReductionStat = (1 - defReduction);
    let vulnerabilityMultiplier = (1 + physicalVulnerability);

    if(defReductionStat < 0) {
        defReductionStat = 0;
    }

    let defMult = (character.stats[level]["level"] + 20)/((enemyLevel + 20) * defReductionStat + character.stats[level]["level"] + 20);
    let breakMult = breakMultiplier;
    
    let critRateStat = critRate;
    
    if(critRateStat > 1) {
        critRateStat = 1;
    }
    
    let maxToughnessMult = 0.5 + (enemyToughness/120);
    let breakBaseDmg = 2 * levelMult[character.stats[level]["level"]] * maxToughnessMult;
    let breakDmg = breakBaseDmg * (1 + breakEffect) * defMult * resMult * (vulnerabilityMultiplier + dotVulnerability) * breakMult * (damageBoost + dotBoost); 

    let bleedBaseDmg = 0;
    if(isBoss) {
        bleedBaseDmg = 0.07 * enemyMaxHP;
    }else {
        bleedBaseDmg = 0.16 * enemyMaxHP;
    }

    if(2 * levelMult[character.stats[level]["level"]] * maxToughnessMult < bleedBaseDmg) {
        bleedBaseDmg = 2 * levelMult[character.stats[level]["level"]] * maxToughnessMult;
    }

    let bleed = bleedBaseDmg * (1 + breakEffect) * resMult * defMult * (vulnerabilityMultiplier + dotBoost) * breakMult;


    let additionalDmg = 0;
    if(eidolon >= 6) {
        additionalDmg = (0.4 * natashaHp) * damageBoost * defMult * vulnerabilityMultiplier * resMult * breakMult;
    }

    let E1Healing = 0;
    if(eidolon >= 1) {
        E1Healing = ((0.4 * natashaHp) + 400) * (1 + outgoingHealing);
    }

    return (
        <div className="damage-details">
            <div className='header'>Damage Details</div>
            <div className="divider"></div>
            <div className="damage-title header">Basic Attack ({basicAttackLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Basic</td>
                        <td className='tb-text'>{(basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult).toFixed(0)}</td>
                        <td className='tb-text'>{(basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult * critDmgBoost).toFixed(0)}</td>
                        <td className='tb-text'>{((basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
                
            </table>

            {eidolon >= 6 &&
                <table className="table table-striped attack-table">
                    <thead>
                        <tr>
                            <th scope="col"></th>
                            <th scope="col">Non-CRIT</th>
                            <th scope="col">CRIT</th>
                            <th scope="col">Average</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>E6 DMG</td>
                            <td className='tb-text'>{(additionalDmg).toFixed(0)}</td>
                            <td className='tb-text'>{(additionalDmg * critDmgBoost).toFixed(0)}</td>
                            <td className='tb-text'>{((additionalDmg) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                        </tr>
                    </tbody>
                </table>
            }


            <div className="damage-title header">Skill Healing ({skillLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Source</th>
                        <th scope="col" className='tb-text-header'>Amount</th>

                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Heal</td>
                        <td className='tb-text'>{(skillHealing).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Continuous</td>
                        <td className='tb-text'>{(conSkillHealing).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(30 * energyRegen).toFixed(1)}</td>
                    </tr>
                </tbody>
            </table>

            {eidolon >= 1 &&
                <table className="table table-striped attack-table">
                    <thead>
                        <tr>
                            <th scope="col">Source</th>
                            <th scope="col" className='tb-text-header'>Amount</th>

                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>E1 Heal</td>
                            <td className='tb-text'>{(E1Healing).toFixed(0)}</td>
                        </tr>
                    </tbody>
                </table>
            }

            <div className="damage-title header">Ultimate Healing ({ultimateLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Source</th>
                        <th scope="col">Amount</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Heal</td>
                        <td className='tb-text'>{(ultimateHealing).toFixed(0)}</td>
                    </tr>
                    {eidolon >= 2 &&
                        <tr>
                            <td>Continuous</td>
                            <td className='tb-text'>{(conUltimateHealing).toFixed(0)}</td>
                        </tr>
                    }
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Technique</div>
                <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>DMG</td>
                        <td className='tb-text'>{((techDmg * damageBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{((techDmg * damageBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((techDmg * damageBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Weaken Chance</td>
                        <td className='tb-text'>{(1 * (1 + effectHitRate) * (0.6) * 100).toFixed(1)}%</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(1 * (1 + effectHitRate) * (0.6) * 100).toFixed(1)}%</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Weakness Break</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Type</th>
                        <th scope="col">Value</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Break Damage</td>
                        <td className='tb-text'>{(breakDmg).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Bleed Damage</td>
                        <td className='tb-text'>{(bleed).toFixed(0)}</td>
                    </tr>
                </tbody>
            </table>
    </div>
    )
}


export default NatashaTable;