class Tingyun {
    constructor() {
        this.stats = [
            {level: 1,  atk: 72,    def: 54,    hp: 115, spd: 112, taunt: 500, energy: 130},
            {level: 20, atk: 140.4, def: 105.3, hp: 225, spd: 112, taunt: 500, energy: 130, atkPercent: 0.04},
            {level: 20, atk: 169.2, def: 126.9, hp: 271, spd: 112, taunt: 500, energy: 130, atkPercent: 0.04},
            {level: 40, atk: 270,   def: 202.5, hp: 432, spd: 112, taunt: 500, energy: 130, atkPercent: 0.08, defPercent: 0.05},
            {level: 40, atk: 298.8, def: 224.1, hp: 478, spd: 112, taunt: 500, energy: 130, atkPercent: 0.08, lightningDmg: 0.032, defPercent: 0.05},
            {level: 50, atk: 334.8, def: 251.1, hp: 536, spd: 112, taunt: 500, energy: 130, atkPercent: 0.14, lightningDmg: 0.032, defPercent: 0.05},
            {level: 50, atk: 363.6, def: 272.7, hp: 582, spd: 112, taunt: 500, energy: 130, atkPercent: 0.14, lightningDmg: 0.032, defPercent: 0.05},
            {level: 60, atk: 399.6, def: 299.7, hp: 639, spd: 112, taunt: 500, energy: 130, atkPercent: 0.14, lightningDmg: 0.032, defPercent: 0.05},
            {level: 60, atk: 428.4, def: 321.3, hp: 685, spd: 112, taunt: 500, energy: 130, atkPercent: 0.2,  lightningDmg: 0.032, defPercent: 0.125},
            {level: 70, atk: 464.4, def: 348.3, hp: 743, spd: 112, taunt: 500, energy: 130, atkPercent: 0.2,  lightningDmg: 0.032, defPercent: 0.125},
            {level: 70, atk: 493.2, def: 369.9, hp: 789, spd: 112, taunt: 500, energy: 130, atkPercent: 0.2,  lightningDmg: 0.032, defPercent: 0.125},
            {level: 80, atk: 529.2, def: 396.9, hp: 847, spd: 112, taunt: 500, energy: 130, atkPercent: 0.28, lightningDmg: 0.08,   defPercent: 0.225},
        ];

        this.name = "Tingyun";
        this.path = "Harmony";
        this.rarity = 'fourStar';
        this.element = "Lightning";
        this.id = "tingyun";
        this.E4On = false;
    }

    setE4On(on) {
        this.E4On = on;
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Dislodged",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {

        if(this.E4On) {
            if(level < 6) {
                return {
                    atkPercent: 0.25 + (level * 0.025),
                    additionalDmg: 0.4 + (level * 0.02),
                    atkCap: 0.15 + (level * 0.01),
                };
            }else if(level < 10){
                return {
                    atkPercent: 0.375 + ((level - 5) * 0.0313),
                    additionalDmg: 0.5 + ((level - 5) * 0.025),
                    atkCap: 0.20 + ((level - 5) * 0.0125),
                }
            }else {
                return {
                    atkPercent: 0.5 + ((level - 9) * 0.025),
                    additionalDmg: 0.6 + ((level - 9) * 0.02),
                    atkCap: 0.25 + ((level - 5) * 0.01),
                }
            }

        }

        if(level < 6) {
            return {
                atkPercent: 0.25 + (level * 0.025),
                additionalDmg: 0.2 + (level * 0.02),
                atkCap: 0.15 + (level * 0.01),
            };
        }else if(level < 10){
            return {
                atkPercent: 0.375 + ((level - 5) * 0.0313),
                additionalDmg: 0.3 + ((level - 5) * 0.025),
                atkCap: 0.20 + ((level - 5) * 0.0125),
            }
        }else {
            return {
                atkPercent: 0.5 + ((level - 9) * 0.025),
                additionalDmg: 0.4 + ((level - 9) * 0.02),
                atkCap: 0.25 + ((level - 5) * 0.01),
            }
        }
    }

    skillDescription(level) {
        return {
            name: "Soothing Melody",
            description: (
                <div className="buff-description">
                    <div> Grants a single ally with Benediction to increase their </div>
                    <div className="buff-text">ATK</div>
                    <div> by </div>
                    <div className="buff-text">{(this.skillAttack(level).atkPercent * 100).toFixed(1)}%,</div>
                    <div> up to  </div>
                    <div className="buff-text">{(this.skillAttack(level).atkCap * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s current </div>
                    <div className="buff-text">ATK</div>
                    <div>. When the ally with Benediction attacks, they will deal Additional Lightning DMG equal to </div>
                    <div className="buff-text">{(this.skillAttack(level).additionalDmg * 100).toFixed(1)}%.</div>
                    <div> of that ally's ATK for 1 time. Benediction lasts for 3 turn(s) and is only effective on the most recent receiver of Tingyun's Skill. </div>
                </div>
            ),
        }
    }

    skillSimple(level) {
        return {
            name: "Soothing Melody",
            description: (
                <div className="buff-description">
                    <div> Increase </div>
                    <div className="buff-text">ATK</div>
                    <div> by </div>
                    <div className="buff-text">{(this.skillAttack(level).atkPercent * 100).toFixed(1)}%,</div>
                    <div> and deal Additional Lightning DMG equal to </div>
                    <div className="buff-text">{(this.skillAttack(level).additionalDmg * 100).toFixed(1)}%.</div>
                    <div> of the active character's ATK </div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level < 6) {
            return 0.2 + (level * 0.03);
        }else if(level < 10){
            return 0.35 + ((level - 5) * 0.0375);
        }else {
            return 0.5 + ((level - 9) * 0.03);
        }
    }

    ultimateDescription(level) {
        return {
            name: "Amidst the Rejoicing Clouds",
            description: (
                <div className="buff-description">
                    <div>Regenerates 50 Energy for a single ally and increases the target's DMG by </div>
                    <div className="buff-text"> {(this.ultimateAttack(level) * 100).toFixed(1)}% </div>
                    <div> for 2 turn(s). </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 0.3 + (level * 0.003);
        }else if(level < 10){
            return 0.45 + ((level - 5) * 0.00375);
        }else {
            return 0.6 + ((level - 9) * 0.003);
        }
    }

    talentDescription(level) { 
        return {
            name: "Violet Sparknado",
            description: (
                <div className="buff-description">
                    <div>When an enemy is attacked by Tingyun, the ally with Benediction immediately deals Additional Lightning DMG equal to </div>
                    <div className="buff-text"> {(this.talent(level) * 100).toFixed(1)}% </div>
                    <div> of that ally's ATK to the same enemy. </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Gentle Breeze",
            description: (
                <div className="buff-description">
                    <div> Tingyun immediately regenerates 50 Energy upon using her Technique. </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Nourished Joviality",
            description: (
                <div className="buff-description">
                    <div> Tingyun's </div>
                    <div className="buff-text"> SPD </div>
                    <div> increases by </div>
                    <div className="buff-text"> 20% </div>
                    <div> for 1 turn after using Skill. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Knell Subdual",
            description: (
                <div className="buff-description">
                    <div> Increases </div>
                    <div className="buff-text"> Basic Attack </div>
                    <div> DMG by </div>
                    <div className="buff-text"> 40% </div>
                    <div>.</div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Jubilant Passage",
            description: (
                <div className="buff-description">
                    <div> Tingyun immediately regenerates 5 Energy at the start of her turn. </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Windfall of Lucky Springs",
            description: (
                <div className="buff-description">
                    <div> After using their Ultimate, the ally with Benediction gains a 20% increase in SPD for 1 turn. </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Gainfully Gives, Givingly Gains",
            description: (
                <div className="buff-description">
                    <div> The ally with Benediction regenerates 5 Energy after defeating an enemy. This effect can only be triggered once per turn. </div>
                </div>
            )
        }
    }

    E3() {
        return {
            name: "Halcyon Bequest",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }
    
    E4() {
        return {
            name: " Jovial Versatility",
            description:(
                <div className="buff-description">
                    <div>The DMG multiplier provided by Benediction increases by 20%.</div>
                </div>
            )
        }
    }
    
    E5() {
        return {
            name: "Sauntering Coquette",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: "Peace Brings Wealth to All",
            description: (
                <div className="buff-description">
                    <div> Ultimate regenerates 10 more Energy for the target ally. </div>
                </div>
            ),
        }
    }
}

export default Tingyun;