export class Worrisome {
    constructor() {
        this.stats = [
            [1,  26.4,    21,     48],
            [20, 101.64,  80.85,  185],
            [20, 133.32, 106.05,  242],
            [40, 254.76, 202.65, 463],
            [40, 297,   236.25, 540],
            [50, 336.6,   267.75, 612],
            [50, 378.84, 301.35, 689],
            [60, 418.44, 332.85, 761],
            [60, 460.68, 366.45, 838],
            [70, 500.28, 397.95, 910],
            [70, 542.52, 431.55, 986],
            [80, 582.12, 463.05, 1058],
        ];
        
        this.name = "Worrisome, Blissful";
        this.path = "Hunt";
        this.id = "worrisome";
        this.isBeta = true;
        this.rarity = "fiveStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text"> CRIT Rate </div> 
                <div> by </div> 
                <div className="buff-text">{((0.18 + imposition * 0.03) * 100).toFixed(1)}%</div>
                <div> and DMG from </div>
                <div className="buff-text"> Follow-up attacks </div>
                <div> by </div>
                <div className="buff-text">{((0.3 + imposition * 0.05) * 100).toFixed(1)}%.</div>
                <div> After the wearer unleashes a </div>
                <div className="buff-text">follow-up attack, </div> 
                <div> apply the Tame state to the target, stacking up to 2 stacks. When allies hit enemy targets under the Tame state, every Tame stack will increase the </div> 
                <div className="buff-text"> CRIT DMG received </div> 
                <div> by </div>
                <div className="buff-text">{((0.12 + imposition * 0.02) * 100).toFixed(1)}%.</div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "silenceb",
            name: this.name,
            effect:{
                critRate: 0.18 + imposition * 0.03,
                followUpBoost: 0.3 + imposition * 0.05,
            },
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition, stack) {
        return {
            0: {
                id: "silence0",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> Increase the </div> 
                        <div className="buff-text"> CRIT DMG received </div> 
                        <div> by </div>
                        <div className="buff-text">{((0.12 + imposition * 0.02) * 100).toFixed(1)}%.</div>
                        <div> Up to 2 stacks. </div>
                    </div>
                ),
                effect: {
                    critDmg: (0.12 + imposition * 0.02) * stack,
                },
                type: "buff",
                hasStack: true,
                stacks: 2,
                hasInput: false,
            },
        }
    }
}

export default Worrisome;