export class Adversarial {
    constructor() {
        this.stats = [
            [1, 16.8, 12, 34],
            [20, 64.68, 46.2, 129],
            [20, 84.84, 60.6, 170],
            [40,162.12,115.8,324],
            [40,189 ,236.135,378],
            [50,214.2 ,267.153,428],
            [50,241.08,172.2,482],
            [60,266.28,190.2,533],
            [60,293.16,209.4,586],
            [70,318.36,227.4,637],
            [70,345.24,246.6,690],
            [80,370.44,264.6,741],
        ];
        
        this.name = "Adversarial";
        this.path = "Hunt";
        this.id = "adversarial";
        this.rarity = "threeStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>When the wearer defeats an enemy, increases </div>
                <div className="buff-text"> SPD </div> 
                <div> by </div> 
                <div className="buff-text">{((0.1 + imposition * 0.02) * 100).toFixed(1)}%</div>
                <div> for </div>
                <div className="buff-text">3 turn(s).</div> 
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{},
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "adve1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div>When the wearer defeats an enemy, increases </div>
                        <div className="buff-text">SPD </div> 
                        <div> by </div> 
                        <div className="buff-text">{((0.1 + imposition * 0.02) * 100).toFixed(1)}%</div>
                        <div> for </div>
                        <div className="buff-text">2 turn(s).</div> 
                    </div>
                ),
                effect: {
                    spdPercent: (0.1 + imposition * 0.02),
                },
                type: "buff",
                hasStacks: false,
                hasInput: false,
            },
        }
    }
}

export default Adversarial;