export class Quid {
    constructor() {
        this.stats = [
            [1,  19.2,   18,     43],
            [20, 73.92,  69.3,  166],
            [20, 96.96,  90.9,  218],
            [40, 185.28, 173.7, 417],
            [40, 216,    202.5, 486],
            [50, 244.8,  229.5, 551],
            [50, 275.52, 258.3, 620],
            [60, 304.32, 285.3, 685],
            [60, 335.04, 314.1, 754],
            [70, 363.84, 341.1, 819],
            [70, 394.56, 369.9, 888],
            [80, 423.36, 396.9, 953],
        ];
        
        this.name = "Quid Pro Quo";
        this.path = "Abundance";
        this.id = "quid";
        this.rarity = "fourStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>At the start of the wearer's turn, </div>
                <div className="buff-text"> regenerates {8 + imposition * 2 } energy </div> 
                <div> for a randomly chosen ally (excluding the wearer) whose current Energy is </div> 
                <div className="buff-text">lower than 50%.</div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{},
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    setER(ER) {
        this.ER = ER;
    }

    getER(imposition) {
        return 0.16 + imposition * 0.04;
    }

    weaponEffect(imposition) {
        return {
            0: {},
        }
    }
}

export default Quid;