import React, { useContext } from 'react';
import { globalData } from '../../../globalData';

function QingqueTable() {

    const levelMult = {
        1: 54.0000,
        20: 139.7703,
        40: 363.6658,
        50: 774.9041,
        60: 1640.3068,
        70: 2659.6406,
        80: 3767.5533,
    }

    const {
        character,
        quantumDmg,
        atk,
        atkPercent,
        atkFlat,
        critRate,
        eidolon,
        critDmg,
        energyRegen,
        level,
        enemyLevel,
        quantumPen,
        breakMultiplier,
        defReduction,
        enemyRes,
        basicAttackLevel,
        breakEffect,
        enemyToughness,
        ultimateLevel,
        basicBoost,
        ultimateBoost,
        quantumVulnerability,
        followUpBoost,
        followUpVulnerability,
        followUpCritDmg,
    } = useContext(globalData);

    let quantumAtk = (atk * atkPercent) + atkFlat;
    let damageBoost = (1 + quantumDmg);
    let critDmgBoost = (1 + critDmg);

    let basicDmgBoost = (damageBoost + basicBoost);
    let basicBaseDmg = quantumAtk * character.basicAttack(basicAttackLevel);

    let enhancedBasicBaseDmg = quantumAtk * character.basicAttack1(basicAttackLevel);

    let followUpDmgBoost = (damageBoost + followUpBoost);

    let ultimateDmgBoost = (damageBoost + ultimateBoost);

    if(eidolon >= 1) {
        ultimateDmgBoost += 0.1;
    }

    let ultimateBaseDmg = quantumAtk * character.ultimateAttack(ultimateLevel);

    let resMult = (1 - (enemyRes - quantumPen));
    let vulnerabilityMultiplier = (1 + quantumVulnerability);
    let defReductionStat = (1 - defReduction);

    if(defReductionStat < 0) {
        defReductionStat = 0;
    }
    
    let defMult = (character.stats[level]["level"] + 20)/((enemyLevel + 20) * defReductionStat + character.stats[level]["level"] + 20);
    let breakMult = breakMultiplier;

    let critRateStat = critRate;

    if(critRateStat > 1) {
        critRateStat = 1;
    }

    let maxToughnessMult = 0.5 + (enemyToughness/120);
    let breakBaseDmg = 0.5 * levelMult[character.stats[level]["level"]] * maxToughnessMult;
    let breakDmg = breakBaseDmg * (1 + breakEffect) * defMult * resMult * vulnerabilityMultiplier * breakMult; 
    let delay = 0.2 * (1 + breakEffect);
    let entanglementBaseDmg = 0.6 * levelMult[character.stats[level]["level"]] * maxToughnessMult;
    let entanglement = entanglementBaseDmg * (1 + breakEffect) * resMult * defMult * vulnerabilityMultiplier * breakMult;

    return (
        <div className="damage-details">
            <div className='header'>Damage Details</div>
            <div className="divider"></div>
            <div className="damage-title header">Basic Attack ({basicAttackLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Basic</td>
                        <td className='tb-text'>{((basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{((basicBaseDmg * basicDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    {eidolon >= 4 && 
                        <>
                            <tr>
                                <td>E4 DMG</td>
                                <td className='tb-text'>{((basicBaseDmg * followUpDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult)).toFixed(0)}</td>
                                <td className='tb-text'>{((basicBaseDmg * followUpDmgBoost * defMult  * (vulnerabilityMultiplier + followUpVulnerability)* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                                <td className='tb-text'>{(((basicBaseDmg * followUpDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                            </tr>
                        </>
                    }
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
            </table>

            <div className="damage-title header">Enhanced Basic Attack ({basicAttackLevel - 1 + 2})</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">Non-CRIT</th>
                        <th scope="col">CRIT</th>
                        <th scope="col">Average</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>DMG</td>
                        <td className='tb-text'>{((enhancedBasicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{((enhancedBasicBaseDmg * basicDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((enhancedBasicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Blast</td>
                        <td className='tb-text'>{((basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                        <td className='tb-text'>{((basicBaseDmg * basicDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                        <td className='tb-text'>{(((basicBaseDmg * basicDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                    </tr>
                    {eidolon >= 4 && 
                        <>
                            <tr>
                                <td>E4 DMG</td>
                                <td className='tb-text'>{((enhancedBasicBaseDmg * followUpDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult)).toFixed(0)}</td>
                                <td className='tb-text'>{((enhancedBasicBaseDmg * followUpDmgBoost * defMult  * (vulnerabilityMultiplier + followUpVulnerability)* resMult * breakMult * (critDmgBoost + followUpCritDmg))).toFixed(0)}</td>
                                <td className='tb-text'>{(((enhancedBasicBaseDmg * followUpDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult)) * (1 + (critDmg + followUpCritDmg) * critRateStat)).toFixed(0)}</td>
                            </tr>
                            <tr>
                                <td>E4 Blast</td>
                                <td className='tb-text'>{((basicBaseDmg * followUpDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult)).toFixed(0)}</td>
                                <td className='tb-text'>{((basicBaseDmg * followUpDmgBoost * defMult  * (vulnerabilityMultiplier + followUpVulnerability)* resMult * breakMult * (critDmgBoost + followUpCritDmg))).toFixed(0)}</td>
                                <td className='tb-text'>{(((basicBaseDmg * followUpDmgBoost * defMult * (vulnerabilityMultiplier + followUpVulnerability) * resMult * breakMult)) * (1 + (critDmg + followUpCritDmg) * critRateStat)).toFixed(0)}</td>
                            </tr>
                        </>
                    }
                    <tr>
                        <td>Energy</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{(20 * energyRegen).toFixed(1)}</td>
                    </tr>
                    <tr>
                        <td>Break</td>
                        <td className='tb-text'>{60}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{60}</td>
                    </tr>
                    <tr>
                        <td>Break Blast</td>
                        <td className='tb-text'>{30}</td>
                        <td className='tb-text'>-</td>
                        <td className='tb-text'>{30}</td>
                    </tr>
                </tbody>
            </table>

        <div className="damage-title header">Ultimate Attack ({ultimateLevel - 1 + 2})</div>
        <table className="table table-striped attack-table">
            <thead>
                <tr>
                    <th scope="col"></th>
                    <th scope="col">Non-CRIT</th>
                    <th scope="col">CRIT</th>
                    <th scope="col">Average</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>DMG</td>
                    <td className='tb-text'>{((ultimateBaseDmg * ultimateDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)).toFixed(0)}</td>
                    <td className='tb-text'>{((ultimateBaseDmg * ultimateDmgBoost * defMult  * vulnerabilityMultiplier* resMult * breakMult * critDmgBoost)).toFixed(0)}</td>
                    <td className='tb-text'>{(((ultimateBaseDmg * ultimateDmgBoost * defMult * vulnerabilityMultiplier * resMult * breakMult)) * (1 + critDmg * critRateStat)).toFixed(0)}</td>
                </tr>
                <tr>
                    <td>Energy</td>
                    <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                    <td className='tb-text'>-</td>
                    <td className='tb-text'>{(5 * energyRegen).toFixed(1)}</td>
                </tr>
                <tr>
                    <td>Break</td>
                    <td className='tb-text'>{60}</td>
                    <td className='tb-text'>-</td>
                    <td className='tb-text'>{60}</td>
                </tr>
            </tbody>
        </table>

        <div className="damage-title header">Weakness Break</div>
            <table className="table table-striped attack-table">
                <thead>
                    <tr>
                        <th scope="col">Type</th>
                        <th scope="col">Value</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>Break Damage</td>
                        <td className='tb-text'>{(breakDmg).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Action Delay</td>
                        <td className='tb-text'>{(delay * 100).toFixed(0)}%</td>
                    </tr>
                    <tr>
                        <td>Entanglement 1 stack</td>
                        <td className='tb-text'>{(entanglement).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Entanglement 2 stack</td>
                        <td className='tb-text'>{(entanglement * 2).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Entanglement 3 stack</td>
                        <td className='tb-text'>{(entanglement * 3).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Entanglement 4 stack</td>
                        <td className='tb-text'>{(entanglement * 4).toFixed(0)}</td>
                    </tr>
                    <tr>
                        <td>Entanglement 5 stack</td>
                        <td className='tb-text'>{(entanglement * 5).toFixed(0)}</td>
                    </tr>
                </tbody>
            </table>
        </div>
    )
}


export default QingqueTable;