export class Dayone {
    constructor() {
        this.stats = [
            [1, 16.8, 21, 43],
            [20, 64.68, 80.85, 166],
            [20, 84.84, 106.05, 218],
            [40, 162.12, 202.65, 417],
            [40, 189, 236.25,  486],
            [50, 214.2, 267.75,  551],
            [50, 241.08, 301.35, 620],
            [60, 266.28, 332.85, 685],
            [60, 293.16, 366.45, 754],
            [70, 318.36, 397.95, 819],
            [70, 345.24, 431.55, 888],
            [80, 370.44, 463.05, 953],
        ];
        
        this.name = "Day One of My New Life";
        this.id = "dayone";
        this.path = "Preservation";
        this.rarity = "fourStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text"> DEF </div>
                <div> by </div>
                <div className="buff-text">{((0.16 + imposition * 0.02) * 100).toFixed(1)}% </div>
                <div> After entering battle, increases All-Type RES of all allies by </div>
                <div className="buff-text">{((0.08 + imposition * 0.01) * 100).toFixed(1)}% </div>
                <div> Effects of the same type cannot stack. </div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{
                defPercent: 0.16 + imposition * 0.02,
            },
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                // id: "adve1",
                // name: this.name,
                // description: (
                //     <div className="buff-description">
                //         <div> If the wearer's current HP is </div>
                //         <div className="buff-text"> lower than 50% </div>
                //         <div> increases their </div>
                //         <div className="buff-text"> DEF </div>
                //         <div> by a further </div>
                //         <div className="buff-text">{((0.16 + imposition * 0.04) * 100).toFixed(1)}% </div>
                //     </div>
                // ),
                // effect: {},
                // type: "buff",
                // hasStack: false,
                // hasInput: false,
            },
        }
    }
}

export default Dayone;