export class Battle {
    constructor() {
        this.stats = [
            [1,  24,   21,     53],
            [20, 92.4,  80.85,  203],
            [20, 121.2,  106.05,  267],
            [40, 231.6, 202.65, 510],
            [40, 270,   236.25, 594],
            [50, 306,  267.75, 673],
            [50, 344.4, 301.35, 758],
            [60, 380.4, 332.85, 837],
            [60, 418.8, 366.45, 921],
            [70, 454.8, 397.95, 1001],
            [70, 493.2, 431.55, 1085],
            [80, 529.2, 463.05, 1164],
        ];
        
        this.name = "But the Battle Isn't Over";
        this.path = "Harmony";
        this.id = "battle";
        this.rarity = "fiveStar";
    }

    weaponDescription(imposition) {
        return (
            <div className="buff-description">
                <div>Increases the wearer's </div>
                <div className="buff-text"> Energy Regeneration Rate </div> 
                <div> by </div> 
                <div className="buff-text">{((0.1 + imposition * 0.02) * 100).toFixed(1)}%</div>
                <div> and </div> 
                <div className="buff-text"> regenerates 1 Skill Point </div> 
                <div> when the wearer uses their Ultimate on an ally. This effect can be triggered once after every 2 uses of the wearer's Ultimate.</div>
                <div> When the wearer uses their  </div>
                <div className="buff-text"> Skill </div> 
                <div>, the next ally taking action (except the wearer) deals </div>
                <div className="buff-text">{((0.3 + imposition * 0.05) * 100).toFixed(1)}% more DMG for 1 turn(s).</div>
            </div>
        )
    }

    weaponBaseEffect(imposition) {
        return {
            id: "advsb",
            name: this.name,
            effect:{},
            type: "buff",
            hasStacks: false,
            hasInput: false,
        }
    }

    weaponEffect(imposition) {
        return {
            0: {
                id: "adve1",
                name: this.name,
                description: (
                    <div className="buff-description">
                        <div> When the wearer uses their  </div>
                        <div className="buff-text"> Skill </div> 
                        <div>, the next ally taking action (except the wearer) deals </div>
                        <div className="buff-text">{((0.3 + imposition * 0.05) * 100).toFixed(1)}% more DMG for 1 turn(s).</div>
                    </div>
                ),
                effect: {
                    iceDmg: 0.3 + imposition * 0.05,
                    quantumDmg: 0.3 + imposition * 0.05,
                    fireDmg: 0.3 + imposition * 0.05,
                    lightningDmg: 0.3 + imposition * 0.05,
                    windDmg: 0.3 + imposition * 0.05,
                    physicalDmg: 0.3 + imposition * 0.05,
                    imaginaryDmg: 0.3 + imposition * 0.05,
                },
                type: "buff",
                hasStack: false,
                hasInput: false,
            },
        }
    }
}

export default Battle;