import React, {useEffect, useState, useContext} from 'react';
import { globalData } from '../../../../globalData';

function RelicSubs(props) {

    const {
        setHpPercent,
        setAtkPercent,
        setDefPercent,
        setHpFlat,
        setAtkFlat,
        setDefFlat,
        setSpdFlat,
        setCritRate,
        setCritDmg,
        setEffectRes,
        setEffectHitRate,
        setBreakEffect,
    } = useContext(globalData);

    const functionMap = {
        'HP%': setHpPercent,
        'ATK%': setAtkPercent,
        'DEF%': setDefPercent,
        'HP': setHpFlat,
        'ATK': setAtkFlat,
        'DEF': setDefFlat,
        'SPD': setSpdFlat,
        'CR': setCritRate,
        'CD': setCritDmg,
        'ER': setEffectRes,
        'EHR': setEffectHitRate,
        'BE': setBreakEffect,
    }

    const {relicPiece, subIndex, initialSubstat} = props;
    const [subStat, setSubstat] = useState(initialSubstat);
    const [subStatValue, setSubstatValue] = useState(0);

    useEffect(() => {
        if(relicPiece) {
            relicPiece.subStats[subIndex].subType = subStat;
        }
    }, [subStat]);

    const handleSubChange = (event) => {
        if(subStat === "HP" || subStat === "ATK" || subStat === "DEF" || subStat === "SPD") {
            functionMap[subStat]((prevValue) => prevValue - (subStatValue));
        } else {
            functionMap[subStat]((prevValue) => prevValue - parseFloat(subStatValue / 100));
        }

        if(event.target.value === "HP" || event.target.value === "ATK" || event.target.value === "DEF" || event.target.value === "SPD") {
            functionMap[event.target.value]((prevValue) => prevValue + parseFloat(subStatValue));
        } else {
            functionMap[event.target.value]((prevValue) => prevValue + parseFloat(subStatValue / 100));
        }

        relicPiece.subStats[subIndex].subType = event.target.value;
        setSubstat(event.target.value);
    }

    const handleSubUpdate = (event) => {

        const re = /^(?:\d+(?:\.\d*)?|\.\d+)$/;
        if (event.target.value === '' || !re.test(event.target.value)) {
            
            if(subStat === "HP" || subStat === "ATK" || subStat === "DEF" || subStat === "SPD") {
                functionMap[subStat]((prevValue) => prevValue - (subStatValue));
            } else {
                functionMap[subStat]((prevValue) => prevValue - (subStatValue / 100));
            }
            
            setSubstatValue(0);
            return;
        }

        if(subStat === "HP" || subStat === "ATK" || subStat === "DEF" || subStat === "SPD") {
            functionMap[subStat]((prevValue) => prevValue - (subStatValue));
            functionMap[subStat]((prevValue) => prevValue + parseFloat(event.target.value));
        } else {
            functionMap[subStat]((prevValue) => prevValue - (subStatValue / 100));
            functionMap[subStat]((prevValue) => prevValue + parseFloat(event.target.value / 100));
        }

        relicPiece.subStats[subIndex].value = parseFloat(event.target.value);
        setSubstatValue(event.target.value);
    }

    return (
        <div className="relic-substats">
            <select name="level" id="level" className='relic-substat-option' onChange={handleSubChange} value={subStat}>
                <option value="SPD">SPD</option>
                <option value="HP">HP</option>
                <option value="HP%">HP%</option>
                <option value="ATK">ATK</option>
                <option value="ATK%">ATK%</option>
                <option value="DEF">DEF</option>
                <option value="DEF%">DEF%</option>
                <option value="BE">Break Effect%</option>
                <option value="EHR">Effect Hit Rate%</option>
                <option value="ER">Effect Res%</option>
                <option value="CR">CRIT Rate%</option>
                <option value="CD">CRIT DMG%</option>
            </select>
            <div>+</div>
            <input type='text' className='substate-value' maxLength={4} onChange={handleSubUpdate} value={subStatValue}></input>
        </div>
    )
}

export default RelicSubs;