class Sampo {
    constructor() {
        this.stats = [
            {level: 1,  atk: 84,    def: 54,    hp: 139, spd: 102, taunt: 100, energy: 120},
            {level: 20, atk: 163.8, def: 105.3, hp: 271, spd: 102, taunt: 100, energy: 120, atkPercent: 0.04},
            {level: 20, atk: 197.4, def: 126.9, hp: 327, spd: 102, taunt: 100, energy: 120, atkPercent: 0.04},
            {level: 40, atk: 315,   def: 202.5, hp: 522, spd: 102, taunt: 100, energy: 120, atkPercent: 0.04, effectHitRate: 0.04},
            {level: 40, atk: 348.6, def: 224.1, hp: 578, spd: 102, taunt: 100, energy: 120, atkPercent: 0.08, effectHitRate: 0.04, effectRes: 0.04},
            {level: 50, atk: 390.6, def: 251.1, hp: 647, spd: 102, taunt: 100, energy: 120, atkPercent: 0.08, effectHitRate: 0.04, effectRes: 0.04},
            {level: 50, atk: 424.2, def: 272.7, hp: 703, spd: 102, taunt: 100, energy: 120, atkPercent: 0.14, effectHitRate: 0.04, effectRes: 0.04},
            {level: 60, atk: 466.2, def: 299.7, hp: 773, spd: 102, taunt: 100, energy: 120, atkPercent: 0.14, effectHitRate: 0.04, effectRes: 0.04},
            {level: 60, atk: 499.8, def: 321.3, hp: 828, spd: 102, taunt: 100, energy: 120, atkPercent: 0.2,  effectHitRate: 0.1, effectRes: 0.04},
            {level: 70, atk: 541.8, def: 348.3, hp: 898, spd: 102, taunt: 100, energy: 120, atkPercent: 0.2,  effectHitRate: 0.1,  effectRes: 0.04},
            {level: 70, atk: 575.4, def: 369.9, hp: 954, spd: 102, taunt: 100, energy: 120, atkPercent: 0.2,  effectHitRate: 0.1,  effectRes: 0.1},
            {level: 80, atk: 617.4, def: 396.9, hp: 1023,spd: 102, taunt: 100, energy: 120, atkPercent: 0.28, effectHitRate: 0.18, effectRes: 0.1},
        ];

        this.name = "Sampo";
        this.path = "Nihility";
        this.rarity = 'fourStar';
        this.element = "Wind";
        this.id = "sampo";
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Dazzling Blades",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return 0.28 + (level * 0.028);
        }else if(level < 10){
            return 0.42 + ((level - 5) * 0.035);
        }else {
            return 0.56 + ((level - 9) * 0.028);
        }
    }

    skillDescription(level) {
        return {
            name: "Ricochet Love",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy, and further deals DMG for 4 extra time(s), with each time dealing </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a random enemy. </div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level < 6) {
            return {
                multiplier: 0.96 + (level * 0.064),
                dotVulnerability: 0.2 + (level * 0.01),
            }
        }else if(level < 10){
            return {
                multiplier: 1.28 + ((level - 5) * 0.08),
                dotVulnerability: 0.25 + ((level - 5) * 0.0125),
            }    
        }else {
            return {
                multiplier: 1.6 + ((level - 9) * 0.064),
                dotVulnerability: 0.3 + ((level - 9) * 0.01),
            }
        }
    }

    ultimateDescription(level) {
        return {
            name: "Surprise Present",
            description: (
                <div className="buff-description">
                    <div>Deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).multiplier * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to all enemies, with a  </div>
                    <div className="buff-text"> 100% base chance </div>
                    <div>  to increase the targets' </div>
                    <div className="buff-text"> DoT taken </div>
                    <div> by </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).dotVulnerability * 100).toFixed(1)}% </div>
                    <div> for 2 turn(s). </div>
                </div>
            ),
        }
    }

    simpleUlt(level) {
        return {
            name: "Surprise Present",
            description: (
                <div className="buff-description">
                    <div>Increase the targets' </div>
                    <div className="buff-text"> DoT taken </div>
                    <div> by </div>
                    <div className="buff-text"> {(this.ultimateAttack(level).dotVulnerability * 100).toFixed(1)}% </div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 0.2 + (level * 0.02);
        }else if(level < 10){
            return 0.31 + ((level - 5) * 0.04);
        }else {
            return 0.52 + ((level - 9) * 0.02);
        }
    }

    talentDescription(level) { 
        return {
            name: "Windtorn Dagger",
            description: (
                <div className="buff-description">
                    <div>Sampo's attacks have a 65% base chance to inflict </div>
                    <div className={this.element}> {this.element} Shear </div>
                    <div>3 turn(s). </div>
                    <br/>
                    <div> Enemies inflicted with Wind Shear will take </div>
                    <div className={this.element}> Wind DoT </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.talent(level) * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> at the beginning of each turn. </div>
                    <br/>
                    <div> Wind Shear can stack up to 5 time(s).</div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Shining Bright",
            description: (
                <div className="buff-description">
                    <div> After Sampo uses his Technique, enemies in a set area are inflicted with Blind for 10 second(s). Blinded enemies cannot detect your team. </div>
                    <br/>
                    <div> When initiating combat against a Blinded enemy, there is a 100% fixed chance to delay all enemies' action by 25%. </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Trap",
            description: (
                <div className="buff-description">
                    <div> Extends the duration of </div>
                    <div className={this.element}> {this.element} Shear </div>
                    <div> caused by Talent by 1 turn(s). </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Defensive Position",
            description: (
                <div className="buff-description">
                    <div> Using Ultimate additionally regenerates 10 Energy. </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Spice Up",
            description: (
                <div className="buff-description">
                    <div> Enemies with Wind Shear effect deal 15% less DMG to Sampo. </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Rising Love",
            description: (
                <div className="buff-description">
                    <div> When using Skill, deals DMG for 1 extra time(s) to a random enemy. </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Infectious Enthusiasm",
            description: (
                <div className="buff-description">
                    <div> Defeating an enemy with Wind Shear has a 100% base chance to inflict all enemies with 1 stack(s) of Wind Shear, equivalent to the Talent's Wind Shear. </div>
                </div>
            )
        }
    }

    E3() {
        return {
            name: "Big Money!",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }

    E4() {
        return {
            name: "The Deeper the Love, the Stronger the Hate",
            description:(
                <div className="buff-description">
                    <div>When Skill hits an enemy with 5 or more stack(s) of Wind Shear, the enemy immediately takes 8% of current Wind Shear DMG. </div>
                </div>
            )
        }
    }

    E5() {
        return {
            name: "Huuuuge Money!",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: "Increased Spending",
            description: (
                <div className="buff-description">
                    <div>Talent's Wind Shear DMG multiplier increases by 15%. </div>
                </div>
            ),
        }
    }
}


export default Sampo;