class Himeko {
    constructor() {
        this.stats = [
            {level: 1,  atk: 102.96, def: 59.4,   hp: 143, spd: 96, taunt: 75, energy: 120},
            {level: 20, atk: 200.77, def: 115.83, hp: 278, spd: 96, taunt: 75, energy: 120, fireDmg: 0.032},
            {level: 20, atk: 241.96, def: 139.59, hp: 335, spd: 96, taunt: 75, energy: 120, fireDmg: 0.032},
            {level: 40, atk: 386.1,  def: 222.75, hp: 535, spd: 96, taunt: 75, energy: 120, fireDmg: 0.032, atkPercent: 0.04},
            {level: 40, atk: 427.28, def: 246.51, hp: 592, spd: 96, taunt: 75, energy: 120, fireDmg: 0.064, atkPercent: 0.04, effectRes: 0.04},
            {level: 50, atk: 478.76, def: 276.21, hp: 663, spd: 96, taunt: 75, energy: 120, fireDmg: 0.064, atkPercent: 0.04, effectRes: 0.04},
            {level: 50, atk: 519.95, def: 299.97, hp: 720, spd: 96, taunt: 75, energy: 120, fireDmg: 0.112, atkPercent: 0.04, effectRes: 0.04},
            {level: 60, atk: 571.43, def: 329.67, hp: 791, spd: 96, taunt: 75, energy: 120, fireDmg: 0.112, atkPercent: 0.1,  effectRes: 0.04},
            {level: 60, atk: 612.61, def: 353.43, hp: 848, spd: 96, taunt: 75, energy: 120, fireDmg: 0.16,  atkPercent: 0.1,  effectRes: 0.04},
            {level: 70, atk: 664.09, def: 383.13, hp: 920, spd: 96, taunt: 75, energy: 120, fireDmg: 0.16,  atkPercent: 0.1,  effectRes: 0.04},
            {level: 70, atk: 705.28, def: 406.89, hp: 977, spd: 96, taunt: 75, energy: 120, fireDmg: 0.16,  atkPercent: 0.1,  effectRes: 0.1},
            {level: 80, atk: 756.76, def: 436.59, hp: 1048,spd: 96, taunt: 75, energy: 120, fireDmg: 0.224, atkPercent: 0.18, effectRes: 0.1},
        ];

        this.name = "Himeko";
        this.path = "Erudition";
        this.rarity = 'fiveStar';
        this.element = "Fire";
        this.id = "himeko";
    }

    basicAttack(level) {
        return 0.5 + (level * 0.1);
    }

    basicDescription(level) {
        return {
            name: "Sawblade Tuning",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.basicAttack(level) * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                </div>
            ),
        }
    }

    skillAttack(level) {
        if(level < 6) {
            return {
                single: 1 + (level * 0.1),
                splash: 0.4 + (level * 0.04),
            }
        }else if(level < 10){
            return {
                single: 1.5 + ((level - 5) * 0.12),
                splash: 0.6 + ((level - 5) * 0.05),
            }
        }else {
            return {
                single: 2 + ((level - 9) * 0.1),
                splash: 0.8 + ((level - 9) * 0.04),
            }
        }
    }

    skillDescription(level) {
        return {
            name: "Molten Detonation",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level).single * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to a single enemy. </div>
                    <div>and deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text">{(this.skillAttack(level).splash * 100).toFixed(1)}%</div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text">ATK</div>
                    <div> to enemies adjacent to it. </div>
                </div>
            ),
        }
    }

    ultimateAttack(level) {
        if(level < 6) {
            return 1.38 + (level * 0.092);
        }else if(level < 10){
            return 1.84 + ((level - 5) * 0.115);
        }else {
            return 2.29 + ((level - 9) * 0.092);
        }
    }

    ultimateDescription(level) {
        return {
            name: "Heavenly Flare",
            description: (
                <div className="buff-description">
                    <div>Deals</div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.ultimateAttack(level) * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to all enemies. </div>
                    <br/>
                    <div>Himeko regenerates 5 extra Energy for each enemy defeated.</div>
                </div>
            ),
        }
    }

    talent(level) {
        if(level < 6) {
            return 0.7 + (level * 0.07);
        }else if(level < 10){
            return 1.05 + ((level - 5) * 0.0875);
        }else {
            return 1.4 + ((level - 9) * 0.07);
        }
    }

    talentDescription(level) { 
        return {
            name: "Superiority of Reach",
            description: (
                <div className="buff-description">
                    <div>When an enemy is inflicted with Weakness Break, Himeko gains 1 point of Charge (max 3 points).</div>
                    <br/>
                    <div>If Himeko is fully Charged when an ally performs an attack, Himeko immediately performs 1 follow-up attack and deals </div>
                    <div className={this.element}> {this.element} DMG </div>
                    <div> equal to </div>
                    <div className="buff-text"> {(this.talent(level) * 100).toFixed(1)}% </div>
                    <div> of {this.name}'s </div>
                    <div className="buff-text"> ATK </div>
                    <div> to all enemies. </div>
                    <div> consuming all Charge points. </div>
                    <br/>
                    <div> At the start of the battle, Himeko gains 1 point of Charge. </div>
                </div>
            ),
        }
    }

    techniqueDescription() {
        return {
            name: "Incomplete Combustion",
            description: (
                <div className="buff-description">
                    <div> After using Technique, creates a dimension that lasts for 15 second(s). After entering battle with enemies in the dimension, there is a 100% base chance to increase Fire DMG taken by enemies by 10% for 2 turn(s). Only 1 dimension created by allies can exist at the same time. </div>
                </div>
            ),
        }
    }

    techSimple() {
        return {
            name: "Incomplete Combustion",
            description: (
                <div className="buff-description">
                    <div> Increase </div>
                    <div className="Fire"> Fire DMG </div>
                    <div> taken by enemies by </div>
                    <div className="buff-text"> 10% </div>
                </div>
            ),
        }
    }

    A2() {
        return {
            name: "Starfire",
            description: (
                <div className="buff-description">
                    <div> After using an attack, there is a 50% base chance to inflict Burn on enemies for 2 turn(s). When afflicted with Burn, enemies take Fire DoT equal to 30% of Himeko's ATK at the start of each turn. </div>
                </div>
            ),
        }
    }

    A4() {
        return {
            name: "Magma",
            description: (
                <div className="buff-description">
                    <div> Skill deals 20% more DMG to enemies currently afflicted with Burn. </div>
                </div>
            ),
        }
    }

    A6() {
        return {
            name: "Benchmark",
            description: (
                <div className="buff-description">
                    <div> When current HP percentage is 80% or higher, CRIT Rate increases by 15% </div>
                </div>
            )
        }
    }

    E1() {
        return {
            name: "Childhood",
            description: (
                <div className="buff-description">
                    <div> After "Victory Rush" is triggered, Himeko's SPD increases by 20% for 2 turn(s). </div>
                </div>
            )
        }
    }

    E2() {
        return {
            name: "Convergence",
            description: (
                <div className="buff-description">
                    <div> Deals 15% more DMG to enemies whose HP percentage is 50% or less. </div>
                </div>
            )
        }
    }

    E3() {
        return {
            name: "Poised",
            description: (
                <div className="buff-description">
                    <div>Skill Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Basic ATK Lv </div>
                    <div className="buff-text">  +1 </div>
                    <div> up to a maximum of Lv. 10. </div>
                </div>
            ),
        }
    }

    E4() {
        return {
            name: "Dedication",
            description:(
                <div className="buff-description">
                    <div>When Himeko's Skill inflicts Weakness Break on an enemy, she gains 1 extra point(s) of Charge.</div>
                </div>
            )
        }
    }

    E5() {
        return {
            name: "Aspiration",
            description: (
                <div className="buff-description">
                    <div>Ultimate Lv. </div>
                    <div className="buff-text"> +2 </div>
                    <div> up to a maximum of Lv. 15. Talent Lv </div>
                    <div className="buff-text">  +2 </div>
                    <div> up to a maximum of Lv. 15. </div>
                </div>
            ),
        }
    }

    E6() {
        return {
            name: "Trailblaze!",
            description: (
                <div className="buff-description">
                    <div>Ultimate deals DMG 2 extra times, each of which deals Fire DMG equal to 40% of the original DMG to a random enemy.</div>
                </div>
            ),
        }
    }
}


export default Himeko;