import React from 'react';
import {Dialog, DialogTitle, DialogContent} from '@mui/material';
import fire from "../../../assets/elements/Fire.webp";
import quantum from "../../../assets/elements/Quantum.webp";
import ice from "../../../assets/elements/Ice.webp";
import lightning from "../../../assets/elements/Lightning.webp";
import wind from "../../../assets/elements/Wind.webp";
import physical from "../../../assets/elements/Physical.webp";
import imaginary from "../../../assets/elements/Imaginary.webp";
import hunt from "../../../assets/paths/Hunt.webp";
import Abundance from "../../../assets/paths/Abundance.webp";
import Destruction from "../../../assets/paths/Destruction.webp";
import Harmony from "../../../assets/paths/Harmony.webp";
import Nihility from "../../../assets/paths/Nihility.webp";
import Preservation from "../../../assets/paths/Preservation.webp";
import Erudition from "../../../assets/paths/Erudition.webp";

function SelectionMenu(props) {

    const titleText = props.text;
    const handleSelection = props.handleSelection;
    const itemList = props.characterList;
    const open = props.open;
    const setOpen = props.setOpen;
    const images = props.images;

    const elementsMap = {
        Fire: fire,
        Quantum: quantum,
        Ice: ice,
        Lightning: lightning,
        Wind: wind,
        Physical: physical,
        Imaginary: imaginary,
    }

    const pathsMap = {
        Hunt: hunt,
        Abundance: Abundance,
        Destruction: Destruction,
        Harmony: Harmony,
        Nihility: Nihility,
        Preservation: Preservation,
        Erudition: Erudition,
    }

    const handleEvent = (event) => {
        handleSelection(event);
        setOpen(false);
    }

    return (
        <div className="dialog">
            <Dialog aria-label='dialog-title' open={open} onClose={() => setOpen(false)} maxWidth="lg" fullWidth>
                <DialogTitle className='dialog-container' id='dialog-title'>
                    <div style={{display: "flex", justifyContent: "space-between", alignItems: "center"}}>
                        <div>{titleText}</div>
                        <button onClick={() => setOpen(false)} className='calc-btn' style={{width: "37px", display: "flex", backgroundColor: "red", textAlign:"center", alignItems: "center", justifyContent: "center", margin: "0"}}>&#10005;</button>
                    </div>
                </DialogTitle>
                <DialogContent className='dialog-content'>
                    <div className='character-list'>
                        {itemList.map((item, index) => (
                            <div className={`character-card ${item.rarity}`} onClick={(event) => handleEvent(event)}  data-id={item.id} key={index}>
                                <div className='test' data-id={item.id}>
                                    <img src={elementsMap[item.element]} width={35} className='character-element' data-id={item.id} alt={item.element}></img>
                                    <img src={pathsMap[item.path]} width={35} className='character-path' data-id={item.id} alt={item.path}></img>
                                </div>
                                <div className='centered' data-id={item.id}>
                                    <img src={images[index]} width={131} height={140} className='character-image' data-id={item.id} alt=""></img>
                                </div>
                                <div className={`selection-name`} data-id={item.id}>{item.name}</div>
                            </div>
                        ))}
                    </div>
                </DialogContent>
            </Dialog>
        </div>
    )
}

export default SelectionMenu;