import { useState, useEffect, useContext } from "react";
import { globalData } from "../../../../../../../globalData";
import { Checkbox,  FormControlLabel } from '@mui/material';

import { Welt } from "../../../../../../../database/characters";

const WeltBuffs = () => {

    const {level, eidolon, talentLevel, character, characterBuffStates, setCharacterBuffStates, updateStatesFromJson } = useContext(globalData);
    
    const welt = new Welt();

    const applyCharacterBuffs = (index, buffValue) => {
        const updatedStates = [...characterBuffStates];
        updateStatesFromJson(buffValue, updatedStates[index]);
        updatedStates[index] = !updatedStates[index];
        setCharacterBuffStates(updatedStates);

    }
    
    const [talent, setTalent] = useState(false);
    const [E1, setE1] = useState(false);

    const A6Buff = {
        effect: {
            iceDmg: 0.2,
            quantumDmg: 0.2,
            fireDmg: 0.2,
            lightningDmg: 0.2,
            windDmg: 0.2,
            physicalDmg: 0.2,
            imaginaryDmg: 0.2,
        }
    }

    useEffect(() => {
        if(eidolon < 1 && E1) {
            setE1(!E1);
            character.setE1ON(!E1);
            const updatedStates = [...characterBuffStates];
            setCharacterBuffStates(updatedStates);
        }
    }, [eidolon]);

    useEffect(() => {
        
        if(level < 11 && characterBuffStates[2]) {
            updateStatesFromJson(A6Buff, true);
            characterBuffStates[2] = false;
        }

    }, [level]);

    const turnE1On = (index) => {
        setE1(!E1);
        character.setE1ON(!E1);
        const updatedStates = [...characterBuffStates];
        setCharacterBuffStates(updatedStates);
    }

    const setTalentOn = () => {
        setTalent(!talent);
        character.setTalentON(!talent);
        const updatedStates = [...characterBuffStates];
        setCharacterBuffStates(updatedStates);
    }

    return (
        <>
            <FormControlLabel
            className='header'
            control={<Checkbox checked={talent || false} onChange={setTalentOn} />}
            label={ welt.talentDescription(talentLevel).name}
            />
            <div>{welt.talentDescription(talentLevel).description}</div>

            {level >= 11 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={characterBuffStates[2] || false} onChange={() => applyCharacterBuffs(2, A6Buff)} />}
                    label={"(A6) " + welt.A6().name}
                    />
                <div>{welt.A6().description}</div>
            </>
            }

            {eidolon >= 1 &&
                <>
                    <FormControlLabel
                    className='header'
                    control={<Checkbox checked={E1 || false} onChange={turnE1On} />}
                    label={"(E1) " + welt.E1().name}
                    />
                    <div>{welt.E1().description}</div>
                </>
            }
        </>
    )
}

export const WeltGlobalBuffs = (props) => {

    const characterSwitched = props.characterSwitched;
    const setDone = props.setCharacterDoneSwitch;
    const done = props.characterDoneSwitch;

    useEffect(() => {
        setDone(!done);
    }, [characterSwitched]);

    return (
        <>
            <div>No Buffs</div>
        </>
    )
}   

export default WeltBuffs;